const planContentMutation = `mutation ($sysId: String!, $contentFieldValue: String!, $statusFieldValue: String!) {
	GlideRecord_Mutation {
        update_{{ tableName }} (
            sys_id: $sysId
            {{ contentField }}: $contentFieldValue
            {{ statusField }}: $statusFieldValue
        ) {
			sys_id {
				value
			}
			{{ contentField }} {
				value
            }
            {{ statusField }} {
                value
            }
		}
	}
}`;

export default planContentMutation;
