import { actionTypes } from '@servicenow/ui-core';
import { DEPENDENCY_ASSESSMENT_DATA_REQUESTED } from '../constants/BiaDependencyAssessmentActionTypes';
import { FETCH_PARENT_TABLE_METADATA_REQUESTED } from '../constants/ImpactAnalysisMetaDataActionTypes';
import { PARENT_TABLE_NAME } from '../constants/BiaDependencyAssessmentConstants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const fetchDependencyAssessmentData = ({ properties, dispatch }) => {
	const {
		parentRecordSysId,
		tableName,
		dependencyAssessmentChildTable,
		dependencyAssessmentStatusField,
		accordionDescriptionField,
		accordionHeaderField,
		dependencyElementReferenceColumnField,
		dependencyAssessmentRecoveryTimeField,
		dependencyAssessmentChildTableField,
		parentTableReferenceField,
	} = properties;

	dispatch(FETCH_PARENT_TABLE_METADATA_REQUESTED, {
		parentRecordSysId,
		parentTable: PARENT_TABLE_NAME,
	});

	dispatch(DEPENDENCY_ASSESSMENT_DATA_REQUESTED, {
		parentRecordSysId,
		tableName,
		dependencyAssessmentChildTable,
		dependencyAssessmentStatusField,
		accordionDescriptionField,
		accordionHeaderField,
		dependencyElementReferenceColumnField,
		dependencyAssessmentRecoveryTimeField,
		dependencyAssessmentChildTableField,
		parentTableReferenceField,
	});
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect: fetchDependencyAssessmentData,
	},
};

export default actionHandlers;
