const getSourceTableDataQuery = `query getSourceTableData {
	GlideRecord_Query {
		sn_bia_template(queryConditions: "sys_id={{sys_id}}") {
				_results {
					name {
						value
					}
				primary_element_assessed {
					_reference {
						name {
							value
						}
						source_table {
							value
							displayValue
						}
					}
				}
			}
		}
	}
}`;

export default getSourceTableDataQuery;
