import { t } from 'sn-translate';
import { get, camelCase, startCase, capitalize } from 'lodash';
import { DEFAULT_REFERENCE_FIELD_NAME } from '../constants/NewBiaDynamicModalConstants';
import { CREATE_BIA_QUERY_INITIATED } from '../constants/NewBiaDynamicModalActionTypes';

const checkMandatoryFields = (state, updateState, dispatch) => {
	const { mandatoryFields } = state;
	const emptyMandatoryFields = [];

	mandatoryFields.map((field) => {
		if (field) {
			const attr = `${camelCase(field)}HasValue`;

			if (!state[field]) {
				emptyMandatoryFields.push(capitalize(startCase(field)));
				updateState({
					[attr]: false,
				});
			} else {
				updateState({
					[attr]: true,
				});
			}
		}
	});

	if (emptyMandatoryFields.length) {
		const alertMessage =
			t(` Please fill the following fields `) + emptyMandatoryFields.join(', ');

		updateState({ showErrorMessage: true, alertMessage, isLoading: false });
	} else {
		updateState({ showErrorMessage: false });
		dispatch(CREATE_BIA_QUERY_INITIATED);
	}
};

const getModalElements = (state, updateState) => {
	const { formElements } = state;
	const renderElements = formElements.map((element) => {
		const type = get(element, `dictionary.type`);
		const label = get(element, `dictionary.label`);
		const isMandatory = get(element, `dictionary.isMandatory`);
		const { name } = element;
		const value = get(state, name);
		let modalInnerFormElement;
		const isReadOnly = !!get(element, 'dictionary.sys_readonly');

		switch (type) {
			case 'string': {
				const maxlength = get(element, 'maxlength');

				modalInnerFormElement = (
					<sn-record-input-connected
						name={name}
						required={isMandatory}
						initialValue={value || ''}
						onValueChange={(event) => updateState({ [name]: event.value })}
						maxlength={maxlength}
						readonly={isReadOnly}
					/>
				);
				break;
			}

			case 'reference': {
				const referenceTable = get(element, `reference`);
				let referenceQualifier = get(element, 'referenceQualifier');
				const dependentField = get(element, 'dictionary.dependentField');

				if (dependentField) {
					const dependentFieldValue = get(state, dependentField);

					referenceQualifier += `^${dependentField}=${dependentFieldValue}^EQ`;
				}

				modalInnerFormElement = (
					<now-record-field-lookup-connected
						required={isMandatory}
						name={name}
						tableName={referenceTable}
						fieldName={DEFAULT_REFERENCE_FIELD_NAME}
						readonly={isReadOnly}
						value={value}
						displayValue={state[`${name}DisplayValue`]}
						additionalQueryString={referenceQualifier || ''}
					/>
				);
				break;
			}

			case 'table_name': {
				const { appliesToTableDisplayValue } = state;

				modalInnerFormElement = (
					<sn-record-input-connected
						initialValue={appliesToTableDisplayValue}
						readonly
						name={name}
					/>
				);
				break;
			}

			case 'document_id': {
				const dependentField = get(element, 'dictionary.dependentField');

				modalInnerFormElement = (
					<now-record-field-lookup-connected
						required={isMandatory}
						name={name}
						tableName={state[dependentField]}
						fieldName={DEFAULT_REFERENCE_FIELD_NAME}
						readonly={isReadOnly}
						value={value}
						displayValue={state[`${name}DisplayValue`]}
					/>
				);
				break;
			}

			case 'choice': {
				const { choices } = element;

				modalInnerFormElement = (
					<sn-record-choice
						name={name}
						value={value}
						displayValue={state[`${name}DisplayValue`]}
						choices={choices}
						readonly={isReadOnly}
					/>
				);
				break;
			}

			default: {
				modalInnerFormElement = (
					<div>
						<sn-record-input-connected value={value} name={name} readonly />
						<p>* Field type{type} is not supported.</p>
					</div>
				);
				break;
			}
		}

		const attr = `${camelCase(name)}HasValue`;
		const className =
			state[attr] === false && isMandatory ? 'label has-error' : 'label';

		return (
			<div className="modal-elements">
				<p className={className}>
					{label}
				</p>
				{modalInnerFormElement}
			</div>
		);
	});

	return renderElements;
};

export { getModalElements, checkMandatoryFields };