import { createAmbSubscriptionEffect as _createAmbSubscriptionEffect } from '@seismic/effect-amb';

const getRecordWatcherFilter = (paramString) => {
	return btoa(paramString).replace(/=/g, '-');
};

const extractParamsFromSubscriptionId = (subscriptionId) => {
	const [table, filter] = subscriptionId.split('/rw/default/')[1].split('/');

	return {
		table,
		filter
	};
};

export {
	getRecordWatcherFilter,
	extractParamsFromSubscriptionId,
};
