import { actionTypes } from '@servicenow/ui-core';
import {
	ITEM_PICKER_TABLE_NAME_REQUESTED,
	BIA_ITEM_PICKER_TABLE_NAME_REQUESTED,
	FETCH_ELEMENT_DEFINITIONS,
} from '../constants/ItemPickerModalActionTypes';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ state, dispatch, updateState }) => {
		const { properties } = state;
		const {
			parentTable,
			parentRecordSysId,
			itemPickerLoadingMessage,
			elementDefinitionTableReferenceField,
			parentTableItemPickerReferenceField,
			isImpactAnalysis,
			isRecoveryEvent,
		} = properties;

		updateState({
			loadingMessage: itemPickerLoadingMessage,
		});
		if (isImpactAnalysis) {
			dispatch(BIA_ITEM_PICKER_TABLE_NAME_REQUESTED, {
				parentRecordSysId,
				parentTable,
				elementDefinitionTableReferenceField,
			});
		} else if (isRecoveryEvent) {
			dispatch(FETCH_ELEMENT_DEFINITIONS);

			updateState({
				isLoading: false,
			});
		} else {
			dispatch(ITEM_PICKER_TABLE_NAME_REQUESTED, {
				parentRecordSysId,
				parentTable,
				parentTableItemPickerReferenceField,
				elementDefinitionTableReferenceField,
			});
		}
	},
};

export default actionHandlers;
