import { actionTypes } from '@servicenow/ui-core';
import { FETCH_PLAN_ASSET_DETAILS_QUERY_REQUESTED } from '../constants/PlanAssetScoreCardActionTypes';
import { RECORD_WATCHER_SUBSCRIPTION_REQUESTED } from '../../../behaviors/RecordWatcherBehavior';
import {
	PLAN_ASSET_TABLE,
} from '../constants/PlanAssetScoreCardConstants';


const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const fetchPlanAssetsDetails = ({properties, dispatch}) =>{
    const { sysid, isRecordWatcherEnabled } = properties;

    dispatch(FETCH_PLAN_ASSET_DETAILS_QUERY_REQUESTED, {
		parentRecordSysId: sysid,
	});
	if (isRecordWatcherEnabled) {
		dispatch(RECORD_WATCHER_SUBSCRIPTION_REQUESTED, {
			table: PLAN_ASSET_TABLE,
			filter: `plan=${sysid}`,
			onItemChangeAction: {
				actionType: FETCH_PLAN_ASSET_DETAILS_QUERY_REQUESTED,
				actionPayload: {
					parentRecordSysId: sysid,
				},
			},
		});
	}
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect: fetchPlanAssetsDetails,
	},
};

export default actionHandlers;