import { createCustomElement } from '@servicenow/ui-core';
import { snabbdom } from '@servicenow/ui-renderer-snabbdom';
import view from './PlanActivitiesListView';
import styles from './PlanActivitiesListElement.scss';
import {
	WORKSPACE_VIEW,
	RECOVERY_TASK_TABLE,
	RECOVERY_TASK_GRID_CONFIG,
} from './constants/PlanActivitiesListColumnConstants';
import planActivitiesParentMetadataActionHandlers from './actions/PlanActivitiesListLifecycleActions';

const TAG_NAME = 'sn-bcm-recovery-task-list';

/**
 * Displays list of related elements for plan
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-recovery-task-list
 * 		table-name=""
 * 		planid=""
 * 		record-load-limit=""
 * ></sn-bcm-recovery-task-list>
 * ```
 *
 * @seismicElement sn-bcm-recovery-task-list
 * @summary Displays list of related elements for plan
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,
	styles,
	initialState: {
		isLoading: true,
		config: RECOVERY_TASK_GRID_CONFIG,
		hasWriteAccess: false,
	},
	properties: {
		/**
		 * The table name on which data should be queried on.
		 *
		 * @type {string}
		 */
		tableName: {
			default: RECOVERY_TASK_TABLE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Sysid of the record in parent table
		 *
		 * @type {string}
		 */
		parentRecordSysId: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * This value determines the maximum number of records loaded in the view
		 *
		 * @type {integer}
		 */
		recordLoadLimit: {
			default: 1000,
			required: false,
			schema: {
				type: 'integer',
			},
		},
		/**
		 * The name of the parent table of the table from which data should be queried on.
		 *
		 * @type {string}
		 */
		parentTable: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The query to be applied for filtering the records
		 *
		 * @type {string}
		 */
		queryCondition: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The view name of the form to be dispalyed on the modal.
		 *
		 * @type {string}
		 */
		modalViewName: {
			default: WORKSPACE_VIEW,
			required: false,
			schema: {
				type: 'string',
			},
		},
	},
	actionHandlers: {
		...planActivitiesParentMetadataActionHandlers,
	},
});

export default TAG_NAME;
