import get from 'lodash/get';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { EDITABLE_LIST_UPDATE_ACTION_TYPES } from '../constants/EditableListCRUDActionTypes';
import { EDITABLE_LIST_QUERY_ACTION_TYPES } from '../constants/EditableListCRUDActionTypes';
import { EDITABLE_LIST_UPDATE_QUERY } from '../queries/EditableListQueries';
import { parseFetchUpdatedRecords } from '../utils/EditableListUtils';

const recordUpdateRequested = createGraphQLEffect(EDITABLE_LIST_UPDATE_QUERY, {
	templateVarList: ['tableName', 'values', 'columns'],
	variableList: ['sysId'],
	successActionType:
		EDITABLE_LIST_UPDATE_ACTION_TYPES.EDITABLE_LIST_UPDATE_SUCCEEDED,
	errorActionType:
		EDITABLE_LIST_UPDATE_ACTION_TYPES.EDITABLE_LIST_UPDATE_FAILED,
});

const recordUpdateFailure = ({ action, properties, updateState }) => {
	const { tableName } = properties;
	const error = action.payload;

	console.error(`An error occured attempting to update data to ${tableName}`);
	console.error(error);
	updateState({
		error: true,
		dataUpdating: false,
	});
};

const recordUpdateSuccess = ({ action, state, updateState, dispatch }) => {
	const { payload } = action;
	const { listRecords, rowEditing } = state;
	const { taskId: _recordSysId } = rowEditing;
	const { tableName, parentRecordIdentification } = state.properties;

	const _updatedRecordData = get(
		payload,
		`data.GlideRecord_Mutation.update_${tableName}`,
		null
	);

	const _updateRecordIndex = listRecords.findIndex(
		(record) => record.id === _recordSysId
	);
	const _updatedRecords = parseFetchUpdatedRecords(
		listRecords,
		_updateRecordIndex,
		_updatedRecordData
	);

	updateState({
		dataUpdating: false,
		rowEditing: null,
		listRecords: _updatedRecords,
	});
	// this action is dispatched to update the parent component with child table records.
	dispatch(EDITABLE_LIST_QUERY_ACTION_TYPES.EDITABLE_LIST_DATA_FETCHED, {listRecords:_updatedRecords, parentRecordIdentification});
};

const actionHandlers = {
	[EDITABLE_LIST_UPDATE_ACTION_TYPES.EDITABLE_LIST_UPDATE_REQUESTED]: recordUpdateRequested,
	[EDITABLE_LIST_UPDATE_ACTION_TYPES.EDITABLE_LIST_UPDATE_SUCCEEDED]: {
		effect: recordUpdateSuccess,
		stopPropogation: true,
	},
	[EDITABLE_LIST_UPDATE_ACTION_TYPES.EDITABLE_LIST_UPDATE_FAILED]: {
		effect: recordUpdateFailure,
		stopPropogation: true,
	},
};

export default actionHandlers;
