import { createAmbSubscriptionEffect } from '@servicenow/ui-effect-amb';
import { actionTypes } from '@servicenow/ui-core';
import {
	getRecordWatcherFilter,
	extractParamsFromSubscriptionId,
} from '../../util/ambSubscriptionUtil';
import {
	RECORD_WATCHER_SUBSCRIPTION_REQUESTED,
	RECORD_WATCHER_SUBSCRIPTION_STARTED,
	RECORD_WATCHER_SUBSCRIPTION_SUCCEEDED,
	RECORD_WATCHER_SUBSCRIPTION_FAILED,
	RECORD_WATCHER_UNSUBSCRIBED,
	RECORD_WATCHER_SUBSCRIPTION_STATUS_CHANGED,
	RECORD_WATCHER_ITEM_CHANGED,
} from './recordWatcherActionTypes';

const { COMPONENT_DISCONNECTED } = actionTypes;

const disconnectRecordWatcher = ({ state, dispatch }) => {
	const { recordWatcherConfiguration } = state;

	dispatch(RECORD_WATCHER_SUBSCRIPTION_STATUS_CHANGED, {
		...recordWatcherConfiguration,
		subscribe: false,
	});
};

const handleSubscriptionRequested = ({ action, updateState, dispatch }) => {
	const { table, filter, onItemChangeAction } = action.payload;
	const encodedFilter = getRecordWatcherFilter(filter);
	const recordWatcherConfiguration = {
		table,
		filter: encodedFilter,
		onItemChangeAction,
	};

	updateState({
		recordWatcherConfiguration,
	});

	dispatch(RECORD_WATCHER_SUBSCRIPTION_STATUS_CHANGED, {
		table,
		filter: encodedFilter,
		subscribe: true,
	});
};

const handleSubscriptionFailure = ({ action, updateState }) => {
	const { subscription } = action.payload.response;
	const { table } = extractParamsFromSubscriptionId(subscription);

	console.error(`Error in attaching record watcher for table: ${table}.`);

	updateState({
		recordWatcherConfiguration: null,
	});
};

const handleItemChanged = ({ state, dispatch }) => {
	const { recordWatcherConfiguration } = state;

	if (recordWatcherConfiguration) {
		const { onItemChangeAction } = recordWatcherConfiguration;
		const { actionType, actionPayload } = onItemChangeAction;

		dispatch(actionType, actionPayload);
	}
};

const handleRecordWatcher = createAmbSubscriptionEffect(
	'/rw/default/:table/:filter',
	{
		subscribeStartedActionType: RECORD_WATCHER_SUBSCRIPTION_STARTED,
		subscribeSucceededActionType: RECORD_WATCHER_SUBSCRIPTION_SUCCEEDED,
		subscribeFailedActionType: RECORD_WATCHER_SUBSCRIPTION_FAILED,
		unsubscribeSucceededActionType: RECORD_WATCHER_UNSUBSCRIBED,
		messageReceivedActionType: RECORD_WATCHER_ITEM_CHANGED,
	}
);

const recordWatcherBehavior = {
	name: 'RECORD_WATCHER_BEHAVIOR',
	actions: {
		[RECORD_WATCHER_SUBSCRIPTION_REQUESTED]: { private: true },
		[RECORD_WATCHER_SUBSCRIPTION_STATUS_CHANGED]: { private: true },
		[RECORD_WATCHER_SUBSCRIPTION_SUCCEEDED]: { private: true },
		[RECORD_WATCHER_SUBSCRIPTION_FAILED]: { private: true },
		[RECORD_WATCHER_ITEM_CHANGED]: { private: true },
		[RECORD_WATCHER_UNSUBSCRIBED]: { private: true },
	},
	actionHandlers: {
		[RECORD_WATCHER_SUBSCRIPTION_REQUESTED]: handleSubscriptionRequested,
		[RECORD_WATCHER_SUBSCRIPTION_STATUS_CHANGED]: handleRecordWatcher,
		[RECORD_WATCHER_SUBSCRIPTION_FAILED]: handleSubscriptionFailure,
		[RECORD_WATCHER_ITEM_CHANGED]: handleItemChanged,
		[COMPONENT_DISCONNECTED]: disconnectRecordWatcher,
	},
};

export default recordWatcherBehavior;
