import { PLAN_LOSS_SCENARIO_FIELD } from '../constants/AssetDependenciesDonutChartCardConstants';

const query = `query getRelatedAssets {
    GlideRecord_Query {
        {{relatedAssetDependenciesTable}} (queryConditions: "${PLAN_LOSS_SCENARIO_FIELD}={{sysId}}") {
            results: _results {
                sys_id {
                    value
                }
            }
        }
    }
    GlideRecord_Query {
        {{recoveryStrategiesTable}} (queryConditions: "${PLAN_LOSS_SCENARIO_FIELD}={{sysId}}") {
            results: _results {
                dependencies_covered {
                    value
                }
            }
        }
    }
}`;

export default query;
