import { actionTypes } from '@servicenow/ui-core';
import EXERCISE_DISPLAY_ACTIONS from '../constants/ExerciseDisplayConditionsActionTypes';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ state, dispatch }) => {
		const { sysId, table } = state.properties;

		dispatch(EXERCISE_DISPLAY_ACTIONS.EXERCISE_DISPLAY_CONDITIONS_REQUESTED, {
			planSysId: sysId,
		});
	},
};

export default actionHandlers;
