const query = `query getLandingPageData ($queryConditions: String!, $maxTypeaheadOptions: Int!) {
    GlideRecord_Query {
        {{selectionTable}}(queryConditions:$queryConditions, pagination: {limit: $maxTypeaheadOptions}) {
            results:_results {
                sys_id {
                    value
                }
                name {
                    value
                }
                nestedQuery:_query {
                    GlideDomain_Query {
                        userInfo(sys_id: "$$parent.sys_id$$") {
                            avatarPath
                        }
                    }
                }
            }
        }
    }
}`;

export default query;