import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './RecordModalView';
import styles from './RecordModalElement.scss';
import {
	DEFAULT_MODAL_SIZE,
	DEFAULT_FORM_VIEW,
} from './constants/RecordPreviewConstants';
import {
	PREVIEW_RECORD,
	RECORD_DATA_CHANGED,
	MODAL_CLOSE_CLICKED,
} from './constants/RecordModalActionTypes';
import { LOADING_VIEW_MESSAGE } from './constants/RecordModalConstants';
import {
	actions as modalVisibilityActions,
	actionHandlers as modalVisibilityActionHandlers,
} from './actions/modalVisibilityActions';
import lifeCycleActionHandlers from './actions/RecordModalLifeCycleActions';

const tagName = 'sn-bcm-record-modal';

/**
 * A modal to create and edit
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-record-modal
 * 		table=""
 * 		sys-id=""
 * 		form-view=""
 * 		header-label=""
 * 		action-button-label=""
 * 		cancel-button-label=""
 * 		delete-button-label=""
 * 		read-only-form=""
 * 		modal-size=""
 * 		query-conditions=""
 * </sn-bcm-record-modal>
 * ```
 *
 * @seismicElement sn-bcm-record-modal
 * @summary A modal to create and edit
 */

createCustomElement(tagName, {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		 * Table name to render
		 *
		 * @type {string}
		 */
		table: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Sys_id of the record to load on the modal to be edited, (-1) will Create a new record
		 *
		 * @type {string}
		 */
		sysId: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The Form View you want to load in the Modal. If not specified, the default view will be loaded
		 *
		 * @type {string}
		 */
		formView: {
			default: DEFAULT_FORM_VIEW,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The header of the Modal, can be left empty
		 *
		 * @type {string}
		 */
		headerLabel: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * action button will not appear if label is not set
		 *
		 * @type {string}
		 */
		actionButtonLabel: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Boolean used to show/hide the modal
		 *
		 * @type {boolean}
		 */
		isModalOpen: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * cancel button will not appear if label is not set
		 *
		 * @type {string}
		 */
		cancelButtonLabel: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Delete button will not appear if label is not set
		 *
		 * @type {string}
		 */
		deleteButtonLabel: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * to reload the page after action is done, or being redirected to the form
		 *
		 * @type {boolean}
		 */
		reloadAfterSave: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * to load the page in read-only format
		 *
		 * @type {boolean}
		 */
		isReadOnly: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * The sice of modal window. "lg", "md", or "sm". Default is large ("lg")
		 *
		 * @type {string}
		 */
		modalSize: {
			default: DEFAULT_MODAL_SIZE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * query conditions to be passed to form section
		 *
		 * @type {String}
		 */
		queryConditions: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Message shown below loader while loading the the modal
		 *
		 * @type {string}
		 */
		loadingViewMessage: {
			default: LOADING_VIEW_MESSAGE,
			required: false,
			schema: {
				type: 'string',
			},
		},
	},
	initialState: {
		/**
		 * State of the component whether it is loaded or not
		 *
		 * @type {boolean}
		 */
		isLoading: false,
		/**
		 * State of the component where shows loader before form loads
		 *
		 * @type {boolean}
		 */
		isModalLoading: true,
		isRecordEditable: false,
	},
	dispatches: {
		/**
		 * Dispatched when reloading the form view
		 *
		 * @type {object}
		 */
		[PREVIEW_RECORD]: {
			type: {
				table: '',
				sys_id: '',
			},
		},
		/**
		 * Dispatched when a record is successfully upserted.
		 *
		 * @type {object}
		 */
		[RECORD_DATA_CHANGED]: {
			type: {
				table: '',
				sys_id: '',
				status: '',
			},
		},
		/**
		 * Dispatched when a modal is closed, with/without saving.
		 *
		 * @type {object}
		 */
		[MODAL_CLOSE_CLICKED]: {
			type: {
				value: false,
			},
		},
		// TODO: we should update thesze changes to be more declarative.
		// TODO: investigate and create the actions
		// TODO: dispatch an action if save was successful.
		// TODO: dispatch an action if save failed.
		// TODO: dispatch an action if clsoed with no save attempt
	},
	actions: {
		...modalVisibilityActions,
	},
	actionHandlers: {
		...modalVisibilityActionHandlers,
		...lifeCycleActionHandlers,
	},
	styles,
});
export default tagName;
