import PieChartCardElement from '../../PieChartCard/PieChartCardElement';
import '@servicenow/now-loader';

const view = (state) => {
	const {
		isLoading,
		assetsWithStrategyCount,
		assetsWithoutStrategyCount,
		properties,
		isAccessibilityModeActive,
	} = state;

	const {
		assetsWithStrategyCountLabel,
		assetsWithoutStrategyCountLabel,
		assetsWithStrategyChartColor,
		assetsWithoutStrategyChartColor,
		totalAssetsCountLabel,
		cardHeaderLabel,
		loadingViewMessage,
	} = properties;

	const seriesData = [
		{
			name: assetsWithoutStrategyCountLabel,
			count: assetsWithoutStrategyCount,
			color: assetsWithoutStrategyChartColor,
		},
		{
			name: assetsWithStrategyCountLabel,
			count: assetsWithStrategyCount,
			color: assetsWithStrategyChartColor,
		},
	];

	const donutChartProperties = {
		seriesData,
		extraMargin: {
			right: 24,
		},
		showDataLabels: false,
		showTotal: true,
		totalLabel: totalAssetsCountLabel,
		showTooltip: true,
		hideRegister: false,
		registerPosition: 'right',
		legendOnly: false,
		showPercentages: false,
	};

	const loadingView = (
		<div className="loading-view">
			<now-loader label={loadingViewMessage} />
		</div>
	);

	if (isLoading) {
		return loadingView;
	}

	return (
		<PieChartCardElement
			fillHeight
			cardHeaderLabel={cardHeaderLabel}
			{...donutChartProperties}
			isChartAccessibilityModeActive= {isAccessibilityModeActive}
		/>
	);
};

export default view;
