import { ROW_SELECTION_UPDATED } from '../constants/ItemPickerModalActionTypes';

const actions = {
	[ROW_SELECTION_UPDATED]: {
		private: true,
	},
};

const updateElectedEncodedQuery = ({ action, updateState }) => {
	const currentlySelectedEncodedQuery = action.payload.query;

	updateState({
		currentlySelectedEncodedQuery,
	});
};

const actionHandlers = {
	[ROW_SELECTION_UPDATED]: updateElectedEncodedQuery,
};

export { actions, actionHandlers };
