import {
	FILTER_BY_CMDB_REL_TYPE_CHOICE,
	SHOW_ALL_RECORDS_CHOICE,
	MODAL_VIEW_TYPES,
	DEFAULT_ITEM_INSERTION_LOADING_MESSAGE,
	ITEM_TABLE_FIELD_NAME,
	ITEM_FIELD_NAME,
	SOURCE_TYPE,
} from '../constants/BiaDependencyItemPickerModalConstants';
import {
	CMDB_CI_DEPENDENCY_QUERY_REQUESTED,
	NOW_RADIO_BUTTON_VALUE_SET,
	ITEM_PICKER_BIA_NEXT_SUCCEEDED,
	BIA_ASSET_ITEM_INSERTION_REQUESTED,
	ITEM_PICKER_BIA_BACK_SUCCEEDED,
} from '../constants/BiaDependencyItemPickerModalActionTypes';
import { ACTION_TYPES } from '../../ItemPickerModal/constants/ItemPickerModalCommonConstants';

const actions = {
	[ACTION_TYPES.ITEM_PICKER_EDIT_CANCELLED]: {
		private: false,
	},
	[ACTION_TYPES.ITEM_PICKER_EDIT_SAVED]: {
		private: true,
	},
	[ITEM_PICKER_BIA_NEXT_SUCCEEDED]: {
		private: true,
	},
};

const closeModal = ({ updateState }) => {
	updateState({
		isModalOpen: false,
	});
};

const requestNewRecordInsertion = ({ state, dispatch, updateState }) => {
	const {
		selectionTable,
		properties,
		currentlySelectedEncodedQuery,
		radioSelectionValue,
	} = state;
	const { table, parentRecordSysId, parentTableReferenceField } = properties;

	const listOfSelectedItemsSysIds = currentlySelectedEncodedQuery
		.substring(
			currentlySelectedEncodedQuery.indexOf('N') + 1,
			currentlySelectedEncodedQuery.length
		)
		.split(',');

	let sourceType = SOURCE_TYPE.BCM;
	if (radioSelectionValue === FILTER_BY_CMDB_REL_TYPE_CHOICE.id) {
		sourceType = SOURCE_TYPE.CMDB;
	}

	dispatch(BIA_ASSET_ITEM_INSERTION_REQUESTED, {
		data: {
			targetTableIdentifier: table,
			itemTableFieldNameValue: selectionTable,
			itemTableFieldName: ITEM_TABLE_FIELD_NAME,
			itemFieldName: ITEM_FIELD_NAME,
			parentRecordSysId,
			parentTableReferenceField,
			listOfSelectedItemsSysIds,
			biaDependencyColSourceType: sourceType,
		},
	});

	updateState({
		loadingMessage: DEFAULT_ITEM_INSERTION_LOADING_MESSAGE,
		isLoading: true,
	});
};

const handleNextModalView = ({ state, dispatch, updateState }) => {
	const {
		selectionTable,
		isElementConfigurationItem,
		appliesToSysId,
		radioSelectionValue,
		currentScreenView,
		selectedRelationshipTypes,
		cmdbRelationshipItems,
		elementDefinitionfilter,
		properties,
	} = state;
	const { maxRecordLimit } = properties;

	if (isElementConfigurationItem) {
		switch (radioSelectionValue) {
			case FILTER_BY_CMDB_REL_TYPE_CHOICE.id:
				if (currentScreenView !== MODAL_VIEW_TYPES.CMDB_REL_TYPE_LIST_PICKER) {
					dispatch(CMDB_CI_DEPENDENCY_QUERY_REQUESTED, {
						queryConditions: `parent=${appliesToSysId}^child.sys_class_name=${selectionTable}`,
						maxRecordLimit,
					});
				} else {
					const includedItemsEncodedQuery = cmdbRelationshipItems.reduce(
						(queryCondition, item, index) => {
							const {
								type: { value: ciRelationshipId = '' },
								child: { value: ciItemId = '' },
							} = item;

							if (selectedRelationshipTypes.includes(ciRelationshipId)) {
								const qCondition = index
									? `${queryCondition},${ciItemId}`
									: `${queryCondition}${ciItemId}`;
								return qCondition;
							}
							return queryCondition;
						},
						'sys_idIN'
					);
					const consolidatedFilterCondition = elementDefinitionfilter
						? includedItemsEncodedQuery + '^' + elementDefinitionfilter
						: includedItemsEncodedQuery;

					updateState({
						cmdbCiRecordListPickerFilterConditions: consolidatedFilterCondition,
						prevScreenView: currentScreenView,
						currentScreenView: MODAL_VIEW_TYPES.CMDB_RECORD_LIST_PICKER,
					});
				}
				break;
			case SHOW_ALL_RECORDS_CHOICE.id:
			default:
				updateState({
					prevScreenView: currentScreenView,
					currentScreenView: MODAL_VIEW_TYPES.RECORD_LIST_PICKER,
				});
				break;
		}
	}
};

const handlePreviousModalView = ({ state, updateState }) => {
	const { currentScreenView, prevScreenView } = state;

	if (currentScreenView === MODAL_VIEW_TYPES.CMDB_RECORD_LIST_PICKER) {
		return updateState({
			prevScreenView: MODAL_VIEW_TYPES.RADIO_BUTTONS,
			currentScreenView: prevScreenView,
		});
	}

	updateState({
		currentScreenView: prevScreenView,
	});
};

const updateRadioSelectionValue = ({ action, updateState }) => {
	const { value } = action.payload;

	if (value) {
		updateState({
			radioSelectionValue: value,
			cmdbRelTypeOptionalProps: {},
			cmdbCiRecordListOptionalProps: { page: 1 },
			recordListOptionalProps: { page: 1 },
			currentlySelectedEncodedQuery: '',
		});
	}
};

const actionHandlers = {
	[ACTION_TYPES.ITEM_PICKER_EDIT_CANCELLED]: closeModal,
	[ACTION_TYPES.ITEM_PICKER_EDIT_SAVED]: requestNewRecordInsertion,
	[ITEM_PICKER_BIA_NEXT_SUCCEEDED]: handleNextModalView,
	[ITEM_PICKER_BIA_BACK_SUCCEEDED]: handlePreviousModalView,
	[NOW_RADIO_BUTTON_VALUE_SET]: updateRadioSelectionValue,
};

export { actions, actionHandlers };
