import {
	SAVE_BUTTON_CLICKED,
	CANCEL_BUTTON_CLICKED,
	MODAL_CLOSE_CLICKED,
} from '../constants/NewBiaDynamicModalActionTypes';
import { checkMandatoryFields } from '../util/NewBiaDynamicModalUtil';

const saveButtonClicked = ({ state, updateState, dispatch }) => {
	updateState({
		isLoading: true,
	});
	checkMandatoryFields(state, updateState, dispatch);
};

const cancelButtonClicked = ({ dispatch }) => {
	dispatch(MODAL_CLOSE_CLICKED, {
		value: false,
	});
};

const actionHandlers = {
	[SAVE_BUTTON_CLICKED]: {
		effect: saveButtonClicked,
		stopPropagation: true,
	},
	[CANCEL_BUTTON_CLICKED]: {
		effect: cancelButtonClicked,
		stopPropagation: true,
	},
};

export default actionHandlers;
