import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './ItemPickerModalView';
import styles from './ItemPickerModalElement.scss';
import lifecycleActionHandlers from './actions/ItemPickerModalLifecycleActions';
import {
	actions as modalButtonActions,
	actionHandlers as modalButtonActionHandlers,
} from './actions/ModalButtonActions';
import {
	actions as itemInsertionActions,
	actionHandlers as itemInsertionActionHandlers,
} from './actions/ItemInsertionActions';
import {
	actions as selectionTableNameQueryActions,
	actionHandlers as selectionTableNameQueryActionHandlers,
} from './actions/SelectionTableNameQueryActions';
import {
	actions as tableInteractionActions,
	actionHandlers as tableInteractionActionHandlers,
} from './actions/TableInteractionActions';
import {
	actions as excludedItemsQueryActions,
	actionHandlers as excludedItemsQueryActionHandlers,
} from './actions/ExcludedItemsQueryActions';
import biaAssetPickerActionHandlers from './actions/ImapctAnalysisActions';
import recoveryEventAssetPickerActionHandlers from './actions/RecoveryEventActions';
import elementDefinitionActionHandlers from './actions/FetchElementDefinitionActions';
import {
	DEFAULT_PAGINATION_RECORD_LIMIT,
	DEFAULT_MODAL_HEADER_LABEL,
	DEFAULT_ITEM_INSERTION_LOADING_MESSAGE,
	DEFAULT_ITEM_PICKER_LOADING_MESSAGE,
} from './constants/ItemPickerModalConstants';

const TAG_NAME = 'sn-bcm-item-picker-modal';

/**
 * Modal component used to add items in bulk to a related list.
 * Intended use is stricly for related-list-actions assignments.
 *
 * ```
 * <sn-bcm-item-picker-modal
 *		parent-table="${PARENT_TABLE}"
 *		parent-record-sys-id="${PARENT_RECORD_SYS_ID}"
 *		list-picker-columns="${LIST_PICKER_COLUMNS}"
 *		insertion-loading-message="${INSERTION_LOADING_MESSAGE}"
 *		pagination-record-limit=${PAGINATION_RECORD_LIMIT}
 *		table="${TABLE}">
 * </sn-bcm-item-picker-modal>
 *
 * ```
 *
 * @seismicElement sn-bcm-item-picker-modal
 * @summary Modal component used to add items in bulk to a related list
 */
createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
	},
	view,
	properties: {
		/**
		 * The maximum number of rows to show (per page) in the modal.
		 * @type {Number}
		 */
		paginationRecordLimit: {
			default: DEFAULT_PAGINATION_RECORD_LIMIT,
			required: false,
			schema: {
				type: 'number',
			},
		},
		/**
		 * The name of the parent table where this modal action is used, not the related table name.
		 * @type {String}
		 */
		parentTable: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The sysId of the parent record, where the action's related list is displayed.
		 * @type {String}
		 */
		parentRecordSysId: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The table name of the related list containing this modal's action. This table is where insertions will occur.
		 * @type {String}
		 */
		table: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The label shown on the modal header. Defaults to 'Add Items'.
		 * @type {String}
		 */
		modalHeaderLabel: {
			default: DEFAULT_MODAL_HEADER_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The message to display (alongside the loader element), while records are being inserted.
		 * @type {String}
		 */
		itemInsertionLoadingMessage: {
			default: DEFAULT_ITEM_INSERTION_LOADING_MESSAGE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The message to display (alongside the loader element), while the modal list loads.
		 * @type {String}
		 */
		itemPickerLoadingMessage: {
			default: DEFAULT_ITEM_PICKER_LOADING_MESSAGE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The reference field name on the parent table which refers to table that has reference to element definition.
		 * Example: template field on sn_bcp_plan Table.
		 * @type {String}
		 */
		parentTableItemPickerReferenceField: {
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The reference field name of the element defintion table on parent table.
		 * Example: primary_element_recovered field on sn_bcp_template table.
		 * @type {String}
		 */
		elementDefinitionTableReferenceField: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The reference field name of the parent table on related table.
		 * @type {String}
		 */
		parentTableReferenceField: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * item table name field for documentID field
		 * @type {string}
		 */
		itemTableFieldName: {
			default: 'item_table',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * item field name documentID field
		 * @type {string}
		 */
		itemFieldName: {
			default: 'item',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Flag to identify the bia scope to add assets for dependency assessment.
		 * Needed for BIA.
		 * @type {boolean}
		 */
		isImpactAnalysis: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Flag to identify the recovery event scope for adding assets
		 * Needed for recovery event
		 *
		 * @type {boolean}
		 */
		isRecoveryEvent: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
	},
	initialState: {
		isModalOpen: true,
		selectionTableLabel: '',
		selectionTable: '',
		selectionTableFields: '',
		itemPickerOptionsFilter: '',
		currentlySelectedEncodedQuery: '',
		isLoading: true,
		loadingMessage: '',
		errorMessage: '',
		isItemTypeSelected: false,
		selectItemTypeError: '',
		listOfElements: [],
		disableNextButton: true,
	},
	actions: {
		...modalButtonActions,
		...itemInsertionActions,
		...selectionTableNameQueryActions,
		...tableInteractionActions,
		...excludedItemsQueryActions,
	},
	actionHandlers: {
		...lifecycleActionHandlers,
		...modalButtonActionHandlers,
		...itemInsertionActionHandlers,
		...selectionTableNameQueryActionHandlers,
		...tableInteractionActionHandlers,
		...excludedItemsQueryActionHandlers,
		...biaAssetPickerActionHandlers,
		...recoveryEventAssetPickerActionHandlers,
		...elementDefinitionActionHandlers,
	},
	styles,
});

export default TAG_NAME;
