import '@servicenow/now-alert';
import '@servicenow/now-modal';
import '@servicenow/now-loader';
import '@devsnc/sn-record-input-connected';
import '@devsnc/sn-glide-form-controls';
import { getModalElements } from './util/NewBiaDynamicModalUtil';
import {
	CANCEL_BUTTON_CLICKED,
	SAVE_BUTTON_CLICKED,
} from './constants/NewBiaDynamicModalActionTypes';
import {
	MODAL_HEADER_LABEL,
	SUBMIT_BUTTON_LABEL,
	CANCEL_BUTTON_LABEL,
	LOADING_MESSAGE,
} from './constants/NewBiaDynamicModalConstants';

const view = (state, { updateState, dispatch }) => {
	const {
		alertMessage,
		properties,
		showErrorMessage,
		isLoading,
		isModalOpen,
	} = state;
	const { modalSize } = properties;

	const SAVE_BUTTON = {
		variant: 'primary',
		label: SUBMIT_BUTTON_LABEL,
		clickActionType: SAVE_BUTTON_CLICKED,
	};
	const CANCEL_BUTTON = {
		variant: 'secondary',
		label: CANCEL_BUTTON_LABEL,
		clickActionType: CANCEL_BUTTON_CLICKED,
	};

	const footerActions = [SAVE_BUTTON, CANCEL_BUTTON];

	return (
		<now-modal
			opened={isModalOpen}
			size={modalSize}
			header-label={MODAL_HEADER_LABEL}
			footer-actions={isLoading ? [] : footerActions}
		>
			{isLoading ? (
				<div className="loading-view">
					<now-loader label={LOADING_MESSAGE} />
				</div>
			) : (
				<div>
					<now-alert
						className={showErrorMessage ? 'is-new-record-alert' : 'hidden'}
						status="critical"
						icon="info-circle-outline"
						content={alertMessage}
					/>
					{getModalElements(state, updateState, dispatch)}
				</div>
			)}
		</now-modal>
	);
};

export default view;