import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './RelatedItemAccordionListView';
import {
	RELATED_TABLE_STATUS_FIELD_NAME,
	DEFAULT_TABLE_RECORD,
} from './constants/RelatedItemAccordionListConstants';
import lifecycleActionHandlers from './actions/RelatedItemAccordionListLifeCycleActions';
import relatedItemAccordionListMutationHandlers from './actions/RelatedItemAccordionMutationActions';
import styles from './RelatedItemAccordionListElement.scss';

const TAG_NAME = 'sn-bia-related-accordion-list';

/**
 * Accordion List to show list  records with name, description and status
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bia-related-accordion-list
 *      related-table-name=""
 *      accordion-item-card-description-field=""
 *      accordion-list-header-field=""
 *      related-table-mandatory-fields=""
 *      related-child-table=""
 * ></sn-bia-related-accordion-list>
 * ```
 *
 * @seismicElement sn-bia-related-accordion-list
 * @summary A accordion list for displaying data
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,

	properties: {
		/**
		 * The table name on which data should be queried on.
		 *
		 * @type {string}
		 */
		relatedTableName: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Name of the status field to fetch the status of a record
		 *
		 * @type {string}
		 */
		relatedTableStatusFieldName: {
			default: RELATED_TABLE_STATUS_FIELD_NAME,
			required: false,
			schema: {
				type: 'string',
			},
		},

		/**
		 * Field name of the record to be shown on Accordion Header
		 *
		 * @type {string}
		 */
		accordionListHeaderField: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 *  Field name of the record to be shown on Card Description
		 *
		 * @type {string}
		 */
		accordionItemCardDescriptionField: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 *  Field names of the record to be mandatory on Card. Multiple field seprated by comma
		 *
		 *  @type {array}
		 */
		relatedTableMandatoryFields: {
			default: [],
			required: false,
			schema: {
				type: 'array',
			},
		},
		/**
		 *  Name of the child table
		 *
		 *  @type {string}
		 */
		relatedChildTable: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 *  Records to generate the accordion items
		 *
		 *  @type {array}
		 */
		accordionRecords: {
			default: DEFAULT_TABLE_RECORD,
			required: false,
			schema: {
				type: 'array',
			},
		},
		/**
		 * flag to show action buttons
		 *
		 * @type {boolean}
		 */
		isActionButtonRequired: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Flag to expand all accordions or not.
		 *
		 * @type {boolean}
		 */
		isExpandAll: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * relatedTable reference field on child Table
		 *
		 * @type {string}
		 */
		relatedTableReferenceField: {
			default: '',
			requried: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Field labels of the record to be mandatory on Card. Multiple field seprated by comma.
		 *
		 * @type {string}
		 */
		relatedTableMandatoryFieldLabels: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Helper text for usability
		 *
		 * @type {string}
		 */
		helperText: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Flag to show card header or not
		 * @type {boolean}
		 */
		isCardHeaderRequired: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
	},
	initialState: {
		/**
		 * Array of all fetched records from table
		 *
		 * @type {object}
		 */
		accordionListItems: [],
		editedRecordId:'',
	},
	actionHandlers: {
		...lifecycleActionHandlers,
		...relatedItemAccordionListMutationHandlers,
	},
	styles,
});

export default TAG_NAME;
