import '@servicenow/now-card';
import '@servicenow/now-loader';
import '@servicenow/now-stylized-text';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import {
	LOADING_VIEW_MESSAGE,
	EVENT_TYPE_VALUE,
	NOW_STYLIZED_TAG_NAME,
} from './constants/GeneralConstants';

const view = (state) => {
	const {
		isLoading,
		totalEventsCount,
		ongoingEventsCount,
		lastEventDate,
		lastEventResultStatus,
		properties,
	} = state;

	const {
		eventType,
		cardHeaderLabel,
		totalEventsLabel,
		ongoingEventsLabel,
		lastEventDateLabel,
		lastEventResultLabel,
	} = properties;

	const cardHeader = (
		<now-card-header
			heading={{
				label: cardHeaderLabel,
				variant: 'secondary',
				size: 'sm',
				level: 1,
				lines: 1,
			}}
			className="events-card__header"
		/>
	);

	const loadingView = (
		<div className="loading-view">
			<now-loader label={LOADING_VIEW_MESSAGE} />
		</div>
	);

	const primaryNowStylizedContainer = (
		<div className="events-card__column">
			<now-stylized-text
				className="events-card__stylized-text -md"
				text={totalEventsLabel}
				tag="span"
			/>
			<now-stylized-text className="events-card__stylized-text -xxxl"
				text={totalEventsCount}
				tag="span"
			/>
		</div>
	);

	const secondaryNowStylizedContainer = (
		<div className="events-card__column">
			<now-stylized-text
				className="events-card__stylized-text -md"
				text={ongoingEventsLabel}
				tag={NOW_STYLIZED_TAG_NAME}
			/>
			<now-stylized-text
				className="events-card__stylized-text -xl"
				text={ongoingEventsCount}
				tag={NOW_STYLIZED_TAG_NAME}
			/>
			<now-stylized-text
				className="events-card__stylized-text -md"
				text={lastEventDateLabel}
				tag={NOW_STYLIZED_TAG_NAME}
			/>
			<now-stylized-text
				className="events-card__stylized-text -xl"
				text={lastEventDate}
				tag={NOW_STYLIZED_TAG_NAME}
			/>
			{eventType === EVENT_TYPE_VALUE.EXERCISE ? (
				<Fragment>
					<now-stylized-text
						className="events-card__stylized-text -md"
						text={lastEventResultLabel}
						tag={NOW_STYLIZED_TAG_NAME}
					/>
					<now-stylized-text
						className="events-card__stylized-text -xl"
						text={lastEventResultStatus}
						tag={NOW_STYLIZED_TAG_NAME}
					/>
				</Fragment>
			) : null}
		</div>
	);

	const eventsCardView = (
		<now-card className="events-card">
			{cardHeader}
			<div className="events-card__content">
				{[primaryNowStylizedContainer, secondaryNowStylizedContainer]}
			</div>
		</now-card>
	);

	if (isLoading) {
		return loadingView;
	}


	return eventsCardView;
};

export default view;
