import '@servicenow/now-modal';
import '@servicenow/now-button';
import '@servicenow/now-record-list-connected';
import {
	CONFIRMATION_MESSAGE,
	CANCEL_BUTTON_LABEL,
	BUTTON_VARIANT_SECONDARY,
	BUTTON_VARIANT_PRIMARY,
	MODAL_HEADER_LABEL,
	RADIO_CHOICE_LABEL,
	SELECT_IMPACTED_ASSET_LABEL,
	ERROR_ALERT_HEADING,
} from './constants/RecoveryAddPlansModalConstants';
import '@servicenow/now-loader';
import '@servicenow/now-radio-buttons';
import '@servicenow/now-alert';

export default (state) => {
	const {
		properties,
		actionButtonLabel,
		isOpenList,
		selectionTable,
		selectionTableFields,
		showEventAssets,
		eventAssetsList,
		listOfSelectedItemsSysIds,
		listQueryConditions,
		showConfirmation,
		modalSubTitle,
		disableActionButton,
		errorMessage,
		modalTitle,
		listPageNumber,
	} = state;
	const { isModalOpen, listOfOptions } = properties;

	const listPageProperty = listPageNumber ? { page: listPageNumber } : {};

	const footerButtons = [];
	footerButtons.push({
		variant: BUTTON_VARIANT_PRIMARY,
		label: actionButtonLabel,
		disabled: disableActionButton,
	});

	footerButtons.push({
		variant: BUTTON_VARIANT_SECONDARY,
		label: CANCEL_BUTTON_LABEL,
	});

	const errorAlert = (
		<now-alert
			className={errorMessage ? 'now-alert-error-element' : 'hidden'}
			status="critical"
			icon="exclamation-triangle-fill"
			header={`${ERROR_ALERT_HEADING}:`}
			content={errorMessage}
		/>
	);

	return (
		<now-modal
			headerLabel={modalTitle}
			size="lg"
			opened={isModalOpen}
			manageOpened
			footer-actions={footerButtons}>
			{errorAlert}
			{showConfirmation ? (
				<div className="confirmationMessage">
					<now-icon
						className="infoIcon"
						icon="circle-info-fill"
						size="md"></now-icon>
					<now-label-value-tabbed
						items={[
							{
								label: '',
								value: { type: 'string', value: CONFIRMATION_MESSAGE },
							},
						]}
						size="md"
						delimiter=","></now-label-value-tabbed>
				</div>
			) : null}
			{!isOpenList && !showConfirmation ? (
				<div className="select-option">
					<now-label-value-tabbed
						items={[
							{
								value: {
									type: 'string',
									value: modalSubTitle,
								},
							},
						]}
						size="md"></now-label-value-tabbed>
					<div className="show-plans">
						<div className="asset-choice">
							<now-radio-buttons
								className="radio-choice-options"
								label={RADIO_CHOICE_LABEL}
								name="choice"
								options={listOfOptions}
							/>
							{showEventAssets ? (
								<now-typeahead
									label={SELECT_IMPACTED_ASSET_LABEL}
									helperContent={SELECT_IMPACTED_ASSET_LABEL}
									search="managed"
									items={eventAssetsList}
								/>
							) : null}
						</div>
					</div>
				</div>
			) : null}
			{isOpenList ? (
				<div>
					<now-label-value-tabbed
						className="list-subtitle"
						items={[
							{
								label: '',
								value: {
									type: 'string',
									value: modalSubTitle,
								},
							},
						]}
						size="md"></now-label-value-tabbed>
					<now-record-list-connected-picker
						className="item-list"
						table={selectionTable}
						limit={10}
						query={listQueryConditions}
						columns={selectionTableFields}
						selectedRecords={listOfSelectedItemsSysIds}
						{...listPageProperty}
					/>
				</div>
			) : null}
		</now-modal>
	);
};
