import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import dependencyFilterQuery  from '../querries/ItemPickerFilterQuery';
import {
    BIA_EXCLUDED_ITEMS_QUERY_REQUESTED,
    BIA_ASSET_ITEM_INSERTION_REQUESTED,
    ITEM_PICKER_TABLE_NAME_STARTED,
    ITEM_PICKER_TABLE_NAME_SUCCEEDED,
    ITEM_PICKER_TABLE_NAME_FAILED,
    EXCLUDED_ITEMS_QUERY_STARTED,
    EXCLUDED_ITEMS_QUERY_SUCCEDED,
    EXCLUDED_ITEMS_QUERY_FAILED,
    BIA_ITEM_PICKER_TABLE_NAME_REQUESTED,
    ITEM_INSERTION_STARTED,
    ITEM_INSERTION_SUCCEEDED,
    ITEM_INSERTION_FAILED,
} from '../constants/ItemPickerModalActionTypes';

import {
    EXCLUDED_ITEMS_QUERY_URL_FOR_IMPACT_ANALYSIS,
    ITEM_INSERTION_URL_FOR_IMPACT_ANALYSIS,
} from '../constants/ItemPickerModalConstants';

const actionHandlers = {
    [BIA_ITEM_PICKER_TABLE_NAME_REQUESTED]: createGraphQLEffect(dependencyFilterQuery, {
		variableList: ['parentRecordSysId'],
		templateVarList: [
			'parentTable',
			'elementDefinitionTableReferenceField',
		],
		startActionType: ITEM_PICKER_TABLE_NAME_STARTED,
		successActionType: ITEM_PICKER_TABLE_NAME_SUCCEEDED,
		errorActionType: ITEM_PICKER_TABLE_NAME_FAILED,
    }),
    
    [BIA_EXCLUDED_ITEMS_QUERY_REQUESTED]: createHttpEffect(EXCLUDED_ITEMS_QUERY_URL_FOR_IMPACT_ANALYSIS, {
		method: 'GET',
		queryParams: ['parentRecordSysId', 'excludedItemsTableIdentifier'],
		startActionType: EXCLUDED_ITEMS_QUERY_STARTED,
		successActionType: EXCLUDED_ITEMS_QUERY_SUCCEDED,
		errorActionType: EXCLUDED_ITEMS_QUERY_FAILED,
		batch: false,
    }),
    [BIA_ASSET_ITEM_INSERTION_REQUESTED]: createHttpEffect(ITEM_INSERTION_URL_FOR_IMPACT_ANALYSIS, {
		method: 'POST',
		dataParam: 'data',
		startActionType: ITEM_INSERTION_STARTED,
		successActionType: ITEM_INSERTION_SUCCEEDED,
		errorActionType: ITEM_INSERTION_FAILED,
		batch: false,
	}),
};

export default actionHandlers;