import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	FETCH_PLAN_ASSET_DETAILS_QUERY_REQUESTED,
	FETCH_PLAN_ASSET_DETAILS_QUERY_SUCCEEDED,
	FETCH_PLAN_ASSET_DETAILS_QUERY_FAILED,
} from '../constants/PlanAssetScoreCardActionTypes';
import {
	FETCH_ERROR_MESSAGE,
	PLAN_ASSET_DETAILS_AGGREGATE_URL,
} from '../constants/PlanAssetScoreCardConstants';

const requestPlanAssetDetailsData = createHttpEffect(
	PLAN_ASSET_DETAILS_AGGREGATE_URL,
	{
		method: 'GET',
		queryParams: [
			'parentRecordSysId',
		],
		successActionType: FETCH_PLAN_ASSET_DETAILS_QUERY_SUCCEEDED,
		errorActionType: FETCH_PLAN_ASSET_DETAILS_QUERY_FAILED,
		batch: false,
	}
);

const getPlanAssetDetailsQuerySuccess = ({ action, updateState }) => {
	const { result } = action.payload;

	if (result.error) {
		console.error(FETCH_ERROR_MESSAGE);
		console.error(result.error);
		updateState({
			error: true,
			isLoading: false,
		});

		return;
	}

	const { totalAssets, planLeastRPO, planLeastRTO, isRPOEnabled } = result;

	updateState({
		isRPOEnabled,
		planLeastRTO,
		planLeastRPO,
		totalAssets,
		isLoading: false,
	});
};

const getPlanAssetDetailsQueryFailed = ({ action, updateState }) => {
	const error = action.payload;

	console.error(FETCH_ERROR_MESSAGE);
	console.error(error);
	updateState({
		error: true,
		isLoading: false,
	});
};

const actionHandlers = {
	[FETCH_PLAN_ASSET_DETAILS_QUERY_REQUESTED]: requestPlanAssetDetailsData,
	[FETCH_PLAN_ASSET_DETAILS_QUERY_SUCCEEDED]: {
		effect: getPlanAssetDetailsQuerySuccess,
		stopPropogation: true,
	},
	[FETCH_PLAN_ASSET_DETAILS_QUERY_FAILED]: {
		effect: getPlanAssetDetailsQueryFailed,
		stopPropogation: true,
	},
};

export default actionHandlers;
