import { actionTypes } from '@servicenow/ui-core';

const { COMPONENT_DOM_READY } = actionTypes;

const fetchAccordionListData = ({ properties, updateState }) => {
	const { accordionRecords, isExpandAll } = properties;

	const records = accordionRecords.map((record) => {
		record.isExpanded = isExpandAll;
		record.isMandatoryFieldEmpty = false;

		return record;
	});

	updateState({
		accordionListItems: records,
	});
};

const actionHandlers = {
	[COMPONENT_DOM_READY]: {
		effect: fetchAccordionListData,
	},
};

export default actionHandlers;
