import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import { reduceResponseObjectByValue } from '../utils';
import { NEW_DOCUMENT_SECTION } from '../constants/GeneralConstants';
import NEW_DOCUMENTATION_RECORD_QUERY from '../constants/DocumentationNewRecordActionTypes';
import planDocumentNewRecordQuery from '../queries/planDocumentNewRecordQuery';

const queryNewPlanDocumentation = createGraphQLEffect(
	planDocumentNewRecordQuery,
	{
		templateVarList: ['table'],
		variableList: ['sysId'],
		startActionType: NEW_DOCUMENTATION_RECORD_QUERY.STARTED,
		successActionType: NEW_DOCUMENTATION_RECORD_QUERY.SUCCEEDED,
		errorActionType: NEW_DOCUMENTATION_RECORD_QUERY.FAILED,
	}
);

const queryRecordSuccess = ({ state, action, updateState, properties }) => {
	const { tableRecords } = state;
	const { tableName } = properties;
	const { payload } = action;
	const newDocumentationRecord = get(
		payload,
		`data.GlideRecord_Query.${tableName}.${NEW_DOCUMENT_SECTION}[0]`
	);

	if (newDocumentationRecord) {
		const newTableRecord = reduceResponseObjectByValue(newDocumentationRecord);

		return updateState({
			loading: false,
			tableRecords: [...tableRecords, newTableRecord],
			newDocumentSectionSysId: newTableRecord.sys_id,
			scrollToNewDocumentSection: true,
		});
	}

	return updateState({
		loading: false,
	});
};

const queryRecordFailed = ({ action, updateState }) => {
	const error = action.payload;

	console.error(error);

	updateState({
		loading: false,
	});
};

const queryRecordStarted = ({ updateState }) => {
	updateState({
		loading: true,
	});
};

const actionHandlers = {
	[NEW_DOCUMENTATION_RECORD_QUERY.REQUESTED]: queryNewPlanDocumentation,
	[NEW_DOCUMENTATION_RECORD_QUERY.STARTED]: queryRecordStarted,
	[NEW_DOCUMENTATION_RECORD_QUERY.SUCCEEDED]: queryRecordSuccess,
	[NEW_DOCUMENTATION_RECORD_QUERY.FAILED]: queryRecordFailed,
};

export default actionHandlers;
