import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { get } from 'lodash';
import {
	SHOW_ALL_PLANS_MESSAGE,
	SELECT_IMPACTED_ASSETS_FOR_PLANS_MESSAGE,
	PLAN_ASSET_FILTER_QUERY_ERROR_MESSAGE,
	FETCH_PLANS_FROM_PLAN_ASSET,
	ADD_BUTTON_LABEL,
	ACTIVATED_PLAN_TABLE_NAME,
	FILTER_PLANS_QUERY_URL,
	SELECTED_ASSET_MESSAGE,
	APPROVED_PLAN_QUERY_CONDITION,
	SELECT_PLANS_TITLE,
	SELECT_ASSETS_FOR_PLANS,
	FILTER_PLAN_ERROR_MESSAGE,
} from '../constants/RecoveryAddPlansModalConstants';
import { FILTER_PLAN_ASSETS_QUERY_ACTION_TYPES, FILTER_PLANS_QUERY_ACTION_TYPES } from '../constants/RecordModalActionTypes';
import fecthActivatedPlanQuery from '../queries/ActivatedPlanQuery'

const planAssetFilterQuerySuccess = ({ action, updateState, dispatch, state }) => {
	const fetchResult = action.payload;
	const fetchErrors = fetchResult.errors;

	const { listOfSelectedItemsSysIds, selectedAsset, assetBased, properties } = state;
	const { planAssetColumns, planColumns } = properties;

	if (fetchErrors.length > 0) {
		console.error(FETCH_PLANS_FROM_PLAN_ASSET);
		console.error(fetchErrors);

		updateState({
			errorMessage: FETCH_PLANS_FROM_PLAN_ASSET,
		});
		return;
	}
	const dataReturned = get(
		action.payload,
		`data.GlideRecord_Query.${ACTIVATED_PLAN_TABLE_NAME}._results`
	);

	let listOfPlanIds = [];
	let encodedquery = APPROVED_PLAN_QUERY_CONDITION;
	let subTitle = '';
	let modalTitle = '';

	if (dataReturned.length > 0) {
		dataReturned.map((plan) => {

			if (
				listOfPlanIds &&
				listOfPlanIds.indexOf(plan.plan_asset.value) == -1
			) {
				listOfPlanIds.push(plan.plan_asset.value);
			}
		});
	}


	if (listOfPlanIds && listOfPlanIds.length > 0) {
		encodedquery = encodedquery + '^sys_idNOT IN' + listOfPlanIds;
	}

	if (assetBased) {
		encodedquery = encodedquery + '^item=' + selectedAsset['asset_name'];
		subTitle = SELECTED_ASSET_MESSAGE + selectedAsset['label'];
		modalTitle = SELECT_PLANS_TITLE;
	} else {
		encodedquery = encodedquery + '^plan.sys_idIN' + listOfSelectedItemsSysIds;
		subTitle = SELECT_IMPACTED_ASSETS_FOR_PLANS_MESSAGE;
		modalTitle = SELECT_ASSETS_FOR_PLANS;
	}

	updateState({
		isOpenList: true,
		modalSubTitle: subTitle,
		selectionTable: 'sn_bcp_plan_asset',
		listOfSelectedItemsSysIds: [],
		listQueryConditions: encodedquery,
		selectionTableFields: planAssetColumns,
		actionButtonLabel: ADD_BUTTON_LABEL,
		disableActionButton: true,
		modalTitle,
		listPageNumber: 1,

	});
};

const planAssetFilterQueryFailure = ({ action, updateState }) => {
	const error = action.payload;

	console.error(error);

	updateState({
		errorMessage: PLAN_ASSET_FILTER_QUERY_ERROR_MESSAGE,
	});
};

const filterPlanQuerySuccess = ({ action, state, updateState }) => {

	const { result } = action.payload;
	const { response } = result;
	const { properties } = state;
	const { planColumns } = properties;

	let filterPlanIds = 'state=approved';

	if (response && response.length > 0) {
		filterPlanIds = filterPlanIds + '^sys_idNOT IN' + response;
	}
	updateState({
		showAllPlans: true,
		selectionTable: 'sn_bcp_plan',
		selectionTableFields: planColumns,
		listQueryConditions: filterPlanIds,
		showEventAssets: false,
		assetBased: false,
		isOpenList: true,
		modalSubTitle: SHOW_ALL_PLANS_MESSAGE,
		disableActionButton: true,
		modalTitle: SELECT_PLANS_TITLE,
	});
};

const filterPlanQueryFailure = ({ action, updateState }) => {
	const error = action.payload;

	console.error(FILTER_PLAN_ERROR_MESSAGE);
	console.error(error);

	updateState({
		errorMessage: FILTER_PLAN_ERROR_MESSAGE,
	});

};

const actionHandlers = {
	[FILTER_PLAN_ASSETS_QUERY_ACTION_TYPES.FILTER_PLAN_ASSETS_REQUESTED]: createGraphQLEffect(
		fecthActivatedPlanQuery,
		{
			variableList: ['queryCondition'],
			templateVarList: ['activatedTableName'],
			successActionType:
				FILTER_PLAN_ASSETS_QUERY_ACTION_TYPES.FILTER_PLAN_ASSETS_SUCCEEDED,
			errorActionType: FILTER_PLAN_ASSETS_QUERY_ACTION_TYPES.FILTER_PLAN_ASSETS_FAILED,
		}
	),
	[FILTER_PLAN_ASSETS_QUERY_ACTION_TYPES.FILTER_PLAN_ASSETS_SUCCEEDED]: {
		effect: planAssetFilterQuerySuccess,
		stopPropogation: true,
	},
	[FILTER_PLAN_ASSETS_QUERY_ACTION_TYPES.FILTER_PLAN_ASSETS_FAILED]: {
		effect: planAssetFilterQueryFailure,
		stopPropogation: true,
	},
	[FILTER_PLANS_QUERY_ACTION_TYPES.FILTER_PLAN_REQUESTED]: createHttpEffect(
		FILTER_PLANS_QUERY_URL, {
		method: 'GET',
		queryParams: ['eventSysId'],
		successActionType: FILTER_PLANS_QUERY_ACTION_TYPES.FILTER_PLAN_SUCCEEDED,
		errorActionType: FILTER_PLANS_QUERY_ACTION_TYPES.FILTER_PLAN_FAILED,
		batch: false,
	}
	),
	[FILTER_PLANS_QUERY_ACTION_TYPES.FILTER_PLAN_SUCCEEDED]: filterPlanQuerySuccess,
	[FILTER_PLANS_QUERY_ACTION_TYPES.FILTER_PLAN_FAILED]: filterPlanQueryFailure

};


export default actionHandlers;
