import '@servicenow/now-card';
import '@servicenow/now-score';
import '@servicenow/now-chart-donut-pie';
import '@servicenow/now-loader';
import '../EmptyState/EmptyStateElement';

const _getCardHeaderElement = (label) => {
	return (
		<now-card-header
			heading={{
				label,
				variant: 'secondary',
				size: 'sm',
				level: 1,
				lines: 1,
			}}
			className="card-header"
		></now-card-header>
	);
};

const _getCardFooterElement = (timestamp) => {
	const footerLabel = timestamp ? `Updated ${timestamp}` : '';

	return <now-card-footer className="now-card-footer" label={footerLabel} />;
};

const _getCardContent = (chartProperties, showEmptyState) => {
	const { dataLabelConfig, seriesData } = chartProperties;
	const { totalLabel } = dataLabelConfig;
	
	let chartAriaLabel = totalLabel;
	
	for( let i=0; i<seriesData.length; i++) {
		chartAriaLabel += seriesData[i].name + seriesData[i].count;
	}

	if (showEmptyState && seriesData && !seriesData.length) {
		return <sn-bcm-empty-state />;
	}

	const getOverridingStyles = () => {
		if (chartProperties.height !== 'auto' && chartProperties.height > 140) {
			const styles = {
				height: chartProperties.height + 'px',
			};

			return styles;
		}
	};

	return (
		<div className="card-content" style={{ ...getOverridingStyles() }}>
			<now-chart-donut-pie
				{...chartProperties}
				configAria={{ 'aria-label': chartAriaLabel }}
			/>
		</div>
	);
};

const _getCardClassName = (fillHeight) => {
	if (fillHeight) {
		return 'now-card-element fill-height';
	}

	return 'now-card-element';
};

const getCardElement = (
	cardHeaderLabel,
	fillHeight,
	donutChartProperties,
	lastUpdated,
	cardInteraction,
	isLoading,
	loadingMessage,
	showEmptyState,
	cardSize
) => {
	return (
		<now-card
			size={cardSize}
			interaction={cardInteraction}
			className={_getCardClassName(fillHeight) + ` -${cardSize}`}>
			{_getCardHeaderElement(cardHeaderLabel)}
			{isLoading ? (
				<div className="loading-view">
					<now-loader label={loadingMessage} />
				</div>
			) : (
				_getCardContent(donutChartProperties, showEmptyState)
			)}
			{_getCardFooterElement(lastUpdated)}
		</now-card>
	);
};

export { getCardElement };
