import { RELATED_LIST_REFRESH_REQUESTED } from '../../ItemPickerModal/constants/ItemPickerModalActionTypes';
import {
	RECORD_SELECTED_COUNT_UPDATED,
	LIST_ROW_CLICK_UPDATED,
	NOW_RECORD_LIST_ROW_CLICKED,
	RECORD_DATA_CHANGED,
	PREVIEW_RECORD,
} from '../constants/PlanAssetsListActionTypes';

const actionHandlers = {
	[LIST_ROW_CLICK_UPDATED]: {
		stopPropagation: true,
	},
	[NOW_RECORD_LIST_ROW_CLICKED]: {
		effect: ({ action, updateState, }) => {
			const { table, row } = action.payload;
			const sysId = row.sys_id.value;

			updateState({
				isEditItemModalOpen: true,
				editItemModalConfig: {
					table,
					sysId,
				},
			});
		},
		stopPropagation: true,
	},
	[RECORD_DATA_CHANGED]: {
		effect: ({dispatch}) => {
			dispatch(RELATED_LIST_REFRESH_REQUESTED);
		},
		stopPropagation: true,
	},
	[PREVIEW_RECORD]: {
		stopPropagation: true,
	},
	[RECORD_SELECTED_COUNT_UPDATED]: ({ action, updateState }) => {
		const { payload } = action;
		const { query } = payload;
		const currentlySelectedEncodedQuery = query;

		updateState({
			currentlySelectedEncodedQuery,
		});
	},
	[RELATED_LIST_REFRESH_REQUESTED]: ({ updateState }) => {
		updateState({
			refreshRequested: {
				timestamp: Date.now(),
				background: false,
				loaderDuration: 0,
			},
		});
	},
};

export default actionHandlers;
