import {
	NEW_DOCUMENT_SECTION,
	DOCUMENTATION_RECORD_TITLE,
} from '../constants/GeneralConstants';

const planDocumentNewRecordQuery = `query ($sysId: String!) {
    GlideRecord_Query {
        {{ table }} (sys_id: $sysId)  {
            ${NEW_DOCUMENT_SECTION}: _results {
                sys_id {
                    value
                }
                contents {
                    value
                }
                status {
                    value
                }
                ${DOCUMENTATION_RECORD_TITLE}: title {
                    value
                }
            }
        }
    }
}`;

export default planDocumentNewRecordQuery;
