import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import SELECTED_VALUES_QUERY_ACTIONS from '../constants/SelectedValuesQueryActionTypes';
import query from '../querries/SelectionTableQuery';

const requestSysUserData = createGraphQLEffect(query, {
	variableList: ['queryConditions', 'maxTypeaheadOptions'],
	templateVarList: ['selectionTable'],
	startActionType: SELECTED_VALUES_QUERY_ACTIONS.SELECTED_VALUES_QUERY_STARTED,
	successActionType: SELECTED_VALUES_QUERY_ACTIONS.SELECTED_VALUES_QUERY_SUCCEEDED,
	errorActionType: SELECTED_VALUES_QUERY_ACTIONS.SELECTED_VALUES_QUERY_FAILED
});

const _orderQueryResultsWithGlideList = (glideList, queryResults) => {
	let orderedQueryResults = [];
	let glideListArray = glideList.split(',');
	for (const glideListValue of glideListArray) {
		for (const queryResultValue of queryResults) {
			let queryResultSysId = get(queryResultValue, 'sys_id.value');
			if (glideListValue === queryResultSysId) {
				orderedQueryResults.push({
					value: queryResultSysId,
					displayValue: get(queryResultValue, 'name.value'),
					avatar: get(queryResultValue, 'nestedQuery.GlideDomain_Query.userInfo.avatarPath') || ''
				});
				break;
			}
		}
	}

	return orderedQueryResults;
};

const getGraphqlEffectSuccess = ({action, state, updateState}) => {
	const { currentlySelectedGlideList, properties } = state;
	const { selectionTable } = properties;
	const currentlySelectedRecords = get(action, `payload.data.GlideRecord_Query.${selectionTable}.results`);

	if (!currentlySelectedGlideList) {
		return updateState({
			currentlySelected: [],
			isLoading: false
		});
	}

	const currentlySelected = _orderQueryResultsWithGlideList(currentlySelectedGlideList, currentlySelectedRecords);

	return updateState({
		currentlySelected,
		isLoading: false
	});
};

const getGraphqlEffectFailure = ({action, updateState, state}) => {
	const { properties, currentlySelectedGlideList } = state;
	const { selectionTable } = properties;
	const error = action.payload;
	console.error(`An error occured attenmpting to fetch currently-selected (${currentlySelectedGlideList}) records from ${selectionTable}`);
	console.error(error);
	updateState({
		error: true
	});
};

const actions = {
	[SELECTED_VALUES_QUERY_ACTIONS.SELECTED_VALUES_QUERY_REQUESTED] :{
		schema: {
			type: 'object',
			properties: {
				queryConditions: {type: 'string'},
				maxTypeaheadOptions: {type: 'number'},
				selectionTable: {type: 'string'}
			},
			required: ['queryConditions', 'maxTypeaheadOptions', 'selectionTable']
		},
		private: true
	},
};

const actionHandlers = {
	[SELECTED_VALUES_QUERY_ACTIONS.SELECTED_VALUES_QUERY_REQUESTED]: requestSysUserData,
	[SELECTED_VALUES_QUERY_ACTIONS.SELECTED_VALUES_QUERY_SUCCEEDED]: {
		effect: getGraphqlEffectSuccess,
		stopPropagation: true
	},
	[SELECTED_VALUES_QUERY_ACTIONS.SELECTED_VALUES_QUERY_FAILED]: {
		effect: getGraphqlEffectFailure,
		stopPropagation: true
	}
};

export {
	actions,
	actionHandlers
};

