import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './PlanDocumentOverviewScoreCardView';
import {
	CARD_HEADER_LABEL,
	PLAN_DOCUMENT_TOTAL_COUNT_LABEL,
	PLAN_DOCUMENT_COMPLETE_COUNT_LABEL,
	PLAN_DOCUMENT_PENDING_COUNT_LABEL,
} from './constants/PlanDocumentOverviewScoreCardConstants';
import lifecycleActionHandlers from './actions/PlanDocumentOverviewScoreCardLifeCycleActions';
import planOverviewQueryActionHandlers from './actions/PlanDocumentOverviewScoreCardQueryActions';
import styles from './PlanDocumentOverviewScoreCardElement.scss';
import { SECONDARY_SCORES_ORIENTATION } from '../CombinedScoreCard/CombinedScoreCardConstants';
import { recordWatcherBehavior } from '../../behaviors/RecordWatcherBehavior';

const TAG_NAME = 'sn-bcm-plan-document-overview-score';

/**
 * This component is to be used in the Plan Overview component. Will display documents' status for a plan
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-plan-document-overview-score
 * 		sysid=""
 * 		plan-document-pending-count-label=""
 * 		plan-document-complete-count-label=""
 * 		plan-document-total-count-label=""
 * 		card-header-label=""
 * 		secondary-score-orientation=""
 * 		related-item-tab-index=""

 * ></sn-bcm-plan-document-overview-score>
 * ```
 *
 * @seismicElement sn-bcm-plan-document-overview-score
 * @summary This component is to be used in the Plan Overview component. Will display documents' status for a plan"
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,

	properties: {
		/**
		 * Sysid of the record the overview will be displayed on
		 *
		 * @type {string}
		 */
		sysid: {
			default: null,
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of card header of the widget
		 *
		 * @type {string}
		 */
		cardHeaderLabel: {
			default: CARD_HEADER_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the pending document count
		 *
		 * @type {string}
		 */
		planDocumentPendingCountLabel: {
			default: PLAN_DOCUMENT_PENDING_COUNT_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the complete document count
		 *
		 * @type {string}
		 */
		planDocumentCompleteCountLabel: {
			default: PLAN_DOCUMENT_COMPLETE_COUNT_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the complete document count
		 *
		 * @type {string}
		 */
		planDocumentTotalCountLabel: {
			default: PLAN_DOCUMENT_TOTAL_COUNT_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Orientation of the secondary score in the component
		 *
		 * @type {string}
		 */
		secondaryScoreOrientation: {
			default: SECONDARY_SCORES_ORIENTATION.VERTICAL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Boolean to enable the icon button
		 *
		 * @type {boolean}
		 */
		isActionButtonDisplayed: {
			required: true,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Related Item Tab Index number for icon button
		 *
		 * @type {number}
		 */
		relatedItemTabIndex: {
			default: 0,
			required: false,
			schema: {
				type: 'number',
			},
		},
		/**
		 * If enabled, a record watcher will keep this component updated in real-time.
		 *
		 * @type {boolean}
		 */
		isRecordWatcherEnabled: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
	},
	initialState: {
		/**
		 * Intial loading state of the component
		 *
		 * @type {boolean}
		 */
		isLoading: true,
		/**
		 * Total document count of the plan
		 *
		 * @type {number}
		 */
		totalDocumentCount: null,
		/**
		 * Total pending documents count of the plan
		 *
		 * @type {number}
		 */
		pendingDocumentCount: null,
		/**
		 * Total completed documents count of the plan
		 *
		 * @type {number}
		 */
		completeDocumentCount: null,
		/**
		 * Configuration for the record-watcher subscription.
		 *
		 * @type {object}
		 */
		recordWatcherConfigurations: null,
	},
	behaviors: [recordWatcherBehavior],
	actionHandlers: {
		...lifecycleActionHandlers,
		...planOverviewQueryActionHandlers,
	},
	styles,
});

export default TAG_NAME;
