import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import planTemplateOrderByQuery from '../queries/planTemplateOrderQuery';
import PLAN_TEMPLATES_ORDER_QUERY from '../constants/PlanTemplateOrderQueryActionsTypes';
import PLAN_DOCUMENT_QUERY from '../constants/DocumentationListQueryActionTypes';
import {
	DEFAULT_PROPERTIES_VALUES,
	PLAN_QUERY_PREFIX,
	PLAN_QUERY_ORDER_BY,
} from '../constants/GeneralConstants';

const queryPlanTemplatesOrder = createGraphQLEffect(planTemplateOrderByQuery, {
	templateVarList: ['table'],
	variableList: ['sysId'],
	startActionType: PLAN_TEMPLATES_ORDER_QUERY.STARTED,
	successActionType: PLAN_TEMPLATES_ORDER_QUERY.SUCCEEDED,
	errorActionType: PLAN_TEMPLATES_ORDER_QUERY.FAILED,
});

const requestPlanDocumentSections = ({
	dispatch,
	properties,
	action,
	updateState,
}) => {
	const { tableName, planSysId, maxDocumentationSectionCount } = properties;
	const { PRIMARY_TABLE } = DEFAULT_PROPERTIES_VALUES;
	const queryConditions = PLAN_QUERY_PREFIX + planSysId + PLAN_QUERY_ORDER_BY;

	const documentSectionRefs = get(
		action.payload,
		`data.GlideRecord_Query.${PRIMARY_TABLE}.plan[0].template.results.document_sections`
	);
	
	const parentRecordCanWrite = get(
		action.payload,
		`data.GlideRecord_Query.${PRIMARY_TABLE}.plan[0]._row_data.canWrite`
	);

	if (documentSectionRefs && documentSectionRefs.value) {
		const planTemplatesOrderArray = documentSectionRefs.value.split(',');

		updateState({
			planTemplatesOrderArray,
			showNewButton:parentRecordCanWrite,
		});
	}

	dispatch(PLAN_DOCUMENT_QUERY.REQUESTED, {
		tableName,
		maxDocumentationSectionCount,
		queryConditions,
	});
};

const templatesOrderQueryFailed = ({ dispatch, properties, action }) => {
	const { tableName, planSysId, maxDocumentationSectionCount } = properties;
	const queryConditions = PLAN_QUERY_PREFIX + planSysId + PLAN_QUERY_ORDER_BY;
	const error = action.payload;

	console.error(error);
	dispatch(PLAN_DOCUMENT_QUERY.REQUESTED, {
		tableName,
		maxDocumentationSectionCount,
		queryConditions,
	});
};

const actionHandlers = {
	[PLAN_TEMPLATES_ORDER_QUERY.REQUESTED]: queryPlanTemplatesOrder,
	[PLAN_TEMPLATES_ORDER_QUERY.SUCCEEDED]: {
		effect: requestPlanDocumentSections,
		stopPropogation: true,
	},
	[PLAN_TEMPLATES_ORDER_QUERY.FAILED]: {
		effect: templatesOrderQueryFailed,
		stopPropogation: true,
	},
};

export default actionHandlers;
