import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './DynamicCarouselElement.scss';
import view from './DynamicCarouselView';
import {
	DEFAULT_CAROUSEL_ITEMS,
	DEFAULT_ACTIVE_SECTION_INDICATOR_COLOR,
	DEFAULT_INACTIVE_SECTION_INDICATOR_COLOR,
	DEFAULT_MAX_ELEMENTS_PER_SECTION,
	DEFAULT_CAROUSEL_SCROLL_BUTTON_SIZE,
} from './constants/DynamicCarouselConstants';
import lifecycleActions from './actions/DynamicCarouselLifecycleActions';
import carouselScrollActionHandlers from './actions/CarouselScrollActions';
import '@servicenow/now-button';

const TAG_NAME = 'sn-bcm-dynamic-carousel';

/**
 * A carousel component to display overflow HTML elements.
 * HTML elements passed in must have their own inline styling (since web components encapsulate styling).
 *
 * ```
 *
 * <sn-bcm-dynamic-carousel
 * 		elements={arrayOfHtmlElements}
 * 		maxElementsPerSection={4}
 * 		activeSectionIndicatorColor="#000000"
 * 		inactiveSectionIndicatorColor="#ffffff"
 * ></sn-bcm-dynamic-carousel>
 *
 * ```
 *
 * @seismicElement sn-bcm-dynamic-carousel
 * @summary A carousel component to display overflow HTML elements.
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		 * Array of html elements to display in carousel.
		 * @type {array}
		 */
		elements: {
			default: DEFAULT_CAROUSEL_ITEMS,
			required: false,
			schema: {
				type: 'array'
			}
		},
		/**
		 * Number of html elements to show per carousel slide/section.
		 * @type {number}
		 */
		maxElementsPerSection: {
			default: DEFAULT_MAX_ELEMENTS_PER_SECTION,
			required: false,
			schema: {
				type: 'number'
			}
		},
		/**
		 * Hex color to indicate active carousel section (ie. #00000).
		 * @type {string}
		 */
		activeSectionIndicatorColor: {
			default: DEFAULT_ACTIVE_SECTION_INDICATOR_COLOR,
			required: false,
			schema: {
				type: 'string'
			}
		},
		/**
		 * Hex color to indicate inactive carousel section (ie. #00000).
		 * @type {string}
		 */
		inactiveSectionIndicatorColor: {
			default: DEFAULT_INACTIVE_SECTION_INDICATOR_COLOR,
			required: false,
			schema: {
				type: 'string'
			}
		},
		/**
		 * Size of carousel scroll buttons (ie. 'sm','md').
		 * @type {string}
		 */
		carouselScrollButtonSize: {
			default: DEFAULT_CAROUSEL_SCROLL_BUTTON_SIZE,
			required: false,
			schema: {
				type: 'string'
			}
		},
	},
	initialState: {
		isCarouselButtonsDisplayed: false,
		startDisplayIndex: 0,
		endDisplayIndex: 0,
		carouselSectionsCount: 0,
		currentSectionIndex: 0,
	},
	actionHandlers: {
		...lifecycleActions,
		...carouselScrollActionHandlers,
	},
	styles,
});

export default TAG_NAME;
