import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { FETCH_ERROR_MESSAGE } from '../constants/NewBiaDynamicModalConstants';
import {
	NOW_RECORD_FIELD_LOOKUP_CONNECTED_VALUE_CHANGED,
	SOURCE_TABLE_QUERY_REQUESTED,
	SOURCE_TABLE_QUERY_SUCCEEDED,
	SOURCE_TABLE_QUERY_FAILED,
	SN_RECORD_CHOICE_VALUE_CHANGED,
} from '../constants/NewBiaDynamicModalActionTypes';
import getSourceTableDataQuery from '../queries/SourceTableQuery';

const fieldValueChanged = ({ action, updateState, dispatch }) => {
	if (action.error) {
		console.error(FETCH_ERROR_MESSAGE);
		console.error(action.error);
		updateState({
			error: true,
		});
	}

	const data = action.payload;
	const field = get(data, 'name');
	const value = get(data, 'value');
	const displayValue = get(data, 'displayValue');

	updateState({
		[field]: value,
		[`${field}DisplayValue`]: displayValue,
	});

	if (field === 'template') {
		if (value) {
			dispatch(SOURCE_TABLE_QUERY_REQUESTED, {
				sys_id: value,
			});
		} else {
			updateState({
				applies_to_table: '',
				appliesToTableDisplayValue: '',
			});
		}
	}
};

const choiceFieldValueChanged = ({ action, updateState }) => {
	if (action.error) {
		console.error(FETCH_ERROR_MESSAGE);
		console.error(action.error);
		updateState({
			error: true,
		});
	}

	const data = action.payload;
	const { value, displayValue, name } = data;

	updateState({
		[name]: value,
		[`${name}DisplayValue`]: displayValue,
	});
};

const requestSourceTable = createGraphQLEffect(getSourceTableDataQuery, {
	templateVarList: ['sys_id'],
	successActionType: SOURCE_TABLE_QUERY_SUCCEEDED,
	errorActionType: SOURCE_TABLE_QUERY_FAILED,
});

const requestSourceTableSuccess = ({ action, updateState }) => {
	const data = action.payload;

	if (data.errors.length) {
		console.error(FETCH_ERROR_MESSAGE);
		console.error(data.errors);
		updateState({
			error: true,
		});

		return;
	}

	const applies_to_table = get(
		data,
		'data.GlideRecord_Query.sn_bia_template._results[0].primary_element_assessed._reference.source_table.value'
	);
	const appliesToTableDisplayValue = get(
		data,
		'data.GlideRecord_Query.sn_bia_template._results[0].primary_element_assessed._reference.source_table.displayValue'
	);

	updateState({ applies_to_table, appliesToTableDisplayValue });
};

const requestSourceTableFailed = ({ action, updateState }) => {
	const data = action.payload;

	console.error(FETCH_ERROR_MESSAGE);
	console.error(data.errors);
	updateState({
		error: true,
		applies_to_table: '',
		appliesToTableDisplayValue: '',
	});
};

const actionHandlers = {
	[NOW_RECORD_FIELD_LOOKUP_CONNECTED_VALUE_CHANGED]: {
		effect: fieldValueChanged,
		stopPropagation: true,
	},
	[SN_RECORD_CHOICE_VALUE_CHANGED]: {
		effect: choiceFieldValueChanged,
		stopPropagation: true,
	},
	[SOURCE_TABLE_QUERY_REQUESTED]: requestSourceTable,
	[SOURCE_TABLE_QUERY_SUCCEEDED]: {
		effect: requestSourceTableSuccess,
		stopPropagation: true,
	},
	[SOURCE_TABLE_QUERY_FAILED]: {
		effect: requestSourceTableFailed,
		stopPropagation: true,
	},
};

export default actionHandlers;
