import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './PlanActivitiesScoreCardView';
import styles from './PlanActivitiesScoreCardElement.scss';
import lifecycleActionHandlers from './actions/PlanActivitiesScoreCardLifeCycleActions';
import planActivitiesActionHandlers from './actions/PlanActivitiesScoreCardDataFetchActions';
import {
	ASSIGNED_ACTIVITIES_COUNT_LABEL,
	UNASSIGNED_ACTIVITIES_COUNT_LABEL,
	TOTAL_ACTIVITIES_COUNT_LABEL,
	CARD_HEADER_LABEL,
} from './constants/PlanActivitiesScoreCardConstants';
import { recordWatcherBehavior } from '../../behaviors/RecordWatcherBehavior';

const TAG_NAME = 'sn-bcm-recovery-tasks-overview-score';

/**
 * This component is to be used in the Plan Overview component. Will display activity scores for a plan
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-recovery-tasks-overview-score
 * 		plan-sys-id=""
 * 		assigned-activities-count-label=""
 * 		un-assigned-activities-count-label=""
 * 		total-activities-count-label=""
 * 		card-header-label=""
 * 		related-item-tab-index=""

 * ></sn-bcm-recovery-tasks-overview-score>
 * ```
 *
 * @seismicElement sn-bcm-recovery-tasks-overview-score
 * @summary This component is to be used in the Plan Overview component. Will display activity scores for a plan
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,

	properties: {
		/**
		 * SysId of the record the overview will be displayed on
		 *
		 * @type {string}
		 */
		planSysId: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the assigned activities count
		 *
		 * @type {string}
		 */
		assignedActivitiesCountLabel: {
			default: ASSIGNED_ACTIVITIES_COUNT_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the unassigned activities count
		 *
		 * @type {string}
		 */
		unAssignedActivitiesCountLabel: {
			default: UNASSIGNED_ACTIVITIES_COUNT_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the total activities count
		 *
		 * @type {string}
		 */
		totalActivitiesCountLabel: {
			default: TOTAL_ACTIVITIES_COUNT_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the card header of score card
		 *
		 * @type {string}
		 */
		cardHeaderLabel: {
			default: CARD_HEADER_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Boolean to enable the icon button
		 *
		 * @type {boolean}
		 */
		isActionButtonDisplayed: {
			required: true,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Related Item Tab Index number for icon button
		 *
		 * @type {number}
		 */
		relatedItemTabIndex: {
			default: 0,
			required: false,
			schema: {
				type: 'number',
			},
		},
		/**
		 * If enabled, a record watcher will keep this component updated in real-time.
		 *
		 * @type {boolean}
		 */
		isRecordWatcherEnabled: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
	},
	initialState: {
		/**
		 * State of the component whether it is loaded or not
		 *
		 * @type {boolean}
		 */
		isLoading: true,
		/**
		 * Total count of the activities
		 *
		 * @type {number}
		 */
		totalActivitiesCount: null,
		/**
		 * Count of the assigned activities
		 *
		 * @type {number}
		 */
		assignedActivitiesCount: null,
		/**
		 * Count of the unassigned activities
		 *
		 * @type {number}
		 */
		unAssignedActivitiesCount: null,
		/**
		 * Configuration for the record-watcher subscription.
		 *
		 * @type {object}
		 */
		recordWatcherConfigurations: null,
	},
	behaviors: [recordWatcherBehavior],
	actionHandlers: {
		...lifecycleActionHandlers,
		...planActivitiesActionHandlers,
	},
	styles,
});

export default TAG_NAME;
