import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './EmptyStateView';
import styles from './EmptyStateElement.scss';
import { EMPTY_STATE_LABEL } from './constants/EmptyStateConstants';

const TAG_NAME = 'sn-bcm-empty-state';

/**
 * This component to be used to indicate empty state.
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-empty-state
 * 		empty-state-label=""
 * ></sn-bcm-empty-state>
 * ```
 *
 * @seismicElement sn-bcm-empty-state
 * @summary Used to indicate empty state of the component.
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,

	properties: {
		/**
		 * Label to displayed
		 *
		 * @type { string }
		 */
		emptyStateLabel: {
			default: EMPTY_STATE_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
	},
	styles,
});

export default TAG_NAME;
