import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './RelatedItemRecordListView';
import styles from './RelatedItemRecordListElement.scss';
import { actionHandlers as relatedItemRecordFetchActionHandlers } from './actions/RelatedItemRecordListActions';
import lifeCycleActionHandlers from './actions/RelatedItemRecordListLifeCycleActions';
import { actions as modalActions } from '../RecordModal/actions/modalVisibilityActions';
import {
	WORKSPACE_VIEW,
	CANCEL_BUTTON_LABEL,
	ACTION_BUTTON_LABEL,
} from './constants/RelatedItemRecordListConstants';

const TAG_NAME = 'sn-bcm-related-item-record-list';

/**
 * Related item record list component to show list view for related items.
 * Opens a modal to edit record.
 *
 * ```
 * import RelatedItemRecordList from 'app-bcm-components';
 * import RelatedItemRecordList from 'relativePathToThisFile';
 *
 * <sn-bcm-related-item-record-list
 *      parentTable={'sn_bcp_plan'}>
 * </sn-bcm-related-item-record-list>
 * ```
 *
 * @seismicElement sn-bcm-related-item-record-list
 * @summary element to render list for related Items
 */
createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,

	properties: {
		/**
		 * sysId of the parent record
		 *
		 * @type {String}
		 */
		parentSysId: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * parent table name
		 *
		 * @type {String}
		 */
		parentTable: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * table name for the related list
		 *
		 * @type {String}
		 */
		relatedTable: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * related list name to render
		 *
		 * @type {String}
		 */
		relatedListName: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * view to render form section
		 *
		 * @type {String}
		 */
		relatedItemFormView: {
			default: WORKSPACE_VIEW,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * query conditions
		 *
		 * @type {String}
		 */
		queryConditions: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * record list title
		 *
		 * @type {String}
		 */
		listTitle: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * now record list connected properties
		 *
		 * @type {object}
		 */
		recordListProperties: {
			default: {},
			required: false,
			schema: {
				type: 'object',
			},
		},
		/**
		 * Modal action button label
		 *
		 * @type {String}
		 */
		formActionButtonLabel: {
			default: ACTION_BUTTON_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Modal cancel button label
		 *
		 * @type {String}
		 */
		formCancelButtonLabel: {
			default: CANCEL_BUTTON_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
	},
	initialState: {
		/**
		 * Indicates the error state of component.
		 *
		 * @type {boolean}
		 */
		error: false,
		/**
		 * Flag to indicate open state of the modal
		 * @type {boolean}
		 */
		isModalOpen: false,
		/**
		 * recordModalSysId of the related table record to open
		 * @type {String}
		 */
		recordModalSysId: '-1',
		/**
		 * refresh record list
		 * @type {Object}
		 */
		refreshRequested: {},
		/**
		 * modal header label
		 */
		headerLabel: '',
	},
	actions: {
		...modalActions,
	},
	actionHandlers: {
		...relatedItemRecordFetchActionHandlers,
		...lifeCycleActionHandlers,
	},
	styles,
});

export default TAG_NAME;
