import { actionTypes } from '@servicenow/ui-core';
import {
	CARD_EDITED_STATUS,
	CARD_EDITABLE_FIELDS,
} from '../constants/GeneralConstants';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const updateCardState = ({ properties, updateState }) => {
	const { contents } = properties;

	updateState({
		richTextContent: contents,
	});
};

const focusEditButtonOnStatusComplete = ({ action }) => {
	const { host, name, previousValue, value } = action.payload;

	if (
		name === CARD_EDITABLE_FIELDS.STATUS_FIELD &&
		previousValue === CARD_EDITED_STATUS.PENDING &&
		value === CARD_EDITED_STATUS.COMPLETE
	) {
		let attempts = 0;
		const totalAttempts = 20;

		const findButtonIntervalId = setInterval(() => {
			try {
				const editButtonElement = host.shadowRoot
					.querySelector('now-button.-edit-btn')
					.shadowRoot.querySelector('button');

				if (editButtonElement instanceof HTMLElement) {
					editButtonElement.focus();
					clearInterval(findButtonIntervalId);
				}
			} catch (err) {
				console.error('ERROR', err);
			}

			attempts += 1;

			if (attempts >= totalAttempts) {
				clearInterval(findButtonIntervalId);
			}
		}, 100);
	}
};
const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: updateCardState,
	[COMPONENT_PROPERTY_CHANGED]: focusEditButtonOnStatusComplete,
};

export default actionHandlers;
