import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	IMPACT_ASSESSMENT_REQUESTED,
	IMPACT_ASSESSMENT_SUCCEEDED,
	IMPACT_ASSESSMENT_FAILED,
} from '../constants/ImpactAssessmentScoreCardActionTypes';
import impactAssessmentQuery from '../queries/ImpactAssessmentScoreCardQuery';
import {
	FETCH_ERROR_MESSAGE,
	NULL_TABLE_ERROR_MESSAGE,
	IMPACT_ASSESSMENT_TABLE,
} from '../constants/ImpactAssessmentScoreCardConstants';

const requestActivitiesData = createGraphQLEffect(impactAssessmentQuery, {
	variableList: ['biaSysId'],
	successActionType: IMPACT_ASSESSMENT_SUCCEEDED,
	errorActionType: IMPACT_ASSESSMENT_FAILED,
});

const _getImpactAssessmentResult = (impactAssessment) => {
	const recoveryTierChoice = impactAssessment.recovery_tier.displayValue;
	const recoveryTimeObjectiveLength =
		impactAssessment.recovery_time_objective.displayValue;

	return {
		recoveryTierChoice,
		recoveryTimeObjectiveLength,
	};
};

const getImpactAssessmentSuccess = ({ action, updateState }) => {
	const fetchResult = action.payload;
	const fetchErrors = fetchResult.errors;

	if (fetchErrors.length) {
		console.error(FETCH_ERROR_MESSAGE);
		console.error(fetchErrors);

		updateState({
			error: true,
			isLoading: false,
		});

		return;
	}

	const dataReturned = get(
		fetchResult,
		`data.GlideRecord_Query.${IMPACT_ASSESSMENT_TABLE}.results[0]`
	);

	if (!dataReturned) {
		console.error(NULL_TABLE_ERROR_MESSAGE);

		updateState({
			error: true,
			isLoading: false,
		});

		return;
	}

	const {
		recoveryTierChoice,
		recoveryTimeObjectiveLength,
	} = _getImpactAssessmentResult(dataReturned);

	updateState({
		recoveryTierChoice,
		recoveryTimeObjectiveLength,
		isLoading: false,
	});
};

const getImpactAssessmentFail = ({ action, updateState }) => {
	const error = action.payload;

	console.error(FETCH_ERROR_MESSAGE);
	console.error(error);

	updateState({
		error: true,
		isLoading: false,
	});
};

const actionHandlers = {
	[IMPACT_ASSESSMENT_REQUESTED]: requestActivitiesData,
	[IMPACT_ASSESSMENT_SUCCEEDED]: {
		effect: getImpactAssessmentSuccess,
		stopPropogation: true,
	},
	[IMPACT_ASSESSMENT_FAILED]: {
		effect: getImpactAssessmentFail,
		stopPropogation: true,
	},
};

export default actionHandlers;
