import {
	FILTER_BY_PLAN_SCOPE_CHOICE,
	SHOW_ALL_RECORDS_CHOICE,
	DEFAULT_ITEM_INSERTION_LOADING_MESSAGE,
	ITEM_TABLE_FIELD_NAME,
	ITEM_FIELD_NAME,
	PARENT_TABLE_REFERENCE_FIELD,
	RELATED_ASSET_DEPENDENCY_TABLE,
	MODAL_VIEW_TYPES,
} from '../constants/RelatedAssetDependencyItemPickerModalConstants';
import {
	MODAL_NEXT_BUTTON_CLICKED,
	MODAL_BACK_BUTTON_CLICKED,
	RELATED_ASSET_DEPENDENCY,
} from '../constants/RelatedAssetDependencyItemPickerModalActionTypes';
import { ACTION_TYPES } from '../../ItemPickerModal/constants/ItemPickerModalCommonConstants';

const actions = {
	[ACTION_TYPES.ITEM_PICKER_EDIT_CANCELLED]: {
		private: false,
	},
	[ACTION_TYPES.ITEM_PICKER_EDIT_SAVED]: {
		private: true,
	},
	[MODAL_NEXT_BUTTON_CLICKED]: {
		private: true,
	},
};

const closeModal = ({ updateState }) => {
	updateState({
		isModalOpen: false,
	});
};

const backButtonClicked = ({ state, updateState }) => {
	const { prevScreenView } = state;

	updateState({
		isLoading: false,
		currentScreenView: prevScreenView,
		prevScreenView: null,
	});
};

const saveItemPickerEdit = ({ state, dispatch, updateState }) => {
	const { selectionTable, properties, currentlySelectedEncodedQuery } = state;
	const { parentRecordSysId } = properties;

	const listOfSelectedItemsSysIds = currentlySelectedEncodedQuery
		.substring(
			currentlySelectedEncodedQuery.indexOf('N') + 1,
			currentlySelectedEncodedQuery.length
		)
		.split(',');

	dispatch(RELATED_ASSET_DEPENDENCY.ITEM_INSERTION_REQUESTED, {
		data: {
			targetTableIdentifier: RELATED_ASSET_DEPENDENCY_TABLE,
			itemTableFieldNameValue: selectionTable,
			itemTableFieldName: ITEM_TABLE_FIELD_NAME,
			itemFieldName: ITEM_FIELD_NAME,
			parentRecordSysId,
			parentTableReferenceField: PARENT_TABLE_REFERENCE_FIELD,
			listOfSelectedItemsSysIds,
		},
	});

	updateState({
		loadingMessage: DEFAULT_ITEM_INSERTION_LOADING_MESSAGE,
		isLoading: true,
	});
};

const showListPickerElement = ({ state, dispatch, updateState }) => {
	const {
		selectionTable,
		radioSelectionValue,
		selectedPlanScopeValue,
		currentScreenView,
	} = state;
	const { parentRecordSysId } = state.properties;

	updateState({
		prevScreenView: currentScreenView,
		currentScreenView: MODAL_VIEW_TYPES.RECORD_LIST_PICKER,
		isLoading: true,
	});

	if (
		radioSelectionValue === FILTER_BY_PLAN_SCOPE_CHOICE.id &&
		selectedPlanScopeValue.fields.bia
	) {
		dispatch(RELATED_ASSET_DEPENDENCY.FETCH_BIA_DEPENDENCIES_QUERY_REQUESTED, {
			itemsToIncludeTableIdentifier: RELATED_ASSET_DEPENDENCY_TABLE,
			biaSysId: selectedPlanScopeValue.fields.bia,
			parentRecordSysId,
			selectionTable,
		});
	} else if (radioSelectionValue === SHOW_ALL_RECORDS_CHOICE.id) {
		dispatch(RELATED_ASSET_DEPENDENCY.EXCLUDED_ITEMS_QUERY_REQUESTED, {
			excludedItemsTableIdentifier: RELATED_ASSET_DEPENDENCY_TABLE,
			parentRecordSysId,
		});
	} else {
		updateState({
			isLoading: false,
			recordListPickerFilterConditions: 'sys_idIN',
		});
	}
};

const actionHandlers = {
	[ACTION_TYPES.ITEM_PICKER_EDIT_CANCELLED]: closeModal,
	[ACTION_TYPES.ITEM_PICKER_EDIT_SAVED]: saveItemPickerEdit,
	[MODAL_NEXT_BUTTON_CLICKED]: showListPickerElement,
	[MODAL_BACK_BUTTON_CLICKED]: backButtonClicked,
};

export { actions, actionHandlers };
