import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-modal';
import '@servicenow/now-record-form-connected';
import '@servicenow/now-button';
import { GFORM_ACTIONS, CLEAR_NOTIFICATIONS} from '@devsnc/sn-scripting-environment';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './RecordModalElement.scss';
import {
	MODAL_CLOSE_CLICKED,
	SYSVERB_WS_SAVE,
	MODAL_RECORD_DELETED,
} from './constants/RecordModalActionTypes';
import { RECORD_MODAL_FOOTER } from './constants/RecordPreviewConstants';
import { CLEAR_NOTIFICATIONS_ACTION_TYPE } from './constants/RecordModalConstants';
import '@servicenow/now-loader';

export default (state) => {
	const { properties, isLoading, isModalLoading, isRecordEditable } = state;
	const {
		table,
		sysId,
		formView,
		headerLabel,
		actionButtonLabel,
		cancelButtonLabel,
		reloadAfterSave,
		deleteButtonLabel,
		isModalOpen,
		isReadOnly,
		modalSize,
		queryConditions,
		loadingViewMessage,
	} = properties;

	const isNewRecord = sysId === '-1';

	const footerView = (state, { dispatch }) => {
		let cancelButton;
		let saveButton;
		let submitButton;
		let deleteButton;
		const {
			footerActionButtonLabel,
			footerCancelButtonLabel,
			showFooterButton,
			footerDeleteActionButtonLabel,
			editRecordSysId,
			editRecordTable,
		} = state.properties;

		if (footerDeleteActionButtonLabel && showFooterButton) {
			deleteButton = (
				<now-button
					className="delete-button"
					size="md"
					variant="secondary-negative"
					label={footerDeleteActionButtonLabel}
					tooltip-content={footerDeleteActionButtonLabel}
					on-click={() => {
						dispatch(MODAL_RECORD_DELETED, {
							sysId: editRecordSysId,
							table: editRecordTable,
						});
					}}
				/>
			);
		}

		if (
			(footerCancelButtonLabel && showFooterButton) ||
			(footerCancelButtonLabel && isNewRecord)
		) {
			cancelButton = (
				<now-button
					className="cancel-button"
					size="md"
					variant="secondary"
					label={footerCancelButtonLabel}
					tooltip-content={footerCancelButtonLabel}
					on-click={() => {
						dispatch(MODAL_CLOSE_CLICKED, {
							value: false,
						});
					}}
				/>
			);
		}

		if (footerActionButtonLabel) {
			if (!isNewRecord && showFooterButton) {
				saveButton = (
					<now-button
						className="save-button"
						size="md"
						variant="primary"
						append-to-payload={{
							source: RECORD_MODAL_FOOTER,
						}}
						label={footerActionButtonLabel}
						tooltip-content={footerActionButtonLabel}
						on-click={() => {
							dispatch(CLEAR_NOTIFICATIONS, {
								notificationAction: {
									type: CLEAR_NOTIFICATIONS_ACTION_TYPE,
								},
							});
							dispatch(GFORM_ACTIONS.GFORM_SAVE);
						}}
					/>
				);
			} else if (isNewRecord) {
				submitButton = (
					<now-button
						className="submit-button"
						size="md"
						variant="primary"
						append-to-payload={{
							source: RECORD_MODAL_FOOTER,
						}}
						label={footerActionButtonLabel}
						tooltip-content={footerActionButtonLabel}
						on-click={() => {
							dispatch(CLEAR_NOTIFICATIONS, {
								notificationAction: {
									type: CLEAR_NOTIFICATIONS_ACTION_TYPE,
								},
							});
							dispatch(GFORM_ACTIONS.GFORM_SUBMIT, {
								submitActionName: SYSVERB_WS_SAVE,
							});
						}}
					/>
				);
			}

			return (
				<div className="button-row">
					{deleteButton}
					{cancelButton}
					{saveButton}
					{submitButton}
				</div>
			);
		}
	};

	createCustomElement(RECORD_MODAL_FOOTER, {
		renderer: {
			type: snabbdom,
		},
		view: footerView,
		properties: {
			// TODO: this is not exposed to the outside. So we probably dont need this much detail.
			footerActionButtonLabel: {
				default: '',
				required: false,
				schema: {
					type: 'string',
				},
			},
			footerCancelButtonLabel: {
				default: '',
				required: false,
				schema: {
					type: 'string',
				},
			},
			footerDeleteActionButtonLabel: {
				default: '',
				required: false,
				schema: {
					type: 'string',
				},
			},
			editRecordTable: {
				default: '',
				required: false,
				schema: {
					type: 'string',
				},
			},
			editRecordSysId: {
				default: '',
				required: false,
				schema: {
					type: 'string',
				},
			},
			reloadAfterSave: {
				default: false,
				required: false,
				schema: {
					type: 'boolean',
				},
			},
			showFooterButton: {
				default: false,
				required: false,
				schema: {
					type: 'boolean',
				},
			},
		},
		styles,
	});

	return (
		<now-modal
			headerLabel={headerLabel}
			size={modalSize}
			opened={isModalOpen}
			manageOpened>
			{/* TODO: isLoading and isModalLoading look like they can be consolidated into one single variable */}
			{/* TODO: they handle the exact same thing (behave the same way) */}
			<div className={isLoading || isModalLoading ? `loading-view` : 'hidden'}>
				<now-loader label={loadingViewMessage} />
			</div>

			<now-record-form-section-connected
				className={isLoading || isModalLoading ? 'hidden' : 'connected-form-element'}
				table={table}
				sysId={sysId}
				view={formView}
				query={queryConditions}
				isReadOnly={isReadOnly}>
				<record-modal-footer
					className="record-modal-footer"
					slot="footer"
					footerActionButtonLabel={actionButtonLabel}
					footerCancelButtonLabel={cancelButtonLabel}
					footerDeleteActionButtonLabel={deleteButtonLabel}
					showFooterButton={isRecordEditable}
					editRecordSysId={sysId}
					editRecordTable={table}
				/>
			</now-record-form-section-connected>
		</now-modal>
	);
};
