import '@servicenow/now-loader';
import '../PieChartCard/PieChartCardElement';
import {
	FETCH_ERROR_MESSAGE,
	LOADING_MESSAGE,
} from './constants/BIAPieChartRibbonCardConstants';

const view = (state) => {
	const { properties, options, seriesData, isLoading, isError, totalLabel, cardHeaderLabel, isAccessibilityModeActive } = state;
	const { legendOnly } = properties;

	if (isError) {
		return (
			<div className="error-view">
				<span>{FETCH_ERROR_MESSAGE}</span>
			</div>
		);
	}

	options.seriesData = seriesData;

	const pieChartCard = (
		<sn-bcm-pie-chart-card
			{...options}
			legendOnly={legendOnly}
			totalLabel={totalLabel}
			cardHeaderLabel={cardHeaderLabel}
			fillHeight
			isLoading={isLoading}
			loadingMessage={LOADING_MESSAGE}
			isChartAccessibilityModeActive= {isAccessibilityModeActive}
		/>
	);

	return pieChartCard;
};

export default view;
