import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	ITEM_INSERTION_REQUESTED,
	ITEM_INSERTION_STARTED,
	ITEM_INSERTION_SUCCEEDED,
	ITEM_INSERTION_FAILED,
	RELATED_LIST_REFRESH_REQUESTED,
} from '../constants/ItemPickerModalActionTypes';
import {
	itemInsertionUrl,
	ITEM_INSERTION_ERROR_MESSAGE,
} from '../constants/ItemPickerModalConstants';

const actions = {
	[ITEM_INSERTION_REQUESTED]: {
		private: true,
	},
	[ITEM_INSERTION_STARTED]: {
		private: true,
	},
	[ITEM_INSERTION_SUCCEEDED]: {
		private: false,
	},
	[ITEM_INSERTION_FAILED]: {
		private: true,
	},
};

const getItemInsertionSuccess = ({ dispatch, updateState }) => {
	dispatch(RELATED_LIST_REFRESH_REQUESTED);
	updateState({
		isLoading: false,
		isModalOpen: false,
	});
};

const getItemInsertionFailure = ({ action, updateState }) => {
	const { data } = action.payload;

	console.error(data);
	updateState({
		isLoading: false,
		errorMessage: ITEM_INSERTION_ERROR_MESSAGE,
	});
};

const actionHandlers = {
	[ITEM_INSERTION_REQUESTED]: createHttpEffect(itemInsertionUrl, {
		method: 'POST',
		dataParam: 'data',
		startActionType: ITEM_INSERTION_STARTED,
		successActionType: ITEM_INSERTION_SUCCEEDED,
		errorActionType: ITEM_INSERTION_FAILED,
		batch: false,
	}),
	[ITEM_INSERTION_SUCCEEDED]: getItemInsertionSuccess,
	[ITEM_INSERTION_FAILED]: getItemInsertionFailure,
};

export { actions, actionHandlers };
