import {
	MODAL_CLOSE_CLICKED,
	ITEM_INSERTION_ACTION_TYPES,
	RECORD_LIST_ACTION_TYPES,
	MODAL_FOOTER_BUTTON_CLICKED,
	RADIO_CHOICE_VALUE_CHANGED,
	TYPEAHEAD_VALUE_SET,
	EVENT_ASSET_DATA_ACTION_TYPES,
	FILTER_PLAN_ASSETS_QUERY_ACTION_TYPES,
	FILTER_PLANS_QUERY_ACTION_TYPES,
} from '../constants/RecordModalActionTypes';

import {
	NEXT_BUTTON_LABEL,
	ADD_BUTTON_LABEL,
	CONFIRM_BUTTON_LABEL,
	ACTIVATED_PLAN_TABLE_NAME,
	CONFIRM_MODAL_TITLE,
} from '../constants/RecoveryAddPlansModalConstants';

const actions = {
	[MODAL_CLOSE_CLICKED]: {
		private: false,
	},
};

const updateSelectedRecords = ({ action, updateState }) => {
	let listOfSelectedSysIds = [];
	listOfSelectedSysIds  = action.payload.selectedRecords;

	updateState({
		listOfSelectedItemsSysIds: listOfSelectedSysIds,
		showAllPlans: false,
		disableActionButton: listOfSelectedSysIds.length == 0,
	});
};

const footerButtonClicked = ({ action, dispatch, state, updateState }) => {
	const { payload } = action;
	const {
		selectedAsset,
		listOfSelectedItemsSysIds,
		showAllPlans,
		properties,
		assetBased,
	} = state;
	const { eventSysId, isEventInProgress } = properties;

	if (payload.action.label === NEXT_BUTTON_LABEL) {
		// all plans scenario

		if (showAllPlans & !assetBased) {
			dispatch(FILTER_PLANS_QUERY_ACTION_TYPES.FILTER_PLAN_REQUESTED, {
				eventSysId,
			});
		} else {
			// select impacted asset scenario
			if (assetBased) {

				dispatch(FILTER_PLAN_ASSETS_QUERY_ACTION_TYPES.FILTER_PLAN_ASSETS_REQUESTED, {
					activatedTableName: ACTIVATED_PLAN_TABLE_NAME,
					queryCondition: 'event=' + eventSysId + '^event_asset' + selectedAsset['asset_name']
				});
			} else {
				dispatch(FILTER_PLAN_ASSETS_QUERY_ACTION_TYPES.FILTER_PLAN_ASSETS_REQUESTED, {
					activatedTableName: ACTIVATED_PLAN_TABLE_NAME,
					queryCondition: 'planIN' + listOfSelectedItemsSysIds + '^event=' + eventSysId
				});

			}
		}
	} else if (payload.action.label === ADD_BUTTON_LABEL) {
		if (isEventInProgress) {
			updateState({
				isOpenList: false,
				actionButtonLabel: CONFIRM_BUTTON_LABEL,
				showConfirmation: true,
				modalTitle: CONFIRM_MODAL_TITLE,
			});
		} else {
			dispatch(ITEM_INSERTION_ACTION_TYPES.ACTIVATED_PLAN_ITEM_INSERTION_REQUESTED, {
				data: {
					eventAsset: selectedAsset['id'],
					targetTableIdentifier: ACTIVATED_PLAN_TABLE_NAME,
					listOfSelectedItemsSysIds,
					assetBased,
					eventSysId,
				},
			});
		}
	} else if (payload.action.label === CONFIRM_BUTTON_LABEL) {
		dispatch(ITEM_INSERTION_ACTION_TYPES.ACTIVATED_PLAN_ITEM_INSERTION_REQUESTED, {
			data: {
				eventAsset: selectedAsset['id'],
				targetTableIdentifier: ACTIVATED_PLAN_TABLE_NAME,
				listOfSelectedItemsSysIds,
				assetBased,
				eventSysId,
			},
		});
	} else {
		dispatch(MODAL_CLOSE_CLICKED);
	}
};

const setChoiceSelection = ({ action, state, updateState, dispatch }) => {
	const { payload } = action;
	const { properties } = state;
	const { eventSysId } = properties;
	console.log(payload.items);
	if (payload.value === 'select asset') {
		updateState({
			showEventAssets: true,
			assetBased: true,
		});
		dispatch(EVENT_ASSET_DATA_ACTION_TYPES.FETCH_EVENT_ASSET_DATA_REQUESTED, {
			eventSysId,
		});
		// query event asset table for that event. show next typeahead.
	} else if (payload.value === 'show all plans') {
		updateState({
			showAllPlans: true,
			disableActionButton: false,
			showEventAssets: false,
			assetBased: false,
		});
	} else {
		updateState({
			selectedAsset: items[0],
		});
	}
};

const actionHandlers = {
	[MODAL_CLOSE_CLICKED]: ({ updateProperties }) => {
		updateProperties({
			isModalOpen: false,
		});
	},
	[MODAL_FOOTER_BUTTON_CLICKED]: footerButtonClicked,
	[TYPEAHEAD_VALUE_SET]: ({ action, updateState }) => {
		const { payload } = action;
		const { item } = payload;
		updateState({
			selectedAsset: item,
			disableActionButton: false,
		});
	},
	[RADIO_CHOICE_VALUE_CHANGED]: {
		effect: setChoiceSelection,
		stopPropogation: true,
	},
	[RECORD_LIST_ACTION_TYPES.LIST_RECORD_SELECTION_UPDATED]: updateSelectedRecords,
};

export { actions, actionHandlers };
