const query = `query getAssetTableAndFilter ($parentRecordSysId: String!) {
    GlideRecord_Query {
        sn_bcp_plan_loss_scenario (sys_id:$parentRecordSysId) {
            _results {
                loss_scenario {
                    value
                    _reference {
                        elements_impacted {
                            value
                            displayValue
                            _reference {
                                source_table {
                                    value
                                }
                                filter {
                                    value
                                }
                                source_table_fields {
                                    value
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}`;

export default query;
