import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	MODAL_CLOSE_CLICKED,
	ITEM_INSERTION_ACTION_TYPES,
} from '../constants/RecordModalActionTypes';
import {
	ITEM_INSERTION_URL,
	ITEM_INSERTION_ERROR_MESSAGE,
} from '../constants/RecoveryAddPlansModalConstants';

const getItemInsertionSuccess = ({ dispatch, updateState }) => {

	updateState({
		showConfirmation: false,
	});

	dispatch(MODAL_CLOSE_CLICKED);

};

const getItemInsertionFailure = ({ action, updateState, dispatch }) => {
	const { data } = action.payload;

	console.error(data);
	updateState({
		errorMessage: ITEM_INSERTION_ERROR_MESSAGE,
	});
	dispatch(MODAL_CLOSE_CLICKED);

};

const actionHandlers = {
	[ITEM_INSERTION_ACTION_TYPES.ACTIVATED_PLAN_ITEM_INSERTION_REQUESTED]: createHttpEffect(
		ITEM_INSERTION_URL,
		{
			method: 'POST',
			dataParam: 'data',
			successActionType: ITEM_INSERTION_ACTION_TYPES.ACTIVATED_PLAN_ITEM_INSERTION_SUCCEEDED,
			errorActionType: ITEM_INSERTION_ACTION_TYPES.ACTIVATED_PLAN_ITEM_INSERTION_FAILED,
			batch: false,
		}
	),
	[ITEM_INSERTION_ACTION_TYPES.ACTIVATED_PLAN_ITEM_INSERTION_SUCCEEDED]: getItemInsertionSuccess,
	[ITEM_INSERTION_ACTION_TYPES.ACTIVATED_PLAN_ITEM_INSERTION_FAILED]: getItemInsertionFailure,
};

export default actionHandlers;
