import '@servicenow/now-card';
import '@servicenow/now-score';
import '@servicenow/now-loader';
import {
	CARD_ORIENTATION,
	MAX_SECONDARY_SCORE_INDEX,
	MAXIMUM_VERTICAL_SECONDARY_SCORES,
	SECONDARY_SCORES_ORIENTATION,
	LOADING_VIEW_MESSAGE,
} from './CombinedScoreCardConstants';

const _isCardVertical = (orientation) => {
	return orientation === CARD_ORIENTATION.VERTICAL;
};

const _getPrimaryScoreElement = (
	orientation,
	primaryScore,
	primaryScoreFontSize
) => {
	return (
		<now-score-basic
			heading={primaryScore.label}
			headingPosition="top"
			score={primaryScore.score}
			canClick={primaryScore.canClick}
			scoreSize={primaryScoreFontSize}
			hideWhitespace={_isCardVertical(orientation)}
		/>
	);
};

const _getSecondaryScoreElements = (
	orientation,
	items,
	secondaryScoreOrientation
) => {
	const secondaryScoreElements = [];
	const isSecondaryScoreLayoutVertical =
		secondaryScoreOrientation === SECONDARY_SCORES_ORIENTATION.VERTICAL &&
		items.length <= MAXIMUM_VERTICAL_SECONDARY_SCORES &&
		!_isCardVertical(orientation);
	const scoreElementClassName = isSecondaryScoreLayoutVertical
		? 'score-horizontal-element'
		: 'score-element';

	for (let i = 0; i < items.length; i++) {
		const secondaryScoreItem = items[i];

		secondaryScoreElements.push(
			<div className={scoreElementClassName}>
				<now-score-basic
					className="secondary-score"
					canClick={secondaryScoreItem.canClick}
					heading={secondaryScoreItem.heading}
					headingPosition="top"
					score={secondaryScoreItem.score}
					scoreSize="xs"
					hideWhitespace
				/>
			</div>
		);
		if (i === MAX_SECONDARY_SCORE_INDEX) break;
	}

	return secondaryScoreElements;
};

const _getCardHeaderElement = (
	label,
	dispatch,
	isActionButtonDisplayed,
	actionButtonIcon,
	actionButtonActionName,
	actionButtonActionPayload,
	actionButtonTooltip,
	actionButtonAriaLabel
) => {
	const actionButtonElement = isActionButtonDisplayed ? (
		<now-button-iconic
			slot="metadata"
			on-click={() => {
				dispatch(actionButtonActionName, actionButtonActionPayload);
			}}
			icon={actionButtonIcon}
			bare
			hidePadding
			highContrast
			tooltipContent={actionButtonTooltip}
			configAria={{ 'aria-label': actionButtonAriaLabel }}
		/>
	) : null;

	return (
		<now-card-header
			heading={{
				label,
				variant: 'secondary',
				size: 'sm',
				level: 1,
				lines: 1,
			}}
			className="card-header">
			{actionButtonElement}
		</now-card-header>
	);
};

const _getCardFooterElement = (timestamp) => {
	const footerLabel = timestamp ? `Updated ${timestamp}` : '';

	return <now-card-footer className="now-card-footer" label={footerLabel} />;
};

const _getVerticalContentLayout = (
	primaryScore,
	secondaryScores,
	primaryScoreFontSize
) => {
	return (
			<div className="vertical-combined-scores-element">
				<div className="score-element">
					{_getPrimaryScoreElement(
						CARD_ORIENTATION.VERTICAL,
						primaryScore,
						primaryScoreFontSize
					)}
				</div>
				{_getSecondaryScoreElements(CARD_ORIENTATION.VERTICAL, secondaryScores)}
			</div>
	);
};

const _getHorizontalContentLayout = (
	primaryScore,
	secondaryScores,
	secondaryScoreOrientation,
	primaryScoreFontSize
) => {
	return (
			<div className="combined-scores-element">
				<div className="primary-score">
					{_getPrimaryScoreElement(
						CARD_ORIENTATION.HORIZONTAL,
						primaryScore,
						primaryScoreFontSize
					)}
				</div>
				<div className="scores-container">
					{_getSecondaryScoreElements(
						CARD_ORIENTATION.HORIZONTAL,
						secondaryScores,
						secondaryScoreOrientation
					)}
				</div>
			</div>
	);
};

const _getCardContent = (
	orientation,
	primaryItem,
	primaryScoreFontSize,
	secondaryItems,
	secondaryScoreOrientation
) => {
	if (_isCardVertical(orientation)) {
		return _getVerticalContentLayout(
			primaryItem,
			secondaryItems,
			primaryScoreFontSize
		);
	}

	return _getHorizontalContentLayout(
		primaryItem,
		secondaryItems,
		secondaryScoreOrientation,
		primaryScoreFontSize
	);
};

const _getCardClassName = (fillHeight, orientation) => {
	if (_isCardVertical(orientation) || fillHeight) {
		return 'now-card-element fill-height';
	}

	return 'now-card-element';
};

const getCardElement = (
	cardHeaderLabel,
	fillHeight,
	orientation,
	primaryScore,
	primaryScoreFontSize,
	secondaryScores,
	secondaryScoreOrientation,
	lastUpdated,
	dispatch,
	isActionButtonDisplayed,
	actionButtonIcon,
	actionButtonActionName,
	actionButtonActionPayload,
	actionButtonTooltip,
	actionButtonAriaLabel,
	cardInteraction,
	isLoading,
	loadingMessage
) => {
	return (
		<now-card
			size="lg"
			interaction={cardInteraction}
			className={_getCardClassName(fillHeight, orientation)}>
			{_getCardHeaderElement(
				cardHeaderLabel,
				dispatch,
				isActionButtonDisplayed,
				actionButtonIcon,
				actionButtonActionName,
				actionButtonActionPayload,
				actionButtonTooltip,
				actionButtonAriaLabel
			)}
			{isLoading ? (
				<div className="loading-view">
					<now-loader label={loadingMessage} />
				</div>
			) : (
				_getCardContent(
					orientation,
					primaryScore,
					primaryScoreFontSize,
					secondaryScores,
					secondaryScoreOrientation
				)
			)}
			{_getCardFooterElement(lastUpdated)}
		</now-card>
	);
};

export { getCardElement };
