import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import RICH_TEXT_CONTENT_CARD from '../constants/RichTextCardInteractionActionTypes';
import DOCUMENT_TEMPLATE_RESET_MUTATION from '../constants/DocumentationTemplateResetActionTypes';
import { CARD_EDITABLE_FIELDS } from '../../RichTextContentCard/constants/GeneralConstants';
import planDocumentResetMutation from '../queries/planDocumentResetMutation';

const requestDocumentTemplateResetMutation = createGraphQLEffect(
	planDocumentResetMutation,
	{
		templateVarList: ['tableName', 'contentField'],
		variableList: ['sysId', 'contentFieldValue'],
		startActionType: DOCUMENT_TEMPLATE_RESET_MUTATION.STARTED,
		successActionType: DOCUMENT_TEMPLATE_RESET_MUTATION.SUCCEEDED,
		errorActionType: DOCUMENT_TEMPLATE_RESET_MUTATION.FAILED,
	}
);

const getDocumentResetMutationSuccess = ({
	state,
	action,
	properties,
	updateState,
}) => {
	const { tableRecords } = state;
	const { tableName } = properties;
	const { payload } = action;
	const payloadNestedFieldsAccessPath = `data.GlideRecord_Mutation.update_${tableName}`;
	const recordSysId = get(
		payload,
		`${payloadNestedFieldsAccessPath}.sys_id.value`
	);
	const contents = get(
		payload,
		`${payloadNestedFieldsAccessPath}.${CARD_EDITABLE_FIELDS.CONTENT_FIELD}.value`
	);

	const updatedRecords = tableRecords.map((record) => {
		if (record.sys_id === recordSysId) {
			return {
				...record,
				[CARD_EDITABLE_FIELDS.CONTENT_FIELD]: contents,
			};
		}

		return record;
	});

	updateState({
		tableRecords: updatedRecords,
	});
};

const getDocumentResetMutationFailure = ({ action }) => {
	const error = action.payload;

	console.error(error);
};

const actionHandlers = {
	[RICH_TEXT_CONTENT_CARD.RESET]: requestDocumentTemplateResetMutation,
	[DOCUMENT_TEMPLATE_RESET_MUTATION.SUCCEEDED]: {
		effect: getDocumentResetMutationSuccess,
		stopPropogation: true,
	},
	[DOCUMENT_TEMPLATE_RESET_MUTATION.FAILED]: {
		effect: getDocumentResetMutationFailure,
		stopPropogation: true,
	},
};

export default actionHandlers;
