import { createCustomElement } from '@servicenow/ui-core';
import { snabbdom } from '@servicenow/ui-renderer-snabbdom';
import view from './EditableListView';
import styles from './EditableListElement.scss';
import lifeCycleActionHandlers from './actions/EditableListLifeCycleActions';
import recordQueryActionHandlers from './actions/EditableListRecordQueryActions';
import modalActionHandlers from './actions/EditableListModalActions';
import editGridActionHandlers from './actions/EditableListGridActions';
import editRecordActionHandlers from './actions/EditableListUpdateActions';
import deleteActionHandlers from './actions/EditableListDeleteActions';
import userPreferenceActionHandlers from '../../util/grid-util/actions/UserPreferenceActions';
import gridActionHandlers from '../../util/grid-util/actions/GridActions';
import { recordWatcherBehavior } from '../../behaviors/RecordWatcherBehavior';
import {
	DEFAULT_LIST_CONFIG,
	WORKSPACE_VIEW,
	LOADING_VIEW_MESSAGE,
	DEFAULT_RECORD_LOAD_LIMIT,
} from './constants/EditableListConstants';
import editEventHandlers from './utils/EditEventHandlers';

const TAG_NAME = 'sn-bcm-editable-list';

/**
 * Displays list of related elements
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-editable-list
 * 		table-name=""
 * 		list-view-name=""
 * 		filter-query-conditions=""
 * 		list-config=""
 * 		record-load-limit=""
 * 		parent-record-identification=""
 * ></sn-bcm-editable-list>
 * ```
 *
 * @seismicElement sn-bcm-editable-list
 * @summary Displays list of related elements
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,
	styles,
	initialState: {
		listRecords: [],
		isLoading: true,
		isModalOpen: false,
		config: DEFAULT_LIST_CONFIG,
		columns: [],
		recordId: '',
		tableLabel: '',
		modalLabels: {
			headerLabel: '',
			actionBtnLabel: '',
			cancelBtnLabel: '',
		},
		recordSelected: [],
		recordSelectedWithParentContext: {},
		hasOffsetWidth: false,
	},
	properties: {
		/**
		 * The table name on which data should be queried on.
		 *
		 * @type {string}
		 */
		tableName: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Name of List View
		 *
		 * @type {string}
		 */
		listViewName: {
			default: WORKSPACE_VIEW,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Query Conditions
		 *
		 * @type {string}
		 */
		filterQueryConditions: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * listConfig takes in properties needed to support editable list using sn-datagrid.
		 * These values are used to override default 'config' variable on bootstrap.
		 * Also editslotRenderer is added in the view to the same config variable before passing it to data grid
		 *
		 * @type {object}
		 */
		listConfig: {
			default: {
				rowHeight: 40,
				enableToolBar: true,
				displayRecordCount: true,
				setGridTitle: true,
				enableColumnGrouping: true,
				enableColumnFilters: true,
				enableColumnSorting: true,
				enableColumnReorder: false,
				enableRecordPreview: true,
				enableRowSelection: false,
				enableListEdit: true,
			},
			required: false,
			schema: {
				type: 'object',
			},
		},
		/**
		 * Loading view message of the component
		 *
		 * @type {string}
		 */
		loadingViewMessage: {
			default: LOADING_VIEW_MESSAGE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * This value determines the maximum number of records loaded in the view
		 *
		 * @type {integer}
		 */
		recordLoadLimit: {
			default: DEFAULT_RECORD_LOAD_LIMIT,
			required: false,
			schema: {
				type: 'integer',
			},
		},
		/**
		 * If enabled, a record watcher will keep this component updated in real-time.
		 *
		 * @type {boolean}
		 */
		isRecordWatcherEnabled: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * parent component identification
		 *
		 * @type {string}
		 */
		parentRecordIdentification: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Flag to identify whether grid is readonly or not
		 *
		 * @type {boolean}
		 */
		isReadOnly: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * View name of the form to be displayed on modal
		 *
		 * @type {string}
		 */
		modalViewName: {
			default: WORKSPACE_VIEW,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * flag to differentiate whether columns on a grid can be hidden or not.
		 *
		 * @type {boolean}
		 */
		allowColumnHidding: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * flag to differentiate whether it is bia dependency assessment or not.
		 *
		 * @type {boolean}
		 */
		isBiaDependency: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * User preference name
		 *
		 * @type {string}
		 */
		userPreferenceName: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
	},
	behaviors: [recordWatcherBehavior],
	actionHandlers: {
		...lifeCycleActionHandlers,
		...recordQueryActionHandlers,
		...modalActionHandlers,
		...editGridActionHandlers,
		...editRecordActionHandlers,
		...deleteActionHandlers,
		...userPreferenceActionHandlers,
		...gridActionHandlers,
	},
	eventHandlers: [...editEventHandlers],
});

export default TAG_NAME;
