import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	REMOVE_PLAN_ASSET_URL,
	ITEM_DELETION_ERROR_MESSAGE,
} from '../constants/EditableListConstants';
import EDITABLE_LIST_MODAL_ACTION_TYPES from '../constants/EditableListModalActionTypes';
import {
	EDITABLE_LIST_DELETE_ACTION_TYPES,
	EDITABLE_LIST_QUERY_ACTION_TYPES,
} from '../constants/EditableListCRUDActionTypes';
import {GRID_ACTIONS} from '../../../util/grid-util/constants/GridActionTypes';
import { USER_PREFERENCE_PREFIX } from '../../../util/grid-util/constants/GeneralConstants';

const handleItemDeletionSuccess = ({ dispatch, state, updateState }) => {
	const {
		tableName,
		listViewName,
		filterQueryConditions,
		recordLoadLimit,
		userPreferenceName,
	} = state.properties;

    updateState({
        recordSelected: []
    });
	dispatch(GRID_ACTIONS.REFRESH_GRID, {
		actionName: EDITABLE_LIST_QUERY_ACTION_TYPES.EDITABLE_LIST_QUERY_REQUESTED,
		actionPayload : {
		tableName,
		listViewName,
		filterQueryConditions,
		recordLoadLimit,
		userPreferenceName,
		}
	});
};

const handleItemDeletionFailure = ({ action, dispatch, updateState }) => {
	const { data } = action.payload;
	const { error } = data;
	const { message } = error;

	const {
		tableName,
		listViewName,
		filterQueryConditions,
		recordLoadLimit,
	} = state.properties;
	const userPreferenceName = `${USER_PREFERENCE_PREFIX}.${tableName}.${listViewName}`;

	console.error(data);
	console.error(ITEM_DELETION_ERROR_MESSAGE);
	updateState({
        errorMessage: message,
        recordSelected: []
	});
	dispatch(GRID_ACTIONS.REFRESH_GRID, {
		actionName: EDITABLE_LIST_QUERY_ACTION_TYPES.EDITABLE_LIST_QUERY_REQUESTED,
		actionPayload : {
		tableName,
		listViewName,
		filterQueryConditions,
		recordLoadLimit,
		userPreferenceName,
		}
	});
};

const actionHandlers = {
	[EDITABLE_LIST_DELETE_ACTION_TYPES.REMOVE_BUTTON_CLICKED]: ({ state, dispatch }) => {
		const { recordSelected, properties } = state;
		const { tableName } = properties;
		let currentlySelectedEncodedQuery = '';

		if (recordSelected && recordSelected.length) {
			currentlySelectedEncodedQuery = `sys_idIN${recordSelected.join(',')}`;
		}

		dispatch(EDITABLE_LIST_DELETE_ACTION_TYPES.ITEM_DELETE_REQUESTED, {
			data: {
				targetTable: tableName,
				currentlySelectedEncodedQuery,
			},
		});
	},
	[EDITABLE_LIST_DELETE_ACTION_TYPES.ITEM_DELETE_REQUESTED]: createHttpEffect(REMOVE_PLAN_ASSET_URL, {
		method: 'POST',
		dataParam: 'data',
		successActionType: EDITABLE_LIST_DELETE_ACTION_TYPES.ITEM_DELETION_SUCCEEDED,
		errorActionType: EDITABLE_LIST_DELETE_ACTION_TYPES.ITEM_DELETION_FAILED,
		batch: false,
	}),
	[EDITABLE_LIST_DELETE_ACTION_TYPES.ITEM_DELETION_SUCCEEDED]: {
		effect: handleItemDeletionSuccess,
		stopPropogation: true,
	},
	[EDITABLE_LIST_DELETE_ACTION_TYPES.ITEM_DELETION_FAILED]: {
		effect: handleItemDeletionFailure,
		stopPropogation: true,
	},
};

export default actionHandlers;
