import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './PlanDocumentCardListView';
import styles from './PlanDocumentCardListElement.scss';
import documentationContentMutationActionHandlers from './actions/DocumentationContentMutationActions';
import documentationListQueryActionHandlers from './actions/DocumentationListQueryActions';
import documentationStatusMutationActionHandlers from './actions/DocumentationStatusMutationActions';
import lifecycleActionHandlers from './actions/LifecycleActions';
import documentationTemplateResetActionHandlers from './actions/DocumentationTemplateResetActions';
import recordModalActionHandlers from './actions/DocumentationRecordModalActions';
import newDocumentationSectionActionHandlers from './actions/DocumentationNewRecordActions';
import planTemplateOrderQueryActionHandlers from './actions/PlanTemplateOrderQueryActions';
import { DEFAULT_PROPERTIES_VALUES } from './constants/GeneralConstants';

const TAG_NAME = 'sn-bcm-plan-document-list';

/**
 * Card list view for displaying stacked rich text document cards
 * New document cards can also be created by adding new section
 *
 * ```
 * import '@relative-path/app-bcm-components';
 * OR
 * import PlanDocumentCardList from '@relative-path/app-bcm-components';
 *
 * <sn-bcm-plan-document-list
 * 		sysId="primaryTableRecordSysIdString"
 * 		tableName="documentsRecordTableName"
 * 		maxDocumentationSectionCount={100}
 * 		formView="createFormViewNameForNewRecord"
 * ></sn-bcm-plan-document-list>
 *
 * ```
 *
 * @seismicElement sn-bcm-plan-document-list
 * @summary A card list view element for displaying a stacked list of rich text cards
 */

createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
	},

	view,

	properties: {
		/**
		 * Unique plan id to query, when performing a data fetch.
		 * @type {string}
		 */
		planSysId: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Name of the table from which documentationSections should be queried
		 * @type {string}
		 */
		tableName: {
			default: DEFAULT_PROPERTIES_VALUES.RELATED_TABLE,
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Maximum number of documentation sections to query/display
		 * @type {number}
		 */
		maxDocumentationSectionCount: {
			default: DEFAULT_PROPERTIES_VALUES.QUERY_DATA_MAX_LIMIT,
			required: false,
			schema: {
				type: 'number',
			},
		},
		/**
		 * Form view to display new section modal
		 *
		 * @type {string}
		 */
		formView: {
			default: DEFAULT_PROPERTIES_VALUES.FORM_VIEW_NAME,
			required: false,
			schema: {
				type: 'string',
			},
		},
	},

	initialState: {
		/**
		 * Stores array of all fetched records from table.
		 * @type {object[]}
		 */
		tableRecords: [],
		/**
		 * Indicates the loading/ready state of component.
		 * @type {boolean}
		 */
		loading: true,
		/**
		 * Indicates the open/close state of record modal component.
		 * @type {boolean}
		 */
		isModalOpen: false,
		/**
		 * Stores order of plan templates
		 * @type {string[]}
		 */
		planTemplatesOrderArray: [],
		/**
		 * Indicates scroll to new section true/false state
		 * true when add new document section is just created
		 * false after scroll to new inserted document section
		 * @type {boolean}
		 */
		scrollToNewDocumentSection: false,
		/**
		 * Stores sys_id of newly added document section
		 * @type {string}
		 */
		newDocumentSectionSysId: '',
		showNewButton: true
	},

	actionHandlers: {
		...documentationContentMutationActionHandlers,
		...documentationListQueryActionHandlers,
		...documentationStatusMutationActionHandlers,
		...lifecycleActionHandlers,
		...documentationTemplateResetActionHandlers,
		...recordModalActionHandlers,
		...newDocumentationSectionActionHandlers,
		...planTemplateOrderQueryActionHandlers,
	},

	styles,
});

export default TAG_NAME;
