import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './HomeActionLinksView';
import styles from './HomeActionLinksElement.scss';
import actionHandlers from './actions/HomeActionLinksActions';
import { ITEM_SELECTED } from './constants/HomeActionLinksActionTypes';
import {
	PLAN_HEADER_LABEL,
	BIA_HEADER_LABEL,
	EXERCISE_HEADER_LABEL,
	RECOVERY_EVENT_HEADER_LABEL,
	BIA_FORM_VIEW,
	PLAN_FORM_VIEW,
	EVENT_FORM_VIEW,
} from './constants/HomeActionLinksConstants';

const TAG_NAME = 'sn-bcm-home-action-links';

/**
 * A Homepage Action Widget component, intended for use on workspace homepage.
 * Displays Homepage Action Links with text-links.
 *
 * ```
 * <sn-bcm-home-action-links/>
 *
 * ```
 *
 * @seismicElement sn-bcm-home-action-links
 * @summary Displays homepage action links from tables:
 * sn_bcp_plan, sn_bia_analysis and sn_recovery_event.
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,

	properties: {
		/**
		 * Boolean to enable the bia text-link
		 *
		 * @type {boolean}
		 */
		isBiaTextLinkDisplayed: {
			required: true,
			default: true,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Boolean to enable the plan text-link
		 *
		 * @type {boolean}
		 */
		isPlanTextLinkDisplayed: {
			required: true,
			default: true,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Boolean to enable the exercise text-link
		 *
		 * @type {boolean}
		 */
		isExerciseTextLinkDisplayed: {
			required: true,
			default: true,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Boolean to enable the recovery event text-link
		 *
		 * @type {boolean}
		 */
		isRecoveryEventTextLinkDisplayed: {
			required: true,
			default: true,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Label of bia link-text and record modal header
		 *
		 * @type {string}
		 */
		biaHeaderLabel: {
			default: BIA_HEADER_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of plan link-text and record modal header
		 *
		 * @type {string}
		 */
		planHeaderLabel: {
			default: PLAN_HEADER_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of exercise link-text
		 *
		 * @type {string}
		 */
		exerciseHeaderLabel: {
			default: EXERCISE_HEADER_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of recoveryEvent link-text
		 *
		 * @type {string}
		 */
		recoveryEventHeaderLabel: {
			default: RECOVERY_EVENT_HEADER_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * View of bia record modal
		 * @type {string}
		 */
		biaFormView: {
			default: BIA_FORM_VIEW,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * View of plan record modal
		 * @type {string}
		 */
		planFormView: {
			default: PLAN_FORM_VIEW,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * View of exercise record modal
		 * @type {string}
		 */
		exerciseOrEventFormView: {
			default: EVENT_FORM_VIEW,
			required: false,
			schema: {
				type: 'string',
			},
		},
	},
	initialState: {
		isRecordModalOpen: false,
		recordModalConfiguration: {},
	},
	dispatches: {
		/**
		 * Dispatched when user clicks on action links.
		 *
		 * @type {object}
		 */
		[ITEM_SELECTED]: {},
	},
	actionHandlers,
	styles,
});

export default TAG_NAME;
