import { IMPACT_ANALYSIS_COLUMN_NAMES } from '../constants/BiaResultsSummaryCardConstants';

const query = `query ($sysId: String!) {
	BiaResultsQuery: GlideRecord_Query {
		{{table}} (sys_id: $sysId) {
			results: _results {
				${IMPACT_ANALYSIS_COLUMN_NAMES.RECOVERY_POINT_OBJECTIVE} {
					displayValue
					value
				}
				${IMPACT_ANALYSIS_COLUMN_NAMES.ADJUSTED_RPO} {
					displayValue
					value
				}
				${IMPACT_ANALYSIS_COLUMN_NAMES.RECOVERY_TIME_OBJECTIVE} {
					displayValue
					value
				}
				${IMPACT_ANALYSIS_COLUMN_NAMES.ADJUSTED_RTO} {
					displayValue
					value
				}
				${IMPACT_ANALYSIS_COLUMN_NAMES.RECOVERY_TIER} {
					displayValue
					value
				}
				template {
					biaTemplateTable: _reference {
						primary_element_assessed {
							elementsDefinitionTable: _reference {
								requires_data_backup {
									value
								}
							}
						}
					}
				}
			}
		}
	}
}`;

export default query;
