import { actionTypes } from '@servicenow/ui-core';
import PLAN_TEMPLATES_ORDER_QUERY from '../constants/PlanTemplateOrderQueryActionsTypes';
import { DEFAULT_PROPERTIES_VALUES } from '../constants/GeneralConstants';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_RENDERED } = actionTypes;

const onComponentBootstrapped = ({ dispatch, properties }) => {
	const { planSysId } = properties;

	dispatch(PLAN_TEMPLATES_ORDER_QUERY.REQUESTED, {
		table: DEFAULT_PROPERTIES_VALUES.PRIMARY_TABLE,
		sysId: planSysId,
	});
};

const scrollNewDocumentSectionInView = ({ state, action, updateState }) => {
	const { scrollToNewDocumentSection, newDocumentSectionSysId } = state;
	const { host } = action.payload;

	if (scrollToNewDocumentSection && host && newDocumentSectionSysId) {
		const allDocumentSections = host.shadowRoot.querySelectorAll(
			'now-accordion-item'
		);

		if (allDocumentSections && allDocumentSections.length) {
			const newDocumentSectionCard =
				allDocumentSections[allDocumentSections.length - 1];
			// repeat with the interval of 50 miliseconds
			const scrollToTimerId = window.setInterval(() => {
				const nowCollapseInsideAccordion = newDocumentSectionCard.shadowRoot.querySelector(
					'now-collapse'
				);
				const isAccordionExpanded = nowCollapseInsideAccordion
					? nowCollapseInsideAccordion.shadowRoot.querySelector('.is-expanded')
					: null;

				const richTextEditorElement = newDocumentSectionCard.querySelector(
					'sn-bcm-rich-text-content-card'
				);

				if (richTextEditorElement && isAccordionExpanded) {
					const richTextEditorElementContents = richTextEditorElement.shadowRoot.querySelector(
						'.rich-text-card > now-card > .rich-text-card-content'
					);

					if (richTextEditorElementContents) {
						window.requestAnimationFrame(() => {
							richTextEditorElement.scrollIntoView();
							window.clearInterval(scrollToTimerId);
						});
					}
				}
			}, 100);

			// stop timer if the element is not found within 2.5 second
			window.setTimeout(() => {
				window.clearInterval(scrollToTimerId);
			}, 2500);

			updateState({
				newDocumentSectionSysId: '',
				scrollToNewDocumentSection: false,
			});
		}
	}
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect: onComponentBootstrapped,
	},
	[COMPONENT_RENDERED]: {
		effect: scrollNewDocumentSectionInView,
	},
};

export default actionHandlers;
