import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './BiaResultsSummaryCardView';
import styles from './BiaResultsSummaryCardStyles.scss';
import lifecycleActionHanlders from './actions/LifecycleActions';
import biaResultsSummaryQueryActionHandlers from './actions/BiaResultsSummaryCardQueryActions';
import { recordWatcherBehavior } from '../../behaviors/RecordWatcherBehavior';
import { REQUIRES_DATA_BACKUP } from './constants/BiaResultsSummaryCardConstants';

const TAG_NAME = 'sn-bcm-bia-results-summary-card';

/**
 * Displays rpo, rto, and recovery tier results summary of current BIA
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-bia-results-summary-card
 * 		sys-id="0aa4bbec5351201040c4ddeeff7b12b5"
 * 		is-record-watcher-enabled={true}
 * ></sn-bcm-bia-results-summary-card>
 * ```
 *
 * @seismicElement sn-bcm-bia-results-summary-card
 * @summary Displays rpo, rto, and recovery tier results summary of current BIA
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		 * sys_id of current bia record
		 *
		 * @type {string}
		 */
		sysid: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * If enabled, a record watcher will keep this component updated in real-time.
		 *
		 * @type {boolean}
		 */
		isRecordWatcherEnabled: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
	},
	initialState: {
		requiresDataBackup: REQUIRES_DATA_BACKUP.NO, // default set to 'no'
		recoveryPointObjective: null,
		adjustedRecoveryPointObjective: null,
		recoveryTimeObjective: null,
		adjustedRecoveryTimeObjective: null,
		recoveryTier: null,
		isLoading: true,
	},
	behaviors: [recordWatcherBehavior],
	actionHandlers: {
		...lifecycleActionHanlders,
		...biaResultsSummaryQueryActionHandlers,
	},
	styles,
});

export default TAG_NAME;
