import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './PlanAssetScoreCardView';
import {
	CARD_HEADER_LABEL,
	PLAN_LEAST_RECOVERY_TIME_OBJECTIVE_LABEL,
	PLAN_LEAST_RECOVERY_POINT_OBJECTIVE_LABEL,
	TOTAL_NUMBER_OF_ASSETS_LABEL,
} from './constants/PlanAssetScoreCardConstants';
import lifecycleActionHandlers from './actions/PlanAssetScoreCardLifeCycleActions';
import planAssetScoreCardQueryActionHandlers from './actions/PlanAssetScoreCardQueryAction';
import styles from './PlanAssetScoreCardElement.scss';
import { recordWatcherBehavior } from '../../behaviors/RecordWatcherBehavior';

const TAG_NAME = 'sn-bcm-plan-asset-score';

/**
 * This component is to be used in the Plan Overview component. Will display assets in scope for a plan.
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-plan-asset-score
 * 		sysid=""
 * 		plan-least-recovery-time-objective-label=""
 * 		plan-least-recovery-point-objective-label=""
 * 		plan-total-asset-count-label=""
 * 		card-header-label=""
 * 		secondary-score-orientation=""
 * 		related-item-tab-index=""
 * ></sn-bcm-plan-asset-score>
 * ```
 *
 * @seismicElement sn-bcm-plan-asset-score
 * @summary This component is to be used in the Plan Overview component. It will display least RTO, least RPO and total Assets for a plan."
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,

	properties: {
		/**
		 * Sysid of the plan record the card will be displayed on
		 *
		 * @type {string}
		 */
		sysid: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of card header of the widget
		 *
		 * @type {string}
		 */
		cardHeaderLabel: {
			default: CARD_HEADER_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the recovery time objective 		 
		 * 
		 * @type {string}
		 */
		planLeastRecoveryTimeObjectiveLabel: {
			default: PLAN_LEAST_RECOVERY_TIME_OBJECTIVE_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the recovery point objective
		 *
		 * @type {string}
		 */
		planLeastRecoveryPointObjectiveLabel: {
			default: PLAN_LEAST_RECOVERY_POINT_OBJECTIVE_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the total assets in scope
		 *
		 * @type {string}
		 */
		planTotalAssetCountLabel: {
			default: TOTAL_NUMBER_OF_ASSETS_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Boolean to enable the icon button
		 *
		 * @type {boolean}
		 */
		isActionButtonDisplayed: {
			required: true,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Related item tab index to which action button must redirect to.
		 *
		 * @type {number}
		 */
		relatedItemTabIndex: {
			default: 1,
			required: false,
			schema: {
				type: 'number',
			},
		},
		/**
		 * If enabled, a record watcher will keep this component updated in real-time.
		 *
		 * @type {boolean}
		 */
		isRecordWatcherEnabled: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
	},
	initialState: {
		/**
		 * Intial loading state of the component
		 *
		 * @type {boolean}
		 */
		isLoading: true,
		/**
		 * Least RTO of the plan
		 *
		 * @type {number}
		 */
		planLeastRTO: '--',
		/**
		 * Least RPO of the plan
		 *
		 * @type {number}
		 */
		planLeastRPO: '--',
		/**
		 * Total assets in scope of the plan
		 *
		 * @type {number}
		 */
		totalAssets: '--',
		/**
		 * RPO enabled status for the plan.
		 *
		 * @type {boolean}
		 */
		isRPOEnabled: true,
		/**
		 * Configuration for the record-watcher subscription.
		 *
		 * @type {object}
		 */
		recordWatcherConfigurations: null,
	},
	behaviors: [recordWatcherBehavior],
	actionHandlers: {
		...lifecycleActionHandlers,
		...planAssetScoreCardQueryActionHandlers,
	},
	styles,
});

export default TAG_NAME;
