import RICH_TEXT_CONTENT_CARD from '../constants/CardInteractionsActionTypes';
import {
	NOW_MODAL_FOOTER_ACTIONS_CLICKED,
	NOW_MODAL_DISMISSED,
} from '../constants/NowUIComponentsActionTypes';
import { CARD_EDITABLE_FIELDS } from '../constants/GeneralConstants';
import { MODAL_RESET_ACTION_LABEL } from '../constants/RecordModalConstants';

const resetModalContent = ({ properties, dispatch, action, updateState }) => {
	const { payload } = action;
	const { tableName, defaultPlanDocumentText, recordSysId } = properties;

	// checks if the target modal footer action is reset
	if (payload.action.label === MODAL_RESET_ACTION_LABEL) {
		dispatch(RICH_TEXT_CONTENT_CARD.RESET, {
			tableName,
			contentField: CARD_EDITABLE_FIELDS.CONTENT_FIELD,
			contentFieldValue: defaultPlanDocumentText,
			sysId: recordSysId,
		});

		return updateState({
			isModalOpen: false,
			richTextContent: defaultPlanDocumentText,
		});
	}

	return updateState({
		isModalOpen: false,
	});
};

const closeResetModal = ({ updateState }) => {
	updateState({
		isModalOpen: false,
	});
};

const actionHandlers = {
	[NOW_MODAL_FOOTER_ACTIONS_CLICKED]: {
		effect: resetModalContent,
		stopPropogation: true,
	},
	[NOW_MODAL_DISMISSED]: {
		effect: closeResetModal,
		stopPropogation: true,
	},
};

export default actionHandlers;
