import 'sn-tinymce';
import '@servicenow/now-card';
import '@servicenow/now-record-html-editor';
import '@servicenow/now-modal';
import '@servicenow/now-rich-text';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import {
	CARD_EDITED_STATUS,
	NOW_HTML_EDITOR_STYLES,
	NOW_BUTTON_TOOLTIPS,
	NOW_BUTTON_LABELS,
	CARD_EDITABLE_FIELDS,
} from './constants/GeneralConstants';
import {
	RESET_MODAL_MANAGED_OPEN,
	NOW_MODAL_DEFAULT_PROPS,
} from './constants/RecordModalConstants';
import RICH_TEXT_CONTENT_CARD from './constants/CardInteractionsActionTypes';

const _getIsEditMode = (status) => {
	if (status === CARD_EDITED_STATUS.PENDING) {
		return true;
	}

	return false;
};

const view = (state, { dispatch, updateState }) => {
	const { richTextContent, isModalOpen, properties, htmlAutoFocus } = state;
	const {
		status,
		title,
		description,
		defaultPlanDocumentText,
		recordSysId,
		tableName,
		isRecordEditable,
	} = properties;
	const isEditorOpen = _getIsEditMode(status) && isRecordEditable;

	const cardHeaderAndCaption = (
		<Fragment>
			{title ? (
				<now-card-header
					heading={{
						label: title,
						variant: 'secondary',
						size: 'md',
						level: 2,
						lines: 1,
					}}
					className="rich-text-card-header"
				/>
			) : null}
			{description ? (
				<now-card-header
					className="rich-text-card-description"
					heading={{
						label: '',
					}}
					caption={{
						label: description,
						lines: 1,
					}}
				/>
			) : null}
		</Fragment>
	);

	const getHtmlEditorAndRichTextViewer = () => {
		const htmlEditorClassName = `now-html-editor ${
			isEditorOpen ? '' : '-hide-editor'
		}`;
		const richTextClassName = `now-rich-text ${
			isEditorOpen ? '-hide-rich-text' : ''
		}`;
		const {
			HEIGHT,
			MIN_HEIGHT,
			MAX_HEIGHT,
			CONTENT_STYLE,
		} = NOW_HTML_EDITOR_STYLES;

		return (
			<div className="rich-text-card-content">
				<div className="html-content-wrapper">
					{isEditorOpen ? (
						<now-record-html-editor
							autofocus={htmlAutoFocus}
							height={HEIGHT}
							minHeight={MIN_HEIGHT}
							maxHeight={MAX_HEIGHT}
							className={htmlEditorClassName}
							value={richTextContent}
							readonly={!isEditorOpen}
							contentStyle={CONTENT_STYLE}
						/>
					) : (
						<now-rich-text
							className={richTextClassName}
							html={richTextContent}
						/>
					)}
				</div>
			</div>
		);
	};

	const getCardFooterActions = () => {
		if (isEditorOpen) {
			return (
				<div className="rich-text-card-footer-actions">
					{defaultPlanDocumentText && defaultPlanDocumentText.length ? (
						<now-button-bare
							className="now-bare-btn"
							variant="secondary"
							size="md"
							label={NOW_BUTTON_LABELS.RESET}
							on-click={() => updateState({ isModalOpen: true })}
						/>
					) : null}
					<now-button
						variant="secondary"
						size="md"
						className="rich-text-card-footer-btn -right-aligned -save-btn"
						label={NOW_BUTTON_LABELS.SAVE}
						tooltipContent={NOW_BUTTON_TOOLTIPS.SAVE}
						on-click={() => {
							return dispatch(RICH_TEXT_CONTENT_CARD.SAVED, {
								tableName,
								contentField: CARD_EDITABLE_FIELDS.CONTENT_FIELD,
								statusField: CARD_EDITABLE_FIELDS.STATUS_FIELD,
								sysId: recordSysId,
								contentFieldValue: richTextContent,
								statusFieldValue: CARD_EDITED_STATUS.PENDING,
							});
						}}
					/>
					<now-button
						variant="primary"
						size="md"
						className="rich-text-card-footer-btn -primary -complete-btn"
						label={NOW_BUTTON_LABELS.COMPLETE}
						tooltipContent={NOW_BUTTON_TOOLTIPS.SAVE_N_COMPLETE}
						on-click={() => {
							return dispatch(RICH_TEXT_CONTENT_CARD.SAVED, {
								tableName,
								contentField: CARD_EDITABLE_FIELDS.CONTENT_FIELD,
								statusField: CARD_EDITABLE_FIELDS.STATUS_FIELD,
								sysId: recordSysId,
								contentFieldValue: richTextContent,
								statusFieldValue: CARD_EDITED_STATUS.COMPLETE,
							});
						}}
					/>
				</div>
			);
		}

		return isRecordEditable ? (
			<now-button
				variant="primary"
				size="md"
				className="rich-text-card-footer-btn -right-aligned -edit-btn"
				label={NOW_BUTTON_LABELS.EDIT}
				tooltipContent={NOW_BUTTON_TOOLTIPS.EDIT}
				on-click={() => {
					updateState({
						htmlAutoFocus: true,
					});

					return dispatch(RICH_TEXT_CONTENT_CARD.EDITED, {
						tableName,
						field: CARD_EDITABLE_FIELDS.STATUS_FIELD,
						sysId: recordSysId,
						statusFieldVal: CARD_EDITED_STATUS.PENDING,
					});
				}}
			/>
		) : null;
	};

	const cardFooterAndActions = (
		<now-card-footer className="rich-text-card-footer">
			{getCardFooterActions()}
		</now-card-footer>
	);

	const resetToDefaultTextConfirmModal = (
		<now-modal
			className="now-reset-modal"
			{...NOW_MODAL_DEFAULT_PROPS}
			opened={isModalOpen}
			manageOpened={RESET_MODAL_MANAGED_OPEN}
		/>
	);

	const richTextContentCardView = (
		<div className="rich-text-card">
			{resetToDefaultTextConfirmModal}
			<now-card>
				{cardHeaderAndCaption}
				{getHtmlEditorAndRichTextViewer()}
				{cardFooterAndActions}
			</now-card>
		</div>
	);

	return richTextContentCardView;
};

export default view;
