import {
	RECORD_MODAL_OPENED,
	MODAL_CLOSE_CLICKED,
	RECORD_DATA_CHANGED,
} from '../constants/DocumentationRecordModalActionTypes';
import NEW_DOCUMENTATION_RECORD_QUERY from '../constants/DocumentationNewRecordActionTypes';
import { NEW_RECORD_STATUS_INSERTED } from '../constants/GeneralConstants';

const closeRecordModal = ({ updateState }) => {
	updateState({
		isModalOpen: false,
	});
};

const openRecordModal = ({ updateState }) => {
	updateState({
		isModalOpen: true,
	});
};

const requestNewDocumentationRecord = ({ action, dispatch }) => {
	const { payload } = action;
	const { sys_id, table, status } = payload;

	if (status === NEW_RECORD_STATUS_INSERTED) {
		dispatch(NEW_DOCUMENTATION_RECORD_QUERY.REQUESTED, {
			table,
			sysId: sys_id,
		});
	}
};

const actionHandlers = {
	[RECORD_MODAL_OPENED]: {
		effect: openRecordModal,
		stopPropogation: true,
	},
	[MODAL_CLOSE_CLICKED]: {
		effect: closeRecordModal,
		stopPropogation: true,
	},
	[RECORD_DATA_CHANGED]: {
		effect: requestNewDocumentationRecord,
		stopPropogation: true,
	},
};

export default actionHandlers;
