import '@servicenow/now-loader';
import PieChartCard from '../PieChartCard/PieChartCardElement';
import {
	LOADING_VIEW_MESSAGE,
	TOTAL_LABEL,
	PIE_CHART_TYPE,
	CARD_HEADER_LABEL,
} from './constants/GeneralConstants';

const view = (state) => {
	const {
		isLoading,
		donutSeriesData,
		showDataLabels,
		isAccessibilityModeActive,
	} = state;

	const loadingView = (
		<div className="loading-view">
			<now-loader label={LOADING_VIEW_MESSAGE} />
		</div>
	);

	const donutPieChartView = (
		<PieChartCard
			height="250"
			cardSize="lg"
			seriesData={donutSeriesData}
			type={PIE_CHART_TYPE}
			hideRegister={true}
			totalLabel={TOTAL_LABEL}
			cardHeaderLabel={CARD_HEADER_LABEL}
			showDataLabels={showDataLabels}
			showEmptyState={true}
			isChartAccessibilityModeActive={isAccessibilityModeActive}
			fillHeight
		/>
	);

	if (isLoading) {
		return loadingView;
	}

	return donutPieChartView;
};

export default view;
