import { get } from 'lodash';
import {
	excludedItemsQueryActions,
	excludedItemsQueryActionHandlers,
	itemInsertionActions,
	itemInsertionActionHandlers,
	selectionTableNameQueryActions,
} from '../../ItemPickerModal/actions/ItemPickerModalCommonActions';
import {
	ACTION_TYPES,
	CONSTANTS,
} from '../../ItemPickerModal/constants/ItemPickerModalCommonConstants';
import { ELEMENT_DEFINITIONS_TABLE_ACTIONS } from '../constants/BiaDependencyItemPickerModalActionTypes';

const getExcludedItemsQuerySuccess = ({ state, action, updateState }) => {
	const { elementDefinitionfilter } = state;
	const excludedItems = get(action, 'payload.result.excludedItems');

	const excludedItemsEncodedQuery = excludedItems.length
		? `sys_idNOT IN${excludedItems.join(',')}`
		: '';
	const consolidatedFilterCondition = elementDefinitionfilter
		? excludedItemsEncodedQuery + '^' + elementDefinitionfilter
		: excludedItemsEncodedQuery;

	updateState({
		excludedItems: excludedItems,
		recordListPickerFilterConditions: consolidatedFilterCondition,
		isLoading: false,
	});
};

const getSelectionTableNameQuerySuccess = ({
	action,
	properties,
	dispatch,
	updateState,
}) => {
	const { parentTable, elementDefinitionTableReferenceField } = properties;

	const dataReturned = get(
		action.payload,
		`data.GlideRecord_Query.${parentTable}._results[0]`
	);

	if (!dataReturned) {
		return updateState({
			isLoading: false,
		});
	}

	const selectionTable = get(
		dataReturned,
		`${elementDefinitionTableReferenceField}._reference.source_table.value`
	);
	const selectionTableLabel = get(
		dataReturned,
		`${elementDefinitionTableReferenceField}.displayValue`
	);
	const selectionTableFields = get(
		dataReturned,
		`${elementDefinitionTableReferenceField}._reference.source_table_fields.value`
	);
	const appliesToSysId = get(
		dataReturned,
		'impact_analysis._reference.applies_to.value',
		''
	);
	const elementDefinitionfilter = get(
		dataReturned,
		`${elementDefinitionTableReferenceField}._reference.filter.value`,
		''
	);

	updateState({
		selectionTable,
		selectionTableLabel,
		selectionTableFields,
		appliesToSysId,
		elementDefinitionfilter,
	});

	dispatch(ELEMENT_DEFINITIONS_TABLE_ACTIONS.CHECK_CONFIG_ITEM_REQUESTED, {
		primaryElementTable: selectionTable,
	});
};

const getSelectionTableNameQueryFailure = ({ action, updateState }) => {
	const error = action.payload;

	console.error(error);

	updateState({
		errorMessage: CONSTANTS.SELECTION_TABLE_NAME_QUERY_ERROR_MESSAGE,
	});
};

const actions = {
	...excludedItemsQueryActions,
	...itemInsertionActions,
	...selectionTableNameQueryActions,
};

const actionHandlers = {
	...itemInsertionActionHandlers,
	...excludedItemsQueryActionHandlers,
	[ACTION_TYPES.EXCLUDED_ITEMS_QUERY_SUCCEDED]: getExcludedItemsQuerySuccess,
	[ACTION_TYPES.ITEM_PICKER_TABLE_NAME_SUCCEEDED]: getSelectionTableNameQuerySuccess,
	[ACTION_TYPES.ITEM_PICKER_TABLE_NAME_FAILED]: getSelectionTableNameQueryFailure,
};

export { actions, actionHandlers };
