import RECORD_QUERY_ACTIONS from '../constants/RecordQueryActionTypes';
import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const getCurrentGlideListValue = ({ state, dispatch }) => {
	const { properties } = state;
	const { table, sysId, field, selectionTable } = properties;

	dispatch(RECORD_QUERY_ACTIONS.RECORD_QUERY_REQUESTED, {
		table,
		sysId,
		field,
		selectionTable,
	});
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect: getCurrentGlideListValue
	}
};

export default actionHandlers;