import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './PieChartCardView';
import styles from './PieChartCardElement.scss';
import {
	LABEL_PLACEHOLDER,
	FILL_HEIGHT_DEFAULT_PROPERTY,
	DEFAULT_CHART_FONT_SIZE,
	CARD_INTERACTION,
	LOADING_VIEW_MESSAGE,
	SERIES_DATA,
	TYPE,
	HEIGHT,
	WIDTH,
	DATA_ORDER,
	REGISTER_POSITION,
	DEFAULT_CARD_SIZE,
	DEFAULT_INNER_RADIUS,
} from './PieChartCardConstants';

const TAG_NAME = 'sn-bcm-pie-chart-card';

/**
 * Displays a donut chart that contains a data serious of multiple categories
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-pie-chart-card
 * 		card-header-label=""
 * 		fill-height=""
 * 		total-label=""
 * 		series-data=""
 * 		chart-colors=""
 * 		type=""
 * ></sn-bcm-pie-chart-card>
 * ```
 *
 * @seismicElement sn-bcm-pie-chart-card
 * @summary Displays a donut chart that contains a data serious of multiple categories
 */

createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
	},
	view,
	properties: {
		/**
		 * Label of card header of pie chart card
		 *
		 * @type {string}
		 */
		cardHeaderLabel: {
			default: LABEL_PLACEHOLDER,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Boolean value to have the height to be as tall as possible, or set to be a fixed height
		 *
		 * @type {boolean}
		 */
		fillHeight: {
			default: FILL_HEIGHT_DEFAULT_PROPERTY,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * The label for the total value on the chart. Default is No Label
		 *
		 * @type {string}
		 */
		totalLabel: {
			default: LABEL_PLACEHOLDER,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Name, count and color for each componet in pie chart
		 *
		 * @type {array}
		 */
		seriesData: {
			default: SERIES_DATA,
			required: false,
			schema: {
				type: 'array',
			},
		},
		/**
		 * Chart colors gets computed from the list of values in seriesData
		 *
		 * @type {object}
		 */
		chartColors: {
			computed({ properties: { seriesData } }) {
				const colors = seriesData.map((category) => category.color);

				return colors;
			},
			required: false,
			schema: {
				type: 'object',
			},
		},
		/**
		 * Type of graph the pie chart card uses, default as 'donut'
		 *
		 * @type {string}
		 */
		type: {
			default: TYPE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Height of the chart
		 *
		 * @type {string}
		 */
		height: {
			default: HEIGHT,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Width of the chart
		 *
		 * @type {string}
		 */
		width: {
			default: WIDTH,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Choice of order from orderby collection
		 *
		 * @type {string}
		 */
		orderBy: {
			default: DATA_ORDER,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Given value to add margins to the side of the visualization, creating space between the chart and the legend
		 *
		 * @type {object}
		 */
		extraMargin: {
			default: {},
			required: false,
			schema: {
				type: 'object',
			},
		},
		/**
		 * Boolean value indicates that shows data labels or not
		 *
		 * @type {boolean}
		 */
		showDataLabels: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Boolean value to show/hide total. Default is True
		 *
		 * @type {boolean}
		 */
		showTotal: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Boolean value to show/hide tooltip. Default is True
		 *
		 * @type {boolean}
		 */
		showTooltip: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Boolean value to show/hide register. Default is false
		 *
		 * @type {boolean}
		 */
		hideRegister: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Indicates the position of register. Default is right
		 *
		 * @type {string}
		 */
		registerPosition: {
			default: REGISTER_POSITION,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Indicates the trace is hidden from the graph or not
		 *
		 * @type {boolean}
		 */
		legendOnly: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Boolean value to show/hide percentages. Default is true
		 *
		 * @type {boolean}
		 */
		showPercentages: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Given label size
		 *
		 * @type {string}
		 */
		totalLabelSize: {
			default: DEFAULT_CHART_FONT_SIZE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Given tooltip font size
		 *
		 * @type {string}
		 */
		tooltipFontSize: {
			default: DEFAULT_CHART_FONT_SIZE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Given register font size
		 *
		 * @type {string}
		 */
		registerFontSize: {
			default: DEFAULT_CHART_FONT_SIZE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Last updated timestamp
		 *
		 * @type {string}
		 */
		lastUpdated: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Card interaction property, makes card clickable or not
		 * options are: "click", "none" and "select"
		 *
		 * @type {string}
		 */
		cardInteraction: {
			default: CARD_INTERACTION,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Determines if component should display loading view
		 *
		 * @type {boolean}
		 */
		isLoading: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Message shown below loader while loading the the card
		 *
		 * @type {string}
		 */
		loadingMessage: {
			default: LOADING_VIEW_MESSAGE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * flag to identify if accessibility mode is true/false.
		 *
		 * @type {boolean}
		 */
		isChartAccessibilityModeActive: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Show empty state component
		 *
		 * @type {boolean}
		 */
		showEmptyState: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Card wrapper size
		 *
		 * @type {string}
		 */
		cardSize: {
			default: DEFAULT_CARD_SIZE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Donut chart inner radius
		 *
		 * @type {number}
		 */
		innerRadius: {
			default: DEFAULT_INNER_RADIUS,
			required: false,
			schema: {
				type: 'number',
			},
		},
	},
	styles,
});

export default TAG_NAME;
