import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './CombinedScoreCardView';
import styles from './CombinedScoreCardElement.scss';
import {
	LABEL_PLACEHOLDER,
	SCORE_PLACEHOLDER,
	FILL_HEIGHT_DEFAULT_PROPERTY,
	CARD_ORIENTATION,
	SECONDARY_SCORES_ORIENTATION,
	PRIMARY_SCORE_FONT_SIZE,
	CARD_INTERACTION,
	LOADING_VIEW_MESSAGE,
} from './CombinedScoreCardConstants';

const TAG_NAME = 'sn-bcm-combined-score-card';

/**
 * Combination of now-score-basic and now-card.
 * Displays primary and secondary fields in a card with its label and score
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-combined-score-card
 * 		card-header-label=""
 * 		primary-score=""
 * 		secondary-score-orientation=""
 * 		action-button-action-name=""
 * ></sn-bcm-combined-score-card>
 * ```
 *
 * @seismicElement sn-bcm-combined-score-card
 * @summary Displays primary and secondary fields in a card with its label and score
 */

createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
	},
	view,
	properties: {
		/**
		 * Label of card header of score card
		 *
		 * @type {string}
		 */
		cardHeaderLabel: {
			default: LABEL_PLACEHOLDER,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label and score of primary score
		 *
		 * @type {object}
		 */
		primaryScore: {
			default: {
				label: SCORE_PLACEHOLDER.LABEL,
				score: SCORE_PLACEHOLDER.SCORE,
			},
			required: false,
			schema: {
				type: 'object',
			},
		},
		/**
		 * Label, score and action of secondary scores
		 *
		 * @type {array}
		 */
		secondaryScores: {
			default: [
				{
					heading: SCORE_PLACEHOLDER.LABEL,
					score: SCORE_PLACEHOLDER.SCORE,
					action: '',
				},
				{
					heading: SCORE_PLACEHOLDER.LABEL,
					score: SCORE_PLACEHOLDER.SCORE,
					action: '',
				},
				{
					heading: SCORE_PLACEHOLDER.LABEL,
					score: SCORE_PLACEHOLDER.SCORE,
					action: '',
				},
				{
					heading: SCORE_PLACEHOLDER.LABEL,
					score: SCORE_PLACEHOLDER.SCORE,
					action: '',
				},
			],
			required: false,
			schema: {
				type: 'array',
			},
		},
		/**
		 * Boolean value to have the height to be as tall as possible, or set to be a fixed height
		 *
		 * @type {boolean}
		 */
		fillHeight: {
			default: FILL_HEIGHT_DEFAULT_PROPERTY,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * The choice to set card orientation. Orientation can be vertical or horizontal
		 *
		 * @type {string}
		 */
		orientation: {
			default: CARD_ORIENTATION.HORIZONTAL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Last updated timestamp
		 *
		 * @type {string}
		 */
		lastUpdated: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The choice to set secondary score orientation. Orientation can be vertical or horizontal
		 *
		 * @type {string}
		 */
		secondaryScoreOrientation: {
			default: SECONDARY_SCORES_ORIENTATION.HORIZONTAL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Defines the font size of primary score
		 *
		 * @type {string}
		 */
		primaryScoreFontSize: {
			default: PRIMARY_SCORE_FONT_SIZE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Boolean to enable the action-button
		 *
		 * @type {boolean}
		 */
		isActionButtonDisplayed: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Defines the icon displayed on the action-button
		 *
		 * @type {string}
		 */
		actionButtonIcon: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Name to define the action dispatched by the action-button
		 *
		 * @type {string}
		 */
		actionButtonActionName: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Payload dispatched from the action-button
		 *
		 * @type {object}
		 */
		actionButtonActionPayload: {
			default: {},
			required: false,
			schema: {
				type: 'object',
			},
		},
		/**
		 * Tooltip label to display, when hovering the action-button
		 *
		 * @type {string}
		 */
		actionButtonTooltip: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Aria label for the action-button
		 *
		 * @type {string}
		 */
		actionButtonAriaLabel: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Card interaction property, makes card clickable or not
		 * options are: "click", "none" and "select"
		 *
		 * @type {string}
		 */
		cardInteraction: {
			default: CARD_INTERACTION,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Determines if component should display loading view
		 *
		 * @type {boolean}
		 */
		isLoading: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Message shown below loader while loading the the card
		 *
		 * @type {string}
		 */
		loadingMessage: {
			default: LOADING_VIEW_MESSAGE,
			required: false,
			schema: {
				type: 'string',
			},
		},
	},
	styles,
});

export default TAG_NAME;
