import { actionTypes } from '@servicenow/ui-core';
import { IMPACT_ASSESSMENT_DATA_REQUESTED } from '../constants/BiaImpactAssessmentActionTypes';
import { FETCH_PARENT_TABLE_METADATA_REQUESTED } from '../constants/ImpactAnalysisMetaDataActionTypes';
import { PARENT_TABLE_NAME } from '../constants/BiaImpactAssessmentConstants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const fetchImpactAssessmentData = ({ properties, dispatch }) => {
	const {
		parentRecordSysId,
		tableName,
		impactAssessmentChildTable,
		impactAssessmentStatusField,
		accordionDescriptionField,
		accordionHeaderField,
		impactAssessmentReferenceColumnField,
		parentTableReferenceField,
		queryFilterCondition,
	} = properties;

	dispatch(FETCH_PARENT_TABLE_METADATA_REQUESTED, {
		parentRecordSysId,
		parentTable: PARENT_TABLE_NAME,
	});

	dispatch(IMPACT_ASSESSMENT_DATA_REQUESTED, {
		tableName,
		queryConditions: `${parentTableReferenceField}=${parentRecordSysId}^${queryFilterCondition}`,
		impactAssessmentChildTable,
		impactAssessmentStatusField,
		accordionDescriptionField,
		accordionHeaderField,
		impactAssessmentReferenceColumnField,
	});
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect: fetchImpactAssessmentData,
	},
};

export default actionHandlers;
