import { set } from 'lodash';
import {
	GRID_COLUMN_WIDTH_ACTION_TYPES,
	GRID_ACTIONS,
	RECORD_ACTIONS,
} from '../constants/GridActionTypes';
import { USER_PREFERENCE_PREFIX } from '../constants/GeneralConstants';
import { USER_PREFERENCE_ACTION_TYPES } from '../constants/UserPreferenceActionTypes';
import {SET_ELEMENT_USER_PREFERENCE} from '../../../components/BiaDependencyAssessment/constants/BiaDependencyAssessmentActionTypes';

/**
 * Function to save column personalization to user preferences
 * EX: workspace.grid.columnWidths.sn_bia_result
 */
const handleColumnPersonalization = ({
	action,
	state,
	updateState,
	dispatch,
}) => {
	const { payload } = action;
	const {
		tableName,
		listViewName,
		parentRecordIdentification,
		isBiaDependency,
	} = state.properties;
	const personalizedColumns = [];
	let column = {};

	for (let i = 0; i < payload.length; i++) {
		const { name = '', width, hidden } = payload[i];
		column = {
			name: name,
			width: width,
			hidden: hidden,
		};
		
		personalizedColumns.push(column);
	}

	updateState({
		columns: [...payload],
	});

	const preferenceValue = JSON.stringify(personalizedColumns);

	if (isBiaDependency) {
		dispatch(SET_ELEMENT_USER_PREFERENCE, {
			parentRecordIdentification,
			preferenceValue,
		});
	} else {
		const preferenceName = `${USER_PREFERENCE_PREFIX}.${tableName}.${listViewName}`;
		dispatch(USER_PREFERENCE_ACTION_TYPES.SET_USER_PREFERENCE, {
			preferenceName,
			preferenceValue,
		});
	}
};

/**
 * Function to set the equal width to all columns of a grid
 * when user preference width is not present.
 */
const handleColumnWidth = ({ action, state, updateState }) => {
	const { width } = action.payload;
	// removing some pixel of width for vertical scroll.
	const finalWidth = width - 80;
	const { columns } = state;
	const updatedColumns = columns.map((column) => {
		set(column, 'width', Math.floor(finalWidth / columns.length));

		return column;
	});

	updateState({
		columns: [...updatedColumns],
		offsetWidth: finalWidth,
		hasOffsetWidth: true,
	});
};

const setDefaultColumnWidth = ({ state, dispatch, action }) => {
	const { payload } = action;
	const { width } = payload;
	const { hasOffsetWidth } = state;

	if (!hasOffsetWidth && width)
		dispatch(GRID_COLUMN_WIDTH_ACTION_TYPES.SET_DEFAULT_COLUMN_WIDTH, {
			width,
		});
};

const handlePreviewIconClicked = ({ action, state, dispatch }) => {
	const { payload } = action;
	const { taskId: recordSysId } = payload;
	const { properties, listOfRecords } = state;
	const { tableName } = properties;

	let modifiedTableName = tableName;

	if (listOfRecords) {
		const previewedRecord = listOfRecords.filter(
			(record) => record.id === recordSysId
		);
		modifiedTableName = previewedRecord[0].tableName;
	}

	const editEventAsset = {
		table: modifiedTableName,
		sys_id: recordSysId,
		row: {
			sys_id: {
				value: recordSysId,
			},
		},
	};

	dispatch(RECORD_ACTIONS.PREVIEW_RECORD, editEventAsset);
};

const handleGridRefresh = ({ action, dispatch }) => {
	const { payload } = action;
	const { actionName, actionPayload } = payload;

	dispatch(actionName, actionPayload);
};

const actionHandlers = {
	[GRID_COLUMN_WIDTH_ACTION_TYPES.SET_DEFAULT_COLUMN_WIDTH]: {
		effect: handleColumnWidth,
		stopPropogation: true,
	},
	[GRID_COLUMN_WIDTH_ACTION_TYPES.COLUMN_RESIZE_UPDATED]: {
		effect: handleColumnPersonalization,
		stopPropogation: true,
	},
	[GRID_COLUMN_WIDTH_ACTION_TYPES.ON_LOAD_FETCH_WIDTH]: {
		effect: setDefaultColumnWidth,
		stopPropogation: true,
	},
	[GRID_ACTIONS.PREVIEW_ICON_CLICKED]: {
		effect: handlePreviewIconClicked,
		stopPropogation: true,
	},
	[GRID_ACTIONS.REFRESH_GRID]: {
		effect: handleGridRefresh,
		stopPropogation: true,
	},
};

export default actionHandlers;
