import '@servicenow/now-button';
import '@devsnc/sn-datagrid';
import '@servicenow/now-loader';
import ItemPickerModal from '../ItemPickerModal/ItemPickerModalElement';
import '../EmptyState/EmptyStateElement';
import RecoveryAddPlansModal from '../RecoveryAddPlansModal/RecoveryAddPlansModalElement';
import {
	LOADING_VIEW_MESSAGE,
	ADD_ASSETS_BUTTON_LABEL,
	ADD_PLANS_BUTTON_LABEL,
	ADD_TASK_BUTTON_LABEL,
	RECOVERY_EVENT_GRID_CONFIG,
	PLUS_OUTLINE_ICON,
	BUTTON_SIZE,
	BUTTON_VARIANT,
	EVENT_ASSET_TABLE_NAME,
	ADD_ASSET_MODAL_HEADER_LABEL,
	SYNC_OUTLINE_ICON,
	REFRESH_BUTTON_TOOLTIP,
} from './constants/RecoveryEventResultConstants';
import '@devsnc/sn-datagrid';
import { GRID_COLUMN_WIDTH_ACTION_TYPES } from '../../util/grid-util/constants/GridActionTypes';
import { TOOLBAR_ACTIONS } from './constants/RecoveryEventResultDataActionTypes';

const view = (state, dispatch) => {
	const {
		isLoading,
		columns,
		listOfRecords,
		showAdHocTaskButton,
		showAddPlansButton,
		showAddAssetsButton,
		isAddAssetsClicked,
		elementsList,
		isPlanModalOpen,
		isEventInProgress,
		errorMessage,
		properties,
	} = state;

	const { tableName, eventSysId, planColumns, planAssetColumns } = properties;

	let toolbarButtonsList = [];

	{
		showAddAssetsButton
			? toolbarButtonsList.push(
					<now-button
						className="add-asset-button"
						label={ADD_ASSETS_BUTTON_LABEL}
						icon={PLUS_OUTLINE_ICON}
						size={BUTTON_SIZE}
						variant={BUTTON_VARIANT}
						tooltipContent={ADD_ASSETS_BUTTON_LABEL}
						bare={false}
						configAria={{ 'aria-label': ADD_ASSETS_BUTTON_LABEL }}
						on-click={() => {
							dispatch(TOOLBAR_ACTIONS.ADD_ASSETS_BUTTON_CLICKED);
						}}
					/>
			  )
			: null;
	}

	{
		showAddPlansButton
			? toolbarButtonsList.push(
					<now-button
						className="add-plan-button"
						label={ADD_PLANS_BUTTON_LABEL}
						icon={PLUS_OUTLINE_ICON}
						size={BUTTON_SIZE}
						variant={BUTTON_VARIANT}
						tooltipContent={ADD_PLANS_BUTTON_LABEL}
						bare={false}
						on-click={() => {
							dispatch(TOOLBAR_ACTIONS.ADD_PLANS_BUTTON_CLICKED);
						}}
						configAria={{ 'aria-label': ADD_PLANS_BUTTON_LABEL }}
					/>
			  )
			: null;
	}
	{
		showAdHocTaskButton
			? toolbarButtonsList.push(
					<now-button
						disabled={listOfRecords && listOfRecords.length > 0 ? false : true}
						className="add-task-button"
						label={ADD_TASK_BUTTON_LABEL}
						icon={PLUS_OUTLINE_ICON}
						size={BUTTON_SIZE}
						variant={BUTTON_VARIANT}
						tooltipContent={ADD_TASK_BUTTON_LABEL}
						bare={false}
						configAria={{ 'aria-label': ADD_TASK_BUTTON_LABEL }}
						on-click={() => {
							if (listOfRecords && listOfRecords.length > 0)
								dispatch(TOOLBAR_ACTIONS.ADD_TASK_BUTTON_CLICKED);
						}}
					/>
			  )
			: null;
	}

	toolbarButtonsList.push(
		<now-button
			className="refresh-button"
			icon={SYNC_OUTLINE_ICON}
			size={BUTTON_SIZE}
			variant={BUTTON_VARIANT}
			tooltipContent={REFRESH_BUTTON_TOOLTIP}
			bare={false}
			configAria={{ 'aria-label': REFRESH_BUTTON_TOOLTIP }}
			on-click={() => {
				dispatch(TOOLBAR_ACTIONS.REFRESH_GRID);
			}}
		/>
	);

	const loadingView = (
		<div className="recovery-event-result-loading-view">
			<now-loader label={LOADING_VIEW_MESSAGE} />
		</div>
	);

	const renderAssetsModal = () => {
		if (isAddAssetsClicked) {
			return (
				<ItemPickerModal
					table={EVENT_ASSET_TABLE_NAME}
					parentTable={tableName}
					parentTableReferenceField="event"
					parentRecordSysId={eventSysId}
					elementDefinitionTableReferenceField=""
					itemFieldName="asset_name"
					itemTableFieldName="asset_table"
					modalHeaderLabel={ADD_ASSET_MODAL_HEADER_LABEL}
					isRecoveryEvent={true}
				/>
			);
		}
	};

	const eventClosedView = (
		<now-alert
			className={errorMessage ? 'now-alert-error-element' : 'hidden'}
			status="info"
			icon="circle-info-outline"
			content={errorMessage}
			action={{ type: 'dismiss' }}
		/>
	);

	const eventResultView = (
		<div className="activities-list-container">
			<div
				className="grid-container"
				hook-update={(vnode) => {
					dispatch(GRID_COLUMN_WIDTH_ACTION_TYPES.ON_LOAD_FETCH_WIDTH, {
						width: vnode.elm.getBoundingClientRect().width,
					});
				}}>
				{eventClosedView}
				<sn-datagrid
					config={RECOVERY_EVENT_GRID_CONFIG}
					columns={columns}
					tasks={listOfRecords}>
					<div slot="toolbar-icons" className="toolbar-buttons">
						{toolbarButtonsList}
					</div>
					<div slot="empty-state" className="empty-view">
						<sn-bcm-empty-state />
					</div>
				</sn-datagrid>
			</div>
			{renderAssetsModal()}
			{isPlanModalOpen ? (
				<RecoveryAddPlansModal
					listOfOptions={elementsList}
					isModalOpen={true}
					eventSysId={eventSysId}
					isEventInProgress={isEventInProgress}
					planColumns={planColumns}
					planAssetColumns={planAssetColumns}
				/>
			) : null}
		</div>
	);
	if (isLoading) {
		return loadingView;
	}

	return eventResultView;
};

export default view;
