import { createHttpEffect } from '@servicenow/ui-effect-http';
import PLAN_EVENT_ACTIONS from '../constants/ActionTypes';
import {
	CRISIS_EVENTS_URL,
	FETCH_ERROR_MESSAGE,
	DATA_PLACEHOLDER,
	NO_RESULTS_TEXT,
} from '../constants/GeneralConstants';

const requestPlanEventsResult = createHttpEffect(CRISIS_EVENTS_URL, {
	method: 'GET',
	queryParams: ['planSysId', 'eventType'],
	successActionType: PLAN_EVENT_ACTIONS.EVENTS_RESULT_DETAILS_SUCCEEDED,
	errorActionType: PLAN_EVENT_ACTIONS.EVENTS_RESULT_DETAILS_FAILED,
	batch: false,
});

const getPlanEventsResultSuccess = ({ action, updateState }) => {
	const { payload } = action;

	if (payload.error) {
		console.error(FETCH_ERROR_MESSAGE);
		return updateState({
			error: true,
			isLoading: false,
		});
	}

	const { result: dataReturned } = payload;

	if (!dataReturned) {
		console.error(NULL_TABLE_ERROR_MESSAGE);
		return updateState({
			error: true,
			isLoading: false,
		});
	}

	const {
		totalEventsCount,
		ongoingEventsCount,
		lastEventDate,
		lastEventResultStatus,
	} = dataReturned;

	let noResultStatus = DATA_PLACEHOLDER;
	if (lastEventDate && lastEventResultStatus === null) {
		noResultStatus = NO_RESULTS_TEXT;
	}

	updateState({
		totalEventsCount,
		ongoingEventsCount,
		lastEventDate: lastEventDate || DATA_PLACEHOLDER,
		lastEventResultStatus: lastEventResultStatus || noResultStatus,
		isLoading: false,
	});
};

const getPlanEventsResultFailed = ({ action, updateState }) => {
	const { data } = action.payload;
	const { error } = data;
	const { message } = error;

	console.error(message);
	console.error(FETCH_ERROR_MESSAGE);
	updateState({
		error: true,
		isLoading: false,
	});
};

const actionHandlers = {
	[PLAN_EVENT_ACTIONS.EVENTS_RESULT_DETAILS_REQUESTED]: requestPlanEventsResult,
	[PLAN_EVENT_ACTIONS.EVENTS_RESULT_DETAILS_SUCCEEDED]: {
		effect: getPlanEventsResultSuccess,
	},
	[PLAN_EVENT_ACTIONS.EVENTS_RESULT_DETAILS_FAILED]: {
		effect: getPlanEventsResultFailed,
	},
};

export default actionHandlers;
