const recordQuery = `query getBcpPlanContributors ($sysId: String!, $selectionTable: String!) {
    GlideRecord_Query {
        {{table}} (sys_id: $sysId) {
            results:_results {
                {{field}} {
                    value
                }
            }
        }
    }
    usersAggregateCountQuery: GlideAggregate_Query (tableName: $selectionTable) {
        count
    }
}`;

export default recordQuery;