import { actionTypes } from '@servicenow/ui-core';
import { EVENT_DATA_FETCH_ACTIONS } from '../constants/RecoveryEventResultDataActionTypes';
import { PAGINATION_LIMIT } from '../constants/RecoveryEventResultConstants';
import { USER_PREFERENCE_PREFIX } from '../../../util/grid-util/constants/GeneralConstants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ properties, dispatch, updateState }) => {
		const {
			eventSysId,
			columns,
			tableName,
			listViewName,
			eventAssetRecordLoadLimit,
			activatedPlanRecordLoadLimit,
			eventTaskRecordLoadLimit,
		} = properties;
		const userPreferenceName = `${USER_PREFERENCE_PREFIX}.${tableName}.${listViewName}`;

		updateState({
			userPreferenceName,
		});

		dispatch(EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_RESULT_DATA_REQUESTED, {
			tableName,
			eventSysId,
			columns,
			userPreferenceName,
			eventAssetRecordLoadLimit,
			activatedPlanRecordLoadLimit,
			eventTaskRecordLoadLimit,
		});
		// Fetches event type and exercise data
		dispatch(EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_TABLE_DATA_REQUESTED, {
			eventSysId: eventSysId,
		});
		// Fetches event state and state attributes.
		dispatch(
			EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_TABLE_STATE_ATTRIBUTES_REQUESTED,
			{
				eventSysId,
			}
		);
	},
};

export default actionHandlers;
