import { NOW_HTML_EDITOR_VALUE_CHANGED } from '../constants/NowUIComponentsActionTypes';

const updateRTEContentState = ({ action, updateState }) => {
	const { value } = action.payload;

	updateState({
		richTextContent: value,
	});
};

const actionHandlers = {
	[NOW_HTML_EDITOR_VALUE_CHANGED]: updateRTEContentState,
};

export default actionHandlers;
