import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './PlanEventsOverviewCardView';
import styles from './PlanEventsOverviewCardStyles.scss';
import lifecycleActionHandlers from './actions/LifeCycleActions';
import dataFetchActionHandlers from './actions/DataFetchActions';
import {
	EVENT_TYPE_VALUE,
	CARD_LABELS,
	DATA_PLACEHOLDER,
} from './constants/GeneralConstants';

const TAG_NAME = 'sn-bcm-plan-events-overview-score';

/**
 * This component is to be used inside the Plan Overview component.
 * And it will display overview of events/exercises results.
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-plan-events-overview-score
 * 		plan-sys-id=""
 * ></sn-bcm-plan-events-overview-score>
 * ```
 *
 * @seismicElement sn-bcm-plan-events-overview-score
 * @summary This component is to be used inside the Plan Overview component.
 * And it will display overview of events/exercises results.
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		 * SysId of the record the overview will be displayed on
		 *
		 * @type {string}
		 */
		planSysId: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Event Type - 'Exercise' or 'Actual'
		 *
		 * @type {string}
		 */
		eventType: {
			default: EVENT_TYPE_VALUE.EXERCISE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Card heading
		 *
		 * @type {string}
		 */
		cardHeaderLabel: {
			default: CARD_LABELS.EXERCISE.CARD_HEADER_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Total events label
		 *
		 * @type {string}
		 */
		totalEventsLabel: {
			default: CARD_LABELS.EXERCISE.TOTAL_EVENTS_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Ongoing events label
		 *
		 * @type {string}
		 */
		ongoingEventsLabel: {
			default: CARD_LABELS.EXERCISE.ONGOING_EVENTS_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Last event date label
		 *
		 * @type {string}
		 */
		lastEventDateLabel: {
			default: CARD_LABELS.EXERCISE.LAST_EVENT_DATE_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Last event result status label
		 *
		 * @type {string}
		 */
		lastEventResultLabel: {
			default: CARD_LABELS.EXERCISE.LAST_EVENT_RESULT_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
	},
	initialState: {
		/**
		 * State of the component whether it is loaded or not
		 *
		 * @type {boolean}
		 */
		isLoading: true,
		/**
		 * Total count of the events
		 *
		 * @type {string}
		 */
		totalEventsCount: DATA_PLACEHOLDER,
		/**
		 * Count of the ongoing/in-progress events
		 *
		 * @type {string}
		 */
		ongoingEventsCount: DATA_PLACEHOLDER,
		/**
		 * Date of last event
		 *
		 * @type {string}
		 */
		lastEventDate: DATA_PLACEHOLDER,
		/**
		 * Result status of last event
		 *
		 * @type {string}
		 */
		lastEventResultStatus: DATA_PLACEHOLDER,
	},
	actionHandlers: {
		...lifecycleActionHandlers,
		...dataFetchActionHandlers,
	},
	styles,
});

export default TAG_NAME;
