import { DEFAULT_BIA_TABLE, CREATE_NEW_RECORD_SYS_ID } from '../constants/NewBiaDynamicModalConstants';

const tableFormLayoutQuery = `query getFormLayout{
	GlideLayout_Query {
	formLayout(table: "${DEFAULT_BIA_TABLE}", view: "{{layoutView}}", sysId: "${CREATE_NEW_RECORD_SYS_ID}") {
		recordValues {
			name
			value
			displayValue
			valuesList {
				value
				displayValue
			}
		}
		elementsData {
		  name: elementName
		  dictionary: dictionaryData {
			type: dictionaryType
			dependentField
			dependentTable
			name
			label
			canWrite
			isMandatory
			sys_readonly: sysReadonly
			attributes {
			  name
			  value
			}
		  }
		  ... on GlideLayout_ChoiceElementType {
			choices {
			  displayValue
			  value
			}
		  }
		  ... on GlideLayout_ReferenceElementType {
			reference
			referenceQualifier
			referenceKey
			useReferenceQualifier
			dependent
			dependentOnField
			refAutoCompleter
		  }
		  ... on GlideLayout_ValueElementType {
			maxLength
			defaultRows
		  }
		}
	  }
	}
  }`;

export default tableFormLayoutQuery;