import '@servicenow/now-loader';
import '@servicenow/now-card';
import '@servicenow/now-chart-donut-pie';
import '@servicenow/now-heading';
import '@servicenow/now-button';
import DynamicCarouselElement from '../DynamicCarousel/DynamicCarouselElement';
import {
	getLegendItem,
	getDonutChartProperties,
	getCarouselVisualizationItem,
} from './util/PlanAssetsOverviewUtil';
import {
	CHART_CONTAINER_STYLING,
	CHART_ELEMENT_STYLING,
	CHART_LABEL_STYLING,
	DEFAULT_LOADING_MESSAGE,
	DEFAULT_PLAN_LOSS_SCENARIO_TABLE,
	ACTION_BUTTON_ACTION_NAME,
	ACTION_BUTTON_TOOLTIP,
	ACTION_BUTTON_ARIA_LABEL,
} from './constants/PlanAssetsOverviewCardConstants';

const view = (state, { dispatch }) => {
	const {
		isLoading,
		assetDependenciesByLossScenario,
		properties,
		isAccessibilityModeActive,
	} = state;

	const {
		cardHeaderLabelText,
		cardHeaderLabelVariant,
		cardHeaderLabelSize,
		visualizationChartType,
		coveredAssetsColorHex,
		uncoveredAssetsColorHex,
		coveredAssetsLabel,
		uncoveredAssetsLabel,
		isTooltipEnabled,
		totalCountLabel,
		emptyStateLabel,
		maxElementsPerSection,
		activeSectionIndicatorColor,
		inactiveSectionIndicatorColor,
		carouselScrollButtonSize,
		relatedItemTabIndex,
	} = properties;

	const loaderElement = (
		<div className="loader-element-container">
			<now-loader label={DEFAULT_LOADING_MESSAGE} />
		</div>
	);

	const actionButtonElement = (
		<now-button-iconic
			slot="metadata"
			on-click={() => {
				dispatch(ACTION_BUTTON_ACTION_NAME, {
					params: {
						selectedTabIndex: relatedItemTabIndex,
					},
					redirectEmptyState: true,
				});
			}}
			icon="open-link-outline"
			tooltipContent={ACTION_BUTTON_TOOLTIP}
			configAria={{ 'aria-label': ACTION_BUTTON_ARIA_LABEL }}
			bare
			hidePadding
			highContrast
		/>
	);

	const cardHeaderElement = (
		<now-card-header
			className="now-card-header-element"
			heading={{
				label: cardHeaderLabelText,
				variant: cardHeaderLabelVariant,
				size: cardHeaderLabelSize,
				level: 1,
				lines: 1,
			}}>
			{actionButtonElement}
		</now-card-header>
	);

	const donutChartElements = assetDependenciesByLossScenario.map(
		(lossScenario) => {
			const donutChartProperties = getDonutChartProperties(
				visualizationChartType,
				coveredAssetsColorHex,
				coveredAssetsLabel,
				uncoveredAssetsColorHex,
				uncoveredAssetsLabel,
				lossScenario,
				isTooltipEnabled,
				totalCountLabel,
				isAccessibilityModeActive
			);
			const minimumCarouselItemWidth = Math.floor(100 / maxElementsPerSection);

			CHART_CONTAINER_STYLING.minWidth = `${minimumCarouselItemWidth}%`;

			return getCarouselVisualizationItem(
				CHART_CONTAINER_STYLING,
				CHART_ELEMENT_STYLING,
				donutChartProperties,
				CHART_LABEL_STYLING,
				lossScenario.scenarioName,
				dispatch,
				DEFAULT_PLAN_LOSS_SCENARIO_TABLE,
				lossScenario.scenarioSysId
			);
		}
	);

	const carouselElement = (
		<DynamicCarouselElement
			className="dynamic-carousel-element"
			elements={donutChartElements}
			maxElementsPerSection={maxElementsPerSection}
			activeSectionIndicatorColor={activeSectionIndicatorColor}
			inactiveSectionIndicatorColor={inactiveSectionIndicatorColor}
			carouselScrollButtonSize={carouselScrollButtonSize}
		/>
	);

	const colorLegendElement = (
		<div className="color-legend-container">
			{getLegendItem(coveredAssetsColorHex, coveredAssetsLabel)}
			{getLegendItem(uncoveredAssetsColorHex, uncoveredAssetsLabel)}
		</div>
	);
	const assetsByLossScenarioVisualizationElement = (
		<div className="overview-visualization-container">
			{carouselElement}
			{colorLegendElement}
		</div>
	);
	const emptyStateElement = (
		<div className="empty-state-container">
			<now-heading
				label={emptyStateLabel}
				level="1"
				variant="title-secondary"
			/>
		</div>
	);
	const mainContent = assetDependenciesByLossScenario.length
		? assetsByLossScenarioVisualizationElement
		: emptyStateElement;
	const assetsOverviewCardElement = (
		<now-card size="lg" className="now-card-element">
			{cardHeaderElement}
			{isLoading ? loaderElement : mainContent}
		</now-card>
	);

	return assetsOverviewCardElement;
};

export default view;
