import { actionTypes } from '@servicenow/ui-core';
import { FORM_LAYOUT_QUERY_REQUESTED } from '../constants/NewBiaDynamicModalActionTypes';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const fetchBiaFormLayout = ({ updateState, properties, dispatch }) => {
	const { layoutView } = properties;

	updateState({
		isLoading: true,
	})

	dispatch(FORM_LAYOUT_QUERY_REQUESTED, {
		layoutView,
	});
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect: fetchBiaFormLayout,
	},
};

export default actionHandlers;