import { t } from 'sn-translate';
import '@servicenow/now-typeahead';
import {
	LIST_PICKER_MESSAGE_FOR_ALL_RECORDS,
	LIST_PICKER_MESSAGE_FOR_PLAN_SCOPE_SELECTED,
	RELATED_ASSET_DEPENDENCY_OPTIONS,
	RELATED_ASSET_DEPENDENCY_HEADING,
	FILTER_BY_PLAN_SCOPE_CHOICE,
	SHOW_ALL_RECORDS_CHOICE,
	MODAL_VIEW_TYPES,
	ERROR_ALERT_HEADING,
	SELECT_FILTER_GROUP_LABEL,
	MODAL_HEADER_LABEL,
	PLAN_SCOPE_LABEL,
	NO_BIA_ATTACHED_TO_PLAN_SCOPE_MESSAGE,
} from './constants/RelatedAssetDependencyItemPickerModalConstants';
import {
	getRecordListPickerElement,
	getErrorAlert,
	getRadioChoiceList,
	getLoaderElement,
	getItemPickerModalView,
} from '../ItemPickerModal/utils/ItemPickerModalCommonUtil';
import {
	getModalFooterAction,
	getIfCheckedRadioList,
	getIfTypeaheadValueIsSet,
} from './RelatedAssetDependencyItemPickerModalUtils';

const view = (state) => {
	const {
		isModalOpen,
		selectionTable,
		recordListPickerFilterConditions,
		selectionTableFields,
		currentlySelectedEncodedQuery,
		errorMessage,
		isLoading,
		properties,
		elementDefinitionLabel,
		currentScreenView,
		radioSelectionValue,
		loadingViewMessage,
		planScopeItemsList,
		selectedPlanScopeValue,
		listPickerOptionalProps,
	} = state;

	const { paginationRecordLimit } = properties;

	const disableListPickerButton = Boolean(
		!(currentlySelectedEncodedQuery && !errorMessage && !isLoading)
	);

	const disableNextButton = !(
		(radioSelectionValue === FILTER_BY_PLAN_SCOPE_CHOICE.id &&
			Object.keys(selectedPlanScopeValue).length !== 0) ||
		radioSelectionValue === SHOW_ALL_RECORDS_CHOICE.id
	);

	const modalFooterButtons = [
		...getModalFooterAction(
			currentScreenView,
			disableListPickerButton,
			disableNextButton
		),
	];

	const loaderElement = getLoaderElement(loadingViewMessage);

	const getListPickerMessage = () => {
		switch (radioSelectionValue) {
			case FILTER_BY_PLAN_SCOPE_CHOICE.id:
				return selectedPlanScopeValue.fields.bia
					? t(
							LIST_PICKER_MESSAGE_FOR_PLAN_SCOPE_SELECTED,
							selectedPlanScopeValue.label
					  )
					: t(
							NO_BIA_ATTACHED_TO_PLAN_SCOPE_MESSAGE,
							selectedPlanScopeValue.label
					  );
			default:
				return t(LIST_PICKER_MESSAGE_FOR_ALL_RECORDS, elementDefinitionLabel);
		}
	};

	const itemRecordListPickerElement = () => {
		return getRecordListPickerElement(
			getListPickerMessage(),
			selectionTable,
			recordListPickerFilterConditions,
			paginationRecordLimit,
			selectionTableFields,
			listPickerOptionalProps
		);
	};

	const errorAlertElement = getErrorAlert(errorMessage, ERROR_ALERT_HEADING);

	const radioChoiceListElement = () => {
		return (
			<div>
				{getRadioChoiceList(
					t(RELATED_ASSET_DEPENDENCY_HEADING, elementDefinitionLabel),
					getIfCheckedRadioList(
						RELATED_ASSET_DEPENDENCY_OPTIONS,
						radioSelectionValue
					),
					SELECT_FILTER_GROUP_LABEL
				)}
				{radioSelectionValue === FILTER_BY_PLAN_SCOPE_CHOICE.id ? (
					<now-typeahead
						label={PLAN_SCOPE_LABEL}
						search="managed"
						items={planScopeItemsList}
						className="plan-scope-choice"
						required={true}
						{...getIfTypeaheadValueIsSet(selectedPlanScopeValue)}
					/>
				) : null}
			</div>
		);
	};

	const getModalContent = () => {
		switch (currentScreenView) {
			case MODAL_VIEW_TYPES.RADIO_BUTTONS:
				return radioChoiceListElement();
			case MODAL_VIEW_TYPES.RECORD_LIST_PICKER:
			default:
				return itemRecordListPickerElement();
		}
	};

	const modalBodyContent = isLoading ? loaderElement : getModalContent();

	return getItemPickerModalView(
		isModalOpen,
		MODAL_HEADER_LABEL,
		modalFooterButtons,
		errorAlertElement,
		modalBodyContent
	);
};

export default view;
