import EDITABLE_LIST_MODAL_ACTION_TYPES from '../constants/EditableListModalActionTypes';
import { EDITABLE_LIST_QUERY_ACTION_TYPES } from '../constants/EditableListCRUDActionTypes';
import { USER_PREFERENCE_PREFIX } from '../../../util/grid-util/constants/GeneralConstants';
import { getModalLabels } from '../utils/EditableListUtils';

const handleModalOpen = ({ action, state, updateState }) => {
	const { sysId } = action.payload;
	const { tableLabel } = state;
	const _modalCreateLabels = getModalLabels('insert', tableLabel);

	updateState({
		isModalOpen: true,
		recordId: sysId,
		modalLabels: _modalCreateLabels,
	});
};

const handleModalClose = ({ updateState }) => {
	updateState({
		isModalOpen: false,
		recordId: '',
	});
};

const handleGridRefresh = ({ state, dispatch }) => {
	const {
		tableName,
		listViewName,
		filterQueryConditions,
		recordLoadLimit,
		userPreferenceName,
	} = state.properties;

	dispatch(EDITABLE_LIST_QUERY_ACTION_TYPES.EDITABLE_LIST_QUERY_REQUESTED, {
		tableName,
		listViewName,
		filterQueryConditions,
		recordLoadLimit,
		userPreferenceName,
	});
};

const actionHandlers = {
	[EDITABLE_LIST_MODAL_ACTION_TYPES.MODAL_OPEN_REQUESTED]: {
		effect: handleModalOpen,
		stopPropogation: true,
	},
	[EDITABLE_LIST_MODAL_ACTION_TYPES.MODAL_CLOSED]: {
		effect: handleModalClose,
		stopPropogation: true,
	},
	[EDITABLE_LIST_MODAL_ACTION_TYPES.MODAL_ACTION_COMPLETE]: {
		effect: handleGridRefresh,
		stopPropogation: true,
	},
};

export default actionHandlers;
