import {
	NOW_RECORD_LIST_ROW_SELECTION_UPDATED,
	NOW_RECORD_LIST_PAGE_UPDATED,
} from '../constants/BiaDependencyItemPickerModalActionTypes';
import { MODAL_VIEW_TYPES } from '../constants/BiaDependencyItemPickerModalConstants';

const actions = {
	[NOW_RECORD_LIST_ROW_SELECTION_UPDATED]: {
		private: true,
	},
};

const updateElectedEncodedQuery = ({ state, action, updateState }) => {
	const {
		currentScreenView,
		cmdbRelTypeOptionalProps,
		cmdbCiRecordListOptionalProps,
		recordListOptionalProps,
	} = state;
	const { selectedRecords, query, table } = action.payload;

	if (table) {
		switch (currentScreenView) {
			case MODAL_VIEW_TYPES.CMDB_REL_TYPE_LIST_PICKER:
				updateState({
					cmdbRelTypeOptionalProps: {
						...cmdbRelTypeOptionalProps,
						selectedRecords,
					},
					selectedRelationshipTypes: selectedRecords,
					cmdbCiRecordListOptionalProps: { page: 1 },
					currentlySelectedEncodedQuery: '',
				});
				break;
			case MODAL_VIEW_TYPES.CMDB_RECORD_LIST_PICKER:
				updateState({
					cmdbCiRecordListOptionalProps: {
						...cmdbCiRecordListOptionalProps,
						selectedRecords,
					},
					currentlySelectedEncodedQuery: query,
				});
				break;
			case MODAL_VIEW_TYPES.RECORD_LIST_PICKER:
				updateState({
					recordListOptionalProps: {
						...recordListOptionalProps,
						selectedRecords,
					},
					currentlySelectedEncodedQuery: query,
				});
				break;
		}
	}
};

const updateActivePage = ({ state, action, updateState }) => {
	const {
		cmdbRelTypeOptionalProps,
		cmdbCiRecordListOptionalProps,
		recordListOptionalProps,
		currentScreenView,
	} = state;
	const { page } = action.payload;

	if (page) {
		switch (currentScreenView) {
			case MODAL_VIEW_TYPES.CMDB_REL_TYPE_LIST_PICKER:
				updateState({
					cmdbRelTypeOptionalProps: {
						...cmdbRelTypeOptionalProps,
						page: page,
					},
				});
				break;
			case MODAL_VIEW_TYPES.CMDB_RECORD_LIST_PICKER:
				updateState({
					cmdbCiRecordListOptionalProps: {
						...cmdbCiRecordListOptionalProps,
						page: page,
					},
				});
				break;
			case MODAL_VIEW_TYPES.RECORD_LIST_PICKER:
				updateState({
					recordListOptionalProps: {
						...recordListOptionalProps,
						page: page,
					},
				});
				break;
		}
	}
};

const actionHandlers = {
	[NOW_RECORD_LIST_ROW_SELECTION_UPDATED]: updateElectedEncodedQuery,
	[NOW_RECORD_LIST_PAGE_UPDATED]: updateActivePage,
};

export { actions, actionHandlers };
