import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './BiaDependencyAssessmentView';
import {
	LOADING_VIEW_MESSAGE,
	DEPENDENCY_ASSESSMENT_STATUS_FIELD,
	DEPENDENCY_GROUP_TABLE,
	DEPENDENCY_ASSESSMENT_CHILD_TABLE,
	DEPENDENCY_ASSESSMENT_RECOVERY_TIMEFRAME,
	DEPENDENCY_ELEMENT_REFERENCE_FIELD_NAME,
	DEPENDENCY_ASSESSMENT_HEADER_FIELD,
	DEPENDENCY_ASSESSMENT_DESCRIPTION_FIELD,
	DEPENDENCY_ASSESSMENT_CHILD_TABLE_REFERENCE_FIELD,
	IMPACT_ANALYSIS_FIELD,
} from './constants/BiaDependencyAssessmentConstants';
import lifecycleActionHandlers from './actions/BiaDependencyAssessmentLifeCycleActions';
import dependencyAssessmentDataActionHandlers from './actions/BiaDependencyAssessmentDataFetchQueryActions';
import itemInsertionActionHandlers from './actions/ItemInsertionActions';
import modalVisibilityActionHandlers from './actions/ModalVisibilityActions';
import cardHeaderButtonsActionHandlers from './actions/CardHeaderButtonsActions';
import impactAnalysisMetaDataActionHandlers from './actions/ImpactAnalysisTableRecordMetaDataActions';
import preferenceActionHandlers from '../../util/grid-util/actions/UserPreferenceActions';
import styles from './BiaDependencyAssessmentElement.scss';

const TAG_NAME = 'sn-bia-dependency-assessment';

/**
 * Accordion List of dependency groups for a particular Impact Analysis
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bia-dependency-assessment
 * 		parent-record-sys-id=""
 * 		dependency-element-reference-column-field=""
 * 		accordion-header-field=""
 * 		accordion-description-field=""
 * ></sn-bia-dependency-assessment>
 * ```
 *
 * @seismicElement sn-bia-dependency-assessment
 * @summary A accordion list for displaying dependencies of dependency group
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		 * Sysid of the record in parent table
		 *
		 * @type {string}
		 */
		parentRecordSysId: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 *  Reference column of the parent table
		 *
		 *  @type {string}
		 */
		parentTableReferenceField: {
			default: IMPACT_ANALYSIS_FIELD,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The table name on which data should be queried on.
		 *
		 * @type {string}
		 */
		tableName: {
			default: DEPENDENCY_GROUP_TABLE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Name of the status field of a related table(i.e sn_bia_dependency_group)
		 *
		 * @type {string}
		 */
		dependencyAssessmentStatusField: {
			default: DEPENDENCY_ASSESSMENT_STATUS_FIELD,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Field Name of the element reference column of related table(i.e sn_bia_dependency_group)
		 *
		 * @type {string}
		 */
		dependencyElementReferenceColumnField: {
			default: DEPENDENCY_ELEMENT_REFERENCE_FIELD_NAME,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Field name of the record to be shown on Accordion Header
		 *
		 * @type {string}
		 */
		accordionHeaderField: {
			default: DEPENDENCY_ASSESSMENT_HEADER_FIELD,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 *  Field name of the record to be shown on Card Description
		 *
		 * @type {string}
		 */
		accordionDescriptionField: {
			default: DEPENDENCY_ASSESSMENT_DESCRIPTION_FIELD,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 *  Reference column(i.e dependency_group) of the parent table on the child table
		 *
		 *  @type {string}
		 */
		dependencyAssessmentChildTableField: {
			default: DEPENDENCY_ASSESSMENT_CHILD_TABLE_REFERENCE_FIELD,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 *  recovery timeframe column of the table
		 *
		 *  @type {string}
		 */
		dependencyAssessmentRecoveryTimeField: {
			default: DEPENDENCY_ASSESSMENT_RECOVERY_TIMEFRAME,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 *  Name of the child table on sn_bia_dependency_group table
		 *
		 *  @type {string}
		 */
		dependencyAssessmentChildTable: {
			default: DEPENDENCY_ASSESSMENT_CHILD_TABLE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Loading view message of the component
		 *
		 * @type {string}
		 */
		loadingViewMessage: {
			default: LOADING_VIEW_MESSAGE,
			required: false,
			schema: {
				type: 'string',
			},
		},
	},
	initialState: {
		/**
		 * Intial loading state of the component
		 *
		 * @type {boolean}
		 */
		isLoading: true,
		/**
		 * Array of all fetched records from table
		 *
		 * @type {object}
		 */
		tableRecords: [],
		isAddItemsModalOpen: false,
		recordSysId: '',
		errorMessage: '',
		isParentRecordEditable: true,
		_listOfRecords:[],
	},
	actionHandlers: {
		...lifecycleActionHandlers,
		...dependencyAssessmentDataActionHandlers,
		...itemInsertionActionHandlers,
		...modalVisibilityActionHandlers,
		...cardHeaderButtonsActionHandlers,
		...impactAnalysisMetaDataActionHandlers,
		...preferenceActionHandlers,
	},
	styles,
});

export default TAG_NAME;
