import { get } from 'lodash';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	FETCH_RECOVERY_TASKS_COUNT_DETAILS_REQUESTED,
	FETCH_RECOVERY_TASKS_COUNT_DETAILS_SUCCEEDED,
	FETCH_RECOVERY_TASKS_COUNT_DETAILS_FAILED,
} from '../constants/PlanActivitiesScoreCardActionTypes';
import {
	FETCH_ERROR_MESSAGE,
	RECOVERY_TASK_AGGREGATE_URL,
} from '../constants/PlanActivitiesScoreCardConstants';

const requestPlanActivitiesCount = createHttpEffect(
	RECOVERY_TASK_AGGREGATE_URL,
	{
		method: 'GET',
		queryParams: ['sysId', 'queryColumns', 'targetTableIdentifier'],
		successActionType: FETCH_RECOVERY_TASKS_COUNT_DETAILS_SUCCEEDED,
		errorActionType: FETCH_RECOVERY_TASKS_COUNT_DETAILS_FAILED,
		batch: false,
	}
);

const getPlanActivitiesCountSuccess = ({ action, updateState }) => {
	const { result } = action.payload;

	if (result.error) {
		console.log(FETCH_ERROR_MESSAGE);
		console.log(result.error);
		updateState({
			error: true,
			isLoading: false,
		});
	}

	const dataReturned = get(action, 'payload.result.requestBody');
	const {
		totalCount: totalActivitiesCount,
		assignedCount: assignedActivitiesCount,
		unassignedCount: unAssignedActivitiesCount,
	} = dataReturned;

	if (
		(totalActivitiesCount === 'undefined' || totalActivitiesCount === null)
		|| (unAssignedActivitiesCount === 'undefined' || unAssignedActivitiesCount === null)
		|| (assignedActivitiesCount === 'undefined' || assignedActivitiesCount === null)
	) {

		console.log(FETCH_ERROR_MESSAGE);
		updateState({
			error: true,
			isLoading: false,
		});
	}

	updateState({
		totalActivitiesCount,
		assignedActivitiesCount,
		unAssignedActivitiesCount,
		isLoading: false,
	});
};

const getPlanActivitiesCountFailed = ({ action, updateState }) => {
	const { data } = action.payload;
	const { error } = data;
	const { message } = error;

	console.error(message);
	console.error(FETCH_ERROR_MESSAGE);
	updateState({
		error: true,
		isLoading: false,
	});
};

const actionHandlers = {
	[FETCH_RECOVERY_TASKS_COUNT_DETAILS_REQUESTED]: requestPlanActivitiesCount,
	[FETCH_RECOVERY_TASKS_COUNT_DETAILS_SUCCEEDED]: {
		effect: getPlanActivitiesCountSuccess,
		stopPropogation: true,
	},

	[FETCH_RECOVERY_TASKS_COUNT_DETAILS_FAILED]: {
		effect: getPlanActivitiesCountFailed,
		stopPropogation: true,
	},
};

export default actionHandlers;
