import REMOVE_SELECTION_ACTIONS from '../constants/RemoveSelectionActionsTypes';
import RECORD_MUTATION_ACTIONS from '../constants/RecordMutationActionTypes';
import { REMOVE_BUTTON_PAYLOAD_KEY } from '../constants/DefaultPropertyValues';

const actions = {
	[REMOVE_SELECTION_ACTIONS.SELECTION_REMOVED]: {
		schema: {
			type: 'object',
			properties: {
				buttonAction: {type: 'string'},
				valueToRemove: {type: 'string'}
			},
			required: ['buttonAction', 'valueToRemove']
		},
		private: true
	}
};

const actionHandlers = {
	[REMOVE_SELECTION_ACTIONS.SELECTION_REMOVED]: ({action, state, dispatch}) => {
		const { buttonAction, valueToRemove } = action.payload; // Recall to check the append-to-payload to ensure th ebutton clicked is specifically for removing
		if (buttonAction !== REMOVE_BUTTON_PAYLOAD_KEY ) {
			return;
		}
		const { currentlySelectedGlideList, properties } = state;
		const { table, sysId, field } = properties;

		const updatedSelectionGlideList = currentlySelectedGlideList.
			replace(`${valueToRemove},`, '').
			replace(`,${valueToRemove}`, '').
			replace(`${valueToRemove}`, '');

		dispatch(RECORD_MUTATION_ACTIONS.RECORD_MUTATION_REQUESTED, {
			table,
			sysId,
			field,
			glideListValue: updatedSelectionGlideList
		});
	},
};

export {
	actions,
	actionHandlers
};