import { actionTypes } from '@servicenow/ui-core';
import { FETCH_RECOVERY_TASKS_COUNT_DETAILS_REQUESTED } from '../constants/PlanActivitiesScoreCardActionTypes';
import { RECORD_WATCHER_SUBSCRIPTION_REQUESTED } from '../../../behaviors/RecordWatcherBehavior';
import {
	RECOVERY_TASK_TABLE,
	DEFAULT_OWNER_COLUMN,
	DEFAULT_ASSIGNMENT_GROUP_COLUMN,
} from '../constants/PlanActivitiesScoreCardConstants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const fetchPlanActivitiesData = ({ properties, dispatch }) => {
	const { planSysId, isRecordWatcherEnabled } = properties;

	dispatch(FETCH_RECOVERY_TASKS_COUNT_DETAILS_REQUESTED, {
		targetTableIdentifier: RECOVERY_TASK_TABLE,
		sysId: planSysId,
		queryColumns: `${DEFAULT_OWNER_COLUMN}, ${DEFAULT_ASSIGNMENT_GROUP_COLUMN}`,
	});

	if (isRecordWatcherEnabled) {
		dispatch(RECORD_WATCHER_SUBSCRIPTION_REQUESTED, {
			table: RECOVERY_TASK_TABLE,
			filter: `plan=${planSysId}`,
			onItemChangeAction: {
				actionType: FETCH_RECOVERY_TASKS_COUNT_DETAILS_REQUESTED,
				actionPayload: {
					targetTableIdentifier: RECOVERY_TASK_TABLE,
					sysId: planSysId,
					queryColumns: `${DEFAULT_OWNER_COLUMN}, ${DEFAULT_ASSIGNMENT_GROUP_COLUMN}`,
				},
			},
		});
	}
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect: fetchPlanActivitiesData,
	},
};

export default actionHandlers;
