import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './AssetDependenciesScoreCardView';
import styles from './AssetDependenciesScoreCardElement.scss';
import lifecycleActionHandlers from './actions/AssetDependenciesScoreCardLifecycleActions';
import relatedAssetsQueryActionHandlers from './actions/AssetDependenciesScoreCardQueryActions';
import {
	actions as recordWatcherActions,
	actionHandlers as recordWatcherActionHandlers,
} from './actions/RecordWatcherActions';
import {
	RELATED_ASSET_DEPENDENCY_TABLE,
	RECOVERY_STRATEGIES_TABLE,
	LOADING_VIEW_MESSAGE,
	ASSETS_WITH_STRATEGY_COUNT_LABEL,
	ASSETS_WITHOUT_STRATEGY_COUNT_LABEL,
	TOTAL_ASSETS_COUNT_LABEL,
	CARD_HEADER_LABEL,
} from './constants/AssetDependenciesScoreCardConstants';

const TAG_NAME = 'sn-bcm-asset-dependencies-combined-score';

/**
 * A card overview widget, intended for use on workpsace ribbons.
 * Displays a combined-score showing assets with/without recovery strategies.
 *
 * ```
 * <sn-bcm-asset-dependencies-combined-score
 * 		sysId="primaryTableRecordSysIdString"
 *  	sysid="primaryTableRecordSysIdString"
 * 		table="sn_bcp_plan_loss_scenario"
 * 		relatedAssetDependenciesTable="sn_bcp_plan_asset_dependency"
 * 		recoveryStrategiesTable="sn_bcp_recovery_strategy"
 * ></sn-bcm-asset-dependencies-combined-score>
 *
 * ```
 *
 * @seismicElement sn-bcm-asset-dependencies-combined-score
 * @summary Displays a combined-score showing assets with/without recovery strategies.
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,

	properties: {
		/**
		 * The name of the table rendeing this ribbon widget.
		 * @type {String}
		 */
		table: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The sys_id value of the record displaying this widget in the ribbon (default: sn_bcp_plan_loss_scenario record sys_id)
		 * @type {String}
		 */
		sysid: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The sys_id value of the record displaying this widget in the ribbon (default: sn_bcp_plan_loss_scenario record sys_id)
		 * @type {String}
		 */
		sysId: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The name of the 'Asset Dependencies' table. (default: sn_bcp_plan_asset_dependency)
		 * @type {String}
		 */
		relatedAssetDependenciesTable: {
			default: RELATED_ASSET_DEPENDENCY_TABLE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The name of the 'Recovery Strategies' table. (default: sn_bcp_recovery_strategy)
		 * @type {String}
		 */
		recoveryStrategiesTable: {
			default: RECOVERY_STRATEGIES_TABLE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The label that appears over the count for assets with strategies.
		 * @type {String}
		 */
		assetsWithStrategyCountLabel: {
			default: ASSETS_WITH_STRATEGY_COUNT_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The label that appears over the count for assets without strategies.
		 * @type {String}
		 */
		assetsWithoutStrategyCountLabel: {
			default: ASSETS_WITHOUT_STRATEGY_COUNT_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The label that appears over the count for total asset dependencies.
		 * @type {String}
		 */
		totalAssetsCountLabel: {
			default: TOTAL_ASSETS_COUNT_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The label that appears on the card header.
		 * @type {String}
		 */
		cardHeaderLabel: {
			default: CARD_HEADER_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The message to display (alongside the spinner) while data is being fetched.
		 * @type {String}
		 */
		loadingViewMessage: {
			default: LOADING_VIEW_MESSAGE,
			required: false,
			schema: {
				type: 'string',
			},
		},
	},
	initialState: {
		isLoading: true,
		totalRelatedAssetsCount: null,
		assetsWithStrategyCount: null,
		assetsWithoutStrategyCount: null,
	},
	actions: {
		...recordWatcherActions,
	},
	actionHandlers: {
		...lifecycleActionHandlers,
		...relatedAssetsQueryActionHandlers,
		...recordWatcherActionHandlers,
	},
	styles,
});

export default TAG_NAME;
