import '@servicenow/now-loader';
import CombinedScoreCard from '../../CombinedScoreCard/CombinedScoreCardElement';
import { CARD_INTERACTION } from './constants/AssetDependenciesScoreCardConstants';

const view = (state) => {
	const {
		isLoading,
		totalRelatedAssetsCount,
		assetsWithStrategyCount,
		assetsWithoutStrategyCount,
		properties,
	} = state;

	const {
		assetsWithStrategyCountLabel,
		assetsWithoutStrategyCountLabel,
		totalAssetsCountLabel,
		cardHeaderLabel,
		loadingViewMessage,
	} = properties;

	const combinedScoreCardProperties = {
		cardHeaderLabel,
		primaryScore: {
			label: totalAssetsCountLabel,
			score: `${totalRelatedAssetsCount}`,
		},
		secondaryScores: [
			{
				heading: assetsWithStrategyCountLabel,
				score: `${assetsWithStrategyCount}`,
			},
			{
				heading: assetsWithoutStrategyCountLabel,
				score: `${assetsWithoutStrategyCount}`,
			},
		],
		cardInteraction: CARD_INTERACTION,
	};

	const loadingView = (
		<div className="loading-view">
			<now-loader label={loadingViewMessage} />
		</div>
	);

	if (isLoading) {
		return loadingView;
	}

	return <CombinedScoreCard fillHeight {...combinedScoreCardProperties} />;
};

export default view;
