import { getPrefixedEditActionHandlers } from '@devsnc/sn-datagrid';
import { set, isEmpty } from 'lodash';
import {
	EDITABLE_LIST_QUERY_ACTION_TYPES,
	EDITABLE_LIST_UPDATE_ACTION_TYPES,
} from '../constants/EditableListCRUDActionTypes';
import {
	QUERY_COLUMN_VALUES,
	GRID_ACTION_PREFIX,
} from '../constants/EditableListConstants';
import EDITABLE_LIST_GRID_ACTION_TYPES from '../constants/EditableListGridActionTypes';
import { USER_PREFERENCE_PREFIX } from '../../../util/grid-util/constants/GeneralConstants';
import { GRID_ACTIONS } from '../../../util/grid-util/constants/GridActionTypes';

const editActionHandlers = getPrefixedEditActionHandlers(GRID_ACTION_PREFIX);

const handleTaskUpdateRequest = ({ action, state, dispatch, updateState }) => {
	const { payload: editedColValues } = action; // this gives a name : value pair of all the edited columns
	const {
		rowEditing,
		properties: { tableName },
	} = state;
	const { taskId: _recordSysId } = rowEditing;

	const _editedColumnNames = Object.keys(editedColValues);
	const _columnsToUpdate = _editedColumnNames
		.map((_colName) => `${_colName} { ${QUERY_COLUMN_VALUES} }`)
		.join('\n');

	const _valuesToUpdate = _editedColumnNames.reduce(
		(_updateValueStr, _colName) => {
			return `${_updateValueStr} ${_colName}: "${editedColValues[_colName]}"`;
		},
		''
	);

	updateState({
		dataUpdating: true,
	});

	dispatch(EDITABLE_LIST_UPDATE_ACTION_TYPES.EDITABLE_LIST_UPDATE_REQUESTED, {
		tableName,
		sysId: _recordSysId,
		columns: _columnsToUpdate,
		values: _valuesToUpdate,
	});
};

const handleRecordSelectRequest = ({ state, updateState, dispatch }) => {
	const { listRecords, recordSelectedWithParentContext, properties } = state;
	const { tableName, parentRecordIdentification } = properties;

	const _selectedIds = listRecords.map((item) => {
		return item.id;
	});

	if (parentRecordIdentification) {
		recordSelectedWithParentContext[parentRecordIdentification] = _selectedIds;
		updateState({ recordSelectedWithParentContext });
		dispatch(
			EDITABLE_LIST_GRID_ACTION_TYPES.EDITABLE_LIST_RECORD_SELECTED_COUNT_UPDATED,
			{
				selectionCount: _selectedIds.length,
				tableName,
				recordSelectedWithParentContext,
				parentRecordIdentification,
			}
		);
	} else {
		updateState({ recordSelected: _selectedIds });

		dispatch(
			EDITABLE_LIST_GRID_ACTION_TYPES.EDITABLE_LIST_RECORD_SELECTED_COUNT_UPDATED,
			{
				selectionCount: _selectedIds.length,
				tableName,
				recordSelected: _selectedIds,
				parentRecordIdentification,
			}
		);
	}
};

const handleRecordRowSelectRequest = ({
	action,
	state,
	updateState,
	dispatch,
}) => {
	const { payload } = action;
	const { taskId: _recordId, value } = payload;
	const { recordSelected, recordSelectedWithParentContext, properties } = state;
	const { tableName, parentRecordIdentification } = properties;

	// TODO: Use Switch case
	if (parentRecordIdentification) {
		if (value) {
			if (recordSelectedWithParentContext[parentRecordIdentification]) {
				recordSelectedWithParentContext[parentRecordIdentification].push(
					_recordId
				);
			} else {
				const selectedRecords = [];

				selectedRecords.push(_recordId);
				recordSelectedWithParentContext[
					parentRecordIdentification
				] = selectedRecords;
			}
		} else {
			const _index = recordSelectedWithParentContext[
				parentRecordIdentification
			].findIndex((record) => record === _recordId);

			recordSelectedWithParentContext[parentRecordIdentification].splice(
				_index,
				1
			);
		}

		updateState({
			recordSelectedWithParentContext,
		});
		dispatch(
			EDITABLE_LIST_GRID_ACTION_TYPES.EDITABLE_LIST_RECORD_SELECTED_COUNT_UPDATED,
			{
				selectionCount: recordSelected.length,
				tableName,
				recordSelectedWithParentContext,
				parentRecordIdentification,
			}
		);
	} else {
		// state is not reactive so reintialized the state variable.
		let _selectedRecords=[];
		if (value) {
			_selectedRecords = [...recordSelected, _recordId];
		} else {
			_selectedRecords = recordSelected.filter(
				(record) => record !== _recordId
			);
		}

		updateState({
			recordSelected:_selectedRecords
		});
		dispatch(
			EDITABLE_LIST_GRID_ACTION_TYPES.EDITABLE_LIST_RECORD_SELECTED_COUNT_UPDATED,
			{
				selectionCount: recordSelected.length,
				tableName,
				recordSelected,
				parentRecordIdentification,
			}
		);
	}
};

const handleRecordUnSelectRequest = ({ state, updateState, dispatch }) => {
	const { recordSelectedWithParentContext, properties } = state;
	const { tableName, parentRecordIdentification } = properties;

	if (parentRecordIdentification) {
		recordSelectedWithParentContext[parentRecordIdentification] = [];
		updateState({
			recordSelectedWithParentContext,
		});
		dispatch(
			EDITABLE_LIST_GRID_ACTION_TYPES.EDITABLE_LIST_RECORD_SELECTED_COUNT_UPDATED,
			{
				selectionCount: 0,
				tableName,
				recordSelectedWithParentContext,
				parentRecordIdentification,
			}
		);
	} else {
		updateState({ recordSelected: [] });

		dispatch(
			EDITABLE_LIST_GRID_ACTION_TYPES.EDITABLE_LIST_RECORD_SELECTED_COUNT_UPDATED,
			{
				selectionCount: 0,
				tableName,
				recordSelected: [],
			}
		);
	}
};

const handleGridRowEdited = ({ dispatch, state }) => {
	const {
		rowEditing: { editValues },
	} = state;

	if (isEmpty(editValues)) {
		dispatch(EDITABLE_LIST_GRID_ACTION_TYPES.GRID_ROW_EDIT_ENDED);
	} else {
		dispatch(EDITABLE_LIST_GRID_ACTION_TYPES.GRID_TASK_DATA_UPDATED, {
			...editValues,
		});
	}
	dispatch(EDITABLE_LIST_GRID_ACTION_TYPES.POST_ACTION_GRID_EDIT_ENDED);
};

const handleGridRowEditStarted = ({ action, state, dispatch }) => {
	const { payload } = action;
	const { tableName } = state.properties;
	dispatch(EDITABLE_LIST_GRID_ACTION_TYPES.GRID_ROW_EDIT_STARTED, {
		...payload,
		table: tableName,
	});
};

const handleGridRowEditEnded = ({ dispatch, updateState }) => {
	dispatch(EDITABLE_LIST_GRID_ACTION_TYPES.POST_ACTION_GRID_EDIT_ENDED);
	updateState({
		rowEditing: null,
		dataUpdating: false,
	});
};


const actionHandlers = {
	...editActionHandlers,
	[EDITABLE_LIST_GRID_ACTION_TYPES.GRID_TASK_DATA_UPDATED]: {
		effect: handleTaskUpdateRequest,
		stopPropogation: true,
	},
	[EDITABLE_LIST_GRID_ACTION_TYPES.GRID_ALL_ROW_SELECTED]: {
		effect: handleRecordSelectRequest,
		stopPropogation: true,
	},
	[EDITABLE_LIST_GRID_ACTION_TYPES.GRID_ALL_ROW_UNSELECTED]: {
		effect: handleRecordUnSelectRequest,
		stopPropogation: true,
	},
	[EDITABLE_LIST_GRID_ACTION_TYPES.GRID_ROW_SELECTED]: {
		effect: handleRecordRowSelectRequest,
		stopPropogation: true,
	},
	[EDITABLE_LIST_GRID_ACTION_TYPES.REFRESH_GRID]: ({ state, dispatch }) => {
		const {
			tableName,
			listViewName,
			filterQueryConditions,
			recordLoadLimit,
		} = state.properties;

		const userPreferenceName = `${USER_PREFERENCE_PREFIX}.${tableName}.${listViewName}`;

		dispatch(GRID_ACTIONS.REFRESH_GRID, {
			actionName:
				EDITABLE_LIST_QUERY_ACTION_TYPES.EDITABLE_LIST_QUERY_REQUESTED,
			actionPayload: {
				tableName,
				listViewName,
				filterQueryConditions,
				recordLoadLimit,
				userPreferenceName,
			},
		});
	},
	[EDITABLE_LIST_GRID_ACTION_TYPES.SN_DATAGRID_ROW_EDIT_STARTED]: handleGridRowEditStarted,
	[EDITABLE_LIST_GRID_ACTION_TYPES.GRID_ROW_EDITED]: {
		effect: handleGridRowEdited,
		stopPropogation: true,
	},
	[EDITABLE_LIST_GRID_ACTION_TYPES.GRID_ROW_EDIT_ENDED]: {
		effect: handleGridRowEditEnded,
		stopPropogation: true,
	},
};

export default actionHandlers;
