import '@servicenow/now-loader';
import CombinedScoreCard from '../CombinedScoreCard/CombinedScoreCardElement';
import { SECONDARY_SCORES_ORIENTATION } from '../CombinedScoreCard/CombinedScoreCardConstants';
import {
	ACTION_BUTTON_ARIA_LABEL,
	ACTION_BUTTON_TOOLTIP,
	DATA_PLACEHOLDER,
	PRIMARY_SCORE_FONT_SIZE,
	ACTION_BUTTON_ICON,
	ACTION_BUTTON_ACTION_NAME,
	CARD_INTERACTION,
	LOADING_MESSAGE,
} from './constants/ImpactAssessmentScoreCardConstants';

const view = (state) => {
	const {
		isLoading,
		properties,
		recoveryTierChoice,
		recoveryTimeObjectiveLength,
	} = state;

	const {
		recoveryTimeObjectiveLabel,
		recoveryTierLabel,
		cardHeaderLabel,
		isActionButtonDisplayed,
		relatedItemTabIndex,
	} = properties;

	const combinedScoreCardProperties = {
		cardHeaderLabel,
		primaryScore: {
			label: recoveryTierLabel,
			score:
				`${recoveryTierChoice}` === 'null'
					? DATA_PLACEHOLDER
					: `${recoveryTierChoice}`,
		},
		secondaryScores: [
			{
				heading: recoveryTimeObjectiveLabel,
				score:
					`${recoveryTimeObjectiveLength}` === 'null'
						? DATA_PLACEHOLDER
						: `${recoveryTimeObjectiveLength}`,
			},
		],
		primaryScoreFontSize: PRIMARY_SCORE_FONT_SIZE,
		secondaryScoreOrientation: SECONDARY_SCORES_ORIENTATION.VERTICAL,
		isActionButtonDisplayed,
		actionButtonIcon: ACTION_BUTTON_ICON,
		actionButtonActionName: ACTION_BUTTON_ACTION_NAME,
		actionButtonActionPayload: {
			params: {
				selectedTabIndex: relatedItemTabIndex,
			},
			redirectEmptyState: true,
		},
		actionButtonTooltip: ACTION_BUTTON_TOOLTIP,
		actionButtonAriaLabel: ACTION_BUTTON_ARIA_LABEL,
		cardInteraction: CARD_INTERACTION,
	};

	return (
		<CombinedScoreCard
			fillHeight
			{...combinedScoreCardProperties}
			isLoading={isLoading}
			loadingMessage={LOADING_MESSAGE}
		/>
	);
};

export default view;
