import { actionTypes } from '@servicenow/ui-core';
import PLAN_EVENT_ACTIONS from '../constants/ActionTypes';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const fetchPlanEventsResult = ({ properties, dispatch }) => {
	const { planSysId, eventType } = properties;

	dispatch(PLAN_EVENT_ACTIONS.EVENTS_RESULT_DETAILS_REQUESTED, {
		planSysId,
		eventType,
	});
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect: fetchPlanEventsResult,
	},
};

export default actionHandlers;
