import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import SELECTION_TABLE_QUERY_ACTIONS from '../constants/SelectionTableQueryActionTypes';
import query from '../querries/SelectionTableQuery';

const requestSysUserData = createGraphQLEffect(query, {
	variableList: ['queryConditions', 'maxTypeaheadOptions'],
	templateVarList: ['selectionTable'],
	successActionType: SELECTION_TABLE_QUERY_ACTIONS.SELECTION_TABLE_QUERY_SUCCEEDED,
	errorActionType: SELECTION_TABLE_QUERY_ACTIONS.SELECTION_TABLE_QUERY_FAILED
});

const getGraphqlEffectSuccess = ({action, state, updateState}) => {
	const { properties } = state;
	const { selectionTable } = properties;
	let typeaheadOptions = get(action, `payload.data.GlideRecord_Query.${selectionTable}.results`);

	typeaheadOptions = typeaheadOptions.map(option => ({
		value: get(option, 'sys_id.value'),
		displayValue: get(option, 'name.value'),
		avatar: get(option, 'nestedQuery.GlideDomain_Query.userInfo.avatarPath')
	}));

	updateState({
		typeaheadOptions
	});
};

const getGraphqlEffectFailure = ({action, updateState}) => {
	const error = action.payload;
	console.error('An error occured attenmpting to fetch typeahead options');
	console.error(error);
	updateState({
		error: true
	});
};

const actions = {
	[SELECTION_TABLE_QUERY_ACTIONS.SELECTION_TABLE_QUERY_REQUESTED]: {
		schema: {
			type: 'object',
			properties: {
				queryConditions: {type: 'string'},
				maxTypeaheadOptions: {type: 'number'},
				selectionTable: {type: 'string'}
			},
			required: ['queryConditions', 'maxTypeaheadOptions', 'selectionTable']
		},
		private: true
	}
};

const actionHandlers = {
	[SELECTION_TABLE_QUERY_ACTIONS.SELECTION_TABLE_QUERY_REQUESTED]: requestSysUserData,
	[SELECTION_TABLE_QUERY_ACTIONS.SELECTION_TABLE_QUERY_SUCCEEDED]: {
		effect: getGraphqlEffectSuccess,
		stopPropagation: true
	},
	[SELECTION_TABLE_QUERY_ACTIONS.SELECTION_TABLE_QUERY_FAILED]: {
		effect: getGraphqlEffectFailure,
		stopPropagation: true
	}
};

export {
	actions,
	actionHandlers
};