import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import RICH_TEXT_CONTENT_CARD from '../constants/RichTextCardInteractionActionTypes';
import PLAN_STATUS_MUTATION from '../constants/DocumentationStatusMutationActionTypes';
import { CARD_EDITABLE_FIELDS } from '../../RichTextContentCard/constants/GeneralConstants';
import planStatusMutation from '../queries/planStatusMutation';

const requestPlanStatusMutation = createGraphQLEffect(planStatusMutation, {
	templateVarList: ['tableName', 'field'],
	variableList: ['sysId', 'statusFieldVal'],
	startActionType: PLAN_STATUS_MUTATION.STARTED,
	successActionType: PLAN_STATUS_MUTATION.SUCCEEDED,
	errorActionType: PLAN_STATUS_MUTATION.FAILED,
});

const updatePlanStatus = ({ state, action, updateState, properties }) => {
	const { tableRecords } = state;
	const { tableName } = properties;
	const path = `data.GlideRecord_Mutation.update_${tableName}`;
	const recordSysId = get(action.payload, `${path}.sys_id.value`);
	const status = get(
		action.payload,
		`${path}.${CARD_EDITABLE_FIELDS.STATUS_FIELD}.value`
	);

	const updatedRecords = tableRecords.map((record) => {
		if (record.sys_id === recordSysId) {
			return {
				...record,
				[CARD_EDITABLE_FIELDS.STATUS_FIELD]: status,
			};
		}

		return record;
	});

	updateState({
		tableRecords: updatedRecords,
	});
};

const handleOnFailure = ({ action }) => {
	const error = action.payload;

	console.error(error);
};

const actionHandlers = {
	[RICH_TEXT_CONTENT_CARD.EDITED]: requestPlanStatusMutation,
	[PLAN_STATUS_MUTATION.SUCCEEDED]: {
		effect: updatePlanStatus,
		stopPropagation: true,
	},
	[PLAN_STATUS_MUTATION.FAILED]: {
		effect: handleOnFailure,
		stopPropagation: true,
	},
};

export default actionHandlers;
