import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './PlanRecoveryTeamsScoreCardView';
import styles from './PlanRecoveryTeamsScoreCardElement.scss';
import lifecycleActionHandlers from './actions/PlanRecoveryTeamsScoreCardLifeCycleActions';
import recoveryTeamsQueryActionHandlers from './actions/PlanRecoveryTeamsScoreCardDataFetchActions';
import {
	LOADING_VIEW_MESSAGE,
	ASSIGNED_RECOVERY_TEAMS_COUNT_LABEL,
	UNASSIGNED_RECOVERY_TEAMS_COUNT_LABEL,
	TOTAL_RECOVERY_TEAMS_COUNT_LABEL,
	CARD_HEADER_LABEL,
} from './constants/PlanRecoveryTeamsScoreCardConstants';
import { SECONDARY_SCORES_ORIENTATION } from '../CombinedScoreCard/CombinedScoreCardConstants';
import { recordWatcherBehavior } from '../../behaviors/RecordWatcherBehavior';

const TAG_NAME = 'sn-bcm-plan-recovery-teams-score';

/**
 * This component is to be used in the Plan Overview component. Will display Recovery Teams' status for a plan
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-plan-recovery-teams-score
 * 		sysid=""
 * 		assigned-recovery-teams-count-label=""
 * 		un-assigned-recovery-teams-count-label=""
 * 		total-recovery-teams-count-label=""
 * 		card-header-label=""
 * 		related-item-tab-index=""

 * ></sn-bcm-plan-recovery-teams-score>
 * ```
 *
 * @seismicElement sn-bcm-plan-recovery-teams-score
 * @summary This component is to be used in the Plan Overview component. Will display Recovery Teams' status for a plan
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,

	properties: {
		/**
		 * SysId of the record the overview will be displayed on
		 *
		 * @type {string}
		 */
		sysid: {
			default: null,
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the assigned recovery teams count
		 *
		 * @type {string}
		 */
		assignedRecoveryTeamsCountLabel: {
			default: ASSIGNED_RECOVERY_TEAMS_COUNT_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the unassigned recovery teams count
		 *
		 * @type {string}
		 */
		unAssignedRecoveryTeamsCountLabel: {
			default: UNASSIGNED_RECOVERY_TEAMS_COUNT_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the total recovery teams count
		 *
		 * @type {string}
		 */
		totalRecoveryTeamsCountLabel: {
			default: TOTAL_RECOVERY_TEAMS_COUNT_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the card header of score card
		 *
		 * @type {string}
		 */
		cardHeaderLabel: {
			default: CARD_HEADER_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Boolean to enable the icon button
		 *
		 * @type {boolean}
		 */
		isActionButtonDisplayed: {
			required: true,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Orientation of secondary scores
		 *
		 * @type {string}
		 */
		secondaryScoreOrientation: {
			default: SECONDARY_SCORES_ORIENTATION.VERTICAL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Related Item Tab Index number for icon button
		 *
		 * @type {number}
		 */
		relatedItemTabIndex: {
			default: 0,
			required: false,
			schema: {
				type: 'number',
			},
		},
		/**
		 * If enabled, a record watcher will keep this component updated in real-time.
		 *
		 * @type {boolean}
		 */
		isRecordWatcherEnabled: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
	},
	initialState: {
		/**
		 * State of the component whether it is loaded or not
		 *
		 * @type {boolean}
		 */
		isLoading: true,
		/**
		 * Total count of the recovery teams
		 *
		 * @type {number}
		 */
		totalRecoveryTeamsCount: null,
		/**
		 * Count of the assigned recovery teams
		 *
		 * @type {number}
		 */
		assignedRecoveryTeamsCount: null,
		/**
		 * Count of the unassigned recovery teams
		 *
		 * @type {number}
		 */
		unAssignedRecoveryTeamsCount: null,
		/**
		 * Configuration for the record-watcher subscription.
		 *
		 * @type {object}
		 */
		recordWatcherConfigurations: null,
	},
	behaviors: [recordWatcherBehavior],
	actionHandlers: {
		...lifecycleActionHandlers,
		...recoveryTeamsQueryActionHandlers,
	},
	styles,
});

export default TAG_NAME;
