import { actionTypes } from '@servicenow/ui-core';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import tableMetadataQuery from '../queries/TableMetaDataQuery';
import {
	FETCH_TABLE_METADATA_REQUESTED,
	FETCH_TABLE_METADATA_SUCCEEDED,
	FETCH_TABLE_METADATA_FAILED,
} from '../constants/RelatedItemRecordListActionTypes';
import { 
	TABLE_METADATA_FETCH_ERROR_MESSAGE	
 } from '../constants/RelatedItemRecordListConstants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const fetchTableMetaData = createGraphQLEffect(tableMetadataQuery, {
	templateVarList: ['tableName'],
	successActionType: FETCH_TABLE_METADATA_SUCCEEDED,
	errorActionType: FETCH_TABLE_METADATA_FAILED,
});

const getTableMetaDataQuerySuccess = ({
	action,
	updateState,
	properties,
}) => {
	const fetchResult = action.payload;
	const fetchErrors = fetchResult.errors;

	if (fetchErrors.length) {
		console.error(TABLE_METADATA_FETCH_ERROR_MESSAGE);
		console.error(fetchErrors);

		return;
	}

	const { relatedTable } = properties;
	const tableLabel = get(
		action.payload,
		`data.GlideRecord_Query.${relatedTable}._table_metadata.label`
	);

	updateState({
		headerLabel: tableLabel,
	});
};

const getTableMetaDataQueryFailure = ({ action }) => {
	const error = action.payload;

	console.error(TABLE_METADATA_FETCH_ERROR_MESSAGE);
	console.error(error);
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ dispatch, state }) => {
		const { properties } = state;
		const { relatedTable } = properties;

		dispatch(FETCH_TABLE_METADATA_REQUESTED, {
			tableName: relatedTable,
		});
	},
	[FETCH_TABLE_METADATA_REQUESTED]: fetchTableMetaData,
	[FETCH_TABLE_METADATA_SUCCEEDED]: getTableMetaDataQuerySuccess,
	[FETCH_TABLE_METADATA_FAILED]: getTableMetaDataQueryFailure,
};

export default actionHandlers;
