import { get, capitalize } from 'lodash';
import '@servicenow/now-accordion';
import '../../EditableList/EditableListElement';
import '../../ItemPickerModal/ItemPickerModalElement';
import {
	ACCORDION_STATUS,
	NOW_ALERT_CONTENT,
	CARD_STATUS_COLOR,
	BUTTON_SIZE,
	ADD_ASSETS_BUTTON_TOOLTIP,
	REMOVE_ASSETS_BUTTON_TOOLTIP,
	BUTTON_VARIANT,
	PLUS_OUTLINE_ICONIC_BUTTON,
	TRASH_OUTLINE_ICONIC_BUTTON,
	ERROR_ALERT_STATUS,
	ERROR_ALERT_ICON,
	ADD_BUTTON_LABEL,
	DELETE_BUTTON_LABEL,
	DISMISS_ACTION_TYPE,
} from '../constants/RelatedItemAccordionListConstants';
import { ACCORDION_LIST_STATUS_UPDATE_QUERY_REQUESTED } from '../constants/RelatedItemAccordionListActionTypes';
import {
	ADD_ASSETS_BUTTON_CLICKED,
	REMOVE_ASSETS_BUTTON_CLICKED,
} from '../../BiaDependencyAssessment/constants/CardHeaderButtonsActionTypes';

const updateMandatoryPropertyFlagRecord = (
	sys_id,
	accordionListItems,
	updateState,
	flag
) => {
	const records = accordionListItems.map((item) => {
		if (item.sys_id.value === sys_id) {
			item.isMandatoryFieldEmpty = flag;
		}

		return item;
	});

	updateState({
		accordionListItems: records,
	});
};

const _checkPropertyValue = (record, state, dispatch, updateState, status) => {
	const { accordionListItems, properties } = state;
	const {
		relatedTableName,
		relatedTableMandatoryFields,
		relatedTableStatusFieldName,
		relatedChildTable,
	} = properties;
	const sysid = get(record, `sys_id.value`);
	const propRecordValues = get(record, `${relatedChildTable}`);
	const invalidFalsyValues = [null, undefined, '', 'null', 'undefined'];

	if (relatedTableMandatoryFields) {
		for (let i = 0; i < relatedTableMandatoryFields.length; i++) {
			for (let j = 0; j < propRecordValues.length; j++) {
				if (
					record[relatedTableStatusFieldName] !== ACCORDION_STATUS.COMPLETE &&
					propRecordValues[j][relatedTableMandatoryFields[i]] &&
					invalidFalsyValues.includes(
						propRecordValues[j][relatedTableMandatoryFields[i]].value
					)
				) {
					updateMandatoryPropertyFlagRecord(
						record.sys_id.value,
						accordionListItems,
						updateState,
						true
					);

					return;
				}
			}
		}
	}

	updateMandatoryPropertyFlagRecord(
		record.sys_id.value,
		accordionListItems,
		updateState,
		false
	);

	dispatch(ACCORDION_LIST_STATUS_UPDATE_QUERY_REQUESTED, {
		sysid,
		relatedTableStatusFieldName,
		statusValue: status,
		relatedTableName,
	});

	for (let i = 0; i < accordionListItems.length; i++) {
		if (accordionListItems[i].sys_id.value === sysid) {
			if (i + 1 < accordionListItems.length)
				accordionListItems[i + 1].isExpanded = true;
			if (status === ACCORDION_STATUS.COMPLETE) {
				accordionListItems[i].isExpanded = false;
				accordionListItems[i].readOnly = true;
			} else {
				accordionListItems[i].readOnly = false;
			}
		}
	}

	updateState({
		accordionListItems,
	});
};

const _getStatus = (status) => {
	if (status) {
		let statusColor;

		if (status === ACCORDION_STATUS.COMPLETE) {
			statusColor = CARD_STATUS_COLOR.POSITIVE;
		} else {
			statusColor = CARD_STATUS_COLOR.WARNING;
		}

		return (
			<now-highlighted-value
				className="now-highlighted-val"
				slot="metadata"
				show-icon
				label={capitalize(status)}
				status={statusColor}
			/>
		);
	}
};

const _getCardFooter = (record, state, dispatch, updateState) => {
	const { properties } = state;
	const { relatedTableStatusFieldName } = properties;
	const status = get(record, `${relatedTableStatusFieldName}`);

	const completeButtonView = (
		<now-button
			variant="primary"
			size="md"
			className="accordion-card-status-button"
			label={capitalize(ACCORDION_STATUS.COMPLETE)}
			disabled={record.disable}
			on-click={() => {
				if (!record.disable) {
					_checkPropertyValue(
						record,
						state,
						dispatch,
						updateState,
						ACCORDION_STATUS.COMPLETE
					);
				}
			}}
		/>
	);
	const editButtonView = (
		<now-button
			variant="primary"
			size="md"
			className="accordion-card-status-button"
			label="Edit"
			on-click={() =>
				_checkPropertyValue(
					record,
					state,
					dispatch,
					updateState,
					ACCORDION_STATUS.PENDING
				)}
		/>
	);

	return (
		<div>
			<now-card-footer className="accordion-card-footer">
				{status === ACCORDION_STATUS.COMPLETE
					? editButtonView
					: completeButtonView}
			</now-card-footer>
		</div>
	);
};

const getCardList = (state, { dispatch, updateState }) => {
	const { properties } = state;
	const {
		accordionRecords,
		relatedTableStatusFieldName,
		accordionListHeaderField,
		accordionItemCardDescriptionField,
		isActionButtonRequired,
		relatedChildTable,
		relatedTableReferenceField,
		relatedTableMandatoryFieldLabels,
		isCardHeaderRequired,
		helperText,
	} = properties;

	const listElements = accordionRecords.map((record) => {
		const statusValue = get(record, `${relatedTableStatusFieldName}`);
		const recordSysId = get(record, 'sys_id.value');
		const currentlySelectedEncodedQuery = get(
			record,
			'currentlySelectedEncodedQuery'
		);
		const gridConfig = get(record, 'gridConfig');
		const accordionHeaderLabel = get(record, `${accordionListHeaderField}`);
		const cardDescription = get(record, `${accordionItemCardDescriptionField}`);
		const { isExpanded, isMandatoryFieldEmpty } = record;
		const isPending = statusValue === ACCORDION_STATUS.PENDING;
		const queryCondition = get(record, 'queryCondition');

		const isMandatoryFieldEmptyAlert = (
			<now-alert
				className={isMandatoryFieldEmpty ? 'now-alert-error-element' : 'hidden'}
				status={ERROR_ALERT_STATUS}
				icon={ERROR_ALERT_ICON}
				content={`${NOW_ALERT_CONTENT} ${relatedTableMandatoryFieldLabels}`}
				action={{ type: DISMISS_ACTION_TYPE, sys_id: record.sys_id }}
			/>
		);

		const addButtonElement = (
			<now-button
				className="add-asset-button"
				slot="metadata"
				label={ADD_BUTTON_LABEL}
				icon={PLUS_OUTLINE_ICONIC_BUTTON}
				size={BUTTON_SIZE}
				variant={BUTTON_VARIANT}
				tooltipContent={ADD_ASSETS_BUTTON_TOOLTIP}
				bare={false}
				on-click={() => {
					dispatch(ADD_ASSETS_BUTTON_CLICKED, {
						recordSysId,
					});
				}}
				configAria={{ "aria-label": ADD_ASSETS_BUTTON_TOOLTIP }}
			/>
		);

		const removeButtonElement = (
			<now-button
				className="remove-asset-button"
				slot="metadata"
				label={DELETE_BUTTON_LABEL}
				icon={TRASH_OUTLINE_ICONIC_BUTTON}
				size={BUTTON_SIZE}
				variant={BUTTON_VARIANT}
				bare={false}
				tooltipContent={REMOVE_ASSETS_BUTTON_TOOLTIP}
				on-click={() => {
					dispatch(REMOVE_ASSETS_BUTTON_CLICKED, {
						currentlySelectedEncodedQuery,
						recordSysId,
					});
				}}
				configAria={{ "aria-label": REMOVE_ASSETS_BUTTON_TOOLTIP }}
			/>
		);


		return (
			<now-accordion-item
				className="accordion-item"
				header={accordionHeaderLabel}
				caption={cardDescription || ''}
				expanded={isExpanded}
				append-to-payload={{ record }}
			>
				<now-card
					slot="content"
					size="lg"
					className="accordion-card"
				>
					<div className="accordion-card-content">
						<now-label-value-inline value={helperText}></now-label-value-inline>
						{isCardHeaderRequired ? (isActionButtonRequired && isPending ? (
							<span>
								{currentlySelectedEncodedQuery ? removeButtonElement : null}
								{addButtonElement}
							</span>
						) : null) : null}

						{isMandatoryFieldEmptyAlert}
							
						<div className="editable-list">
							<sn-bcm-editable-list
								tableName={relatedChildTable}
								listViewName={record.view}
								listConfig={gridConfig}
								filterQueryConditions={queryCondition}
								isReadOnly={record.readOnly}
								isRecordWatcherEnabled={isCardHeaderRequired && isActionButtonRequired}
								parentRecordIdentification={recordSysId}
								isRefreshRequested={true}
								allowColumnHidding={record.allowColumnHidding}
								isBiaDependency={record.isBiaDependency}
								userPreferenceName={record.userPreferenceName}
							/>
						</div>
						
						{isActionButtonRequired ? _getCardFooter(record, state, dispatch, updateState) : null}
					</div>
				</now-card>
				{_getStatus(statusValue)}
			</now-accordion-item>
		);
	});

	return listElements;
};

export { updateMandatoryPropertyFlagRecord };
export default getCardList;
