import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import RECORD_QUERY_ACTIONS from '../constants/RecordQueryActionTypes';
import SELECTED_VALUES_QUERY_ACTIONS from '../constants/SelectedValuesQueryActionTypes';
import recordQuery from '../querries/SelectedValuesQuery';

const requestRecordQuery = createGraphQLEffect(recordQuery, {
	variableList: ['sysId', 'selectionTable'],
	templateVarList: ['table', 'field'],
	startActionType: RECORD_QUERY_ACTIONS.RECORD_QUERY_STARTED,
	successActionType: RECORD_QUERY_ACTIONS.RECORD_QUERY_SUCCEEDED,
	errorActionType: RECORD_QUERY_ACTIONS.RECORD_QUERY_FAILED
});

const getRecordQuerySuccess = ({ action, state, dispatch, updateState }) => {
	const { properties } = state;
	const { table, field, selectionTable, maxGlideListItems } = properties;
	let currentlySelectedGlideList = get(action, `payload.data.GlideRecord_Query.${table}.results[0].${field}.value`);
	const usersAggregateCount = get(
		action,
		`payload.data.usersAggregateCountQuery[0].count`
	);

	if (currentlySelectedGlideList) {
		let queryConditions = `sys_idIN${currentlySelectedGlideList}`;
		updateState({
			currentlySelectedGlideList,
			usersAggregateCount
		});
		return dispatch(SELECTED_VALUES_QUERY_ACTIONS.SELECTED_VALUES_QUERY_REQUESTED, {
			selectionTable,
			queryConditions,
			maxTypeaheadOptions: maxGlideListItems
		});
	}

	return updateState({
		isLoading: false
	});
};

const getRecordQueryFailure = ({ action, state, updateState }) => {
	const { properties } = state;
	const { table, field } = properties;
	const error = action.payload;
	console.error(`An error occured attenmpting to fetch ${field} data from ${table}`);
	console.error(error);
	updateState({
		error: true
	});
};

const actions = {
	[RECORD_QUERY_ACTIONS.RECORD_QUERY_REQUESTED]: {
		schema: {
			type: 'object',
			properties: {
				sysId: {type: 'string'},
				table: {type: 'string'},
				field: {type: 'string'},
				selectionTable: {type: 'string'}
			},
			required: ['sysId', 'table', 'field', 'selectionTable']
		},
		private: true
	}
};

const actionHandlers = {
	[RECORD_QUERY_ACTIONS.RECORD_QUERY_REQUESTED]: requestRecordQuery,
	[RECORD_QUERY_ACTIONS.RECORD_QUERY_SUCCEEDED]: {
		effect: getRecordQuerySuccess,
		stopPropagation: true
	},
	[RECORD_QUERY_ACTIONS.RECORD_QUERY_FAILED]: {
		effect: getRecordQueryFailure,
		stopPropagation: true
	}
};

export {
	actions,
	actionHandlers
};