import { createHttpEffect } from '@servicenow/ui-effect-http';
import { get } from 'lodash';
import { ACTION_TYPES } from '../constants/BIAPieChartRibbonCardActionTypes';
import {
	FETCH_ERROR_MESSAGE,
	IMPACT_AND_DEPENDENCY_ASSESSMENT_COUNT_URL,
	COMPLETE_STATUS_NAME,
	PENDING_STATUS_NAME,
} from '../constants/BIAPieChartRibbonCardConstants';
import { NOW_COLOR_PALETTE_2 } from '../../../util/nowColorPalette';

const actions = {
	[ACTION_TYPES.BIA_PIE_CHART_DATA_REQUESTED]: { private: true },
	[ACTION_TYPES.BIA_PIE_CHART_DATA_FETCH_SUCCEEDED]: { private: true },
	[ACTION_TYPES.BIA_PIE_CHART_DATA_FETCH_FAILED]: { private: true },
};

const requestBIAPieChartData = createHttpEffect(
	IMPACT_AND_DEPENDENCY_ASSESSMENT_COUNT_URL,
	{
		method: 'GET',
		queryParams: ['queryTableIdentifier', 'parentRecordSysId', 'groupByColumn'],
		successActionType: ACTION_TYPES.BIA_PIE_CHART_DATA_FETCH_SUCCEEDED,
		errorActionType: ACTION_TYPES.BIA_PIE_CHART_DATA_FETCH_FAILED,
		batch: false,
	}
);

const fetchSucceeded = ({ action, updateState, state }) => {
	const { result } = action.payload;
	const { totalLabel, cardHeaderLabel } = state.properties;

	if (result.error) {
		console.error(FETCH_ERROR_MESSAGE);
		console.error(result.error);

		updateState({
			isError: true,
			isLoading: false,
		});

		return;
	}

	const dataReturned = get(result, `requestBody`);

	if (dataReturned) {
		const chartData = [];

		chartData.push({
			name: COMPLETE_STATUS_NAME,
			count: 0,
			color: NOW_COLOR_PALETTE_2.POSITIVE,
		});
		chartData.push({
			name: PENDING_STATUS_NAME,
			count: 0,
			color: NOW_COLOR_PALETTE_2.NEUTRAL,
		});
		const groupByCounts = dataReturned.groupByResult;

		if (groupByCounts.length) {
			for (let i = 0; i < chartData.length; i++) {
				for (let j = 0; j < groupByCounts.length; j++) {
					const statusCount = get(groupByCounts[j], chartData[i].name);

					if (statusCount) {
						chartData[i].count = statusCount;
						break;
					}
				}
			}
		}

		updateState({
			seriesData: chartData,
			isLoading: false,
			totalLabel: dataReturned.totalLabel,
			cardHeaderLabel: dataReturned.cardHeaderLabel,
		});
	} else {
		console.error(FETCH_ERROR_MESSAGE);

		updateState({
			isLoading: false,
			isError: false,
		});
	}
};

const fetchFailed = ({ updateState, action }) => {
	const { data } = action.payload;

	console.error(FETCH_ERROR_MESSAGE);
	console.error(data);

	updateState({
		isError: true,
		isLoading: false,
	});
};

const actionHandlers = {
	[ACTION_TYPES.BIA_PIE_CHART_DATA_REQUESTED]: requestBIAPieChartData,
	[ACTION_TYPES.BIA_PIE_CHART_DATA_FETCH_SUCCEEDED]: fetchSucceeded,
	[ACTION_TYPES.BIA_PIE_CHART_DATA_FETCH_FAILED]: fetchFailed,
};

export { actions, actionHandlers };
