import {
	ROW_FIRST_LINK_CLICKED,
	ROW_LINK_CLICKED,
	PREVIEW_RECORD,
} from '../constants/RelatedItemRecordListActionTypes';
import { RECORD_EDIT_ERROR_MESSAGE } from '../constants/RelatedItemRecordListConstants';
import {
	MODAL_CLOSE_CLICKED,
	RECORD_DATA_CHANGED,
} from '../../RecordModal/constants/RecordModalActionTypes';

/**
 * Action handler when a row is clicked in record list.
 * Set isModalOpen property of modal to true to open modal.
 * @param {*} result returned.
 */

const editRecord = (result) => {
	const { action, updateState, dispatch } = result;
	const { row, error } = action.payload;

	if (error) {
		console.error(RECORD_EDIT_ERROR_MESSAGE);
		return;
	}
	const { sys_id } = row;
	updateState({
		isModalOpen: true,
		recordModalSysId: sys_id.value,
	});
};

const actionHandlers = {
	[ROW_FIRST_LINK_CLICKED]: {
		effect: editRecord,
		stopPropagation: true,
	},
	[ROW_LINK_CLICKED]: {
		stopPropagation: true,
	},
	[PREVIEW_RECORD]: {
		stopPropagation: true,
	},
	[MODAL_CLOSE_CLICKED]: ({ updateState }) => {
		updateState({
			isModalOpen: false,
		});
	},

	[RECORD_DATA_CHANGED]: ({ updateState }) => {
		updateState({
			refreshRequested: {
				timestamp: Date.now(),
				background: false,
				loaderDuration: 0,
			},
		});
	}
};

export { actionHandlers };
