const query = `query getDependencyAssessmentData {
    GlideRecord_Query {
      {{tableName}}(queryConditions: "{{parentTableReferenceField}}={{parentRecordSysId}}") {
        _results {
            sys_id {
                value
            }
            {{dependencyAssessmentStatusField}} {
                value
            }
            requires_data_backup {
                value
            }

            {{dependencyElementReferenceColumnField}} {
                    _reference {
                        sys_id {
                            value
                        }
                        {{accordionHeaderField}} {
                            value
                        }
                        {{accordionDescriptionField}} {
                            value
                        }
                    }
                }
            }
            
        }
    }
}`;

export default query;
