import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import PLAN_DOCUMENT_QUERY from '../constants/DocumentationListQueryActionTypes';
import {
	PLAN_DOCUMENTATION_RESULTS,
	PLAN_TEMPLATE_FIELD_NAME,
} from '../constants/GeneralConstants';
import planDocumentationListQuery from '../queries/planDocumentationListQuery';
import {
	reduceResponseObjectByValue,
	sortDocumentsByTemplateOrder,
} from '../utils';

const queryPlanDocumentations = createGraphQLEffect(
	planDocumentationListQuery,
	{
		templateVarList: ['tableName', 'maxDocumentationSectionCount'],
		variableList: ['queryConditions'],
		startActionType: PLAN_DOCUMENT_QUERY.STARTED,
		successActionType: PLAN_DOCUMENT_QUERY.SUCCEEDED,
		errorActiontype: PLAN_DOCUMENT_QUERY.FAILED,
	}
);

const graphqlDocumentationListQueryStarted = ({ updateState }) => {
	updateState({
		loading: true,
	});
};

const graphqlDocumentationListQuerySuccess = ({
	state,
	action,
	updateState,
	properties,
}) => {
	const { planTemplatesOrderArray } = state;
	const { tableName } = properties;
	const responseDocumentationList = get(
		action.payload,
		`data.GlideRecord_Query.${tableName}.${PLAN_DOCUMENTATION_RESULTS}`
	);

	if (responseDocumentationList) {
		const reducedResponseDocumentationList = responseDocumentationList.map(
			(documentationRecord) => reduceResponseObjectByValue(documentationRecord)
		);
		const sortedDocumentSectionsByTemplateOrder = sortDocumentsByTemplateOrder(
			planTemplatesOrderArray,
			reducedResponseDocumentationList,
			PLAN_TEMPLATE_FIELD_NAME
		);

		return updateState({
			loading: false,
			tableRecords: sortedDocumentSectionsByTemplateOrder,
		});
	}

	return updateState({
		loading: false,
	});
};

const graphqlDocumentationListQueryFail = ({ action, updateState }) => {
	updateState({
		loading: false,
	});
	const error = action.payload;

	console.error(error);
};

const actionHandlers = {
	[PLAN_DOCUMENT_QUERY.REQUESTED]: queryPlanDocumentations,
	[PLAN_DOCUMENT_QUERY.STARTED]: {
		effect: graphqlDocumentationListQueryStarted,
		stopPropagation: true,
	},
	[PLAN_DOCUMENT_QUERY.SUCCEEDED]: {
		effect: graphqlDocumentationListQuerySuccess,
		stopPropagation: true,
	},
	[PLAN_DOCUMENT_QUERY.FAILED]: {
		effect: graphqlDocumentationListQueryFail,
		stopPropagation: true,
	},
};

export default actionHandlers;
