import CombinedScoreCard from '../CombinedScoreCard/CombinedScoreCardElement';
import {
	ACTION_BUTTON_ICON,
	ACTION_BUTTON_ACTION_NAME,
	ACTION_BUTTON_ARIA_LABEL,
	ACTION_BUTTON_TOOLTIP,
	LOADING_VIEW_MESSAGE,
} from './constants/PlanDocumentOverviewScoreCardConstants';

const view = (state) => {
	const {
		isLoading,
		totalDocumentCount,
		pendingDocumentCount,
		completeDocumentCount,
		properties,
	} = state;

	const {
		planDocumentTotalCountLabel,
		planDocumentCompleteCountLabel,
		planDocumentPendingCountLabel,
		cardHeaderLabel,
		isActionButtonDisplayed,
		relatedItemTabIndex,
		secondaryScoreOrientation,
	} = properties;

	const combinedScoreCardProperties = {
		cardHeaderLabel,
		primaryScore: {
			label: planDocumentTotalCountLabel,
			score: `${totalDocumentCount}`,
		},
		secondaryScores: [
			{
				heading: planDocumentCompleteCountLabel,
				score: `${completeDocumentCount}`,
			},
			{
				heading: planDocumentPendingCountLabel,
				score: `${pendingDocumentCount}`,
			},
		],
		secondaryScoreOrientation,
		isActionButtonDisplayed,
		actionButtonIcon: ACTION_BUTTON_ICON,
		actionButtonActionName: ACTION_BUTTON_ACTION_NAME,
		actionButtonActionPayload: {
			params: {
				selectedTabIndex: relatedItemTabIndex,
			},
			redirectEmptyState: true,
		},
		actionButtonTooltip: ACTION_BUTTON_TOOLTIP,
		actionButtonAriaLabel: ACTION_BUTTON_ARIA_LABEL,
	};

	const loadingView = (
		<div className="loading-view">
			<now-loader label={LOADING_VIEW_MESSAGE} />
		</div>
	);

	if (isLoading) {
		return loadingView;
	}

	return <CombinedScoreCard fillHeight {...combinedScoreCardProperties} />;
};

export default view;
