import '@servicenow/now-uxf-page';
import '@servicenow/now-card';
import '@servicenow/now-label-value';
import '@servicenow/now-icon';
import '@servicenow/now-heading';
import '@servicenow/now-loader';
import NOW_SPACING from '../../util/nowSpacing';
import getIconLabelValueElement from './BiaResultsSummaryCardUtils';
import {
	REQUIRES_DATA_BACKUP,
	DATA_PLACEHOLDER,
	CARD_LABELS,
	LOADING_VIEW_MESSAGE,
} from './constants/BiaResultsSummaryCardConstants';

const view = (state) => {
	const {
		requiresDataBackup,
		recoveryPointObjective,
		adjustedRecoveryPointObjective,
		recoveryTimeObjective,
		adjustedRecoveryTimeObjective,
		recoveryTier,
		isLoading,
	} = state;
	const {
		HEADING: cardHeading,
		RECOVERY_POINT_OBJECTIVE: recoveryPointObjectiveLabel,
		ADJUSTED_RPO: adjustedRpoLabel,
		RECOVERY_TIME_OBJECTIVE: recoveryTimeObjectiveLabel,
		ADJUSTED_RTO: adjustedRtoLabel,
		RECOVERY_TIER: recoveryTierLabel,
	} = CARD_LABELS;

	const cardElements = {
		recoveryPointObjectiveUiItem: {
			icon: 'eye-outline',
			nowLabelValueStackedProps: {
				label: recoveryPointObjectiveLabel,
				value: {
					type: 'string',
					value: recoveryPointObjective || DATA_PLACEHOLDER,
				},
			},
		},
		adjustedRpoUiItem: {
			icon: 'gear-outline',
			nowLabelValueStackedProps: {
				label: adjustedRpoLabel,
				value: {
					type: 'string',
					value: adjustedRecoveryPointObjective || DATA_PLACEHOLDER,
				},
			},
		},
		recoveryTimeObjectiveUiItem: {
			icon: 'circle-semi-check-outline',
			nowLabelValueStackedProps: {
				label: recoveryTimeObjectiveLabel,
				value: {
					type: 'string',
					value: recoveryTimeObjective || DATA_PLACEHOLDER,
				},
			},
		},
		adjustedRtoUiItem: {
			icon: 'gear-outline',
			nowLabelValueStackedProps: {
				label: adjustedRtoLabel,
				value: {
					type: 'string',
					value: adjustedRecoveryTimeObjective || DATA_PLACEHOLDER,
				},
			},
		},
		recoveryTierUiItem: {
			icon: '',
			nowLabelValueStackedProps: {
				label: recoveryTierLabel,
				value: {
					type: 'jsx',
					value: (
						<now-heading
							variant="header-secondary"
							level="5"
							label={recoveryTier || DATA_PLACEHOLDER}
							style={{ marginTop: NOW_SPACING.md }}
						/>
					),
				},
			},
		},
	};

	const recoveryPointObjectiveElement = getIconLabelValueElement(
		cardElements.recoveryPointObjectiveUiItem
	);
	const adjustedRpoElement = getIconLabelValueElement(
		cardElements.adjustedRpoUiItem
	);
	const recoveryTimeObjectiveElement = getIconLabelValueElement(
		cardElements.recoveryTimeObjectiveUiItem
	);
	const adjustedRtoElement = getIconLabelValueElement(
		cardElements.adjustedRtoUiItem
	);
	const recoveryTierElement = getIconLabelValueElement(
		cardElements.recoveryTierUiItem
	);

	const recoveryPointUiSectionElement = () => {
		if (requiresDataBackup === REQUIRES_DATA_BACKUP.YES) {
			return (
				<now-uxf-page-simple-container className="bia-summary-card__col --recovery-point-objective">
					{[recoveryPointObjectiveElement, adjustedRpoElement]}
				</now-uxf-page-simple-container>
			);
		}
		return;
	};

	const recoveryTimeUiSectionElement = (
		<now-uxf-page-simple-container className="bia-summary-card__col --recovery-time-objective">
			{[recoveryTimeObjectiveElement, adjustedRtoElement]}
		</now-uxf-page-simple-container>
	);
	const recoveyTierUiSectionElement = (
		<now-uxf-page-simple-container className="bia-summary-card__col --recovery-tier">
			{[recoveryTierElement]}
		</now-uxf-page-simple-container>
	);

	const cardHeaderElement = (
		<now-card-header
			className="bia-summary-card__header"
			heading={{
				label: cardHeading,
				variant: 'secondary',
				size: 'sm',
			}}
		/>
	);

	const _getContentClassName = () => {
		if (requiresDataBackup !== REQUIRES_DATA_BACKUP.YES) {
			return 'bia-summary-card__content --hide-recovery-point-objective';
		}
		return 'bia-summary-card__content';
	};

	const biaSummaryResultsCardContent = (
		<div className={_getContentClassName()}>
			{[
				recoveryPointUiSectionElement(),
				recoveryTimeUiSectionElement,
				recoveyTierUiSectionElement,
			]}
		</div>
	);

	const loadingView = (
		<div className="loading-view">
			<now-loader label={LOADING_VIEW_MESSAGE} />
		</div>
	);

	const biaSummaryResultsCard = (
		<now-card size="md" className="bia-summary-card">
			{[
				cardHeaderElement,
				isLoading ? loadingView : biaSummaryResultsCardContent,
			]}
		</now-card>
	);

	return biaSummaryResultsCard;
};

export default view;
