import {
	RECORD_STATUS_CHANGED,
	MODAL_CLOSE_CLICKED,
	PREVIEW_RECORD,
	RECORD_DATA_CHANGED,
	NOW_BUTTON_CLICKED,
	RECORD_NOTIFICATIONS_CHANGED,
} from '../constants/RecordModalActionTypes';
import {
	MODAL_SUCCESS_STATUS_LIST,
	MODAL_FAILURE_STATUS,
} from '../constants/RecordStatusConstants';
import { RECORD_MODAL_FOOTER } from '../constants/RecordPreviewConstants';

const actions = {
	[MODAL_CLOSE_CLICKED]: {
		private: false,
	},
};

const actionHandlers = {
	[MODAL_CLOSE_CLICKED]: ({ updateProperties, dispatch }) => {
		updateProperties({
			isModalOpen: false,
		});
	},
	[RECORD_STATUS_CHANGED]: {
		effect: ({ state, action, dispatch }) => {
			const { table, sys_id, status } = action.payload;

			if (MODAL_SUCCESS_STATUS_LIST.includes(status)) {
				// To handle actions on invoking component after insert/update
				dispatch(RECORD_DATA_CHANGED, {
					table,
					sys_id,
					status,
				});
				// Reloading the form view
				if (state.properties.reloadAfterSave) {
					dispatch(PREVIEW_RECORD, {
						table,
						sys_id,
					});
				}

				dispatch(MODAL_CLOSE_CLICKED, {
					value: false,
				});
			}
		},
		stopPropagation: true,
	},
	[RECORD_NOTIFICATIONS_CHANGED]: {
		effect: ({ action: { payload }, updateState }) => {
			if (payload.type === MODAL_FAILURE_STATUS) {
				updateState({
					isLoading: false,
				});
			}
		},
	},
	[NOW_BUTTON_CLICKED]: {
		effect: ({ action: { payload }, updateState }) => {
			if (payload.source === RECORD_MODAL_FOOTER) {
				updateState({
					isLoading: true,
				});
			}
		},
	},
};

export { actions, actionHandlers };
