import { MODAL_CLOSE_CLICKED } from '../constants/NewBiaDynamicModalActionTypes';

const closeModal = ({ updateState }) => {
	updateState({
		isModalOpen: false,
	});
};

const actionHandlers = {
	[MODAL_CLOSE_CLICKED]: {
		effect: closeModal,
		stopPropagation: false,
	},
};

export default actionHandlers;
