import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import recoveryEventAssetDataQuery from '../queries/EventAssetDataQuery';
import { EVENT_ASSET_DATA_FETCH_ERROR_MESSAGE } from '../constants/RecoveryAddPlansModalConstants';
import { EVENT_ASSET_DATA_ACTION_TYPES } from '../constants/RecordModalActionTypes';

const fetchEventAssetData = createGraphQLEffect(recoveryEventAssetDataQuery, {
	templateVarList: ['eventSysId'],
	successActionType:
		EVENT_ASSET_DATA_ACTION_TYPES.FETCH_EVENT_ASSET_DATA_SUCCEEDED,
	errorActionType: EVENT_ASSET_DATA_ACTION_TYPES.FETCH_EVENT_ASSET_DATA_FAILED,
});
const fetchEventAssetDataSuccess = ({ action, updateState, state }) => {
	const fetchResult = action.payload;
	const fetchErrors = fetchResult.errors;

	if (fetchErrors.length) {
		console.error(EVENT_ASSET_DATA_FETCH_ERROR_MESSAGE);
		console.error(fetchErrors);

		return;
	}

	const listOfAssets = get(
		action.payload,
		`data.GlideRecord_Query.sn_recovery_event_asset._results`
	);

	let eventAssetsList = [];

	eventAssetsList = listOfAssets.map((assetRecord) => {
		let record = {};
		record['id'] = get(assetRecord, 'sys_id.value');
		record['label'] = get(assetRecord, 'short_description.displayValue');
		record['asset_name'] = get(assetRecord, 'asset_name.value');
		return record;
	});

	updateState({
		eventAssetsList,
	});
};
const fetchEventAssetDataFailure = ({ action }) => {
	const error = action.payload;

	console.error(EVENT_ASSET_DATA_FETCH_ERROR_MESSAGE);
	console.error(error);
};
const actionHandlers = {
	[EVENT_ASSET_DATA_ACTION_TYPES.FETCH_EVENT_ASSET_DATA_REQUESTED]: fetchEventAssetData,
	[EVENT_ASSET_DATA_ACTION_TYPES.FETCH_EVENT_ASSET_DATA_SUCCEEDED]: {
		effect: fetchEventAssetDataSuccess,
		stopPropogation: true,
	},
	[EVENT_ASSET_DATA_ACTION_TYPES.FETCH_EVENT_ASSET_DATA_FAILED]: {
		effect: fetchEventAssetDataFailure,
		stopPropogation: true,
	},
};

export default actionHandlers;
