import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './BiaImpactAssessmentView';
import {
	LOADING_VIEW_MESSAGE,
	IMPACT_ASSESSMENT_STATUS_FIELD,
	IMPACT_CATEGORY_RESULT_TABLE,
	IMPACT_ASSESSMENT_CHILD_TABLE,
	IMPACT_ASSESSMENT_RECOVERY_TIMEFRAME,
	IMPACT_ASSESSMENT_RATING_FIELD,
	IMPACT_CATEGORY_COLUMN_NAME,
	IMPACT_ASSESSMENT_HEADER_FIELD,
	IMPACT_ASSESSMENT_DESCRIPTION_FIELD,
	IMPACT_ASSESSMENT_CATEGORY_RESULT,
	IMPACT_ANALYSIS_FIELD,
} from './constants/BiaImpactAssessmentConstants';
import lifecycleActionHandlers from './actions/BiaImpactAssessmentLifeCycleActions';
import impactAssessmentDataActionHandlers from './actions/BiaImpactAssessmentDataFetchQueryActions';
import impactAnalysisMetaDataActionHandlers from './actions/ImpactAnalysisTableRecordMetaDataActions';
import styles from './BiaImpactAssessmentElement.scss';

const TAG_NAME = 'sn-bia-impact-assessment';

/**
 * Accordion List of Impact Categories of Impact Analysis
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bia-impact-assessment
 * 		parent-record-sys-id=""
 * 		impact-assessment-reference-column-field=""
 * 		accordion-header-field=""
 * 		accordion-description-field=""
 * ></sn-bia-impact-assessment>
 * ```
 *
 * @seismicElement sn-bia-impact-assessment
 * @summary A accordion list for displaying impact ratings of impact categories
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,

	properties: {
		/**
		 * Sysid of the record in parent table
		 *
		 * @type {string}
		 */
		parentRecordSysId: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 *  Reference column of the parent table
		 *
		 *  @type {string}
		 */
		parentTableReferenceField: {
			default: IMPACT_ANALYSIS_FIELD,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The table name on which data should be queried on.
		 *
		 * @type {string}
		 */
		tableName: {
			default: IMPACT_CATEGORY_RESULT_TABLE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Name of the status field to fetch the status of a record
		 *
		 * @type {string}
		 */
		impactAssessmentStatusField: {
			default: IMPACT_ASSESSMENT_STATUS_FIELD,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Field Name of the reference column of table
		 *
		 * @type {string}
		 */
		impactAssessmentReferenceColumnField: {
			default: IMPACT_CATEGORY_COLUMN_NAME,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Field name of the record to be shown on Accordion Header
		 *
		 * @type {string}
		 */
		accordionHeaderField: {
			default: IMPACT_ASSESSMENT_HEADER_FIELD,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 *  Field name of the record to be shown on Card Description
		 *
		 * @type {string}
		 */
		accordionDescriptionField: {
			default: IMPACT_ASSESSMENT_DESCRIPTION_FIELD,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 *  Rating column of the table
		 *
		 *  @type {string}
		 */
		impactAssessmentRatingReferenceField: {
			default: IMPACT_ASSESSMENT_RATING_FIELD,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 *  Column of the child table
		 *
		 *  @type {string}
		 */
		impactAssessmentChildTableField: {
			default: IMPACT_ASSESSMENT_CATEGORY_RESULT,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 *  Timeframe column of the table
		 *
		 *  @type {string}
		 */
		impactAssessmentRecoveryTimeField: {
			default: IMPACT_ASSESSMENT_RECOVERY_TIMEFRAME,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 *  Name of the child table
		 *
		 *  @type {string}
		 */
		impactAssessmentChildTable: {
			default: IMPACT_ASSESSMENT_CHILD_TABLE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Loading view message of the component
		 *
		 * @type {string}
		 */
		loadingViewMessage: {
			default: LOADING_VIEW_MESSAGE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Query filter condition for category results table.
		 *
		 * @type {string}
		 */
		queryFilterCondition: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
	},
	initialState: {
		/**
		 * Intial loading state of the component
		 *
		 * @type {boolean}
		 */
		isLoading: true,
		/**
		 * Array of all fetched records from table
		 *
		 * @type {object}
		 */
		tableRecords: [],
		isParentRecordEditable: true,
	},
	actionHandlers: {
		...lifecycleActionHandlers,
		...impactAssessmentDataActionHandlers,
		...impactAnalysisMetaDataActionHandlers,
	},
	styles,
});

export default TAG_NAME;
