import { IMPACT_ASSESSMENT_TABLE, RECOVERY_TIER_COLUMN_NAME, RECOVERY_TIME_OBJECTIVE_COLUMN_NAME } from '../constants/ImpactAssessmentScoreCardConstants';

const query = `query getImpactAssessmentData ($biaSysId: String!) {
  GlideRecord_Query {
     ${IMPACT_ASSESSMENT_TABLE}(sys_id:$biaSysId){
      results: _results {
        sys_id {
          value
        }
        ${RECOVERY_TIER_COLUMN_NAME} {
          displayValue
        }
        ${RECOVERY_TIME_OBJECTIVE_COLUMN_NAME} {
          displayValue
        }
      }
    }
  }
}`;

export default query;
