import TYPEAHEAD_ACTIONS from '../constants/TypeAheadActionTypes';
import SELECTION_TABLE_QUERY_ACTIONS from '../constants/SelectionTableQueryActionTypes';
import RECORD_MUTATION_ACTIONS from '../constants/RecordMutationActionTypes';
import { get } from 'lodash';

const getExcludeSelectedEncodedQuery = (selected) => {
	const encodedQuery = selected.map(selectedValue => `sys_id!=${selectedValue.value}`).join('^');
	return encodedQuery;
};

const getTypeaheadDropdownOptions = ({ state, action, dispatch }) => {
	const { properties, currentlySelected } = state;
	const { selectionTable, maxTypeaheadOptions } = properties;
	const { payload } = action;
	const inputValue = payload.value;

	const excludeSelectedEncodedQuery = getExcludeSelectedEncodedQuery(
		currentlySelected
	);
	let queryConditions = `${excludeSelectedEncodedQuery}`;

	if (inputValue) {
		const typeAheadQuery = `nameLIKE${inputValue}`;
		queryConditions = [typeAheadQuery, excludeSelectedEncodedQuery].join('^');
	}

	dispatch(SELECTION_TABLE_QUERY_ACTIONS.SELECTION_TABLE_QUERY_REQUESTED, {
		selectionTable,
		queryConditions,
		maxTypeaheadOptions,
	});
};

const getSelectedTypeaheadValue = ({state, dispatch, action, updateState}) => {
	const { typeaheadOptions, currentlySelectedGlideList, properties } = state;
	const { table, sysId, field } = properties;
	const selectedOptionSysId = get(action, 'payload.value');

	if (!selectedOptionSysId) {
		return console.error('Invalid option chosen. Selection has no sys_id.');
	}

	const updatedTypeaheadOptions = typeaheadOptions.filter(option => option.value!==selectedOptionSysId);
	const updatedSelectionGlideList = currentlySelectedGlideList ?
		[selectedOptionSysId, currentlySelectedGlideList].join(',') :
		selectedOptionSysId;

	updateState({
		typeaheadOptions: updatedTypeaheadOptions
	});

	dispatch(RECORD_MUTATION_ACTIONS.RECORD_MUTATION_REQUESTED, {
		table,
		sysId,
		field,
		glideListValue: updatedSelectionGlideList
	});
};

const actionHandlers = {
	[TYPEAHEAD_ACTIONS.TYPEAHEAD_VALUE_ENTERED]: {
		effect: getTypeaheadDropdownOptions,
		stopPropogation: true
	},
	[TYPEAHEAD_ACTIONS.TYPEAHEAD_VALUE_SELECTED]: {
		effect: getSelectedTypeaheadValue,
		stopPropogation: true
	}
};

export default actionHandlers;