import { get } from 'lodash';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	FETCH_PLAN_RECOVERY_TEAMS_DETAILS_REQUESTED,
	FETCH_PLAN_RECOVERY_TEAMS_DETAILS_SUCCEEDED,
	FETCH_PLAN_RECOVERY_TEAMS_DETAILS_FAILED,
} from '../constants/PlanRecoveryTeamsScoreCardActionTypes';
import {
	FETCH_ERROR_MESSAGE,
	PLAN_RECOVERY_TEAMS_URL,
} from '../constants/PlanRecoveryTeamsScoreCardConstants';

const requestTotalRecoveryTeamCount = createHttpEffect(
	PLAN_RECOVERY_TEAMS_URL,
	{
		method: 'GET',
		queryParams: ['sysId', 'queryColumns', 'targetTableIdentifier'],
		successActionType: FETCH_PLAN_RECOVERY_TEAMS_DETAILS_SUCCEEDED,
		errorActionType: FETCH_PLAN_RECOVERY_TEAMS_DETAILS_FAILED,
		batch: false,
	}
);

const getPlanRecoveryTeamsCountSuccess = ({ action, updateState }) => {
	const { result } = action.payload;

	if (result.error) {
		console.error(FETCH_ERROR_MESSAGE);
		console.error(result.error);

		updateState({
			error: true,
			isLoading: false,
		});

		return;
	}

	const dataReturned = get(action, 'payload.result.requestBody');
	const {
		totalCount: totalRecoveryTeamsCount,
		assignedCount: assignedRecoveryTeamsCount,
		unassignedCount: unAssignedRecoveryTeamsCount,
	} = dataReturned;

	if (
		totalRecoveryTeamsCount === 'undefined' ||
		totalRecoveryTeamsCount === null ||
		assignedRecoveryTeamsCount === 'undefined' ||
		assignedRecoveryTeamsCount === null ||
		unAssignedRecoveryTeamsCount === 'undefined' ||
		unAssignedRecoveryTeamsCount === null
	) {
		console.error(FETCH_ERROR_MESSAGE);

		updateState({
			error: true,
			isLoading: false,
		});

		return;
	}

	updateState({
		totalRecoveryTeamsCount,
		assignedRecoveryTeamsCount,
		unAssignedRecoveryTeamsCount,
		isLoading: false,
	});
};

const getPlanRecoveryTeamsCountFailed = ({ action, updateState }) => {
	const error = action.payload;

	console.error(FETCH_ERROR_MESSAGE);
	console.error(error);

	updateState({
		error: true,
		isLoading: false,
	});
};

const actionHandlers = {
	[FETCH_PLAN_RECOVERY_TEAMS_DETAILS_REQUESTED]: requestTotalRecoveryTeamCount,
	[FETCH_PLAN_RECOVERY_TEAMS_DETAILS_SUCCEEDED]: {
		effect: getPlanRecoveryTeamsCountSuccess,
		stopPropogation: true,
	},
	[FETCH_PLAN_RECOVERY_TEAMS_DETAILS_FAILED]: {
		effect: getPlanRecoveryTeamsCountFailed,
		stopPropogation: true,
	},
};

export default actionHandlers;
