import { actionTypes } from '@servicenow/ui-core';
import BIA_RESULTS_SUMMARY_ACTION from '../constants/BiaResultsSummaryCardQueryActionTypes';
import { RECORD_WATCHER_SUBSCRIPTION_REQUESTED } from '../../../behaviors/RecordWatcherBehavior';
import { IMPACT_ANALYSIS_TABLE } from '../constants/BiaResultsSummaryCardConstants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const getBiaResultsSummary = ({ state, dispatch }) => {
	const { properties } = state;
	const { sysid, isRecordWatcherEnabled } = properties;

	dispatch(BIA_RESULTS_SUMMARY_ACTION.BIA_RESULTS_SUMMARY_RECORD_REQUESTED, {
		sysId: sysid,
		table: IMPACT_ANALYSIS_TABLE,
	});

	if (isRecordWatcherEnabled) {
		dispatch(RECORD_WATCHER_SUBSCRIPTION_REQUESTED, {
			table: IMPACT_ANALYSIS_TABLE,
			filter: `sys_id=${sysid}`,
			onItemChangeAction: {
				actionType:
					BIA_RESULTS_SUMMARY_ACTION.BIA_RESULTS_SUMMARY_RECORD_REQUESTED,
				actionPayload: {
					sysId: sysid,
					table: IMPACT_ANALYSIS_TABLE,
				},
			},
		});
	}
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: getBiaResultsSummary,
};

export default actionHandlers;
