import { use } from 'chai';
import { get, set } from 'lodash';
import { t } from 'sn-translate';
import {
	SUPPORTED_DATA_TYPES,
	MODAL_ACTION_LABELS,
	DEFAULT_LIST_CONFIG,
} from '../constants/EditableListConstants';

const setGridConfig = (tableLabel, listConfig, config, isReadOnly) => {
	// If we have multiple instances of grid then each can have independent config.
	// So copying the config and using it to avoid modification on reference object.
	const _config = _.cloneDeep(config);

	set(_config, 'rowHeight', get(listConfig, 'rowHeight', DEFAULT_LIST_CONFIG.rowHeight));
	set(_config, 'rowGroup', get(listConfig, 'enableColumnGrouping', DEFAULT_LIST_CONFIG.rowGroup));
	set(_config, 'filter', get(listConfig, 'enableColumnFilters', DEFAULT_LIST_CONFIG.filter));
	set(_config, 'sort', get(listConfig, 'enableColumnSorting', DEFAULT_LIST_CONFIG.sort));
	set(_config, 'toolbar.display', get(listConfig, 'enableToolBar', DEFAULT_LIST_CONFIG.toolbar.display));
	if (get(listConfig, 'setGridTitle', true)) {
		set(_config, 'toolbar.title', tableLabel);
	}

	set(_config, 'toolbar.rowCount', get(listConfig, 'displayRecordCount', DEFAULT_LIST_CONFIG.toolbar.rowCount));
	set(_config, 'previewRecordIcon', get(listConfig, 'enableRecordPreview', DEFAULT_LIST_CONFIG.previewRecordIcon));
	set(_config, 'rowSelectionCheckbox', get(listConfig, 'enableRowSelection', DEFAULT_LIST_CONFIG.rowSelectionCheckbox));
	set(_config, 'readonly', isReadOnly);

	return _config;
};

const transformQueryValues = (queryValues, viewColumns, tableName) => {
	const _resultRows = get(
		JSON.parse(queryValues),
		`data.GlideRecord_Query.${tableName}._results`
	);

	const transformedValues = _resultRows.map((row) => {
		const _item = {};
		let _colValue = {};

		_item.id = get(row, `sys_id.value`);

		viewColumns.forEach((column) => {
			const _columnName = get(column, 'name');

			if (get(column, 'isdotwalked', false)) {
				const _parentColumn = get(column, 'parentColumn');

				_colValue = get(row, `${_parentColumn}._reference.${_columnName}`);
			} else {
				_colValue = get(row, _columnName);
			}

			if (column.editable && _colValue.canWrite) {
				set(column, 'editable', true);
			} else {
				set(column, 'editable', false);
			}

			set(_item, _columnName, _colValue);
		});

		return _item;
	});

	return transformedValues;
};

const transformColumnDefinition = (
	viewColumns,
	userPreferenceArr,
	allowColumnHidding
) => {
	let index = 0;
	const _transformedColDefs = viewColumns.map((column) => {
		const _column = {};
		let _isDotwalked = false;
		let _isDatatypeAllowed = false;

		const _elementName = get(column, 'elementName');
		const _colName = get(column, 'dictionaryData.name');
		const _colType = get(
			column,
			'dictionaryData.internalType',
			SUPPORTED_DATA_TYPES.STRING_TYPE
		);
		const _colTypePosition = Object.values(SUPPORTED_DATA_TYPES).indexOf(
			_colType
		);

		if (_elementName !== _colName) {
			set(_column, 'parentColumn', _elementName.split('.')[0]);
			_isDotwalked = true;
		}

		if (_colTypePosition >= 0) {
			_isDatatypeAllowed = true;
			set(_column, 'type', _colType);
		} else {
			set(_column, 'type', SUPPORTED_DATA_TYPES.STRING_TYPE);
		}

		set(_column, 'isdotwalked', _isDotwalked);
		set(_column, 'elementName', _elementName);
		set(_column, 'name', _colName);
		set(_column, 'label', get(column, 'dictionaryData.label'));
		set(_column, 'sortable', get(column, 'dictionaryData.isSortable'));
		set(_column, 'groupable', get(column, 'dictionaryData.isGroupable'));
		set(_column, 'filterable', get(column, 'dictionaryData.isFilterable'));
		set(_column, 'isChoice', get(column, 'dictionaryData.isChoice'));
		set(_column, 'isMandatory', get(column, 'dictionaryData.isMandatory'));

		if (
			userPreferenceArr &&
			userPreferenceArr.length > index &&
			userPreferenceArr[index] &&
			!userPreferenceArr[index].name	
		) {
			set(_column, 'width', userPreferenceArr[index]);
		} else if (userPreferenceArr && userPreferenceArr.length) {
			userPreferenceArr.map((preference) => {
				if (preference && preference.name == _colName) {
					if (preference.width) {
						set(_column, 'width', preference.width);
					}
					if (preference.hidden) {
						set(_column, 'hidden', preference.hidden);
					}
				}
			});
		}

		if (!_isDotwalked && _isDatatypeAllowed) {
			set(_column, 'editable', true);
		} else {
			set(_column, 'editable', false);
		}
		set(_column, 'attributes', get(column, 'dictionaryData.attributes'));
		set(_column, 'copyPaste', false);
		set(_column, 'hideable', allowColumnHidding);
		index++;

		return _column;
	});

	return _transformedColDefs;
};

const getModalLabels = (actionName, tableName) => {
	let _modalLabels = {
		headerLabel: '',
		actionBtnLabel: '',
		cancelBtnLabel: '',
	};

	switch (actionName) {
		case 'insert':
			_modalLabels = {
				headerLabel: t('Create {0}', tableName),
				actionBtnLabel: MODAL_ACTION_LABELS.MODAL_CREATE_LABEL,
				cancelBtnLabel: MODAL_ACTION_LABELS.MODAL_CANCEL_LABEL,
			};
			break;
		case 'edit':
			_modalLabels = {
				headerLabel: t('Update {0}', tableName),
				actionBtnLabel: MODAL_ACTION_LABELS.MODAL_UPDATE_LABEL,
				cancelBtnLabel: MODAL_ACTION_LABELS.MODAL_CANCEL_LABEL,
			};
			break;
		default:
			return _modalLabels;
	}

	return _modalLabels;
};

const parseFetchUpdatedRecords = (listRecords, index, updatedValues) => {
	/* merge the updated column values to edited record at the given index and add it back to the list of records */
	const updatedRecord = {
		...listRecords[index],
		...updatedValues,
	};

	return [
		...listRecords.slice(0, index),
		updatedRecord,
		...listRecords.slice(index + 1),
	];
};

const containsClassOrTagInPath = (event, matcher) => {
	const path = event.path || (event.composedPath && event.composedPath());

	for (let i = 0; i < path.length; i++) {
		if (
			path[i].localName === matcher ||
			(path[i].className && path[i].classList.contains(matcher))
		) {
			return true;
		}
	}

	return false;
};

export {
	setGridConfig,
	transformQueryValues,
	transformColumnDefinition,
	getModalLabels,
	parseFetchUpdatedRecords,
	containsClassOrTagInPath,
};
