const query = `query getImpactAssessmentData ($queryConditions: String!) {
    GlideRecord_Query {
        {{tableName}}(queryConditions: $queryConditions) {
            _results {
                sys_id {
                    value
                }
                {{impactAssessmentStatusField}} {
                    value
                }
                {{impactAssessmentReferenceColumnField}} {
                    _reference {
                        sys_id {
                            value
                        }
                        {{accordionHeaderField}} {
                            value
                        }
                        {{accordionDescriptionField}} {
                            value
                        }
                    }
                }
            }
        }
    }
}`;

export default query;
