import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import {
	USER_ACCESSIBILITY_PREFERENCE_REQUESTED,
	USER_ACCESSIBILITY_PREFERENCE_SUCCEEDED,
	USER_ACCESSIBILITY_PREFERENCE_FAILED,
} from '../constants/AccessibilityActionTypes';
import userAccessibilityPreferenceQuery from '../queries/userAccessibilityPreferenceQuery';

const requestAccessibilityPreference = createGraphQLEffect(
	userAccessibilityPreferenceQuery,
	{
		successActionType: USER_ACCESSIBILITY_PREFERENCE_SUCCEEDED,
		errorActionType: USER_ACCESSIBILITY_PREFERENCE_FAILED,
	}
);

const getUserPreferenceSuccess = ({ action, updateState }) => {
	const { payload } = action;

	const userPreferences = get(
		payload,
		'data.GlideDomain_Query.user.preferences[0].value'
	);

	if (userPreferences && userPreferences === 'true') {
		updateState({
			isAccessibilityModeActive: userPreferences,
		});
	}
};

const getUserPreferenceFailure = ({ action }) => {
	const { data } = action.payload;

	console.error(data.error);
	console.error('Error occured fetching accessibility user preference');
};

const actionHandlers = {
	[USER_ACCESSIBILITY_PREFERENCE_REQUESTED]: requestAccessibilityPreference,
	[USER_ACCESSIBILITY_PREFERENCE_SUCCEEDED]: {
		effect: getUserPreferenceSuccess,
		stopPropagation: true,
	},
	[USER_ACCESSIBILITY_PREFERENCE_FAILED]: {
		effect: getUserPreferenceFailure,
		stopPropagation: true,
	},
};

export default actionHandlers;
