import { get, set } from 'lodash';

const parseNestedData = (nestedQueryData, columns, parentRecord, tableName) => {

    let records = [];
    for (var i = 0; i < nestedQueryData.length; i++) {
        let childRecord = {};
        childRecord.id = get(nestedQueryData[i], 'sys_id.value');
        columns.map((column) => {
            childRecord[column.name] = get(nestedQueryData[i], `${column.name}`);

        });
        childRecord.parent = get(parentRecord, 'sys_id.value');
        childRecord.tableName = tableName;
        records.push(childRecord);
    }
    return records;
};

const transformColumnDefintion = (record, columns, groupColumnName, userPreferencesArr) => {
    let index = 0;

    const _columnDefinitions = columns.map((column) => {
		if (record && !column.label) {
        	column.label = get(record, `${column.name}.label`);
		}

        if (!column.type && column.name === groupColumnName) {
            set(column, 'type', 'text');
        }
        column.hideable = true;

		if (
			userPreferencesArr &&
			userPreferencesArr.length > index &&
			userPreferencesArr[index] &&
			!userPreferencesArr[index].name
		) {
			set(column, 'width', userPreferencesArr[index]);
			index++;
		} else if (userPreferencesArr && userPreferencesArr.length) {
			userPreferencesArr.map((preference) => {
				if (preference && preference.name == column.name) {
					if (preference.width) {
						set(column, 'width', preference.width);
					}
					if (preference.hidden) {
						set(column, 'hidden', preference.hidden);
					}
				}
			});
		}

		return column;
	});
	return _columnDefinitions;
};

export { parseNestedData, transformColumnDefintion };
