import '@servicenow/now-modal';
import '@servicenow/now-record-list-connected';
import '@servicenow/now-select';
import '@servicenow/now-label-value';
import {
	ITEM_PICKER_EDIT_CANCELLED,
	ITEM_PICKER_EDIT_SAVED,
	ITEM_PICKER_EDIT_NEXT,
} from './constants/ItemPickerModalActionTypes';
import {
	DEFAULT_CANCEL_BUTTON_LABEL,
	DEFAULT_SAVE_BUTTON_LABEL,
	DEFAULT_NEXT_BUTTON_LABEL,
	ERROR_ALERT_HEADING,
	SELECT_IMPACTED_ITEM_TYPE_MESSAGE,
	LIST_PICKER_MESSAGE,
	IMPACTED_ITEM_TYPE_LABEL,
} from './constants/ItemPickerModalConstants';

const view = (state) => {
	const {
		isModalOpen,
		selectionTable,
		itemPickerOptionsFilter,
		selectionTableFields,
		currentlySelectedEncodedQuery,
		errorMessage,
		isLoading,
		loadingMessage,
		properties,
		isItemTypeSelected,
		selectionTableLabel,
		selectItemTypeError,
		listOfElements,
		disableNextButton,
	} = state;
	const { paginationRecordLimit, modalHeaderLabel, isRecoveryEvent } = properties;

	const cancelButton = {
		variant: 'secondary',
		label: DEFAULT_CANCEL_BUTTON_LABEL,
		clickActionType: ITEM_PICKER_EDIT_CANCELLED,
	};
	const saveButton = {
		variant: 'primary',
		label: DEFAULT_SAVE_BUTTON_LABEL,
		clickActionType: ITEM_PICKER_EDIT_SAVED,
	};
	const nextButton = {
		variant: 'primary',
		label: DEFAULT_NEXT_BUTTON_LABEL,
		clickActionType: ITEM_PICKER_EDIT_NEXT,
		disabled: disableNextButton,
	};
	const modalFooterButtons = [cancelButton];

	if (isRecoveryEvent && !isItemTypeSelected) {
		modalFooterButtons.unshift(nextButton);
	}
	const isValidToSave =
		currentlySelectedEncodedQuery && !errorMessage && !isLoading;

	if (isValidToSave) {
		modalFooterButtons.unshift(saveButton);
	}

	const loaderElement = (
		<div className="loading">
			<now-loader label={loadingMessage} />
		</div>
	);

	const listPickerMessage = LIST_PICKER_MESSAGE + selectionTableLabel;

	const listPickerElement = (
		<div className="item-list-container">
			{isRecoveryEvent ? (
				<now-label-value-stacked
					className="item-list-container-message"
					items={[
						{
							value: {
								type: 'string',
								value: listPickerMessage,
							},
						},
					]}
				/>
			) : null}
			<now-record-list-connected-picker
				className="item-list"
				table={selectionTable}
				query={itemPickerOptionsFilter}
				limit={paginationRecordLimit}
				columns={selectionTableFields}
				hideSelectAll
			/>
		</div>
	);

	const errorAlert = (
		<now-alert
			className={errorMessage ? 'now-alert-error-element' : 'hidden'}
			status="critical"
			icon="exclamation-triangle-fill"
			header={`${ERROR_ALERT_HEADING}:`}
			content={errorMessage}
		/>
	);

	const selectImpactedItem = (
		<div>
			<div>
				<now-label-value-stacked
					items={[
						{
							value: {
								type: 'string',
								value: SELECT_IMPACTED_ITEM_TYPE_MESSAGE,
							},
						},
					]}
				/>
			</div>
			<div className="select-impacted-item-container">
				<now-select
					items={listOfElements}
					label={IMPACTED_ITEM_TYPE_LABEL}
					required={true}
					search="initial"
					messages={
						selectItemTypeError
							? [
									{
										status: 'critical',
										content: selectItemTypeError,
										icon: 'circle-info-outline',
									},
							  ]
							: null
					}
				/>
			</div>
		</div>
	);

	const modalBody = isRecoveryEvent
		? isItemTypeSelected
			? listPickerElement
			: selectImpactedItem
		: listPickerElement;

	const itemPickerModalView = (
		<div>
			<now-modal
				opened={isModalOpen}
				size="lg"
				header-label={modalHeaderLabel}
				footer-actions={modalFooterButtons}>
				{errorAlert}
				{isLoading ? loaderElement : modalBody}
			</now-modal>
		</div>
	);

	return itemPickerModalView;
};

export default view;
