const query = `query getEventAssetData {
    GlideRecord_Query {
        sn_recovery_event_asset (queryConditions:"event={{eventSysId}}") {
            _results {
                sys_id {
                    value
                }
                asset_name {
                    value
                    displayValue
                }
                short_description {
                    value
                    displayValue
                }
            }
        }
    }
}`;

export default query;
