import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	BIA_CHECK_IS_ELEMENT_CONFIG_ITEM,
	DEFAULT_ITEM_PICKER_LOADING_MESSAGE,
	CHECK_CONFIG_ITEM_ERROR_MSG,
	MODAL_VIEW_TYPES,
} from '../constants/BiaDependencyItemPickerModalConstants';
import {
	ELEMENT_DEFINITIONS_TABLE_ACTIONS,
	BIA_EXCLUDED_ITEMS_QUERY_REQUESTED,
} from '../constants/BiaDependencyItemPickerModalActionTypes';

const requestIsElementConfigurationItem = createHttpEffect(
	BIA_CHECK_IS_ELEMENT_CONFIG_ITEM,
	{
		method: 'GET',
		queryParams: ['primaryElementTable'],
		startActionType:
			ELEMENT_DEFINITIONS_TABLE_ACTIONS.CHECK_CONFIG_ITEM_STARTED,
		successActionType:
			ELEMENT_DEFINITIONS_TABLE_ACTIONS.CHECK_CONFIG_ITEM_SUCCEEDED,
		errorActionType: ELEMENT_DEFINITIONS_TABLE_ACTIONS.CHECK_CONFIG_ITEM_FAILED,
		batch: false,
	}
);

const getIsElementConfigurationItemQueryStarted = ({ updateState }) => {
	updateState({
		loadingMessage: DEFAULT_ITEM_PICKER_LOADING_MESSAGE,
		isLoading: true,
	});
};

const getIsElementConfigurationItemQuerySuccess = ({
	properties,
	action,
	dispatch,
	updateState,
}) => {
	const { parentRecordSysId, table } = properties;
	const { result } = action.payload;

	dispatch(BIA_EXCLUDED_ITEMS_QUERY_REQUESTED, {
		parentRecordSysId,
		excludedItemsTableIdentifier: table,
	});

	if (result && result.isElementConfigurationItem) {
		updateState({
			currentScreenView: MODAL_VIEW_TYPES.RADIO_BUTTONS,
			isElementConfigurationItem: result.isElementConfigurationItem,
		});
	}

	updateState({
		isLoading: false,
	});
};

const getIsElementConfigurationItemQueryFailed = ({ action, updateState }) => {
	const error = action.payload;

	console.error(error);

	updateState({
		isLoading: false,
		errorMessage: CHECK_CONFIG_ITEM_ERROR_MSG,
	});
};

const actions = {
	[ELEMENT_DEFINITIONS_TABLE_ACTIONS.CHECK_CONFIG_ITEM_REQUESTED]: {
		private: true,
	},
	[ELEMENT_DEFINITIONS_TABLE_ACTIONS.CHECK_CONFIG_ITEM_STARTED]: {
		private: true,
	},
	[ELEMENT_DEFINITIONS_TABLE_ACTIONS.CHECK_CONFIG_ITEM_SUCCEEDED]: {
		private: true,
	},
	[ELEMENT_DEFINITIONS_TABLE_ACTIONS.CHECK_CONFIG_ITEM_FAILED]: {
		private: true,
	},
};

const actionHandlers = {
	[ELEMENT_DEFINITIONS_TABLE_ACTIONS.CHECK_CONFIG_ITEM_REQUESTED]: requestIsElementConfigurationItem,
	[ELEMENT_DEFINITIONS_TABLE_ACTIONS.CHECK_CONFIG_ITEM_STARTED]: getIsElementConfigurationItemQueryStarted,
	[ELEMENT_DEFINITIONS_TABLE_ACTIONS.CHECK_CONFIG_ITEM_SUCCEEDED]: getIsElementConfigurationItemQuerySuccess,
	[ELEMENT_DEFINITIONS_TABLE_ACTIONS.CHECK_CONFIG_ITEM_FAILED]: getIsElementConfigurationItemQueryFailed,
};

export { actions, actionHandlers };
