const EDITABLE_LIST_FETCH_QUERY = `query($tableName: String!, $listViewName: String!, $filterQueryConditions: String, $paginationLimit: Glide_PaginationInputType) {
  GlideLayout_Query {
		listLayout(listTableName: $tableName, listViewName: $listViewName, listQueryConditions: $filterQueryConditions)  {
			metadata {
				canCreate
				canDelete
				canWrite
			}
			table
			conditions
			allElements {
				elementName
				dictionaryData {
					name
					label
					internalType
					dictionaryType
					isFilterable
					isSortable
					isGroupable
					isMandatory
					isChoice
					canRead
					canCreate
					attributes{
						name
						value
					}
				}
				
		}
		_graphql(queryGeneratorArguments: [{name: "fields", value: "value, displayValue, canWrite"}]) {
			_query
				_result(
					variables: [
					{ name: "table", value: $tableName}, 
					{ name: "queryConditions", value: "$$parent.conditions$$" }
					], 
					pagination: $paginationLimit
				)
			}
		}
	}
  GlideRecord_Query{
    {{tableName}}{
      _table_metadata {
        displayValue
        label
      }
  	}
  } 
  GlideDomain_Query {
		user {
			preferences(name: ["{{userPreferenceName}}"]) {
        		name
        		value
      		}
		}
	}
}`;

const EDITABLE_LIST_UPDATE_QUERY = `
  mutation ($sysId: String!) {
  GlideRecord_Mutation {
    update_{{tableName}} (sys_id: $sysId {{{values}}}){
		  {{columns}}
    }
  }
}`;


export {
	EDITABLE_LIST_FETCH_QUERY,
	EDITABLE_LIST_UPDATE_QUERY,
};
