const reduceResponseObjectByValue = (graphqlResponseObject) => {
	let reducedObj = {};

	Object.entries(graphqlResponseObject).forEach(([field, fieldValue]) => {
		if(field == '_row_data'){
			reducedObj["isEditable"]= fieldValue.canWrite; 
		}
		 else if ('value' in fieldValue) {
			reducedObj[field] = fieldValue.value;
		} else {
			reducedObj = {
				...reducedObj,
				...reduceResponseObjectByValue(graphqlResponseObject[field]),
			};
		}
	});

	return reducedObj;
};

const sortDocumentsByTemplateOrder = (
	planTemplatesOrderArray,
	planDocumentationList,
	planTemplateField
) => {
	let sortedByTemplatesOrderDocumentsArray = [...planDocumentationList];
	const newDocumentSectionsArray = [];

	sortedByTemplatesOrderDocumentsArray = sortedByTemplatesOrderDocumentsArray
		.sort((currentDocument, nextDocument) => {
			const currentDocumentTemplate = currentDocument[planTemplateField];
			const nextDocumentTemplate = nextDocument[planTemplateField];
			const currentDocumentOrderIndex = planTemplatesOrderArray.indexOf(
				currentDocumentTemplate
			);
			const nextDocumentOrderIndex = planTemplatesOrderArray.indexOf(
				nextDocumentTemplate
			);

			if (currentDocumentTemplate && currentDocumentOrderIndex === -1) {
				return 1;
			}

			if (nextDocumentTemplate && nextDocumentOrderIndex === -1) {
				return -1;
			}

			return currentDocumentOrderIndex - nextDocumentOrderIndex;
		})
		.filter((documentSection) => {
			const documentationTemplate = documentSection[planTemplateField];

			if (!documentationTemplate) {
				newDocumentSectionsArray.push(documentSection);
			}

			return Boolean(documentationTemplate);
		});

	return [...sortedByTemplatesOrderDocumentsArray, ...newDocumentSectionsArray];
};

export { reduceResponseObjectByValue, sortDocumentsByTemplateOrder };
