const recordMutation = `mutation SetBcpPlanContributors ($sysId: String!, $glideListValue: String!) {
    GlideRecord_Mutation {
        update_{{table}} (
            sys_id: $sysId
            {{field}}: $glideListValue
        ) {
            {{field}} {
                value
            }
        }
    }
}`;

export default recordMutation;