import { actionTypes } from '@servicenow/ui-core';
import { EDITABLE_LIST_QUERY_ACTION_TYPES } from '../constants/EditableListCRUDActionTypes';
import { RECORD_WATCHER_SUBSCRIPTION_REQUESTED } from '../../../behaviors/RecordWatcherBehavior';
import {USER_PREFERENCE_PREFIX} from '../../../util/grid-util/constants/GeneralConstants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ properties, dispatch }) => {
		const {
			tableName,
			listViewName,
			filterQueryConditions,
			recordLoadLimit,
			isRecordWatcherEnabled,
			userPreferenceName,
		} = properties;

		const paginationLimit = { limit: recordLoadLimit };

		dispatch(EDITABLE_LIST_QUERY_ACTION_TYPES.EDITABLE_LIST_QUERY_REQUESTED, {
			tableName,
			listViewName,
			filterQueryConditions,
			paginationLimit,
			userPreferenceName
		});
		if (isRecordWatcherEnabled) {
			dispatch(RECORD_WATCHER_SUBSCRIPTION_REQUESTED, {
				table: tableName,
				filter: filterQueryConditions,
				onItemChangeAction: {
					actionType:
						EDITABLE_LIST_QUERY_ACTION_TYPES.EDITABLE_LIST_QUERY_REQUESTED,
					actionPayload: {
						tableName,
						listViewName,
						filterQueryConditions,
						userPreferenceName
					},
				},
			});
		}
	},
};

export default actionHandlers;
