import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { createHttpEffect} from '@servicenow/ui-effect-http';
import {
	DEPENDENCY_ASSESSMENT_DATA_REQUESTED,
	DEPENDENCY_ASSESSMENT_DATA_SUCCEEDED,
	DEPENDENCY_ASSESSMENT_DATA_FAILED,
	ELEMENT_USER_PREFERENCE_REQUESTED,
	ELEMENT_USER_PREFERENCE_SUCCEEDED,
	ELEMENT_USER_PREFERENCE_FAILED,
	SET_ELEMENT_USER_PREFERENCE,
} from '../constants/BiaDependencyAssessmentActionTypes';
import EDITABLE_LIST_GRID_ACTION_TYPES from '../../EditableList/constants/EditableListGridActionTypes';
import {
	FETCH_ERROR_MESSAGE,
	NULL_TABLE_ERROR_MESSAGE,
	GRID_CONFIGURATION,
	DEFAULT_VIEW_NAME,
	NON_IT_ASSET_VIEW_NAME,
	IT_ASSET_VIEW_NAME,
	ELEMENT_USER_PREFERENCE_URL,
	ELEMENT_USER_PREFERENCE_ERROR_MESSAGE,
	USER_PREFERNCE_PREFIX_DEPENDENCY,
} from '../constants/BiaDependencyAssessmentConstants';
import dependencyAssessmentDataQuery from '../queries/BiaDependencyAssessmentQuery';
import { ACCORDION_STATUS } from '../../RelatedItemAccordionList/constants/RelatedItemAccordionListConstants';
import { USER_PREFERENCE_PREFIX } from '../../../util/grid-util/constants/GeneralConstants';
import { USER_PREFERENCE_ACTION_TYPES } from '../../../util/grid-util/constants/UserPreferenceActionTypes';

const requestDependencyAssessmentData = createGraphQLEffect(
	dependencyAssessmentDataQuery,
	{
		templateVarList: [
			'parentRecordSysId',
			'tableName',
			'dependencyAssessmentChildTable',
			'dependencyAssessmentStatusField',
			'accordionDescriptionField',
			'accordionHeaderField',
			'dependencyElementReferenceColumnField',
			'dependencyAssessmentRecoveryTimeField',
			'parentTableReferenceField',
		],
		successActionType: DEPENDENCY_ASSESSMENT_DATA_SUCCEEDED,
		errorActionType: DEPENDENCY_ASSESSMENT_DATA_FAILED,
	}
);

const getDependencyAssessmentDataQuerySuccess = ({
	action,
	state,
	updateState,
	dispatch,
}) => {
	const fetchResult = action.payload;
	const fetchErrors = fetchResult.errors;
	const {
		tableName,
		dependencyAssessmentStatusField,
		dependencyElementReferenceColumnField,
		accordionHeaderField,
		accordionDescriptionField,
		dependencyAssessmentChildTableField,
		dependencyAssessmentChildTable,
	} = state.properties;

	if (fetchErrors.length) {
		console.error(FETCH_ERROR_MESSAGE);
		console.error(fetchErrors);
		updateState({
			error: true,
			isLoading: false,
		});

		return;
	}

	const dataReturned = get(
		fetchResult,
		`data.GlideRecord_Query.${tableName}._results`
	);

	const userPreferenceNames =[];
	let tableRecords = [];

	if(dataReturned.length) {

		tableRecords = dataReturned.map((record) => {
			record[dependencyAssessmentStatusField] = get(
				record,
				`${dependencyAssessmentStatusField}.value`
			);

			record[
				`${dependencyElementReferenceColumnField}_${accordionHeaderField}`
			] = get(
				record,
				`${dependencyElementReferenceColumnField}._reference.${accordionHeaderField}.value`
			);
			record[
				`${dependencyElementReferenceColumnField}_${accordionDescriptionField}`
			] = get(
				record,
				`${dependencyElementReferenceColumnField}._reference.${accordionDescriptionField}.value`
			);
			const requireDataBackup = get(record, `requires_data_backup.value`);

			record.currentlySelectedEncodedQuery = '';
			// Doing a shallow copy to avoid modification of reference object.
			// Here we have multiple records and each of them can have independent grid configuration
			record.gridConfig = { ...GRID_CONFIGURATION };
			if (record[dependencyAssessmentStatusField] === ACCORDION_STATUS.COMPLETE) {
				record.readOnly = true;
			} else {
				record.readOnly = false;
			}

			record.view = DEFAULT_VIEW_NAME;

			if (requireDataBackup) {
				if (requireDataBackup === "no") {
					record.view = NON_IT_ASSET_VIEW_NAME;
				} else {
					record.view = IT_ASSET_VIEW_NAME;
				}
			}

			record.queryCondition = `${dependencyAssessmentChildTableField}=${record.sys_id.value}`;
			record.allowColumnHidding = true;

			const accordionHeaderFieldName = record[`${dependencyElementReferenceColumnField}_${accordionHeaderField}`];
			userPreferenceNames.push(`${USER_PREFERNCE_PREFIX_DEPENDENCY}.${accordionHeaderFieldName}`);
			record.userPreferenceName = `${USER_PREFERENCE_PREFIX}.${dependencyAssessmentChildTable}.${record.view}`;
			record.isBiaDependency = true;
		
			delete record._query;
			delete record[dependencyElementReferenceColumnField];

			return record;
		});

		if (!tableRecords) {
			console.error(NULL_TABLE_ERROR_MESSAGE);
			updateState({
				error: true,
				isLoading: false,
			});

			return;
		}

		updateState({
			_listOfRecords: tableRecords
		});

		dispatch(ELEMENT_USER_PREFERENCE_REQUESTED, {
			userPreferenceNames: JSON.stringify(userPreferenceNames),
		});
	} else {
		updateState({
			_listOfRecords: tableRecords,
			isLoading: false,
		});
	}
};

const getDependencyAssessmentDataQueryFailed = ({ action, updateState }) => {
	const error = action.payload;

	console.error(FETCH_ERROR_MESSAGE);
	console.error(error);
	updateState({
		error: true,
		isLoading: false,
	});
};

const actionHandlers = {
	[DEPENDENCY_ASSESSMENT_DATA_REQUESTED]: requestDependencyAssessmentData,
	[DEPENDENCY_ASSESSMENT_DATA_SUCCEEDED]: {
		effect: getDependencyAssessmentDataQuerySuccess,
		stopPropogation: true,
	},
	[DEPENDENCY_ASSESSMENT_DATA_FAILED]: {
		effect: getDependencyAssessmentDataQueryFailed,
		stopPropogation: true,
	},

	[EDITABLE_LIST_GRID_ACTION_TYPES.EDITABLE_LIST_RECORD_SELECTED_COUNT_UPDATED]: ({
		action,
		state,
		updateState,
	}) => {
		const { payload } = action;
		const { tableRecords } = state;
		const {
			recordSelectedWithParentContext,
			parentRecordIdentification,
		} = payload;
		let encodedQuery = '';
		const selectedRecords =
			recordSelectedWithParentContext[parentRecordIdentification];

		if (selectedRecords && selectedRecords.length > 0) {
			encodedQuery = 'sys_idIN';

			for (let i = 0; i < selectedRecords.length; i++) {
				encodedQuery += `${selectedRecords[i]},`;
			}
		}

		encodedQuery = encodedQuery.substr(0, encodedQuery.length - 1);
		const updatedRecords = tableRecords.map((item) => {
			if (item.sys_id.value === parentRecordIdentification) {
				item.currentlySelectedEncodedQuery = encodedQuery;
			}

			return item;
		});

		updateState({
			tableRecords: updatedRecords,
		});
	},
	[SET_ELEMENT_USER_PREFERENCE]: ({ action, state, updateState, dispatch }) => {
		const { payload } = action;
		const { parentRecordIdentification, preferenceValue } = payload;
		const { tableRecords, properties } = state;
		const {
			dependencyElementReferenceColumnField,
			accordionHeaderField,
		} = properties;
		let elementName;

		const updatedRecords = tableRecords.map((record) => {
			if (record.sys_id.value == parentRecordIdentification) {
				elementName =
					record[
						`${dependencyElementReferenceColumnField}_${accordionHeaderField}`
					];
			}
			return record;
		});
		dispatch(USER_PREFERENCE_ACTION_TYPES.SET_USER_PREFERENCE, {
			preferenceName: `${USER_PREFERNCE_PREFIX_DEPENDENCY}.${elementName}`,
			preferenceValue,
		});
		updateState({
			tableRecords: updatedRecords,
		});
	},
	[ELEMENT_USER_PREFERENCE_REQUESTED]: createHttpEffect(
		ELEMENT_USER_PREFERENCE_URL,
		{
			method: 'GET',
			queryParams: ['userPreferenceNames'],
			successActionType: ELEMENT_USER_PREFERENCE_SUCCEEDED,
			errorActionType: ELEMENT_USER_PREFERENCE_FAILED,
			batch: false,
		}
	),
	[ELEMENT_USER_PREFERENCE_SUCCEEDED]: ({ action, state, updateState }) => {
		const { payload } = action;
		const { result } = payload;
		const { _listOfRecords, properties } = state;
		const {
			dependencyElementReferenceColumnField,
			accordionHeaderField,
		} = properties;

		if (result.length) {
			result.map((userPreference) => {
				_listOfRecords.map((record) => {
					const elementName =
						record[
							`${dependencyElementReferenceColumnField}_${accordionHeaderField}`
						];
					if (
						`${USER_PREFERNCE_PREFIX_DEPENDENCY}.${elementName}` == userPreference.name
					) {
						record.userPreferenceName = `${USER_PREFERNCE_PREFIX_DEPENDENCY}.${elementName}`;
					}
				});
			});
		}
		updateState({
			tableRecords: _listOfRecords,
			isLoading: false,
		});
	},
	[ELEMENT_USER_PREFERENCE_FAILED]: ({ action, updateState }) => {
		const { data } = action.payload;
		const { error } = data;
		const { message } = error;

		console.error(data);
		console.error(ELEMENT_USER_PREFERENCE_ERROR_MESSAGE);
		updateState({
			errorMessage: message,
			isLoading: false,
		});
	},
};

export default actionHandlers;
