import { createHttpEffect } from '@servicenow/ui-effect-http';
import { get } from 'lodash';
import {
	RELATED_ASSET_DEPENDENCY,
	BIA_PLUGIN_INSTALLATION_STATUS,
} from '../constants/RelatedAssetDependencyItemPickerModalActionTypes';
import {
	IS_BIA_PLUGIN_INSTALLED_QUERY_URL,
	BIA_PLUGIN_INSTALLATION_FAILURE_MESSAGE,
	MODAL_VIEW_TYPES,
	RELATED_ASSET_DEPENDENCY_TABLE,
} from '../constants/RelatedAssetDependencyItemPickerModalConstants';

const checkBiaPluginInstallation = createHttpEffect(
	IS_BIA_PLUGIN_INSTALLED_QUERY_URL,
	{
		method: 'GET',
		startActionType: BIA_PLUGIN_INSTALLATION_STATUS.STARTED,
		successActionType: BIA_PLUGIN_INSTALLATION_STATUS.SUCCEEDED,
		errorActionType: BIA_PLUGIN_INSTALLATION_STATUS.FAILED,
		batch: false,
	}
);

const checkBiaPluginInstallationSuccess = ({
	properties,
	action,
	updateState,
	dispatch,
}) => {
	const { parentRecordSysId } = properties;

	const isBiaInstalled = get(action, 'payload.result.isBiaInstalled');

	if (!isBiaInstalled) {
		dispatch(RELATED_ASSET_DEPENDENCY.EXCLUDED_ITEMS_QUERY_REQUESTED, {
			parentRecordSysId,
			excludedItemsTableIdentifier: RELATED_ASSET_DEPENDENCY_TABLE,
		});
		updateState({
			currentScreenView: MODAL_VIEW_TYPES.RECORD_LIST_PICKER,
		});
	} else {
		updateState({
			currentScreenView: MODAL_VIEW_TYPES.RADIO_BUTTONS,
		});
	}
};

const checkBiaPluginInstallationFailure = ({ action }) => {
	const { data } = action.payload;

	console.error(data);
	updateState({
		isLoading: false,
		errorMessage: BIA_PLUGIN_INSTALLATION_FAILURE_MESSAGE,
	});
};

const actionHandlers = {
	[BIA_PLUGIN_INSTALLATION_STATUS.REQUESTED]: checkBiaPluginInstallation,
	[BIA_PLUGIN_INSTALLATION_STATUS.SUCCEEDED]: checkBiaPluginInstallationSuccess,
	[BIA_PLUGIN_INSTALLATION_STATUS.FAILED]: checkBiaPluginInstallationFailure,
};

export { actionHandlers };
