import {
	ITEM_PICKER_EDIT_SAVED,
	ITEM_PICKER_EDIT_CANCELLED,
	ITEM_INSERTION_REQUESTED,
	BIA_ASSET_ITEM_INSERTION_REQUESTED,
	ITEM_PICKER_EDIT_NEXT,
	IMPACTED_ITEM_TYPE_SELECTED,
	RECOVERY_EVENT_EXCLUDED_ITEMS_QUERY_REQUESTED,
	RECOVERY_EVENT_ASSET_ITEM_INSERTION_REQUESTED,
} from '../constants/ItemPickerModalActionTypes';
import {
	SELECT_ITEM_TYPE_ERROR_MESSAGE,
} from '../constants/ItemPickerModalConstants';

const actions = {
	[ITEM_PICKER_EDIT_CANCELLED]: {
		private: false,
	},
	[ITEM_PICKER_EDIT_SAVED]: {
		private: true,
	},
	[ITEM_PICKER_EDIT_NEXT]: {
		private: true,
	},
};

const cancelItemPickerEdit = ({ updateState }) => {
	updateState({
		isModalOpen: false,
	});
};

const saveItemPickerEdit = ({ state, dispatch, updateState }) => {
	const { selectionTable, properties, currentlySelectedEncodedQuery } = state;
	const {
		table,
		parentRecordSysId,
		itemInsertionLoadingMessage,
		parentTableReferenceField,
		itemTableFieldName,
		itemFieldName,
		isImpactAnalysis,
		isRecoveryEvent,
	} = properties;

	let actionToBeDispatched;

	if (isImpactAnalysis) {
		actionToBeDispatched = BIA_ASSET_ITEM_INSERTION_REQUESTED;
	} else if (isRecoveryEvent) {
		actionToBeDispatched = RECOVERY_EVENT_ASSET_ITEM_INSERTION_REQUESTED;
	} else {
		actionToBeDispatched = ITEM_INSERTION_REQUESTED;
	}

	const listOfSelectedItemsSysIds = currentlySelectedEncodedQuery
		.substring(
			currentlySelectedEncodedQuery.indexOf('N') + 1,
			currentlySelectedEncodedQuery.length
		)
		.split(',');

	dispatch(actionToBeDispatched, {
		data: {
			targetTableIdentifier: table,
			itemTableFieldNameValue: selectionTable,
			itemTableFieldName,
			itemFieldName,
			parentRecordSysId,
			parentTableReferenceField,
			listOfSelectedItemsSysIds,
		},
	});

	updateState({
		loadingMessage: itemInsertionLoadingMessage,
		isLoading: true,
	});
};

const showListPickerElement = ({ state, dispatch, updateState }) => {
	const { properties, selectionTable } = state;
	const { table, parentRecordSysId } = properties;

	if (!selectionTable) {
		updateState({
			isLoading: false,
			selectItemTypeError: SELECT_ITEM_TYPE_ERROR_MESSAGE,
		});
	} else {
		dispatch(RECOVERY_EVENT_EXCLUDED_ITEMS_QUERY_REQUESTED, {
			parentRecordSysId,
			excludedItemsTableIdentifier: table,
		});

		updateState({
			isItemTypeSelected: true,
			selectItemTypeError: '',
		});
	}
};

const actionHandlers = {
	[ITEM_PICKER_EDIT_CANCELLED]: cancelItemPickerEdit,
	[ITEM_PICKER_EDIT_SAVED]: saveItemPickerEdit,
	[ITEM_PICKER_EDIT_NEXT]: showListPickerElement,
	[IMPACTED_ITEM_TYPE_SELECTED]: ({action, updateState}) => {
		const { payload } = action;
		
		updateState({
			selectionTableLabel: payload.item.label,
			selectionTable: payload.item.id,
			selectionTableFields : payload.item.fields,
			selectItemTypeError: '',
			disableNextButton: false,
		});
	}
}

export { actions, actionHandlers };
