import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './ImpactAssessmentScoreCardView';
import styles from './ImpactAssessmentScoreCardElement.scss';
import lifecycleActionHandlers from './actions/ImpactAssessmentScoreCardLifeCycleActions';
import impactAssessmentActionHandlers from './actions/ImpactAssessmentScoreCardDataFetchActions';
import {
	LOADING_MESSAGE,
	RTO_LABEL,
	RECOVERY_TIER_LABEL,
	CARD_HEADER_LABEL,
} from './constants/ImpactAssessmentScoreCardConstants';
import { recordWatcherBehavior } from '../../behaviors/RecordWatcherBehavior';

const TAG_NAME = 'sn-bcm-impact-assessment-result-score-card';

/**
 * A BIA resullt card component, intended for use on workspace Ribbon.
 * Displays impact assessment result card with three fields from bia_analysis table.
 *
 * ```
 * <sn-bcm-impact-assessment-result-score-card
 * 		sysId="biaSysId"
 * ></sn-bcm-impact-assessment-result-score-card>
 *
 * ```
 *
 * @seismicElement sn-bcm-impact-assessment-result-score-card
 * @summary Displays impact assessment result card with three fields from bia_analysis table.
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,

	properties: {
		/**
		 * SysId of the record the result will be displayed on
		 *
		 * @type {string}
		 */
		sysid: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the recovery time objective
		 *
		 * @type {string}
		 */
		recoveryTimeObjectiveLabel: {
			default: RTO_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the recovery tier
		 *
		 * @type {string}
		 */
		recoveryTierLabel: {
			default: RECOVERY_TIER_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label of the card header of score card
		 *
		 * @type {string}
		 */
		cardHeaderLabel: {
			default: CARD_HEADER_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Boolean to enable the icon button
		 *
		 * @type {boolean}
		 */
		isActionButtonDisplayed: {
			required: true,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Related Item Tab Index number for icon button
		 *
		 * @type {number}
		 */
		relatedItemTabIndex: {
			default: 0,
			required: false,
			schema: {
				type: 'number',
			},
		},
		/**
		 * If enabled, a record watcher will keep this component updated in real-time.
		 *
		 * @type {boolean}
		 */
		isRecordWatcherEnabled: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
	},
	initialState: {
		/**
		 * State of the component whether it is loaded or not
		 *
		 * @type {boolean}
		 */
		isLoading: true,
		/**
		 * List of choices of the recovery tier
		 *
		 * @type {number}
		 */
		recoveryTierChoice: null,
		/**
		 * Length of the recovery time objective hours
		 *
		 * @type {number}
		 */
		recoveryTimeObjectiveLength: null,
	},
	behaviors: [recordWatcherBehavior],
	actionHandlers: {
		...lifecycleActionHandlers,
		...impactAssessmentActionHandlers,
	},
	styles,
});

export default TAG_NAME;
