import { actionTypes } from '@servicenow/ui-core';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import parentTableDataQuery from '../queries/TableDataquery';
import {
	FETCH_PARENT_TABLE_METADATA_REQUESTED,
	FETCH_PARENT_TABLE_METADATA_SUCCEEDED,
	FETCH_PARENT_TABLE_METADATA_FAILED,
} from '../constants/PlanAssetsListActionTypes';
import { PARENT_TABLE_METADATA_FETCH_ERROR_MESSAGE } from '../constants/PlanAssetsListConstants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const fetchParentTableMetaData = createGraphQLEffect(parentTableDataQuery, {
	variableList: ['parentRecordSysId'],
	templateVarList: ['parentTable'],
	successActionType: FETCH_PARENT_TABLE_METADATA_SUCCEEDED,
	errorActionType: FETCH_PARENT_TABLE_METADATA_FAILED,
});

const getParentTableMetaDataQuerySuccess = ({
	action,
	updateState,
	properties,
}) => {
	const fetchResult = action.payload;
	const fetchErrors = fetchResult.errors;

	if (fetchErrors.length) {
		console.error(PARENT_TABLE_METADATA_FETCH_ERROR_MESSAGE);
		console.error(fetchErrors);

		return;
	}

	const { parentTable } = properties;
	const parentRecordCanWrite = get(
		action.payload,
		`data.GlideRecord_Query.${parentTable}._results[0]._row_data.canWrite`
	);

	updateState({
		isParentRecordEditable: parentRecordCanWrite,
	});
};

const getParentTableMetaDataQueryFailure = ({ action }) => {
	const error = action.payload;

	console.error(PARENT_TABLE_METADATA_FETCH_ERROR_MESSAGE);
	console.error(error);
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ dispatch, state }) => {
		const { properties } = state;
		const { parentRecordSysId, parentTable } = properties;

		dispatch(FETCH_PARENT_TABLE_METADATA_REQUESTED, {
			parentRecordSysId,
			parentTable,
		});
	},
	[FETCH_PARENT_TABLE_METADATA_REQUESTED]: fetchParentTableMetaData,
	[FETCH_PARENT_TABLE_METADATA_SUCCEEDED]: getParentTableMetaDataQuerySuccess,
	[FETCH_PARENT_TABLE_METADATA_FAILED]: getParentTableMetaDataQueryFailure,
};

export default actionHandlers;
