import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './RelatedAssetDependencyItemPickerModalStyles.scss';
import view from './RelatedAssetDependencyItemPickerModalView';
import { actionHandlers as lifecycleActionHandlers } from './actions/LifecycleActions';
import { actionHandlers as selectionTableActionHandlers } from './actions/RelatedAssetDependencyActions';
import { actionHandlers as itemPickerModalOverrideActionHandlers } from './actions/ItemPickerModalOverrideActions';
import { actionHandlers as modalButtonActionHandlers } from './actions/ModalButtonActions';
import { actionHandlers as biaPluginStatusActionHandlers } from './actions/BiaPluginStatusActions';
import { actionHandlers as dependencyFilterSelectionActionHandlers } from './actions/DependencyFilterSelectionActions';
import { actionHandlers as nowItemPickerInteractionActionHandlers } from './actions/NowRecordListPickerIntercationActions';
import {
	actions as itemPickerCommonActions,
	actionHandlers as itemPickerCommonActionHandlers,
} from './actions/ItemPickerCommonActions';
import {
	MODAL_VIEW_TYPES,
	DEFAULT_ITEM_PICKER_LOADING_MESSAGE,
} from './constants/RelatedAssetDependencyItemPickerModalConstants';

const TAG_NAME = 'sn-bcm-plan-dependency-item-picker-modal';

/**
 *
 * Modal component used to add items in bulk to loss scenarios related asset dependency
 *
 * ```
 * <sn-bcm-plan-dependency-item-picker-modal>
 * 	parentRecordSysId="8ad1e3c453d3201040c4ddeeff7b12a1"
 * 	paginationRecordLimit={5}
 * </sn-bcm-plan-dependency-item-picker-modal>
 * ```
 *
 * @seismicElement sn-bcm-plan-dependency-item-picker-modal
 * @summary Modal component used to add items in bulk to loss scenarios related asset dependency
 */
createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
	},
	view,
	properties: {
		/**
		 * SysId of the parent record
		 * @type {String}
		 */
		parentRecordSysId: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The maximum number of rows to show (per page) in the modal.
		 * @type {Number}
		 */
		paginationRecordLimit: {
			default: 5,
			required: false,
			schema: {
				type: 'number',
			},
		},
	},
	initialState: {
		isModalOpen: true,
		elementDefinitionLabel: '',
		selectionTable: '',
		selectionTableFields: '',
		fetchedTableEncodedQuery: '',
		recordListPickerFilterConditions: '',
		currentlySelectedEncodedQuery: '',
		radioSelectionValue: '',
		prevScreenView: null,
		currentScreenView: MODAL_VIEW_TYPES.RADIO_BUTTONS,
		isLoading: true,
		loadingViewMessage: DEFAULT_ITEM_PICKER_LOADING_MESSAGE,
		errorMessage: '',
		selectedPlanScopeValue: {},
		planScopeItemsList: [],
		listPickerOptionalProps: {},
	},
	actions: {
		...itemPickerCommonActions,
	},
	actionHandlers: {
		...lifecycleActionHandlers,
		...selectionTableActionHandlers,
		...itemPickerModalOverrideActionHandlers,
		...modalButtonActionHandlers,
		...itemPickerCommonActionHandlers,
		...biaPluginStatusActionHandlers,
		...dependencyFilterSelectionActionHandlers,
		...nowItemPickerInteractionActionHandlers,
	},
	styles,
});

export default TAG_NAME;
