import { actionTypes } from '@servicenow/ui-core';
import { BIA_ITEM_PICKER_TABLE_NAME_REQUESTED } from '../constants/BiaDependencyItemPickerModalActionTypes';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ state, dispatch }) => {
		const { properties } = state;
		const {
			parentTable,
			parentRecordSysId,
			elementDefinitionTableReferenceField,
		} = properties;

		dispatch(BIA_ITEM_PICKER_TABLE_NAME_REQUESTED, {
			parentRecordSysId,
			parentTable,
			elementDefinitionTableReferenceField,
		});
	},
};

export { actionHandlers };
