import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { get } from 'lodash';
import { EVENT_DATA_FETCH_ACTIONS } from '../constants/RecoveryEventResultDataActionTypes';
import {
	EVENT_RESULT_DATA_FETCH_ERROR_MESSAGE,
	EVENT_TABLE_DATA_QUERY_URL,
	FETCH_EVENT_TABLE_DATA_ERROR_MESSAGE,
	FETCH_EVENT_STATE_ERROR_MESSAGE,
	EVENT_STATE_QUERY_URL,
} from '../constants/RecoveryEventResultConstants';
import { EVENT_RESULT_HIERARCHY_QUERY } from '../queries/RecoveryEventResultDataQuery';
import {
	parseNestedData,
	transformColumnDefintion,
} from '../util/RecoveryEventResultUtil';

const fetchEventResultData = createGraphQLEffect(EVENT_RESULT_HIERARCHY_QUERY, {
	templateVarList: [
		'tableName',
		'eventSysId',
		'columns',
		'userPreferenceName',	
		'eventAssetRecordLoadLimit',
		'activatedPlanRecordLoadLimit',
		'eventTaskRecordLoadLimit',
	],
	successActionType: EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_RESULT_DATA_SUCCEEDED,
	errorActionType: EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_RESULT_DATA_FAILED,
});

const fetchEventResultDataSuccess = ({
	action,
	updateState,
	state,
	dispatch,
}) => {
	const fetchResult = action.payload;
	const fetchErrors = fetchResult.errors;
	const {
		properties,
		showAdHocTaskButton,
		showAddAssetsButton,
		showAddPlansButton,
	} = state;
	const { columns, groupColumnName, tableName, eventSysId } = properties;

	if (fetchErrors.length > 0) {
		console.error(EVENT_RESULT_DATA_FETCH_ERROR_MESSAGE);
		console.error(fetchErrors);

		updateState({
			isLoading: false,
		});

		return;
	}

	const dataReturned = get(
		action.payload,
		`data.GlideRecord_Query.${tableName}._results`
	);

	const _userPreferences = get(
		action.payload,
		'data.GlideDomain_Query.user.preferences[0].value',
		{}
	);
	const _userPreferenceArr = _userPreferences
		? JSON.parse(_userPreferences)
		: _userPreferences;

	let _transformedColumns = columns;
	let _transformedRecords = [];
	let isUserPreferenceWidth = false;

	if(dataReturned.length) {

		dataReturned.map((record) => {
		let assetRecord = {};
			assetRecord['id'] = get(record, 'sys_id.value');

			if (get(record, '_query')) {
				const _activatedPlanData = get(
					record,
					`_query.queryWithSysId.sn_recovery_activated_plan._results`
				);
				_activatedPlanData.map((planRecord) => {
					if (get(planRecord, '_query')) {
						const _eventTaskData = get(
							planRecord,
							`_query.queryWithSysId.sn_recovery_event_task._results`
						);
						const taskRecords = parseNestedData(
							_eventTaskData,
							columns,
							planRecord,
							'sn_recovery_event_task'
						);
						_transformedRecords = [..._transformedRecords, ...taskRecords];
						_transformedColumns = transformColumnDefintion(
							taskRecords[0],
							columns,
							groupColumnName,
							_userPreferenceArr
						);
					}
				});
				const planRecords = parseNestedData(
					_activatedPlanData,
					columns,
					record,
					'sn_recovery_activated_plan'
				);
				_transformedRecords = [..._transformedRecords, ...planRecords];
				_transformedColumns = transformColumnDefintion(
					planRecords[0],
					_transformedColumns,
					groupColumnName,
					_userPreferenceArr
				);
			}

			_transformedColumns = transformColumnDefintion(
				record,
				_transformedColumns,
				groupColumnName,
				_userPreferenceArr
			);
			assetRecord.tableName = 'sn_recovery_event_asset';
			columns.map((column) => {
				assetRecord[column.name] = get(record, `${column.name}`);
			});
			_transformedRecords.push(assetRecord);
		});
	} else {
		_transformedColumns = transformColumnDefintion(
			null,
			columns,
			groupColumnName,
			_userPreferenceArr
		);
	}

	const listOfRecords = _transformedRecords;

	const _eventAssetCreateAccess = get(
		action.payload,
		`data.GlideRecord_Query.sn_recovery_event_asset._table_metadata.canCreate`
	);

	const _activatedPlanCreateAccess = get(
		action.payload,
		`data.GlideRecord_Query.sn_recovery_activated_plan._table_metadata.canCreate`
	);

	const _eventTaskCreateAccess = get(
		action.payload,
		`data.GlideRecord_Query.sn_recovery_event_task._table_metadata.canCreate`
	);
	if(_userPreferenceArr && _userPreferenceArr.length ){
		isUserPreferenceWidth = true;
	}

	updateState({
		columns: _transformedColumns,
		listOfRecords,
		hasOffsetWidth: isUserPreferenceWidth,
		showAdHocTaskButton: _eventTaskCreateAccess && showAdHocTaskButton,
		showAddAssetsButton: _eventAssetCreateAccess && showAddAssetsButton,
		showAddPlansButton:
			_activatedPlanCreateAccess &&
			_eventAssetCreateAccess &&
			showAddPlansButton,
		isLoading: false,
	});
};

const fetchEventResultDataFailure = ({ action, updateState }) => {
	const error = action.payload;

	console.error(EVENT_RESULT_DATA_FETCH_ERROR_MESSAGE);
	console.error(error);

	updateState({
		isLoading: false,
	});
};

const fetchEventTableData = createHttpEffect(EVENT_TABLE_DATA_QUERY_URL, {
	method: 'GET',
	queryParams: ['eventSysId'],
	successActionType: EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_TABLE_DATA_SUCCEEDED,
	errorActionType: EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_TABLE_DATA_FAILED,
	batch: false,
});

const fetchEventTableDataSuccess = ({ action, state, updateState }) => {
	const { result } = action.payload;
	const { showAdHocTaskButton } = state;

	if (result.error) {
		console.error(FETCH_EVENT_TABLE_DATA_ERROR_MESSAGE);
		console.error(result.error);

		updateState({
			isLoading: false,
		});

		return;
	}

	const canShowAndCreateTask = get(result, 'showAndCreateTask');

	updateState({
		showAdHocTaskButton: showAdHocTaskButton && canShowAndCreateTask,
		isLoading: false,
	});
};

const fetchEventTableDatafailure = ({ action, updateState }) => {
	const error = action.payload;

	console.error(FETCH_EVENT_TABLE_DATA_ERROR_MESSAGE);
	console.error(error);

	updateState({
		isLoading: false,
	});
};

const fetchEventStateAttributes = createHttpEffect(EVENT_STATE_QUERY_URL, {
	method: 'GET',
	queryParams: ['eventSysId'],
	successActionType:
		EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_TABLE_STATE_ATTRIBUTES_SUCCEEDED,
	errorActionType:
		EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_TABLE_STATE_ATTRIBUTES_FAILED,
	batch: false,
});

const fetchEventStateAttributesSuccess = ({ action, state, updateState }) => {
	const { result } = action.payload;

	if (result.error) {
		console.error(FETCH_EVENT_STATE_ERROR_MESSAGE);
		console.error(result.error);

		updateState({
			isLoading: false,
		});

		return;
	}

	const pendingStates = get(result, 'pending');
	const inProgressStates = get(result, 'InProgress');
	const closeStates = get(result, 'close');
	const status = get(result, 'state');

	updateState({
		eventProgressStates: inProgressStates,
		eventCloseStates: closeStates,
		eventPendingStates: pendingStates,
		isLoading: false,
	});

	if (closeStates && closeStates.indexOf(status) != -1) {
		updateState({
			showAdHocTaskButton: false,
			showAddAssetsButton: false,
			showAddPlansButton: false,
			isPlanModalOpen: false,
			isEventInProgress: false,
			isLoading: false,
		});
	}
};

const fetchEventStateAttributefailure = ({ action, updateState }) => {
	const error = action.payload;

	console.error(FETCH_EVENT_STATE_ERROR_MESSAGE);
	console.error(error);

	updateState({
		isLoading: false,
	});
};

const actionHandlers = {
	[EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_RESULT_DATA_REQUESTED]: fetchEventResultData,
	[EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_RESULT_DATA_SUCCEEDED]: fetchEventResultDataSuccess,
	[EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_RESULT_DATA_FAILED]: fetchEventResultDataFailure,
	[EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_TABLE_DATA_REQUESTED]: fetchEventTableData,
	[EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_TABLE_DATA_SUCCEEDED]: fetchEventTableDataSuccess,
	[EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_TABLE_DATA_FAILED]: fetchEventTableDatafailure,
	[EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_TABLE_STATE_ATTRIBUTES_REQUESTED]: fetchEventStateAttributes,
	[EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_TABLE_STATE_ATTRIBUTES_SUCCEEDED]: fetchEventStateAttributesSuccess,
	[EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_TABLE_STATE_ATTRIBUTES_FAILED]: fetchEventStateAttributefailure,
};

export default actionHandlers;
