import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './RichTextContentCardView';
import styles from './RichTextContentCardElement.scss';
import updateHtmlContentsActionHandlers from './actions/UpdateHtmlContentsActions';
import resetContentModalActionHandlers from './actions/ResetContentModalActions';
import lifecycleActionHandlers from './actions/LifecycleActions';

const TAG_NAME = 'sn-bcm-rich-text-content-card';

/**
 * Card component to render document html into text form.
 * Uses tinymce edior for displaying the html as a text content.
 *
 * ```
 * import '@relative-path/app-bcm-components';
 * OR
 * import RichTextContentCard from '@relative-path/app-bcm-components';
 *
 * <sn-bcm-rich-text-content-card
 * 		title="Title goes here"
 * 		description="description of card"
 * 		contents="<p>hello world!</p>"
 * 		status="pending"
 * 		recordSysId="recordssys_id"
 * 		tableName="sn_xx_tablexx"
 * 		defaultPlanDocumentText="<p>Default Text for reset</p>"
 * ></sn-bcm-rich-text-content-card>
 * ```
 *
 * @seismicElement sn-bcm-rich-text-content-card
 * @summary Card to view html in text form, and contents can be saved, edited, or resetted.
 */

createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
	},
	view,
	properties: {
		/**
		 * HTML contents of card document, to be displayed inside editor.
		 *
		 * @type {string}
		 */
		contents: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Description of the card document
		 *
		 * @type {string}
		 */
		description: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Card activity status, to be displayed on the card's header badge
		 *
		 * @type {string}
		 */
		status: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Heading/Title of the card
		 *
		 * @type {string}
		 */
		title: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * sys_id of the record data
		 *
		 * @type {string}
		 */
		recordSysId: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Name of the record's table
		 *
		 * @type {string}
		 */
		tableName: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Default html content for reset
		 *
		 * @type {string}
		 */
		defaultPlanDocumentText: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * flag to identify whether record is editable or not.
		 *
		 * @type {boolean}
		 */
		isRecordEditable: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
	},
	initialState: {
		/**
		 * Stores the html content of wsiwyg text editor
		 *
		 * @type {string}
		 */
		richTextContent: '',
		/**
		 * open/close state of reset confirm modal
		 *
		 * @type {boolean}
		 */
		isModalOpen: false,
		/**
		 * focus on html editor when clicked on edit
		 *
		 * @type {boolean}
		 */
		htmlAutoFocus: false,
	},
	dispatches: {
		/**
		 * Dispatched when the user clicks reset button on the card.
		 * @type {{ tableName: string, contentField: string, contentFieldValue: string, sysId: string }}
		 */
		'RICH_TEXT_CONTENT_CARD_RESET': {},
		/**
		 * Dispatched when the user clicks Edit button on the card.
		 * @type {{ tableName: string, field: string, sysId: string, statusFieldVal: string }}
		 */
		'RICH_TEXT_CONTENT_CARD_EDITED': {},
		/**
		 * Dispatched when the user clicks Save/Complete button on the card.
		 * @type {{ tableName: string, contentField: string, statusField: string, sysId: string, contentFieldValue: string, statusFieldValue: string }}
		 */
		'RICH_TEXT_CONTENT_CARD_SAVED': {}
	},
	actionHandlers: {
		...updateHtmlContentsActionHandlers,
		...resetContentModalActionHandlers,
		...lifecycleActionHandlers,
	},

	styles,
});

export default TAG_NAME;
