import { createHttpEffect } from '@servicenow/ui-effect-http';
import { RELATED_LIST_REFRESH_REQUESTED } from '../../ItemPickerModal/constants/ItemPickerModalActionTypes';
import {
	removePlanAssetsUrl,
	ITEM_DELETION_ERROR_MESSAGE,
} from '../constants/PlanAssetsListConstants';
import {
	REMOVE_CLICKED,
	ITEM_DELETION_FAILED,
	ITEM_DELETION_SUCCEEDED,
	ADD_ASSETS_BUTTON_CLICKED,
	REMOVE_ASSETS_BUTTON_CLICKED,
} from '../constants/PlanAssetsListActionTypes';

const actions = {
	[REMOVE_CLICKED]: {
		private: true,
	},
};

const actionHandlers = {
	[REMOVE_CLICKED]: createHttpEffect(removePlanAssetsUrl, {
		method: 'POST',
		dataParam: 'data',
		successActionType: ITEM_DELETION_SUCCEEDED,
		errorActionType: ITEM_DELETION_FAILED,
		batch: false,
	}),
	[ITEM_DELETION_FAILED]: ({ action, updateState, dispatch }) => {
		const { data } = action.payload;
		const { error } = data;
		const { message } = error;

		console.error(data);
		console.error(ITEM_DELETION_ERROR_MESSAGE);
		updateState({
			errorMessage: message,
		});
		dispatch(RELATED_LIST_REFRESH_REQUESTED);
	},
	[ITEM_DELETION_SUCCEEDED]: ({ dispatch }) => {
		dispatch(RELATED_LIST_REFRESH_REQUESTED);
	},
	[REMOVE_ASSETS_BUTTON_CLICKED]: ({ state, dispatch }) => {
		const { currentlySelectedEncodedQuery, properties } = state;
		const { table } = properties;

		dispatch(REMOVE_CLICKED, {
			data: {
				targetTable: table,
				currentlySelectedEncodedQuery,
			},
		});
	},
	[ADD_ASSETS_BUTTON_CLICKED]: ({ updateState }) => {
		updateState({
			isAddItemsModalOpen: true,
		});
	},
};

export { actions, actionHandlers };
