const planStatusMutation = `mutation ($sysId: String!, $statusFieldVal: String!) {
	GlideRecord_Mutation {
		update_{{ tableName }} (
            sys_id: $sysId
            {{ field }}: $statusFieldVal
        ) {
			sys_id {
				value
			}
			{{ field }} {
				value
			}
		}
	}
}`;

export default planStatusMutation;
