import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './PlanExercisePieChartCardView';
import styles from './PlanExercisePieChartCardStyles.scss';
import lifecycleActionHandlers from './actions/LifeCycleActions';
import dataFetchActionHandlers from './actions/DataFetchActions';
import userAccessibilityPreferenceActions from './actions/UserAccessibilityPreferenceActions';

const TAG_NAME = 'sn-bcm-plan-exercise-pie-chart';

/**
 * This component responsible for displaying results of completed exercises grouped by result state inside a donut pie chart card
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-plan-exercise-pie-chart
 * 		plan-sys-id=""
 * ></sn-bcm-plan-exercise-pie-chart>
 * ```
 *
 * @seismicElement sn-bcm-plan-exercise-pie-chart
 * @summary This component responsible for displaying results of completed exercises grouped by result state inside a donut pie chart card
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		 * SysId of the record the overview will be displayed on
		 *
		 * @type {string}
		 */
		planSysId: {
			required: true,
			schema: {
				type: 'string',
			},
		},
	},
	initialState: {
		/**
		 * State of the component whether it is loaded or not
		 *
		 * @type {boolean}
		 */
		isLoading: true,
		/**
		 * Show / hide data labels on pie chard
		 *
		 * @type {boolean}
		 */
		showDataLabels: false,
		/**
		 * Donut pie chart series data
		 *
		 * @type {array}
		 */
		donutSeriesData: [],
		/**
		 * Accessibility mode on pie chart
		 *
		 * @type {boolean}
		 */
		isAccessibilityModeActive: false,
	},
	actionHandlers: {
		...lifecycleActionHandlers,
		...dataFetchActionHandlers,
		...userAccessibilityPreferenceActions,
	},
	styles,
});

export default TAG_NAME;
