import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	ADD_ASSETS_BUTTON_CLICKED,
	REMOVE_ASSETS_BUTTON_CLICKED,
	ITEM_DELETION_REQUESTED,
	ITEM_DELETION_FAILED,
	ITEM_DELETION_SUCCEEDDED,
} from '../constants/CardHeaderButtonsActionTypes';
import {
	REMOVE_BIA_ASSET_URL,
	ITEM_DELETION_ERROR_MESSAGE,
} from '../constants/BiaDependencyAssessmentConstants';

const actionHandlers = {
	[ADD_ASSETS_BUTTON_CLICKED]: ({ updateState, action }) => {
		const { payload } = action;
		const { recordSysId } = payload;

		updateState({
			isAddItemsModalOpen: true,
			recordSysId,
		});
	},
	[REMOVE_ASSETS_BUTTON_CLICKED]: ({ action, state, dispatch }) => {
		const { payload } = action;
		const { currentlySelectedEncodedQuery, recordSysId } = payload;
		const { properties } = state;
		const { dependencyAssessmentChildTable } = properties;

		dispatch(ITEM_DELETION_REQUESTED, {
			data: {
				targetTable: dependencyAssessmentChildTable,
				currentlySelectedEncodedQuery,
				parentRecordSysId: recordSysId,
			},
		});
	},
	[ITEM_DELETION_REQUESTED]: createHttpEffect(REMOVE_BIA_ASSET_URL, {
		method: 'POST',
		dataParam: 'data',
		errorActionType: ITEM_DELETION_FAILED,
		successActionType: ITEM_DELETION_SUCCEEDDED,
		batch: false,
	}),
	[ITEM_DELETION_FAILED]: ({ action, updateState, dispatch }) => {
		const { data } = action.payload;
		const { error } = data;
		const { message } = error;

		console.error(data);
		console.error(ITEM_DELETION_ERROR_MESSAGE);
		updateState({
			errorMessage: message,
		});
	},
	[ITEM_DELETION_SUCCEEDDED]: ({ dispatch, action, state, updateState }) => {
		const { payload } = action;
		const { result } = payload;
		const { requestBody } = result;
		const { parentRecordSysId } = requestBody;
		const { tableRecords } = state;
		const updatedRecords = tableRecords.map((record) => {
			if (record.sys_id.value === parentRecordSysId) {
				if (requestBody.currentlySelectedEncodedQuery) {
					record.currentlySelectedEncodedQuery = '';
				}
			}

			return record;
		});

		updateState({
			tableRecords: updatedRecords,
		});
	},
};

export default actionHandlers;
