import { capitalize } from 'lodash';
import '@servicenow/now-loader';
import '@servicenow/now-accordion';
import '@servicenow/now-button';
import '@servicenow/now-heading';
import '@servicenow/now-highlighted-value';
import '@servicenow/now-card';
import '../EmptyState/EmptyStateElement';
import RichTextContentCard from '../RichTextContentCard/RichTextContentCardElement';
import RecordModal from '../RecordModal/RecordModalElement';
import {
	LOADING_MESSAGE,
	CREATE_NEW_SECTION,
	RECORD_MODAL_DEFAULT_PROPS,
	PLAN_DOCUMENT_STATUS,
} from './constants/GeneralConstants';
import { RECORD_MODAL_OPENED } from './constants/DocumentationRecordModalActionTypes';

const view = (state, { dispatch }) => {
	const {
		tableRecords,
		loading,
		isModalOpen,
		properties,
		showNewButton,
	} = state;
	const { planSysId, tableName, formView } = properties;
	const newDocumentationSectionQueryCondition = `plan=${planSysId}`;

	const loadingView = (
		<div className="plan-documentation">
			<div className="loading-container">
				<now-loader label={LOADING_MESSAGE} />
			</div>
		</div>
	);

	const recordModalSection = () => {
		if (isModalOpen) {
			return (
				<RecordModal
					table={tableName}
					formView={formView}
					queryConditions={newDocumentationSectionQueryCondition}
					isModalOpen={isModalOpen}
					headerLabel={RECORD_MODAL_DEFAULT_PROPS.HEADER_LABEL}
					actionButtonLabel={RECORD_MODAL_DEFAULT_PROPS.ACTION_BUTTON_LABEL}
					cancelButtonLabel={RECORD_MODAL_DEFAULT_PROPS.CANCEL_BUTTON_LABEL}
					sysId={RECORD_MODAL_DEFAULT_PROPS.sysId}
					reloadAfterSave={RECORD_MODAL_DEFAULT_PROPS.reloadAfterSave}
				/>
			);
		}
	};

	const _getRichTextCardElementsList = (documentationSections) => {
		const documentationSectionsArray = [];

		if (documentationSections.length) {
			for (let i = 0; i < documentationSections.length; i++) {
				const {
					sys_id,
					contents,
					status,
					documentTitle,
					description,
					planTemplateTitle,
					default_text,
					isEditable,
				} = documentationSections[i];
				const title = planTemplateTitle || documentTitle;
				const descriptionText = description || '';
				const defaultPlanDocumentText = default_text || '';
				const htmlContents = contents || '';
				const isDocumentRecordEditable = isEditable;
				const accordionItemProps = {
					header: {
						label: title,
						weight: 'bold',
					},
					caption: {
						label: descriptionText,
					},
					expanded: true,
				};
				const statusBackgroundColor =
					status === PLAN_DOCUMENT_STATUS.VALUE_PENDING
						? PLAN_DOCUMENT_STATUS.BG_COLOR_WARNING
						: PLAN_DOCUMENT_STATUS.BG_COLOR_POSITIVE;

				documentationSectionsArray.push(
					<now-accordion-item {...accordionItemProps}>
						<now-highlighted-value
							slot="metadata"
							show-icon
							label={capitalize(status)}
							status={statusBackgroundColor}
						/>
						<RichTextContentCard
							slot="content"
							tableName={tableName}
							recordSysId={sys_id}
							contents={htmlContents}
							isRecordEditable={isDocumentRecordEditable}
							status={status}
							defaultPlanDocumentText={defaultPlanDocumentText}
							key={sys_id}
						/>
					</now-accordion-item>
				);
			}

			return documentationSectionsArray;
		}
	};

	const newButtonElement = showNewButton ? (
		<now-button
			className="new-section-button"
			variant="primary"
			size="md"
			label={CREATE_NEW_SECTION.BUTTON_LABEL}
			tooltipContent={CREATE_NEW_SECTION.BUTTON_TOOLTIP}
			on-click={() => dispatch(RECORD_MODAL_OPENED)}
		/>
	) : null;

	const newDocumentationSectionTopBar = (
		<now-card className="plan-documentation-header-bar">
			<div className="now-align -inline -block-center">
				<now-heading
					className="card-heading"
					variant="header-secondary"
					level="2"
					label={CREATE_NEW_SECTION.HEADING_LABEL}
					hasNoMargin={CREATE_NEW_SECTION.HEADING_HAS_NO_MARGIN}
				/>
				{newButtonElement}
			</div>
		</now-card>
	);

	const cardListView = (viewCardsList) => (
		<div className="plan-documentation">
			<div className="plan-documentation-section -top-bar">
				{newDocumentationSectionTopBar}
				{recordModalSection(isModalOpen)}
			</div>
			<div className="plan-documentation-section -card-list">
				{viewCardsList.length ? (
					<now-accordion
						className="now-accordion-wrapper"
						expand-single={false}
						heading-level="3"
						toggle-icon={{
							type: 'plus-minus',
							position: 'start',
						}}>
						{_getRichTextCardElementsList(viewCardsList)}
					</now-accordion>
				) : (
					<div className="empty-state-wrapper">
						<sn-bcm-empty-state />
					</div>
				)}
			</div>
		</div>
	);

	if (loading) {
		return loadingView;
	}

	return cardListView(tableRecords);
};

export default view;
