import '@servicenow/now-text-link';
import { t } from 'sn-translate';
import RecordModal from '../RecordModal/RecordModalElement';
import {
	SYSID,
	BIA_TABLE,
	PLAN_TABLE,
	EXERCISE_TABLE,
	SUBMIT_BUTTON_LABEL,
	CANCEL_BUTTON_LABEL,
	TYPE_FIELD_EXERCISE,
	TYPE_FIELD_RECOVERY_EVENT,
	TEXT_LINK_STYLE,
	EXERCISE_MODAL_HEADER_LABEL,
	CRISIS_MODAL_HEADER_LABEL,
} from './constants/HomeActionLinksConstants';

const view = (state, { dispatch, updateState }) => {
	const {
		isRecordModalOpen,
		recordModalConfiguration,
		properties,
	} = state;

	const {
		isBiaTextLinkDisplayed,
		isPlanTextLinkDisplayed,
		isExerciseTextLinkDisplayed,
		isRecoveryEventTextLinkDisplayed,
		biaHeaderLabel,
		planHeaderLabel,
		exerciseHeaderLabel,
		recoveryEventHeaderLabel,
		biaFormView,
		planFormView,
		exerciseOrEventFormView,
	} = properties;

	const recordModalElement = (
		<RecordModal
			isModalOpen={isRecordModalOpen}
			{...recordModalConfiguration}
		/>
	);

	const newBIARecordModalConfiguration = {
		table: BIA_TABLE,
		sysId: SYSID,
		formView: biaFormView,
		headerLabel: biaHeaderLabel,
		actionButtonLabel: SUBMIT_BUTTON_LABEL,
		cancelButtonLabel: CANCEL_BUTTON_LABEL,
	};

	const newPlanRecordModalConfiguration = {
		table: PLAN_TABLE,
		sysId: SYSID,
		formView: planFormView,
		headerLabel: planHeaderLabel,
		actionButtonLabel: SUBMIT_BUTTON_LABEL,
		cancelButtonLabel: CANCEL_BUTTON_LABEL,
	};

	const newExerciseRecordModalConfiguration = {
		table: EXERCISE_TABLE,
		sysId: SYSID,
		formView: exerciseOrEventFormView,
		queryConditions: TYPE_FIELD_EXERCISE,
		headerLabel: EXERCISE_MODAL_HEADER_LABEL,
		actionButtonLabel: SUBMIT_BUTTON_LABEL,
		cancelButtonLabel: CANCEL_BUTTON_LABEL,
	};

	const newEventRecordModalConfiguration = {
		table: EXERCISE_TABLE,
		sysId: SYSID,
		formView: exerciseOrEventFormView,
		queryConditions: TYPE_FIELD_RECOVERY_EVENT,
		headerLabel: CRISIS_MODAL_HEADER_LABEL,
		actionButtonLabel: SUBMIT_BUTTON_LABEL,
		cancelButtonLabel: CANCEL_BUTTON_LABEL,
	};

	const homeLinksView = (
		<div className="action-link-background">
			{isBiaTextLinkDisplayed ? (
				<now-text-link
					className="action-link-element create-bia-link"
					label={biaHeaderLabel}
					href="javascript:void(0)"
					variant={TEXT_LINK_STYLE}
					on-click={() => {
						updateState({
							recordModalConfiguration: newBIARecordModalConfiguration,
							isRecordModalOpen: true,
						});
					}}
				/>
			) : null}

			{isPlanTextLinkDisplayed ? (
				<now-text-link
					className="action-link-element create-plan-link"
					label={planHeaderLabel}
					href="javascript:void(0)"
					variant={TEXT_LINK_STYLE}
					on-click={() => {
						updateState({
							recordModalConfiguration: newPlanRecordModalConfiguration,
							isRecordModalOpen: true,
						});
					}}
				/>
			) : null}
			{isExerciseTextLinkDisplayed ? (
				<now-text-link
					className="action-link-element create-exercise-link"
					label={exerciseHeaderLabel}
					href="javascript:void(0)"
					variant={TEXT_LINK_STYLE}
					on-click={() => {
						updateState({
							recordModalConfiguration: newExerciseRecordModalConfiguration,
							isRecordModalOpen: true,
						});
					}}
				/>
			) : null}
			{isRecoveryEventTextLinkDisplayed ? (
				<now-text-link
					className="action-link-element create-recovery-link padding-bottom"
					label={recoveryEventHeaderLabel}
					href="javascript:void(0)"
					variant={TEXT_LINK_STYLE}
					on-click={() => {
						updateState({
							recordModalConfiguration: newEventRecordModalConfiguration,
							isRecordModalOpen: true,
						});
					}}
				/>
			) : null}
			{isRecordModalOpen ? recordModalElement : null}
		</div>
	);

	return homeLinksView;
};

export default view;
