import {
	NOW_RECORD_LIST_ROW_SELECTION_UPDATED,
	NOW_RECORD_LIST_PAGE_UPDATED,
} from '../constants/RelatedAssetDependencyItemPickerModalActionTypes';

const actions = {
	[NOW_RECORD_LIST_ROW_SELECTION_UPDATED]: {
		private: true,
	},
};

const updateElectedEncodedQuery = ({ state, action, updateState }) => {
	const { listPickerOptionalProps } = state;
	const { selectedRecords, query } = action.payload;

	if (selectedRecords && selectedRecords.length) {
		updateState({
			listPickerOptionalProps: {
				...listPickerOptionalProps,
				selectedRecords,
			},
		});
	}

	updateState({
		currentlySelectedEncodedQuery: query,
	});
};

const updateActivePage = ({ state, action, updateState }) => {
	const { listPickerOptionalProps } = state;
	const { page } = action.payload;

	updateState({
		listPickerOptionalProps: {
			...listPickerOptionalProps,
			page: page,
		},
	});
};

const actionHandlers = {
	[NOW_RECORD_LIST_ROW_SELECTION_UPDATED]: {
		effect: updateElectedEncodedQuery,
		stopPropogation: true,
	},
	[NOW_RECORD_LIST_PAGE_UPDATED]: {
		effect: updateActivePage,
		stopPropogation: true,
	},
};

export { actions, actionHandlers };
