const query = ` query getCmdbCiItems ($queryConditions: String!, $maxRecordLimit: Int!) {
	GlideRecord_Query {
        cmdb_rel_ci (queryConditions: $queryConditions, pagination: {limit: $maxRecordLimit}) {
            _results {
                sys_id {
                    value
                }
                parent {
                    value
                    displayValue
                }
                child {
                    value
                    displayValue
                }
                type {
                    value
                    displayValue
                }
            }
        }
    }
}
`;
export default query;
