import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	CURRENT_USER_QUERY_REQUESTED,
	CURRENT_USER_QUERY_SUCCEEDED,
	CURRENT_USER_QUERY_FAILED,
} from '../constants/NewBiaDynamicModalActionTypes';
import { FETCH_ERROR_MESSAGE } from '../constants/NewBiaDynamicModalConstants';
import getCurrentUserQuery from '../queries/CurrentUserQuery';

const requestCurrentUser = createGraphQLEffect(getCurrentUserQuery, {
	successActionType: CURRENT_USER_QUERY_SUCCEEDED,
	errorActionType: CURRENT_USER_QUERY_FAILED,
});

const requestCurrentUserSuccess = ({ state, action, updateState }) => {
	const data = action.payload;
	const { userAssignedColumnName } = state.properties;

	if (data.errors.length) {
		console.error(FETCH_ERROR_MESSAGE);
		console.error(data.errors);
		updateState({
			error: true,
			isLoading: false,
		});

		return;
	}

	const userFullName = get(data, 'data.GlideDomain_Query.user.fullName');
	const userSysID = get(data, 'data.GlideDomain_Query.user.sys_id');

	updateState({
		[userAssignedColumnName]: userSysID,
		[`${userAssignedColumnName}DisplayValue`]: userFullName,
	});
};

const requestCurrentUserFail = ({ action, updateState }) => {
	const data = action.payload;

	console.error(FETCH_ERROR_MESSAGE);
	console.error(data.errors);
	updateState({
		error: true,
		isLoading: false,
	});
};

const actionHandlers = {
	[CURRENT_USER_QUERY_REQUESTED]: requestCurrentUser,
	[CURRENT_USER_QUERY_SUCCEEDED]: {
		effect: requestCurrentUserSuccess,
		stopPropagation: true,
	},
	[CURRENT_USER_QUERY_FAILED]: {
		effect: requestCurrentUserFail,
		stopPropagation: true,
	},
};

export default actionHandlers;
