import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './PlanOverviewLayoutView';
import styles from './PlanOverviewLayoutElement.scss';
import lifecycleActionHandlers from './actions/LifecycleActions';
import exerciseDisplayConditionsActionHandlers from './actions/ExerciseDisplayConditionsActions';

const TAG_NAME = 'sn-bcm-plan-overview-layout';

/**
 * @seismicElement sn-bcm-plan-overview
 * @summary Plan overview component that leverages sn-layout to display reporting widgets
 * 			on the plan Overview tab
 */
createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,

	properties: {
		/**
		 * Sysid of the record the overview will be displayed on
		 *
		 * @type {string}
		 */
		sysId: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Table of the record the overview will be displayed on
		 *
		 * @type {string}
		 */
		table: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Plan Recovery Teams Widget icon link tab index [DEPRECATED]
		 *
		 * @type {string}
		 */
		planRecoveryTeamsTabIndex: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Plan Documentation Widget icon link tab index [DEPRECATED]
		 *
		 * @type {string}
		 */
		planDocumentationTabIndex: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Plan Activities Widget icon link tab index [DEPRECATED]
		 *
		 * @type {string}
		 */
		planActivitiesTabIndex: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Plan Assets Overview Card Widget icon link tab index [DEPRECATED]
		 *
		 * @type {string}
		 */
		planAssetsOverviewCardTabIndex: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
	},
	initialState: {
		planAuthoringType: null,
		isPlanExerciseRowDisplayed: false,
		scopeTabIndex: 1,
		documentationTabIndex: 2,
		recoveryTeamsTabIndex: 3,
		lossScenariosTabIndex: 4,
		recoveryTasksTabIndex: 5,
	},
	actionHandlers: {
		...lifecycleActionHandlers,
		...exerciseDisplayConditionsActionHandlers,
	},
	styles,
});

export default TAG_NAME;
