const SET_USER_PREFERNCE_QUERY = `
	mutation ($preferenceName: String!, $preferenceValue: String!){
		GlideDomain_Mutation {
			user {
			setPreferences(preferences: [
        		{name: $preferenceName, value: $preferenceValue}
      		]) {
        		name
        		value
	  		}  
		} 
	}
}
`;

export default SET_USER_PREFERNCE_QUERY;