import { createAmbSubscriptionEffect } from '@servicenow/ui-effect-amb';
import {
	FETCH_PLAN_ASSET_DETAILS_REQUESTED_REQUESTED,
	RECORD_WATCHER_STATUS_CHANGE_REQUESTED,
	LOSS_SCENARIO_RECORD_WATCHER_STATUS_CHANGED,
	RECOVERY_STRATEGIES_RECORD_WATCHER_STATUS_CHANGED,
	ASSET_DEPENDENCIES_RECORD_WATCHER_STATUS_CHANGED,
	RECORD_WATCHER_MESSAGE_RECEIVED,
	RECORD_WATCHER_SUBSCRIBE_FAILED,
	RECORD_WATCHER_SUBSCRIBE_SUCCEEDED,
	RECORD_WATCHER_UNSUBSCRIBE_SUCCEEDED,
} from '../constants/PlanAssetsOverviewCardActionTypes';
import {
	RECORD_WATCHER_SUBSCIPTION_FAILURE_MESSAGE,
	DEFAULT_ASSET_DEPENDENCY_TABLE,
	DEFAULT_RECOVERY_STRATEGY_TABLE,
} from '../constants/PlanAssetsOverviewCardConstants';

const actions = {
	[RECORD_WATCHER_STATUS_CHANGE_REQUESTED]: {
		private: true,
	},
	[LOSS_SCENARIO_RECORD_WATCHER_STATUS_CHANGED]: {
		private: true,
	},
	[ASSET_DEPENDENCIES_RECORD_WATCHER_STATUS_CHANGED]: {
		private: true,
	},
	[RECOVERY_STRATEGIES_RECORD_WATCHER_STATUS_CHANGED]: {
		private: true,
	},
	[RECORD_WATCHER_MESSAGE_RECEIVED]: {
		private: true,
	},
	[RECORD_WATCHER_SUBSCRIBE_FAILED]: {
		private: true,
	},
};

const updateRecordWatcherStatus = ({ action, dispatch, updateState }) => {
	const { subscribeToRecordWatcher, table, filter } = action.payload;

	let recordWatcherAction;

	switch (table) {
		case DEFAULT_ASSET_DEPENDENCY_TABLE:
			recordWatcherAction = ASSET_DEPENDENCIES_RECORD_WATCHER_STATUS_CHANGED;
			break;
		case DEFAULT_RECOVERY_STRATEGY_TABLE:
			recordWatcherAction = RECOVERY_STRATEGIES_RECORD_WATCHER_STATUS_CHANGED;
			break;
		default:
			recordWatcherAction = LOSS_SCENARIO_RECORD_WATCHER_STATUS_CHANGED;
			updateState({
				isRecordWatcherActive: true,
			});
			break;
	}

	dispatch(recordWatcherAction, {
		table,
		filter,
		subscribe: subscribeToRecordWatcher,
	});
};

const assetDependenciesRecordWatcherHandler = createAmbSubscriptionEffect(
	'/rw/default/:table/:filter',
	{
		subscribeFailedActionType: RECORD_WATCHER_SUBSCRIBE_FAILED,
		messageReceivedActionType: RECORD_WATCHER_MESSAGE_RECEIVED,
		subscribeSucceededActionType: RECORD_WATCHER_SUBSCRIBE_SUCCEEDED,
		unsubscribeSucceededActionType: RECORD_WATCHER_UNSUBSCRIBE_SUCCEEDED,
	}
);

const recoveryStrategiesRecordWatcherHandler = createAmbSubscriptionEffect(
	'/rw/default/:table/:filter',
	{
		subscribeFailedActionType: RECORD_WATCHER_SUBSCRIBE_FAILED,
		messageReceivedActionType: RECORD_WATCHER_MESSAGE_RECEIVED,
		subscribeSucceededActionType: RECORD_WATCHER_SUBSCRIBE_SUCCEEDED,
		unsubscribeSucceededActionType: RECORD_WATCHER_UNSUBSCRIBE_SUCCEEDED,
	}
);

const lossScenarioRecordWatcherHandler = createAmbSubscriptionEffect(
	'/rw/default/:table/:filter',
	{
		subscribeFailedActionType: RECORD_WATCHER_SUBSCRIBE_FAILED,
		messageReceivedActionType: RECORD_WATCHER_MESSAGE_RECEIVED,
		subscribeSucceededActionType: RECORD_WATCHER_SUBSCRIBE_SUCCEEDED,
		unsubscribeSucceededActionType: RECORD_WATCHER_UNSUBSCRIBE_SUCCEEDED,
	}
);

const messageReceivedHandler = ({ properties, dispatch }) => {
	const { businessContinuityPlanSysId } = properties;

	dispatch(FETCH_PLAN_ASSET_DETAILS_REQUESTED_REQUESTED, {
		businessContinuityPlanSysId,
	});
};

const processSubscriptionFailure = ({ action }) => {
	console.error(RECORD_WATCHER_SUBSCIPTION_FAILURE_MESSAGE);
	console.error(action.payload.message);
};

const actionHandlers = {
	[RECORD_WATCHER_STATUS_CHANGE_REQUESTED]: updateRecordWatcherStatus,

	[LOSS_SCENARIO_RECORD_WATCHER_STATUS_CHANGED]: lossScenarioRecordWatcherHandler,
	[ASSET_DEPENDENCIES_RECORD_WATCHER_STATUS_CHANGED]: assetDependenciesRecordWatcherHandler,
	[RECOVERY_STRATEGIES_RECORD_WATCHER_STATUS_CHANGED]: recoveryStrategiesRecordWatcherHandler,

	[RECORD_WATCHER_MESSAGE_RECEIVED]: messageReceivedHandler,

	[RECORD_WATCHER_SUBSCRIBE_FAILED]: processSubscriptionFailure,
};

export { actions, actionHandlers };
