import { createAmbSubscriptionEffect } from '@servicenow/ui-effect-amb';
import {
	ACTION_TYPES,
	RECORD_WATCHER_ACTION_TYPES,
} from '../constants/BIAPieChartRibbonCardActionTypes';
import { RECORD_WATCHER_SUBSCIPTION_FAILURE_MESSAGE } from '../constants/BIAPieChartRibbonCardConstants';

const actions = {
	[RECORD_WATCHER_ACTION_TYPES.STATUS_CHANGE_REQUESTED]: {
		private: true,
	},
	[RECORD_WATCHER_ACTION_TYPES.STATUS_CHANGED]: {
		private: true,
	},
	[RECORD_WATCHER_ACTION_TYPES.MESSAGE_RECEIVED]: {
		private: true,
	},
	[RECORD_WATCHER_ACTION_TYPES.SUBSCRIBE_FAILED]: {
		private: true,
	},
};

const updateRecordWatcherStatus = ({ action, dispatch }) => {
	const { subscribeToRecordWatcher, table, filter } = action.payload;

	dispatch(RECORD_WATCHER_ACTION_TYPES.STATUS_CHANGED, {
		table,
		filter,
		subscribe: subscribeToRecordWatcher,
	});
};

const pieChartRibbonRecordWatcherHandler = createAmbSubscriptionEffect(
	'/rw/default/:table/:filter',
	{
		subscribeFailedActionType: RECORD_WATCHER_ACTION_TYPES.SUBSCRIBE_FAILED,
		messageReceivedActionType: RECORD_WATCHER_ACTION_TYPES.MESSAGE_RECEIVED,
	}
);

const messageReceivedHandler = ({ properties, dispatch }) => {
	const { sysid, queryTable, groupByColumn } = properties;

	dispatch(ACTION_TYPES.BIA_PIE_CHART_DATA_REQUESTED, {
		queryTableIdentifier: queryTable,
		parentRecordSysId: sysid,
		groupByColumn,
	});
};

const subcribeFailed = ({ action }) => {
	console.error(RECORD_WATCHER_SUBSCIPTION_FAILURE_MESSAGE);
	console.error(action.payload.message);
};

const actionHandlers = {
	[RECORD_WATCHER_ACTION_TYPES.STATUS_CHANGE_REQUESTED]: updateRecordWatcherStatus,
	[RECORD_WATCHER_ACTION_TYPES.STATUS_CHANGED]: pieChartRibbonRecordWatcherHandler,
	[RECORD_WATCHER_ACTION_TYPES.MESSAGE_RECEIVED]: messageReceivedHandler,
	[RECORD_WATCHER_ACTION_TYPES.SUBSCRIBE_FAILED]: subcribeFailed,
};

export { actions, actionHandlers };
