import '../RecordModal/RecordModalElement';
import {
	NEW_RECORD_SYS_ID,
	MODAL_ACTION_BUTTON_LABEL,
	MODAL_CANCEL_BUTTON_LABEL,
} from './constants/NewRecordModalConstants';

const view = (state) => {
	const { properties, modalHeaderLabel } = state;
	const {
		tableName,
		formView,
		modalSize,
		queryConditions,
		isReadOnly,
		reloadAfterSave,
	} = properties;

	return (
		<sn-bcm-record-modal
			isModalOpen
			table={tableName}
			modal-size={modalSize}
			form-view={formView}
			sys-id={NEW_RECORD_SYS_ID}
			header-label={modalHeaderLabel}
			action-button-label={MODAL_ACTION_BUTTON_LABEL}
			cancel-button-label={MODAL_CANCEL_BUTTON_LABEL}
			query-conditions={queryConditions}
			reloadAfterSave={reloadAfterSave}
			isReadOnly={isReadOnly}
		/>
	);
};

export default view;
