import '@servicenow/now-loader';
import '@servicenow/now-card';
import '@servicenow/now-score';
import '@servicenow/now-record-list-connected';
import '@servicenow/now-button';
import '../ItemPickerModal/ItemPickerModalElement';
import '../RecordModal/RecordModalElement';
import {
	CARD_SIZE,
	CARD_HEADER_VARIANT,
	CARD_HEADER_SIZE,
	REMOVE_ASSETS_BUTTON_TOOLTIP,
	ADD_ASSETS_BUTTON_TOOLTIP,
	BUTTON_SIZE,
	BUTTON_VARIANT,
	RECORD_LIST_LIMIT,
	PLUS_OUTLINE_ICONIC_BUTTON,
	TRASH_OUTLINE_ICONIC_BUTTON,
	ERROR_ALERT_HEADING,
	RELATED_LIST_NAME,
	LIST_TYPE,
	RECORD_MODAL_SAVE_BUTTON_LABEL,
	RECORD_MODAL_CANCEL_BUTTON_LABEL,
	RECORD_MODAL_DELETE_BUTTON_LABEL,
	ADD_BUTTON_LABEL,
	DELETE_BUTTON_LABEL,
} from './constants/PlanAssetsListConstants';
import {
	REMOVE_ASSETS_BUTTON_CLICKED,
	ADD_ASSETS_BUTTON_CLICKED,
} from './constants/PlanAssetsListActionTypes';

const view = (state, { dispatch }) => {
	const {
		isAddItemsModalOpen,
		isEditItemModalOpen,
		editItemModalConfig,
		refreshRequested,
		properties,
		errorMessage,
		currentlySelectedEncodedQuery,
		isParentRecordEditable,
	} = state;
	const {
		table,
		recordListColumns,
		parentRecordSysId,
		parentTable,
		parentTableItemPickerReferenceField,
		elementDefinitionTableReferenceField,
		parentTableReferenceField,
		cardHeaderLabel,
		recordModalFormView,
		recordModalHeading,
	} = properties;
	const query = `${parentTableReferenceField}=${parentRecordSysId}`;

	const addButtonElement = isParentRecordEditable ? (
		<now-button
			className="add-asset-button"
			slot="metadata"
			label={ADD_BUTTON_LABEL}
			icon={PLUS_OUTLINE_ICONIC_BUTTON}
			size={BUTTON_SIZE}
			variant={BUTTON_VARIANT}
			tooltipContent={ADD_ASSETS_BUTTON_TOOLTIP}
			bare={false}
			on-click={() => {
				dispatch(ADD_ASSETS_BUTTON_CLICKED);
			}}
			configAria={{ "aria-label": ADD_ASSETS_BUTTON_TOOLTIP }}
		/>
	) : null;

	const removeButtonElement = isParentRecordEditable ? (
		<now-button
			className={currentlySelectedEncodedQuery ? '' : 'hidden'}
			slot="metadata"
			label={DELETE_BUTTON_LABEL}
			icon={TRASH_OUTLINE_ICONIC_BUTTON}
			size={BUTTON_SIZE}
			variant={BUTTON_VARIANT}
			bare={false}
			tooltipContent={REMOVE_ASSETS_BUTTON_TOOLTIP}
			on-click={() => {
				dispatch(REMOVE_ASSETS_BUTTON_CLICKED);
			}}
			configAria={{ "aria-label": REMOVE_ASSETS_BUTTON_TOOLTIP }}
		/>
	) : null;

	const cardView = (
		<now-card className="card" size={CARD_SIZE}>
			<now-card-header
				className="card-header"
				heading={{
					label: cardHeaderLabel,
					variant: CARD_HEADER_VARIANT,
					size: CARD_HEADER_SIZE,
					level: 1,
					lines: 2,
				}}>
				{addButtonElement}
				{removeButtonElement}
			</now-card-header>
			<now-alert
				className={errorMessage ? 'now-alert-error-element' : 'hidden'}
				status="critical"
				icon="exclamation-triangle-fill"
				header={`${ERROR_ALERT_HEADING}:`}
				content={errorMessage}
			/>
			<div className="record-list">
				<now-record-list-connected
					listType={LIST_TYPE}
					table={table}
					parentTable={parentTable}
					parentRecordSysId={parentRecordSysId}
					limit={RECORD_LIST_LIMIT}
					columns={recordListColumns}
					query={query}
					relatedListName={RELATED_LIST_NAME}
					refreshRequested={refreshRequested}
					hideHeader
					hideQuickEdit
				/>
			</div>
		</now-card>
	);

	const addItemsModalView = (
		<sn-bcm-item-picker-modal
			table={table}
			parentTable={parentTable}
			parentTableReferenceField={parentTableReferenceField}
			elementDefinitionTableReferenceField={
				elementDefinitionTableReferenceField
			}
			parentTableItemPickerReferenceField={parentTableItemPickerReferenceField}
			parentRecordSysId={parentRecordSysId}
		/>
	);

	const editItemModalView = (
		<sn-bcm-record-modal
			isModalOpen={isEditItemModalOpen}
			table={editItemModalConfig.table}
			form-view={recordModalFormView}
			sys-id={editItemModalConfig.sysId}
			header-label={recordModalHeading}
			action-button-label={RECORD_MODAL_SAVE_BUTTON_LABEL}
			cancel-button-label={RECORD_MODAL_CANCEL_BUTTON_LABEL}
			delete-button-label={RECORD_MODAL_DELETE_BUTTON_LABEL}
		/>
	);

	return (
		<div className="plan-assets-card">
			{cardView}
			{isAddItemsModalOpen ? addItemsModalView : null}
			{isEditItemModalOpen ? editItemModalView : null}
		</div>
	);
};

export default view;
