import { PREVIEW_RECORD } from '../constants/PlanAssetsOverviewCardActionTypes';

const getCarouselVisualizationItem = (
	carouselItemContainerStyling,
	chartContainerStyling,
	donutChartProperties,
	chartLabelStyling,
	chartLabelText,
	dispatch,
	table,
	sysId
) => (
	<a
		className="keyboard-accessible"
		href="javascript:void(0)"
		on-click={() =>
			dispatch(PREVIEW_RECORD, {
				table,
				sys_id: sysId,
			})
		}
		style={carouselItemContainerStyling}>
		<div style={chartContainerStyling}>
			<now-chart-donut-pie
				className="donut-chart-element"
				{...donutChartProperties}
			/>
		</div>
		<div style={chartLabelStyling}>{chartLabelText}</div>
	</a>
);

const getDonutChartProperties = (
	chartType,
	coveredAssetsColor,
	coveredAssetsLabel,
	uncoveredAssetsColor,
	uncoveredAssetsLabel,
	planLossScenario,
	isTooltipEnabled,
	totalCountLabel,
	isAccessibilityModeActive
) => ({
	type: chartType,
	width: 'auto',
	height: 130,
	showTooltip: true,
	tooltipConfig: {
		showPercentages: false,
		config: {
			fontWeight: '500',
		},
	},
	seriesConfig: {
		series1: {
			x: 'name',
			y: 'count',
			colorPalette: [uncoveredAssetsColor, coveredAssetsColor],
		},
	},
	seriesData: [
		{
			name: uncoveredAssetsLabel,
			count: planLossScenario.uncoveredAssetsCount,
		},
		{
			name: coveredAssetsLabel,
			count: planLossScenario.coveredAssetsCount,
		},
	],
	showDataLabels: false,
	dataLabelConfig: {
		showTotal: true,
		totalLabel: totalCountLabel,
	},
	innerRadius: 0.8,
	orderBy: 'dataOrder',
	isTooltipEnabled,
	hideRegister: true,
	accessibilityMode: isAccessibilityModeActive,
	accessibilityConfig: {
		dashPatterns: false,
		monochrome: false,
		strokeWidth: false,
		opacity: false,
		markers: false,
	},
	yFormat: ',d', // d3 formatter http://bl.ocks.org/zanarmstrong/05c1e95bf7aa16c4768e
});

const getLegendItem = (color, label) => (
	<div className="legend-item">
		<div
			className="color-swatch"
			style={{
				backgroundColor: color,
			}}></div>
		<div className={`color-label ${label}`}>{label}</div>
	</div>
);

export { getDonutChartProperties, getCarouselVisualizationItem, getLegendItem };
