import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './BiaDependencyItemPickerModalView';
import styles from './BiaDependencyItemPickerModalStyles.scss';
import { actionHandlers as lifecycleActionHandlers } from './actions/LifecycleActions';
import { actionHandlers as selectionTableActionHandlers } from './actions/SelectionTableActions';
import { actionHandlers as elementConfigurationItemActionHandlers } from './actions/ElementConfigurationItemActions';
import { actionHandlers as nowListPickerInteractionActionHandlers } from './actions/NowListPickerInteractionActions';
import { actionHandlers as modalButtonActionHandlers } from './actions/ModalButtonActions';
import { actionHandlers as fetchCmdbCiRelationshipsActionHandlers } from './actions/FetchCmdbCiRelationshipsActions';
import {
	actions as itemPickerModalActions,
	actionHandlers as itemPickerModalActionsHandlers,
} from './actions/ItemPickerModalActions';
import {
	MODAL_VIEW_TYPES,
	DEFAULT_ITEM_PICKER_LOADING_MESSAGE,
	PAGINATION_RECORD_LIMIT,
} from './constants/BiaDependencyItemPickerModalConstants';

const TAG_NAME = 'sn-bcm-bia-dependency-item-picker-modal';

/**
 *
 * Modal component used to add items in bulk to BIA depedency grid
 *
 * ```
 * <sn-bcm-bia-dependency-item-picker-modal>
 * 	table="sn_bia_dependency"
 * 	parentTable="sn_bia_dependency_group"
 * 	parentTableReferenceField="dependency_group"
 * 	parentRecordSysId="8ad1e3c453d3201040c4ddeeff7b12a1"
 * 	elementDefinitionTableReferenceField="element_definition"
 * 	paginationRecordLimit={5}
 * </sn-bcm-bia-dependency-item-picker-modal>
 * ```
 *
 * @seismicElement sn-bcm-bia-dependency-item-picker-modal
 * @summary Modal component used to add items in bulk to BIA depedency grid
 */
createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
	},
	view,
	properties: {
		/**
		 * Insertion table name
		 * @type {String}
		 */
		table: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Table from where all dependency assessment sections are being fetched (dependency groups table)
		 * @type {String}
		 */
		parentTable: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The reference field name of the parent table on related table.
		 * @type {String}
		 */
		parentTableReferenceField: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * SysId of the parent record
		 * @type {String}
		 */
		parentRecordSysId: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Reference field name of the element defintion table on parent table.
		 * Example: element_definition field on sn_bia_dependency_group table.
		 * @type {String}
		 */
		elementDefinitionTableReferenceField: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The maximum number of records to be fetched
		 * @type {Number}
		 */
		maxRecordLimit: {
			default: 1000,
			required: false,
			schema: {
				type: 'number',
			},
		},
		/**
		 * The maximum number of rows to show (per page) in the modal.
		 * @type {Number}
		 */
		paginationRecordLimit: {
			default: PAGINATION_RECORD_LIMIT,
			required: false,
			schema: {
				type: 'number',
			},
		},
	},
	initialState: {
		isModalOpen: true,
		selectionTableLabel: '',
		selectionTable: '',
		selectionTableFields: '',
		recordListPickerFilterConditions: '',
		cmdbCiRecordListPickerFilterConditions: '',
		cmdbRelationshipTypesFilterConditions: '',
		currentlySelectedEncodedQuery: '',
		isElementConfigurationItem: false,
		radioSelectionValue: '',
		appliesToSysId: '',
		excludedItems: [],
		cmdbRelTypeOptionalProps: {},
		cmdbCiRecordListOptionalProps: { page: 1 },
		recordListOptionalProps: { page: 1 },
		prevScreenView: null,
		currentScreenView: MODAL_VIEW_TYPES.RECORD_LIST_PICKER,
		isLoading: true,
		loadingViewMessage: DEFAULT_ITEM_PICKER_LOADING_MESSAGE,
		errorMessage: '',
		selectedRelationshipTypes: [],
		cmdbRelationshipItems: [],
		elementDefinitionfilter: '',
	},
	actions: {
		...itemPickerModalActions,
	},
	actionHandlers: {
		...lifecycleActionHandlers,
		...itemPickerModalActionsHandlers,
		...selectionTableActionHandlers,
		...elementConfigurationItemActionHandlers,
		...nowListPickerInteractionActionHandlers,
		...modalButtonActionHandlers,
		...fetchCmdbCiRelationshipsActionHandlers,
	},
	styles,
});

export default TAG_NAME;
