import { t } from 'sn-translate';
import {
	CMDB_CI_REL_TYPE_TABLE,
	IMPACT_ANALYSIS_DEPENDENCY_OPTIONS,
	IMPACT_ANALYSIS_DEPENDENCY_HEADING,
	CMDB_CI_REL_TYPE_TABLE_FIELDS,
	MODAL_VIEW_TYPES,
	ERROR_ALERT_HEADING,
	MODAL_HEADER_LABEL,
	SELECT_FILTER_GROUP_LABEL,
	MODAL_RADIO_GROUP_HELPER_CONTENT,
} from './constants/BiaDependencyItemPickerModalConstants';
import {
	getRecordListPickerElement,
	getErrorAlert,
	getRadioChoiceList,
	getLoaderElement,
	getItemPickerModalView,
} from '../ItemPickerModal/utils/ItemPickerModalCommonUtil';
import {
	getModalFooterAction,
	getIfCheckedRadioList,
	getCurrentScreenHeading,
} from './BiaDependencyItemPickerModalUtils';

const view = (state) => {
	const {
		isModalOpen,
		selectionTable,
		recordListPickerFilterConditions,
		selectionTableFields,
		currentlySelectedEncodedQuery,
		errorMessage,
		isLoading,
		selectionTableLabel,
		currentScreenView,
		radioSelectionValue,
		loadingViewMessage,
		cmdbCiRecordListPickerFilterConditions,
		cmdbRelTypeOptionalProps,
		selectedRelationshipTypes,
		cmdbCiRecordListOptionalProps,
		recordListOptionalProps,
		cmdbRelationshipTypesFilterConditions,
		properties,
	} = state;

	const { paginationRecordLimit } = properties;

	const disableListPickerButton = Boolean(
		!(currentlySelectedEncodedQuery && !errorMessage && !isLoading)
	);
	const disableNextButton = Boolean(
		!(
			(selectedRelationshipTypes.length && !errorMessage) ||
			(radioSelectionValue &&
				currentScreenView !== MODAL_VIEW_TYPES.CMDB_REL_TYPE_LIST_PICKER)
		)
	);

	const modalFooterButtons = [
		...getModalFooterAction(
			radioSelectionValue,
			currentScreenView,
			disableNextButton,
			disableListPickerButton
		),
	];

	const loaderElement = getLoaderElement(loadingViewMessage);

	const itemRecordListPickerElement = getRecordListPickerElement(
		getCurrentScreenHeading(radioSelectionValue, selectionTableLabel),
		selectionTable,
		recordListPickerFilterConditions,
		paginationRecordLimit,
		selectionTableFields,
		recordListOptionalProps
	);

	const cmdbRelTypeListPicker = getRecordListPickerElement(
		getCurrentScreenHeading(radioSelectionValue, selectionTableLabel),
		CMDB_CI_REL_TYPE_TABLE,
		`sys_idIN${cmdbRelationshipTypesFilterConditions}`,
		paginationRecordLimit,
		CMDB_CI_REL_TYPE_TABLE_FIELDS,
		cmdbRelTypeOptionalProps
	);

	const cmdbCiRecordListPickerElement = getRecordListPickerElement(
		getCurrentScreenHeading(radioSelectionValue, selectionTableLabel),
		selectionTable,
		cmdbCiRecordListPickerFilterConditions,
		paginationRecordLimit,
		selectionTableFields,
		cmdbCiRecordListOptionalProps
	);

	const errorAlertElement = getErrorAlert(errorMessage, ERROR_ALERT_HEADING);

	const radioChoiceListElement = getRadioChoiceList(
		t(IMPACT_ANALYSIS_DEPENDENCY_HEADING, selectionTableLabel),
		getIfCheckedRadioList(
			IMPACT_ANALYSIS_DEPENDENCY_OPTIONS,
			radioSelectionValue
		),
		SELECT_FILTER_GROUP_LABEL,
		MODAL_RADIO_GROUP_HELPER_CONTENT
	);

	const getModalContent = () => {
		switch (currentScreenView) {
			case MODAL_VIEW_TYPES.RADIO_BUTTONS:
				return radioChoiceListElement;
			case MODAL_VIEW_TYPES.CMDB_REL_TYPE_LIST_PICKER:
				return cmdbRelTypeListPicker;
			case MODAL_VIEW_TYPES.CMDB_RECORD_LIST_PICKER:
				return cmdbCiRecordListPickerElement;
			case MODAL_VIEW_TYPES.RECORD_LIST_PICKER:
				return itemRecordListPickerElement;
		}
	};

	const modalBodyContent = isLoading ? loaderElement : getModalContent();

	return getItemPickerModalView(
		isModalOpen,
		MODAL_HEADER_LABEL,
		modalFooterButtons,
		errorAlertElement,
		modalBodyContent
	);
};

export default view;
