import { actionTypes } from '@servicenow/ui-core';
import {
	BIA_PLUGIN_INSTALLATION_STATUS,
	RELATED_ASSET_DEPENDENCY,
} from '../constants/RelatedAssetDependencyItemPickerModalActionTypes';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ properties, dispatch, updateState }) => {
		const { parentRecordSysId } = properties;

		dispatch(BIA_PLUGIN_INSTALLATION_STATUS.REQUESTED);

		dispatch(RELATED_ASSET_DEPENDENCY.ITEM_PICKER_TABLE_NAME_REQUESTED, {
			parentRecordSysId,
		});
	},
};

export { actionHandlers };
