import '@servicenow/now-record-list-connected';
import '@servicenow/now-button';
import '@servicenow/now-modal';
import '@servicenow/now-record-form-connected';
import '../RecordModal/RecordModalElement';
import {
	WORKSPACE_VIEW,
	LIST_TYPE,
} from './constants/RelatedItemRecordListConstants';

const view = (state) => {
	const { properties, isModalOpen, recordModalSysId, refreshRequested , headerLabel} = state;
	const {
		parentSysId,
		parentTable,
		relatedTable,
		relatedListName,
		relatedItemFormView,
		recordListProperties,
		listTitle,
		queryConditions,
		formActionButtonLabel,
		formCancelButtonLabel,
	} = properties;

	const listView = (
		<now-record-list-connected-related
			listType={LIST_TYPE}
			table={relatedTable}
			listTitle={recordListProperties.listTitle? recordListProperties.listTitle: headerLabel}
			parentRecordSysId={parentSysId}
			parentTable={parentTable}
			query={queryConditions}
			relatedListName={relatedListName}
			view={WORKSPACE_VIEW}
			refreshRequested={refreshRequested}
			{...recordListProperties}
		/>
	);

	// now-record-form-section-connected is persiting the record1 values until
	// graphql query for the record2 is successfull. So rendering modal conditionally.
	if (isModalOpen) {
		return (
			<div className="related-item-list">
				{listView}
				<sn-bcm-record-modal
					isModalOpen={isModalOpen}
					table={relatedTable}
					form-view={relatedItemFormView}
					sys-id={recordModalSysId}
					header-label={recordListProperties.listTitle? recordListProperties.listTitle: headerLabel}
					action-button-label={formActionButtonLabel}
					cancel-button-label={formCancelButtonLabel}
					query-conditions={queryConditions}
				/>
			</div>
		);
	}

	return <div className="related-item-list">{listView}</div>;
};

export default view;
