import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	NOW_ALERT_ACTION_CLICKED,
	ACCORDION_LIST_STATUS_UPDATE_QUERY_REQUESTED,
	ACCORDION_LIST_STATUS_UPDATE_QUERY_SUCCEEDED,
	ACCORDION_LIST_STATUS_UPDATE_QUERY_FAILED,
	NOW_ACCORDION_ITEM_EXPANDED,
} from '../constants/RelatedItemAccordionListActionTypes';
import {
	FETCH_ERROR_MESSAGE,
	NULL_TABLE_ERROR_MESSAGE,
} from '../constants/RelatedItemAccordionListConstants';
import relatedItemStatusMutationQuery from '../queries/RelatedItemAccordionListQueries';
import { updateMandatoryPropertyFlagRecord } from '../util/RelatedItemAccordionListUtil';
import { EDITABLE_LIST_QUERY_ACTION_TYPES } from '../../EditableList/constants/EditableListCRUDActionTypes';
import EDITABLE_LIST_GRID_ACTION_TYPES from '../../EditableList/constants/EditableListGridActionTypes';

const requestRelatedAccordionListStatusMutation = createGraphQLEffect(
	relatedItemStatusMutationQuery,
	{
		templateVarList: ['relatedTableName', 'relatedTableStatusFieldName'],
		variableList: ['statusValue', 'sysid'],
		successActionType: ACCORDION_LIST_STATUS_UPDATE_QUERY_SUCCEEDED,
		errorActionType: ACCORDION_LIST_STATUS_UPDATE_QUERY_FAILED,
	}
);

const updateRelatedAccordionListStatusSuccess = ({
	action,
	state,
	updateState,
}) => {
	const fetchResult = action.payload;
	const fetchErrors = fetchResult.errors;
	const { relatedTableName, relatedTableStatusFieldName } = state.properties;
	const { accordionListItems } = state;

	if (fetchErrors.length) {
		console.error(FETCH_ERROR_MESSAGE);
		console.error(fetchErrors);
		updateState({
			error: true,
			isLoading: false,
		});

		return;
	}

	const updatedRecord = get(
		fetchResult,
		`data.GlideRecord_Mutation.update_${relatedTableName}`
	);

	if (!updatedRecord) {
		console.error(NULL_TABLE_ERROR_MESSAGE);
		updateState({
			error: true,
			isLoading: false,
		});

		return;
	}

	const updatedListItems = accordionListItems.map((record) => {
		if (record.sys_id.value === updatedRecord.sys_id.value) {
			record[`${relatedTableStatusFieldName}`] =
				updatedRecord[`${relatedTableStatusFieldName}`].value;
		}

		return record;
	});

	updateState({
		accordionListItems: updatedListItems,
		isLoading: false,
	});
};

const updateRelatedAccordionListStatusFailed = ({ action, updateState }) => {
	const error = action.payload;

	console.error(FETCH_ERROR_MESSAGE);
	console.error(error);
	updateState({
		error: true,
		isLoading: false,
	});
};

const updateMandatoryPropertyFlag = ({ action, updateState, state }) => {
	const sysid = get(action, 'payload.action.sys_id.value');

	updateMandatoryPropertyFlagRecord(
		sysid,
		state.accordionListItems,
		updateState,
		false
	);
};

const actionHandlers = {
	[ACCORDION_LIST_STATUS_UPDATE_QUERY_REQUESTED]: requestRelatedAccordionListStatusMutation,
	[ACCORDION_LIST_STATUS_UPDATE_QUERY_SUCCEEDED]: {
		effect: updateRelatedAccordionListStatusSuccess,
		stopPropogation: true,
	},
	[ACCORDION_LIST_STATUS_UPDATE_QUERY_FAILED]: {
		effect: updateRelatedAccordionListStatusFailed,
		stopPropogation: true,
	},
	[NOW_ALERT_ACTION_CLICKED]: {
		effect: updateMandatoryPropertyFlag,
		stopPropogation: true,
	},
	[NOW_ACCORDION_ITEM_EXPANDED]: ({ action, state, updateState }) => {
		const { record, value } = action.payload;
		const { accordionListItems } = state;
		const updatedListItems = accordionListItems.map((item) => {
			if (item.sys_id.value === record.sys_id.value) {
				item.isExpanded = value;
			}

			return item;
		});

		updateState({
			accordionListItems: updatedListItems,
		});
	},
	[EDITABLE_LIST_QUERY_ACTION_TYPES.EDITABLE_LIST_DATA_FETCHED]: ({
		action,
		state,
		updateState,
		updateProperties,
	}) => {
		const { payload } = action;
		const {
			parentRecordIdentification,
			listRecords,
			columnConfig,
			isUpdate,
		} = payload;
		const { accordionListItems, properties } = state;
		const {
			relatedChildTable,
			relatedTableMandatoryFieldLabels,
			relatedTableMandatoryFields,
		} = properties;
		let mandatoryFieldLabels = relatedTableMandatoryFieldLabels;
		const mandatoryColumnNames = relatedTableMandatoryFields;

		if (columnConfig) {
			if (!relatedTableMandatoryFieldLabels) {
				for (let i = 0; i < columnConfig.length; i++) {
					if (
						columnConfig[i].isMandatory &&
						columnConfig[i].editable &&
						!columnConfig[i].isdotwalked
					) {
						mandatoryFieldLabels += `${columnConfig[i].label},`;
						mandatoryColumnNames.push(columnConfig[i].name);
					}
				}

				mandatoryFieldLabels = mandatoryFieldLabels.substr(
					0,
					mandatoryFieldLabels.length - 1
				);
			}
		}

		const updatedListItems = accordionListItems.map((item) => {
			if (item.sys_id.value === parentRecordIdentification) {
				item[relatedChildTable] = listRecords;
			}

			return item;
		});

		updateProperties({
			relatedTableMandatoryFields: mandatoryColumnNames,
			relatedTableMandatoryFieldLabels: mandatoryFieldLabels,
		});

		updateState({
			accordionListItems: updatedListItems,
		});
	},
	[EDITABLE_LIST_GRID_ACTION_TYPES.SN_DATAGRID_ROW_EDIT_STARTED]: ({
		action,
		state,
		updateState,
	}) => {
		const { taskId } = action.payload;
		const { accordionListItems, properties } = state;
		const { relatedChildTable } = properties;
		const updatedItems = accordionListItems.map((item) => {
			const childTableRecords = item[relatedChildTable];
			childTableRecords.map((childRecord) => {
				if (childRecord.id == taskId) {
					item.disable = true;
					updateState({
						editedRecordId: item.sys_id.value,
					});
				}
			});
			return item;
		});
		updateState({
			accordionListItems: updatedItems,
		});
	},
	[EDITABLE_LIST_GRID_ACTION_TYPES.POST_ACTION_GRID_EDIT_ENDED]: ({
		state,
		updateState,
	}) => {
		const { editedRecordId, accordionListItems } = state;
		const updatedItems = accordionListItems.map((item) => {
			if (item.sys_id.value == editedRecordId) {
				item.disable = false;
			}
			return item;
		});
		updateState({
			accordionListItems: updatedItems,
		});
	},
};

export default actionHandlers;
