import { actionTypes } from '@servicenow/ui-core';
import {
	ACTION_TYPES,
	RECORD_WATCHER_ACTION_TYPES,
} from '../constants/BIAPieChartRibbonCardActionTypes';
import { getRecordWatcherFilter } from '../../../util/ambSubscriptionUtil';
import { REPORT_ACCESSIBILITY_PREFERENCE_REQUESTED } from '../constants/AccessibilityUserPreferenceActionTypes';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED } = actionTypes;

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ state, dispatch }) => {
		const {
			sysid,
			queryTable,
			groupByColumn,
			queryFilterColumn,
		} = state.properties;

		dispatch(ACTION_TYPES.BIA_PIE_CHART_DATA_REQUESTED, {
			queryTableIdentifier: queryTable,
			parentRecordSysId: sysid,
			groupByColumn,
		});

		const biaPieChartDataFilter = getRecordWatcherFilter(
			`${queryFilterColumn}=${sysid}`
		);

		dispatch(RECORD_WATCHER_ACTION_TYPES.STATUS_CHANGE_REQUESTED, {
			table: queryTable,
			filter: biaPieChartDataFilter,
			subscribeToRecordWatcher: true,
		});
		dispatch(REPORT_ACCESSIBILITY_PREFERENCE_REQUESTED);
	},
	[COMPONENT_DISCONNECTED]: ({ state, dispatch }) => {
		const { sysid, queryTable, queryFilterColumn } = state.properties;

		const biaPieChartDataFilter = getRecordWatcherFilter(
			`${queryFilterColumn}=${sysid}`
		);

		dispatch(RECORD_WATCHER_ACTION_TYPES.STATUS_CHANGED, {
			table: queryTable,
			filter: biaPieChartDataFilter,
			subscribeToRecordWatcher: false,
		});
	},
};

export default actionHandlers;
