const query = `query getAssetTableAndFilter ($parentRecordSysId: String!) {
    GlideRecord_Query {
        {{parentTable}} (sys_id:$parentRecordSysId) {
            _results {
                {{parentTableItemPickerReferenceField}} {
                    value
                    _reference {
                        {{elementDefinitionTableReferenceField}} {
                            value
                            _reference {
                                source_table {
                                    value
                                }
                                filter {
                                    value
                                }
                                source_table_fields {
                                    value
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}`;

export default query;
