import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import {
	TOOLBAR_ACTIONS,
	MODAL_ACTIONS,
	RECORD_ACTIONS,
	EVENT_DATA_FETCH_ACTIONS,
	NOW_ALERT_ACTION_CLICKED,
} from '../constants/RecoveryEventResultDataActionTypes';
import {
	ITEM_INSERTION_SUCCEEDED,
	ITEM_PICKER_EDIT_CANCELLED,
	ITEM_INSERTION_FAILED,
} from '../../ItemPickerModal/constants/ItemPickerModalActionTypes';
import { ITEM_INSERTION_ACTION_TYPES } from '../../RecoveryAddPlansModal/constants/RecordModalActionTypes';
import { GRID_ACTIONS } from '../../../util/grid-util/constants/GridActionTypes';
import {
	PAGINATION_LIMIT,
	EVENT_STATE_FETCH_ERROR_MESSAGE,
	CLOSED_EVENT_INFO,
} from '../constants/RecoveryEventResultConstants';
import { EVENT_STATE_QUERY } from '../queries/RecoveryEventResultDataQuery';

const openAdHocTask = ({ state, dispatch, updateState }) => {
	const { properties } = state;
	const { eventSysId } = properties;

	updateState({
		isAddTask: true,
	});
	dispatch(EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_STATE_DATA_REQUESTED, {
		eventSysId,
	});
};

const showAddPlanModal = ({ state, dispatch, updateState }) => {
	const { properties } = state;
	const { eventSysId } = properties;

	updateState({
		isAddAsset: false,
		isAddTask: false,
	});

	dispatch(EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_STATE_DATA_REQUESTED, {
		eventSysId,
	});
};

const showAddAssetModal = ({ state, dispatch, updateState }) => {
	const { properties } = state;
	const { eventSysId } = properties;

	updateState({
		isAddAsset: true,
		isAddTask: false,
	});

	dispatch(EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_STATE_DATA_REQUESTED, {
		eventSysId,
	});
};

const hideAlert = ({ updateState }) => {
	updateState({
		errorMessage: '',
	});
};

const fetchEventState = createGraphQLEffect(EVENT_STATE_QUERY, {
	templateVarList: ['eventSysId'],
	successActionType: EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_STATE_DATA_SUCCEEDED,
	errorActionType: EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_STATE_DATA_FAILED,
});

const fetchEventStateSuccess = ({ action, updateState, state, dispatch }) => {
	const fetchResult = action.payload;
	const fetchErrors = fetchResult.errors;
	const {
		eventCloseStates,
		eventPendingStates,
		listOfRecords,
		elementsList,
		isAddAsset,
		isAddTask,
		properties,
	} = state;

	const selectable = !(listOfRecords && listOfRecords.length > 0);
	elementsList[0].disabled = selectable;

	if (fetchErrors.length > 0) {
		console.error(EVENT_STATE_FETCH_ERROR_MESSAGE);
		console.error(fetchErrors);

		return;
	}

	const dataReturned = get(
		action.payload,
		`data.GlideRecord_Query.sn_recovery_event._results[0]`
	);

	const eventStatus = get(dataReturned, 'state.value');
	if (
		eventPendingStates &&
		eventPendingStates.indexOf(eventStatus) == -1 &&
		eventCloseStates &&
		eventCloseStates.indexOf(eventStatus) == -1
	) {
		if (isAddAsset) {
			updateState({
				isAddAssetsClicked: true,
			});
		} else if (isAddTask) {
			const adHocTask = {
				table: 'sn_recovery_event_task',
				sys_id: '-1',
				params: {
					query: `parent=${properties.eventSysId}^task_type=Adhoc`,
				},
			};

			dispatch(RECORD_ACTIONS.PREVIEW_RECORD, adHocTask);
		} else {
			updateState({
				isPlanModalOpen: true,
				isEventInProgress: true,
				elementsList: elementsList,
			});
		}
	} else if (eventCloseStates && eventCloseStates.indexOf(eventStatus) != -1) {
		updateState({
			showAdHocTaskButton: false,
			showAddAssetsButton: false,
			showAddPlansButton: false,
			isPlanModalOpen: false,
			isAddAssetsClicked: false,
			isEventInProgress: false,
			isAddTask: false,
			errorMessage: CLOSED_EVENT_INFO,
		});
	} else {
		if (isAddAsset) {
			updateState({
				isAddAssetsClicked: true,
			});
		} else if (isAddTask) {
			const adHocTask = {
				table: 'sn_recovery_event_task',
				sys_id: '-1',
				params: {
					query: `parent=${properties.eventSysId}^task_type=Adhoc`,
				},
			};

			dispatch(RECORD_ACTIONS.PREVIEW_RECORD, adHocTask);
		} else {
			updateState({
				isPlanModalOpen: true,
				isEventInProgress: false,
				elementsList: elementsList,
			});
		}
	}
};

const fetchEventStateFailure = ({ action, updateState }) => {
	const error = action.payload;

	console.error(EVENT_STATE_FETCH_ERROR_MESSAGE);
	console.error(error);

	updateState({
		isLoading: false,
	});
};

const itemInsertionSuccess = ({ state, dispatch }) => {
	const { properties, userPreferenceName } = state;
	const {
		columns,
		eventSysId,
		tableName,
		listViewName,
		eventAssetRecordLoadLimit,
		activatedPlanRecordLoadLimit,
		eventTaskRecordLoadLimit,
	} = properties;

	const actionPayload = {
		tableName,
		eventSysId,
		columns,
		userPreferenceName,
		eventAssetRecordLoadLimit,
		activatedPlanRecordLoadLimit,
		eventTaskRecordLoadLimit,	
	};

	dispatch(GRID_ACTIONS.REFRESH_GRID, {
		actionName: EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_RESULT_DATA_REQUESTED,
		actionPayload,
	});
};

const assetInsertionSuccess = ({ state, properties, dispatch, updateState, }) => {
	const { userPreferenceName } = state;
	const {
		eventSysId,
		columns,
		tableName,
		eventAssetRecordLoadLimit,
		activatedPlanRecordLoadLimit,
		eventTaskRecordLoadLimit,
	} = properties;

	updateState({
		isAddAsset: false,
		isAddAssetsClicked: false,
	});

	dispatch(GRID_ACTIONS.REFRESH_GRID, {
		actionName: EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_RESULT_DATA_REQUESTED,
		actionPayload: {
			eventSysId,
			tableName,
			columns,
			userPreferenceName,
			eventAssetRecordLoadLimit,
			activatedPlanRecordLoadLimit,
			eventTaskRecordLoadLimit,
		},
	});
};

const itemInsertionFailure = ({ state, dispatch }) => {
	const { properties, userPreferenceName } = state;
	const {
		columns,
		eventSysId,
		tableName,
		listViewName,
		eventAssetRecordLoadLimit,
		activatedPlanRecordLoadLimit,
		eventTaskRecordLoadLimit,
	} = properties;

	const actionPayload = {
		tableName,
		eventSysId,
		columns,
		userPreferenceName,
		eventAssetRecordLoadLimit,
		activatedPlanRecordLoadLimit,
		eventTaskRecordLoadLimit,
		
	};
	dispatch(GRID_ACTIONS.REFRESH_GRID, {
		actionName: EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_RESULT_DATA_REQUESTED,
		actionPayload,
	});
};

const refreshGrid = ({ dispatch, state }) => {
	const { properties, userPreferenceName } = state;
	const {
		columns,
		eventSysId,
		tableName,
		eventAssetRecordLoadLimit,
		activatedPlanRecordLoadLimit,
		eventTaskRecordLoadLimit,
	} = properties;

	const actionPayload = {
		tableName,
		eventSysId,
		columns,
		userPreferenceName,
		eventAssetRecordLoadLimit,
		activatedPlanRecordLoadLimit,
		eventTaskRecordLoadLimit,
	};
	dispatch(GRID_ACTIONS.REFRESH_GRID, {
		actionName: EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_RESULT_DATA_REQUESTED,
		actionPayload,
	});
};

const actionHandlers = {
	[TOOLBAR_ACTIONS.ADD_TASK_BUTTON_CLICKED]: openAdHocTask,
	[TOOLBAR_ACTIONS.ADD_ASSETS_BUTTON_CLICKED]: showAddAssetModal,
	[ITEM_INSERTION_SUCCEEDED]: assetInsertionSuccess,
	[ITEM_PICKER_EDIT_CANCELLED]: ({ updateState }) => {
		updateState({
			isAddAsset: false,
			isAddAssetsClicked: false,
		});
	},
	[ITEM_INSERTION_FAILED]: itemInsertionFailure,
	[TOOLBAR_ACTIONS.ADD_PLANS_BUTTON_CLICKED]: showAddPlanModal,
	[MODAL_ACTIONS.MODAL_CLOSE_CLICKED]: ({ updateState }) => {
		updateState({
			isPlanModalOpen: false,
			isAddAssetsClicked: false,
		});
	},
	[ITEM_INSERTION_ACTION_TYPES.ACTIVATED_PLAN_ITEM_INSERTION_SUCCEEDED]: itemInsertionSuccess,
	[ITEM_INSERTION_ACTION_TYPES.ACTIVATED_PLAN_ITEM_INSERTION_FAILED]: itemInsertionFailure,
	[EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_STATE_DATA_REQUESTED]: fetchEventState,
	[EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_STATE_DATA_SUCCEEDED]: fetchEventStateSuccess,
	[EVENT_DATA_FETCH_ACTIONS.FETCH_EVENT_STATE_DATA_FAILED]: fetchEventStateFailure,
	[NOW_ALERT_ACTION_CLICKED]: {
		effect: hideAlert,
		stopPropogation: true,
	},
	[TOOLBAR_ACTIONS.REFRESH_GRID]: refreshGrid,
};

export default actionHandlers;
