import { actionTypes } from '@servicenow/ui-core';
import { IMPACT_ASSESSMENT_REQUESTED } from '../constants/ImpactAssessmentScoreCardActionTypes';
import { RECORD_WATCHER_SUBSCRIPTION_REQUESTED } from '../../../behaviors/RecordWatcherBehavior';
import { IMPACT_ASSESSMENT_TABLE } from '../constants/ImpactAssessmentScoreCardConstants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const fetchImpactAssessmentData = ({ properties, dispatch }) => {
	const { sysid, isRecordWatcherEnabled } = properties;

	dispatch(IMPACT_ASSESSMENT_REQUESTED, {
		biaSysId: sysid,
	});

	if (isRecordWatcherEnabled) {
		dispatch(RECORD_WATCHER_SUBSCRIPTION_REQUESTED, {
			table: IMPACT_ASSESSMENT_TABLE,
			filter: `sys_id=${sysid}`,
			onItemChangeAction: {
				actionType: IMPACT_ASSESSMENT_REQUESTED,
				actionPayload: {
					biaSysId: sysid,
				},
			},
		});
	}
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect: fetchImpactAssessmentData,
	},
};

export default actionHandlers;
