import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { EDITABLE_LIST_QUERY_ACTION_TYPES } from '../constants/EditableListCRUDActionTypes';
import { EDITABLE_LIST_FETCH_QUERY } from '../queries/EditableListQueries';
import {
	setGridConfig,
	transformQueryValues,
	transformColumnDefinition,
} from '../utils/EditableListUtils';

const fetchRecordRequested = createGraphQLEffect(EDITABLE_LIST_FETCH_QUERY, {
	templateVarList: ['tableName', 'userPreferenceName'],
	variableList: [
		'tableName',
		'listViewName',
		'filterQueryConditions',
		'paginationLimit',
	],
	successActionType:
		EDITABLE_LIST_QUERY_ACTION_TYPES.EDITABLE_LIST_QUERY_SUCCEEDED,
	errorActionType: EDITABLE_LIST_QUERY_ACTION_TYPES.EDITABLE_LIST_QUERY_FAILED,
});

const getRecordQueryFailure = ({ action, properties, updateState }) => {
	const { tableName } = properties;
	const error = action.payload;

	console.error(`An error occured attempting to fetch data from ${tableName}`);
	console.error(error);
	updateState({
		error: true,
		isLoading: false,
	});
};

const getRecordQuerySuccess = ({ action, state, updateState, dispatch, updateProperties }) => {
	const { payload } = action;
	const { tableName, listConfig, parentRecordIdentification, isReadOnly, allowColumnHidding } = state.properties;
	const { config } = state;

	/* set grid config object based on the properties passed */
	const _tableLabel = get(
		payload,
		`data.GlideRecord_Query.${tableName}._table_metadata.label`,
		tableName
	);

	const _gridConfig = setGridConfig(_tableLabel, listConfig, config, isReadOnly);

	/* read column names from list layout */
	const _viewColumns = get(
		payload,
		'data.GlideLayout_Query.listLayout.allElements',
		[]
	);

	// get user preference
	const _userPreferences = get(
		payload,
		'data.GlideDomain_Query.user.preferences[0].value',
		{}
	);

	const _userPreferenceArr = JSON.parse(_userPreferences);

	/* get the json string from the query results */
	const _queryResults = get(
		payload,
		'data.GlideLayout_Query.listLayout._graphql._result',
		{}
	);

	/* transform columns array as required by grid */
	const _columnNames = transformColumnDefinition(_viewColumns, _userPreferenceArr, allowColumnHidding);
	let isUserPreferenceWidth = false;

	if ( _userPreferenceArr && _userPreferenceArr.length ) {
		isUserPreferenceWidth = true;
	}

	/* get the rows to match the format required for rendering the rows */
	const _transformedRows = transformQueryValues(
		_queryResults,
		_columnNames,
		tableName
	);

	updateState({
		config: _gridConfig,
		columns: _columnNames,
		listRecords: _transformedRows,
		isLoading: false,
		tableLabel: _tableLabel,
		hasOffsetWidth: isUserPreferenceWidth,
	});

	// this action is dispatched to update the parent component with child table records.
	dispatch(EDITABLE_LIST_QUERY_ACTION_TYPES.EDITABLE_LIST_DATA_FETCHED, {listRecords:_transformedRows, columnConfig:_columnNames, parentRecordIdentification});
};

const actionHandlers = {
	[EDITABLE_LIST_QUERY_ACTION_TYPES.EDITABLE_LIST_QUERY_REQUESTED]: fetchRecordRequested,
	[EDITABLE_LIST_QUERY_ACTION_TYPES.EDITABLE_LIST_QUERY_SUCCEEDED]: {
		effect: getRecordQuerySuccess,
		stopPropogation: true,
	},
	[EDITABLE_LIST_QUERY_ACTION_TYPES.EDITABLE_LIST_QUERY_FAILED]: {
		effect: getRecordQueryFailure,
		stopPropogation: true,
	},
};

export default actionHandlers;
