import { createHttpEffect } from '@servicenow/ui-effect-http';
import EXERCISE_DISPLAY_ACTIONS from '../constants/ExerciseDisplayConditionsActionTypes';
import {
	EXERCISE_DISPLAY_CONDITIONS_URL,
	DEFAULT_TAB_INDEX,
} from '../constants/PlanOverviewLayoutConstants';

const requestExerciseDisplayConditions = createHttpEffect(
	EXERCISE_DISPLAY_CONDITIONS_URL,
	{
		method: 'GET',
		queryParams: ['planSysId'],
		successActionType:
			EXERCISE_DISPLAY_ACTIONS.EXERCISE_DISPLAY_CONDITIONS_SUCCEEDED,
		errorActionType:
			EXERCISE_DISPLAY_ACTIONS.EXERCISE_DISPLAY_CONDITIONS_FAILED,
		batch: false,
	}
);

const getExerciseDisplayConditionsSuccess = ({ action, updateState }) => {
	const { payload } = action;

	if (payload.error) {
		console.error(payload.error);
		return;
	}

	const {
		result: { isPlanExerciseRowDisplayed, planAuthoringType, formTabOrderList },
	} = payload;

	const {
		scopeTabIndex = DEFAULT_TAB_INDEX,
		documentationTabIndex = DEFAULT_TAB_INDEX,
		recoveryTeamsTabIndex = DEFAULT_TAB_INDEX,
		lossScenariosTabIndex = DEFAULT_TAB_INDEX,
		recoveryTasksTabIndex = DEFAULT_TAB_INDEX,
	} = formTabOrderList;

	updateState({
		isPlanExerciseRowDisplayed,
		planAuthoringType,
		scopeTabIndex,
		documentationTabIndex,
		recoveryTeamsTabIndex,
		lossScenariosTabIndex,
		recoveryTasksTabIndex,
	});
};

const getExerciseDisplayConditionsFailed = ({ action }) => {
	const { data } = action.payload;
	const { error } = data;
	const { message } = error;

	console.error(message);
};

const actionHandlers = {
	[EXERCISE_DISPLAY_ACTIONS.EXERCISE_DISPLAY_CONDITIONS_REQUESTED]: requestExerciseDisplayConditions,
	[EXERCISE_DISPLAY_ACTIONS.EXERCISE_DISPLAY_CONDITIONS_SUCCEEDED]: {
		effect: getExerciseDisplayConditionsSuccess,
	},
	[EXERCISE_DISPLAY_ACTIONS.EXERCISE_DISPLAY_CONDITIONS_FAILED]: {
		effect: getExerciseDisplayConditionsFailed,
	},
};

export default actionHandlers;
