import { actionTypes } from '@servicenow/ui-core';
import { CAROUSEL_SECTION_COUNT_UPDATED } from '../constants/DynamicCarouselActionTypes';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ properties, dispatch }) => {
		const { elements, maxElementsPerSection } = properties;
		dispatch(CAROUSEL_SECTION_COUNT_UPDATED, {
			elements,
			maxElementsPerSection,
		});
	},
	[COMPONENT_PROPERTY_CHANGED]: ({ properties, action, dispatch }) => {
		const { previousValue } = action.payload;
		const { elements, maxElementsPerSection } = properties;

		if (elements.length !== previousValue.length) {
			dispatch(CAROUSEL_SECTION_COUNT_UPDATED, {
				elements,
				maxElementsPerSection,
			});
		}
	},
};

export default actionHandlers;
