import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import lifecycleActionHandlers from './actions/LifecycleActions';
import typeaheadActionHandlers from './actions/TypeaheadActions';
import {
	actions as selectionTableQueryActions,
	actionHandlers as selectionTableQueryActionHandlers,
} from './actions/SelectionTableQueryActions';
import {
	actions as selectedValuesQueryActions,
	actionHandlers as selectedValuesQueryActionHandlers,
} from './actions/SelectedValuesQueryActions';
import {
	actions as removeSelectionActions,
	actionHandlers as removeSelectionActionHandlers,
} from './actions/RemoveSelectionActions';
import {
	actions as recordQueryActions,
	actionHandlers as recordQueryActionHandlers,
} from './actions/RecordQueryActions';
import {
	actions as recordMutationActions,
	actionHandlers as recordMutationActionHandlers,
} from './actions/RecordMutationActions';
import view from './ContributorsSideActionView';
import styles from './ContributorsSideActionElement.scss';
import * as DEFAULT_PROPERTY_VALUES from './constants/DefaultPropertyValues';

const TAG_NAME = 'sn-bcm-contributors-side-action';

/**
 * This component to be used on the contextual sidebar.
 * Will allow a user to edit contributors using a glide-list field and typeahead dropdown
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-contributors-side-action
 * 		table=""
 * 		sys-id=""
 * 		field=""
 * 		selection-table=""
 * 		max-typeahead-options=""
 * 		max-glide-list-items=""
 * ></sn-bcm-contributors-side-action>
 * ```
 *
 * @seismicElement sn-bcm-contributors-side-action
 * @summary Allows a user to edit contributors using a glide-list field and typeahead dropdown
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		 * The table-record on which to display the widget
		 *
		 * @type {string}
		 */
		table: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The sysid of the record currently displaying the widget
		 *
		 * @type {string}
		 */
		sysId: {
			default: '',
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The name of the 'user' list-field, which the widget will display/modify
		 *
		 *  @type {string}
		 */
		field: {
			default: DEFAULT_PROPERTY_VALUES.FIELD,
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The gform object passed on via declarative action
		 *
		 *  @type {object}
		 */
		gForm: {
			default: {},
			required: true,
			schema: {
				type: 'object',
			},
		},
		/**
		 * The table from which 'user' selections can be made from
		 *
		 *  @type {string}
		 */
		selectionTable: {
			default: DEFAULT_PROPERTY_VALUES.SELECTION_TABLE,
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The maximum number of records that should be shown on the dropdown at once
		 *
		 *  @type {number}
		 */
		maxTypeaheadOptions: {
			default: DEFAULT_PROPERTY_VALUES.MAX_TYPE_AHEAD_OPTIONS,
			required: true,
			schema: {
				type: 'number',
			},
		},
		/**
		 * The maximum number of items that should be returned in the query
		 *
		 *  @type {number}
		 */
		maxGlideListItems: {
			default: DEFAULT_PROPERTY_VALUES.MAX_GLIDE_LIST_ITEMS, // Unrealistic number that should never get exceeded
			required: true,
			schema: {
				type: 'number',
			},
		},
		/**
		 * Used to compute whether or not the widgte is read-only
		 *
		 *  @type {boolean}
		 */
		isNewRecord: {
			default: false,
			required: true,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Message that gets displayed while the widget is loading data
		 *
		 *  @type {string}
		 */
		loadingMessage: {
			default: DEFAULT_PROPERTY_VALUES.LOADING_MESSAGE,
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Message that gets displayed when no 'users' have been selected (empty state)
		 *
		 *  @type {string}
		 */
		emptyStateLabel: {
			default: DEFAULT_PROPERTY_VALUES.EMPTY_STATE_LABEL,
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Used to set the size of the avatar images (ie. sm, md, etc)
		 *
		 *  @type {string}
		 */
		avatarSize: {
			default: DEFAULT_PROPERTY_VALUES.AVATAR_SIZE,
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Content that gets displayed on the widget header
		 *
		 *  @type {string}
		 */
		sideActionHeaderLabel: {
			default: DEFAULT_PROPERTY_VALUES.SIDE_ACTION_HEADER_LABEL,
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Content displayed in the label above the typeahead input element
		 *
		 *  @type {string}
		 */
		typeaheadLabel: {
			default: DEFAULT_PROPERTY_VALUES.TYPEAHEAD_LABEL,
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The placeholder text displayed inside the typeahead iput element, when no user input is present
		 *
		 *  @type {string}
		 */
		typeaheadPlaceholder: {
			default: DEFAULT_PROPERTY_VALUES.TYPEAHEAD_PLACEHOLDER,
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Message that gets when the current record being displayed is a new record, and the widget becomes read-only
		 *
		 *  @type {string}
		 */
		newRecordAlertMessage: {
			default: DEFAULT_PROPERTY_VALUES.NEW_RECORD_ALERT_MESSAGE,
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label on the 'remove' button. 'Remove' is shown by default
		 *
		 *  @type {string}
		 */
		removeButtonLabel: {
			default: DEFAULT_PROPERTY_VALUES.REMOVE_BUTTON_LABEL,
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Text that will preceed the user's name on the remove button tooltip (ie. <value> Abel tuter)
		 *
		 *  @type {string}
		 */
		removeButtonTooltipAddendum: {
			default: DEFAULT_PROPERTY_VALUES.REMOVE_BUTTON_TOOLTIP_ADDENDUM,
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Text that will preceed the user's name on the remove button aria (ie. <value> Abel tuter)
		 *
		 *  @type {string}
		 */
		removeButtonAriaAddendum: {
			default: DEFAULT_PROPERTY_VALUES.REMOVE_BUTTON_ARIA_ADDENDUM,
			required: true,
			schema: {
				type: 'string',
			},
		},
	},
	initialState: {
		isLoading: true,
		readOnly: false,
		typeaheadSearchTerm: '',
		typeaheadOptions: [],
		currentlySelected: [],
		currentlySelectedGlideList: '',
		usersAggregateCount: 0,
	},
	actions: {
		...recordQueryActions,
		...recordMutationActions,
		...removeSelectionActions,
		...selectionTableQueryActions,
		...selectedValuesQueryActions,
	},
	actionHandlers: {
		...lifecycleActionHandlers,
		...recordQueryActionHandlers,
		...recordMutationActionHandlers,
		...typeaheadActionHandlers,
		...removeSelectionActionHandlers,
		...selectionTableQueryActionHandlers,
		...selectedValuesQueryActionHandlers,
	},
	styles,
});

export default TAG_NAME;
