import { t } from 'sn-translate';
import { getDefaultModalButton } from '../ItemPickerModal/utils/ItemPickerModalCommonUtil';
import {
	MODAL_VIEW_TYPES,
	DEFAULT_NEXT_BUTTON_LABEL,
	FILTER_BY_CMDB_REL_TYPE_CHOICE,
	SHOW_ALL_RECORDS_CHOICE,
	DEFAULT_BACK_BUTTON_LABEL,
	DEFAULT_LIST_PICKER_MESSAGE,
	CMDB_CHOICE_LIST_PICKER_MESSAGE,
} from './constants/BiaDependencyItemPickerModalConstants';
import {
	ITEM_PICKER_BIA_BACK_SUCCEEDED,
	ITEM_PICKER_BIA_NEXT_SUCCEEDED,
} from './constants/BiaDependencyItemPickerModalActionTypes';

const getModalFooterAction = (
	radioSelection,
	modalView,
	disableNextButton,
	disableListPickerButton
) => {
	const nextButton = {
		variant: 'primary',
		label: DEFAULT_NEXT_BUTTON_LABEL,
		clickActionType: ITEM_PICKER_BIA_NEXT_SUCCEEDED,
		disabled: disableNextButton,
	};
	const backButton = {
		variant: 'tertiary',
		label: DEFAULT_BACK_BUTTON_LABEL,
		clickActionType: ITEM_PICKER_BIA_BACK_SUCCEEDED,
	};

	let modalFooterButtons = [];
	switch (modalView) {
		case MODAL_VIEW_TYPES.RADIO_BUTTONS:
		case MODAL_VIEW_TYPES.CMDB_REL_TYPE_LIST_PICKER:
			modalFooterButtons = [nextButton, ...getDefaultModalButton(false)];
			break;
		case MODAL_VIEW_TYPES.RECORD_LIST_PICKER:
		case MODAL_VIEW_TYPES.CMDB_RECORD_LIST_PICKER:
			modalFooterButtons = [
				...getDefaultModalButton(true, disableListPickerButton),
			];
			break;
	}

	if (radioSelection && modalView !== MODAL_VIEW_TYPES.RADIO_BUTTONS) {
		modalFooterButtons.push(backButton);
	}

	return modalFooterButtons;
};

const getIfCheckedRadioList = (list, selectedValue) => {
	if (list && list.length) {
		return list.map((choice) => {
			if (choice.id === selectedValue) {
				return {
					...choice,
					checked: true,
				};
			}
			return choice;
		});
	}

	return list;
};

const getCurrentScreenHeading = (radioSelection, tableLabel) => {
	switch (radioSelection) {
		case FILTER_BY_CMDB_REL_TYPE_CHOICE.id:
			return t(CMDB_CHOICE_LIST_PICKER_MESSAGE, tableLabel);
		case SHOW_ALL_RECORDS_CHOICE.id:
		default:
			return t(DEFAULT_LIST_PICKER_MESSAGE, tableLabel);
	}
};

export { getModalFooterAction, getIfCheckedRadioList, getCurrentScreenHeading };
