import { createCustomElement } from '@servicenow/ui-core';
import { snabbdom } from '@servicenow/ui-renderer-snabbdom';
import view from './RecoveryEventResultView';
import styles from './RecoveryEventResultElement.scss';
import {
	HIERARCHY_COLUMN,
	WORKSPACE_VIEW,
	DEFAULT_COLUMNS,
	EVENT_ASSET_TABLE_NAME,
	DEFAULT_CHOICE_OPTIONS,
	DEFAULT_PLAN_COLUMNS,
	DEFAULT_PLAN_ASSET_COLUMNS,
	EVENT_ASSET_PAGINATION_LIMIT,
	ACTIVATED_PLAN_PAGINATION_LIMIT,
	EVENT_TASK_PAGINATION_LIMIT,
} from './constants/RecoveryEventResultConstants';
import lifecycleActionHandlers from './actions/RecoveryEventResultLifecycleActions';
import recoveryEventResultDataActionHandlers from './actions/RecoveryEventDataQueryActions';
import userPreferenceActionHandlers from '../../util/grid-util/actions/UserPreferenceActions';
import gridActionHandlers from '../../util/grid-util/actions/GridActions';
import toolbarActionHandlers from './actions/RecoveryEventResultToolbarActions';

const TAG_NAME = 'sn-bcm-recovery-event-result-list';

/**
 * Displays list of related elements for plan
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-recovery-event-result-list
 * parent-record-sys-id=
 * ></sn-bcm-recovery-event-result-list>
 * ```
 *
 * @seismicElement sn-bcm-recovery-event-result-list
 * @summary Displays list of related assets, activated plans and tasks for event
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,
	styles,
	initialState: {
		isLoading: true,
		listOfRecords: [],
		columns: [],
		hasOffsetWidth: false,
		showAdHocTaskButton: true,
		showAddPlansButton: true,
		showAddAssetsButton: true,
		isAddAssetsClicked: false,
		userPreferenceName: '',
		elementsList: DEFAULT_CHOICE_OPTIONS,
		isPlanModalOpen: false,
		isEventInProgress: false,
		errorMessage: '',
		eventProgressStates: [],
		eventCloseStates: [],
		eventPendingStates: [],
		isAddAsset: false,
		isAddTask: false,
	},
	properties: {
		/**
		 * The parent table name on which child tables data to be queried on
		 *
		 * @type {String}
		 */
		tableName: {
			default: EVENT_ASSET_TABLE_NAME,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Sysid of the event table
		 *
		 * @type {string}
		 */
		eventSysId: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The list view name. Added only for user preference name.
		 *
		 * @type {String}
		 */
		listViewName: {
			default: WORKSPACE_VIEW,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Group column name on which we want to show hierarchy
		 *
		 * @type {String}
		 */
		groupColumnName: {
			default: HIERARCHY_COLUMN,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Columns to be displayed on grid.
		 *
		 * @type {Object}
		 */
		columns: {
			default: DEFAULT_COLUMNS,
			required: false,
			schema: {
				type: 'object',
			},
		},
		/**
		 * Columns to be displayed on Add Plans modal to show list of plans.
		 *
		 * @type {string}
		 */
		planColumns: {
			default: DEFAULT_PLAN_COLUMNS,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Columns to be displayed on Add Plans modal to show list assets for selected plans.
		 *
		 * @type {string}
		 */
		planAssetColumns: {
			default: DEFAULT_PLAN_ASSET_COLUMNS,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * This value determines the maximum number of records loaded for event asset. Default: 100
		 *
		 * @type {integer}
		 */
		eventAssetRecordLoadLimit: {
			default: EVENT_ASSET_PAGINATION_LIMIT,
			required: false,
			schema: {
				type: 'integer',
			},
		},
		/**
		 * This value determines the maximum number of records loaded for activated plans. Default: 100
		 *
		 * @type {integer}
		 */
		activatedPlanRecordLoadLimit: {
			default: ACTIVATED_PLAN_PAGINATION_LIMIT,
			required: false,
			schema: {
				type: 'integer',
			},
		},
		/**
		 * This value determines the maximum number of records loaded for event tasks. Default: 1000
		 *
		 * @type {integer}
		 */
		eventTaskRecordLoadLimit: {
			default: EVENT_TASK_PAGINATION_LIMIT,
			required: false,
			schema: {
				type: 'integer',
			},
		},
	},
	actionHandlers: {
		...lifecycleActionHandlers,
		...userPreferenceActionHandlers,
		...gridActionHandlers,
		...recoveryEventResultDataActionHandlers,
		...toolbarActionHandlers,
	},
});

export default TAG_NAME;
