import { actionTypes } from '@servicenow/ui-core';
import PLAN_EXERCISE_ACTIONS from '../constants/ActionTypes';
import { EVENT_TYPE_VALUE } from '../constants/GeneralConstants';
import { USER_ACCESSIBILITY_PREFERENCE_REQUESTED } from '../constants/AccessibilityActionTypes';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const fetchPlanExerciseResult = ({ properties, dispatch }) => {
	const { planSysId } = properties;

	dispatch(PLAN_EXERCISE_ACTIONS.OVERALL_EXERCISE_RESULTS_REQUESTED, {
		planSysId,
		eventType: EVENT_TYPE_VALUE.EXERCISE,
	});

	dispatch(USER_ACCESSIBILITY_PREFERENCE_REQUESTED);
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect: fetchPlanExerciseResult,
	},
};

export default actionHandlers;
