import { actionTypes } from '@servicenow/ui-core';
import { FETCH_PLAN_RECOVERY_TEAMS_DETAILS_REQUESTED } from '../constants/PlanRecoveryTeamsScoreCardActionTypes';
import { RECORD_WATCHER_SUBSCRIPTION_REQUESTED } from '../../../behaviors/RecordWatcherBehavior';
import {
	PLAN_RECOVERY_TEAMS_TABLE,
	DEFAULT_GROUP_COLUMN,
	DEFAULT_USER_COLUMN,
} from '../constants/PlanRecoveryTeamsScoreCardConstants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const fetchPlanRecoveryTeamData = ({ properties, dispatch }) => {
	const { sysid, isRecordWatcherEnabled } = properties;

	dispatch(FETCH_PLAN_RECOVERY_TEAMS_DETAILS_REQUESTED, {
		targetTableIdentifier: PLAN_RECOVERY_TEAMS_TABLE,
		sysId: sysid,
		queryColumns: `${DEFAULT_USER_COLUMN}, ${DEFAULT_GROUP_COLUMN}`,
	});

	if (isRecordWatcherEnabled) {
		dispatch(RECORD_WATCHER_SUBSCRIPTION_REQUESTED, {
			table: PLAN_RECOVERY_TEAMS_TABLE,
			filter: `plan=${sysid}`,
			onItemChangeAction: {
				actionType: FETCH_PLAN_RECOVERY_TEAMS_DETAILS_REQUESTED,
				actionPayload: {
					targetTableIdentifier: PLAN_RECOVERY_TEAMS_TABLE,
					sysId: sysid,
					queryColumns: `${DEFAULT_USER_COLUMN}, ${DEFAULT_GROUP_COLUMN}`,
				},
			},
		});
	}
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect: fetchPlanRecoveryTeamData,
	},
};

export default actionHandlers;
