const planTemplateOrderByQuery = `query ($sysId: String!) {
    GlideRecord_Query {
        {{ table }} (sys_id: $sysId) {
            plan: _results {
                _row_data{
                    canWrite
                }
                template {
                    results: _reference {
                        document_sections {
                            value
                        }
                    }
                }
            }
        }
    }
}`;

export default planTemplateOrderByQuery;
