import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import BIA_RESULTS_SUMMARY_ACTION from '../constants/BiaResultsSummaryCardQueryActionTypes';
import biaResultsSummaryQuery from '../queries/BiaResultsSummaryCardQuery';
import {
	IMPACT_ANALYSIS_TABLE,
	IMPACT_ANALYSIS_COLUMN_NAMES,
	FETCH_ERROR_MESSAGE,
} from '../constants/BiaResultsSummaryCardConstants';

const requestBiaResultsSummary = createGraphQLEffect(biaResultsSummaryQuery, {
	variableList: ['sysId'],
	templateVarList: ['table'],
	successActionType:
		BIA_RESULTS_SUMMARY_ACTION.BIA_RESULTS_SUMMARY_RECORD_SUCCEEDED,
	errorActionType: BIA_RESULTS_SUMMARY_ACTION.BIA_RESULTS_SUMMARY_RECORD_FAILED,
});

const getRecordFetchSuccess = ({ updateState, action }) => {
	const dataReturned = get(
		action.payload,
		`data.BiaResultsQuery.${IMPACT_ANALYSIS_TABLE}.results[0]`
	);

	if (dataReturned) {
		const requiresDataBackup = get(
			dataReturned,
			`template.biaTemplateTable.primary_element_assessed.elementsDefinitionTable.requires_data_backup.value`
		);
		const {
			[IMPACT_ANALYSIS_COLUMN_NAMES.RECOVERY_POINT_OBJECTIVE]: {
				displayValue: recoveryPointObjective,
			},
			[IMPACT_ANALYSIS_COLUMN_NAMES.ADJUSTED_RPO]: {
				displayValue: adjustedRecoveryPointObjective,
			},
			[IMPACT_ANALYSIS_COLUMN_NAMES.RECOVERY_TIME_OBJECTIVE]: {
				displayValue: recoveryTimeObjective,
			},
			[IMPACT_ANALYSIS_COLUMN_NAMES.ADJUSTED_RTO]: {
				displayValue: adjustedRecoveryTimeObjective,
			},
			[IMPACT_ANALYSIS_COLUMN_NAMES.RECOVERY_TIER]: {
				displayValue: recoveryTier,
			},
		} = dataReturned;

		updateState({
			requiresDataBackup,
			recoveryPointObjective,
			adjustedRecoveryPointObjective,
			recoveryTimeObjective,
			adjustedRecoveryTimeObjective,
			recoveryTier,
			isLoading: false,
		});
	}

	updateState({
		isLoading: false,
	});
};

const getRecordFetchFailure = ({ action, updateState }) => {
	const error = action.payload;
	console.error(FETCH_ERROR_MESSAGE);
	console.error(error);

	updateState({
		isLoading: false,
	});
};

const actionHandlers = {
	[BIA_RESULTS_SUMMARY_ACTION.BIA_RESULTS_SUMMARY_RECORD_REQUESTED]: requestBiaResultsSummary,
	[BIA_RESULTS_SUMMARY_ACTION.BIA_RESULTS_SUMMARY_RECORD_SUCCEEDED]: getRecordFetchSuccess,
	[BIA_RESULTS_SUMMARY_ACTION.BIA_RESULTS_SUMMARY_RECORD_FAILED]: getRecordFetchFailure,
};

export default actionHandlers;
