import { get } from 'lodash';
import {
	CONSTANTS,
	ACTION_TYPES,
} from '../../ItemPickerModal/constants/ItemPickerModalCommonConstants';
import { selectionTableNameQueryActions } from '../../ItemPickerModal/actions/ItemPickerModalCommonActions';

const actions = {
	...selectionTableNameQueryActions,
};

const getSelectionTableNameQuerySuccess = ({ action, updateState }) => {

	const elementDefinitionLabel = get(
		action.payload,
		`data.GlideRecord_Query.sn_bcp_plan_loss_scenario._results[0].loss_scenario._reference.elements_impacted.displayValue`
	);

	let response = get(
		action.payload,
		`data.GlideRecord_Query.sn_bcp_plan_loss_scenario._results[0].loss_scenario._reference.elements_impacted._reference`
	);

	const selectionTable = get(response, 'source_table.value');
	const fetchedTableEncodedQuery = get(response, 'filter.value');
	const selectionTableFields = get(response, 'source_table_fields.value');

	updateState({
		selectionTable,
		elementDefinitionLabel,
		fetchedTableEncodedQuery,
		selectionTableFields,
		isLoading: false,
	});
};

const getSelectionTableNameQueryFailure = ({ action, updateState }) => {
	const error = action.payload;

	console.error(error);

	updateState({
		isLoading: false,
		errorMessage: CONSTANTS.SELECTION_TABLE_NAME_QUERY_ERROR_MESSAGE,
	});
};

const actionHandlers = {
	[ACTION_TYPES.ITEM_PICKER_TABLE_NAME_SUCCEEDED]: getSelectionTableNameQuerySuccess,
	[ACTION_TYPES.ITEM_PICKER_TABLE_NAME_FAILED]: getSelectionTableNameQueryFailure,
};

export { actions, actionHandlers };
