import {
	LEFT_CAROUSEL_SCROLL_REQUESTED,
	RIGHT_CAROUSEL_SCROLL_REQUESTED,
	CAROUSEL_SECTION_COUNT_UPDATED,
} from '../constants/DynamicCarouselActionTypes';

const scrollLeft = ({ state, properties, updateState }) => {
	const {
		startDisplayIndex,
		endDisplayIndex,
		currentSectionIndex,
		carouselSectionsCount,
	} = state;
	const { elements, maxElementsPerSection } = properties;
	const elementsCount = elements.length;
	let newStartDisplayIndex;
	let newEndDisplayIndex;
	let newCurrentSectionIndex;

	if (startDisplayIndex === 0) {
		const modulusOffset = elementsCount % maxElementsPerSection;
		const isOffsetNeeded = modulusOffset !== 0;
		newStartDisplayIndex = isOffsetNeeded
			? elementsCount - modulusOffset
			: elementsCount - maxElementsPerSection;
		newEndDisplayIndex = Math.ceil(elementsCount / maxElementsPerSection) *
		maxElementsPerSection;
		newCurrentSectionIndex = carouselSectionsCount - 1;

		return updateState({
			startDisplayIndex: newStartDisplayIndex,
			endDisplayIndex: newEndDisplayIndex,
			currentSectionIndex: newCurrentSectionIndex,
		});
	}

	newStartDisplayIndex = startDisplayIndex - maxElementsPerSection;
	newEndDisplayIndex = endDisplayIndex - maxElementsPerSection;
	newCurrentSectionIndex = currentSectionIndex - 1;

	return updateState({
		startDisplayIndex: newStartDisplayIndex,
		endDisplayIndex: newEndDisplayIndex,
		currentSectionIndex: newCurrentSectionIndex,
	});
};

const scrollRight = ({ state, properties, updateState }) => {
	const { startDisplayIndex, endDisplayIndex, currentSectionIndex } = state;
	const { elements, maxElementsPerSection } = properties;

	if (endDisplayIndex < elements.length) {
		return updateState({
			startDisplayIndex: startDisplayIndex + maxElementsPerSection,
			endDisplayIndex: endDisplayIndex + maxElementsPerSection,
			currentSectionIndex: currentSectionIndex + 1,
		});
	}

	return updateState({
		startDisplayIndex: 0,
		endDisplayIndex: maxElementsPerSection,
		currentSectionIndex: 0,
	});
};

const updateSectionCountAndEndIndex = ({action, updateState}) => {
	const { elements, maxElementsPerSection } = action.payload;
	const carouselSectionsCount = Math.ceil(
		elements.length / maxElementsPerSection
	);
	const endDisplayIndex = maxElementsPerSection;

	return updateState({
		carouselSectionsCount,
		endDisplayIndex,
	});
}

const actionHandlers = {
	[LEFT_CAROUSEL_SCROLL_REQUESTED]: {
		effect: scrollLeft,
	},
	[RIGHT_CAROUSEL_SCROLL_REQUESTED]: {
		effect: scrollRight,
	},
	[CAROUSEL_SECTION_COUNT_UPDATED]: {
		effect: updateSectionCountAndEndIndex,
	}
};

export default actionHandlers;
