import '@servicenow/now-loader';
import CombinedScoreCard from '../CombinedScoreCard/CombinedScoreCardElement';
import { SECONDARY_SCORES_ORIENTATION } from '../CombinedScoreCard/CombinedScoreCardConstants';
import {
	ACTION_BUTTON_ICON,
	ACTION_BUTTON_ACTION_NAME,
	ACTION_BUTTON_ARIA_LABEL,
	ACTION_BUTTON_TOOLTIP,
	LOADING_VIEW_MESSAGE,
} from './constants/PlanActivitiesScoreCardConstants';

const view = (state) => {
	const {
		isLoading,
		totalActivitiesCount,
		assignedActivitiesCount,
		unAssignedActivitiesCount,
		properties,
	} = state;

	const {
		totalActivitiesCountLabel,
		assignedActivitiesCountLabel,
		unAssignedActivitiesCountLabel,
		cardHeaderLabel,
		isActionButtonDisplayed,
		relatedItemTabIndex,
	} = properties;

	const combinedScoreCardProperties = {
		cardHeaderLabel,
		primaryScore: {
			label: totalActivitiesCountLabel,
			score: `${totalActivitiesCount}`,
		},
		secondaryScores: [
			{
				heading: assignedActivitiesCountLabel,
				score: `${assignedActivitiesCount}`,
			},
			{
				heading: unAssignedActivitiesCountLabel,
				score: `${unAssignedActivitiesCount}`,
			},
		],
		secondaryScoreOrientation: SECONDARY_SCORES_ORIENTATION.VERTICAL,
		isActionButtonDisplayed,
		actionButtonIcon: ACTION_BUTTON_ICON,
		actionButtonActionName: ACTION_BUTTON_ACTION_NAME,
		actionButtonActionPayload: {
			params: {
				selectedTabIndex: relatedItemTabIndex,
			},
			redirectEmptyState: true,
		},
		actionButtonTooltip: ACTION_BUTTON_TOOLTIP,
		actionButtonAriaLabel: ACTION_BUTTON_ARIA_LABEL,
	};

	const loadingView = (
		<div className="loading-view">
			<now-loader label={LOADING_VIEW_MESSAGE} />
		</div>
	);

	if (isLoading) {
		return loadingView;
	}

	return <CombinedScoreCard fillHeight {...combinedScoreCardProperties} />;
};

export default view;
