import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import {
	FETCH_PLAN_SCOPE_ITEMS,
	NOW_RADIO_BUTTON_VALUE_SET,
	NOW_TYPEAHEAD_ITEM_SELECTED,
} from '../constants/RelatedAssetDependencyItemPickerModalActionTypes';
import {
	FETCH_PLAN_SCOPE_ITEMS_ERROR_MESSAGE,
	FILTER_BY_PLAN_SCOPE_CHOICE,
} from '../constants/RelatedAssetDependencyItemPickerModalConstants';
import planAssetQuery from '../queries/FetchPlanAssetsDataQuery';

const fetchPlanScopeItems = createGraphQLEffect(planAssetQuery, {
	variableList: ['parentRecordSysId'],
	templateVarList: [],
	successActionType: FETCH_PLAN_SCOPE_ITEMS.SUCCEEDED,
	errorActionType: FETCH_PLAN_SCOPE_ITEMS.FAILED,
});

const fetchPlanScopeItemsSuccess = ({ action, updateState }) => {
	const dataResults = get(
		action.payload,
		`data.GlideRecord_Query.sn_bcp_plan_loss_scenario._results[0]._query.queryForPlanAssets.sn_bcp_plan_asset._results`
	);

	if (!dataResults) {
		updateState({
			isLoading: false,
			errorMessage: FETCH_PLAN_SCOPE_ITEMS_ERROR_MESSAGE,
		});
	} else {
		const planScopeItemsList = dataResults.map((record) => {
			let planAsset = {};

			planAsset['id'] = get(record, `sys_id.value`);
			planAsset['label'] = get(record, `name.displayValue`);
			planAsset['fields'] = {
				bia: get(record, `impact_analysis.value`),
			};

			return planAsset;
		});

		updateState({
			planScopeItemsList,
		});
	}
};

const fetchPlanScopeItemsFailure = ({ action, updateState }) => {
	const { data } = action.payload;

	console.error(data);
	updateState({
		isLoading: false,
		errorMessage: FETCH_PLAN_SCOPE_ITEMS_ERROR_MESSAGE,
	});
};

const setChoiceSelection = ({ action, properties, updateState, dispatch }) => {
	const { value } = action.payload;
	const { parentRecordSysId } = properties;

	if (value === FILTER_BY_PLAN_SCOPE_CHOICE.id) {
		dispatch(FETCH_PLAN_SCOPE_ITEMS.REQUESTED, {
			parentRecordSysId,
		});
	} else {
		updateState({
			selectedPlanScopeValue: {},
		});
	}

	updateState({
		radioSelectionValue: value,
		listPickerOptionalProps: { page: 1 },
		recordListPickerFilterConditions: '',
		currentlySelectedEncodedQuery: '',
	});
};

const setPlanScopeItem = ({ action, updateState }) => {
	const { payload } = action;
	const { item } = payload;

	updateState({
		selectedPlanScopeValue: item.id ? item : {},
	});
};

const actionHandlers = {
	[NOW_RADIO_BUTTON_VALUE_SET]: {
		effect: setChoiceSelection,
		stopPropogation: true,
	},
	[NOW_TYPEAHEAD_ITEM_SELECTED]: {
		effect: setPlanScopeItem,
		stopPropogation: true,
	},
	[FETCH_PLAN_SCOPE_ITEMS.REQUESTED]: fetchPlanScopeItems,
	[FETCH_PLAN_SCOPE_ITEMS.SUCCEEDED]: fetchPlanScopeItemsSuccess,
	[FETCH_PLAN_SCOPE_ITEMS.FAILED]: fetchPlanScopeItemsFailure,
};

export { actionHandlers };
