import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	FETCH_ERROR_MESSAGE,
	QUERY_COLUMN_VALUES,
	DEFAULT_BIA_TABLE,
} from '../constants/NewBiaDynamicModalConstants';
import {
	CREATE_BIA_QUERY_INITIATED,
	CREATE_BIA_QUERY_REQUESTED,
	CREATE_BIA_QUERY_SUCCEEDED,
	CREATE_BIA_QUERY_FAILED,
	PREVIEW_RECORD,
	MODAL_CLOSE_CLICKED,
} from '../constants/NewBiaDynamicModalActionTypes';
import insertBiaQuery from '../queries/InsertBiaQuery';

const initiateCreateBia = ({ state, dispatch }) => {
	const { formElements } = state;
	const fields = formElements.map((element) => {
		return element.name;
	});
	const columns = fields
		.map((_colName) => `${_colName} { ${QUERY_COLUMN_VALUES} }`)
		.join('\n');

	const values = fields.reduce((_updateValueStr, _colName) => {
		return `${_updateValueStr} ${_colName}: "${state[_colName]}"`;
	}, '');

	dispatch(CREATE_BIA_QUERY_REQUESTED, {
		values,
		columns,
	});
};

const createBiaRequested = createGraphQLEffect(insertBiaQuery, {
	templateVarList: ['values', 'columns'],
	successActionType: CREATE_BIA_QUERY_SUCCEEDED,
	errorActionType: CREATE_BIA_QUERY_FAILED,
});

const createBiaSucceeded = ({ action, updateState, dispatch }) => {
	const data = action.payload;

	if (data.errors.length) {
		console.error(FETCH_ERROR_MESSAGE);
		console.error(data.errors);
		updateState({
			error: true,
			isLoading: false,
		});

		return;
	}

	const sysID = get(
		data,
		`data.GlideRecord_Mutation.insert_${DEFAULT_BIA_TABLE}.sys_id.value`
	);

	updateState({
		isLoading: false,
	});
	dispatch(PREVIEW_RECORD, {
		table: DEFAULT_BIA_TABLE,
		sys_id: sysID,
	});
	dispatch(MODAL_CLOSE_CLICKED, {
		value: false,
	});
};

const createBiaFailed = ({ action, updateState }) => {
	const data = action.payload;

	console.error(FETCH_ERROR_MESSAGE);
	console.error(data.errors);
	updateState({
		error: true,
		isLoading: false,
	});
};

const actionHandlers = {
	[CREATE_BIA_QUERY_INITIATED]: {
		effect: initiateCreateBia,
		stopPropagation: true,
	},
	[CREATE_BIA_QUERY_REQUESTED]: createBiaRequested,
	[CREATE_BIA_QUERY_SUCCEEDED]: {
		effect: createBiaSucceeded,
		stopPropagation: true,
	},
	[CREATE_BIA_QUERY_FAILED]: {
		effect: createBiaFailed,
		stopPropagation: true,
	},
};

export default actionHandlers;