import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './PlanAssetsOverviewCardView';
import styles from './PlanAssetsOverviewCardElement.scss';
import lifecycleActionHandlers from './actions/PlanAssetsOverviewCardLifecycleActions';
import planAssetsQueryActionHandlers from './actions/PlanAssetsOverviewQueryActions';
import {
	actions as recordWatcherActions,
	actionHandlers as recordWatcherActionHandlers,
} from './actions/RecordWatcherActions';
import {
	DEFAULT_CARD_HEADER_LABEL,
	DEFAULT_CARD_HEADER_LABEL_VARIANT,
	DEFAULT_CARD_HEADER_LABEL_SIZE,
	DEFAULT_VISUALIZATION_CHART_TYPE,
	DEFAULT_COVERED_ASSETS_COLOR_HEX,
	DEFAULT_UNCOVERED_ASSETS_COLOR_HEX,
	DEFAULT_SHOW_TOOLTIP_SETTING,
	DEFAULT_COVERED_ASSETS_LABEL,
	DEFAULT_UNCOVERED_ASSETS_LABEL,
	DEFAULT_TOTAL_ASSETS_COUNT_LABEL,
	DEFAULT_EMPTY_STATE_LABEL,
	DEFAULT_SECTION_ELEMENT_COUNT,
	DEFAULT_ACTIVE_SECTION_INDICATOR_COLOR,
	DEFAULT_INACTIVE_SECTION_INDICATOR_COLOR,
	DEFAULT_CAROUSEL_BUTTON_SIZE,
	DEFAULT_CARD_TAB_INDEX,
} from './constants/PlanAssetsOverviewCardConstants';

const TAG_NAME = 'sn-bcm-plan-assets-overview-card';

/**
 * A carousel type card, displaying Plan Asset's coverage.
 *
 * ```
 *
 * <sn-bcm-plan-assets-overview-card
 * 		maxElementsPerSection={4}
 * 		activeSectionIndicatorColor="#000000"
 * 		inactiveSectionIndicatorColor="#ffffff"
 * ></sn-bcm-plan-assets-overview-card>
 *
 * ```
 *
 * @seismicElement sn-bcm-plan-assets-overview-card
 * @summary A carousel type card, displaying plan's asset coverage.
 */

createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		 * Text label to display on the card header.
		 *
		 * @type {string}
		 */
		cardHeaderLabelText: {
			default: DEFAULT_CARD_HEADER_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Style variant of the card header label.
		 *
		 * @type {string}
		 */
		cardHeaderLabelVariant: {
			default: DEFAULT_CARD_HEADER_LABEL_VARIANT,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Size of the card header label text.
		 *
		 * @type {string}
		 */
		cardHeaderLabelSize: {
			default: DEFAULT_CARD_HEADER_LABEL_SIZE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Type of visualization to display. Choose between 'donut' or 'pie'.
		 *
		 * @type {string}
		 */
		visualizationChartType: {
			default: DEFAULT_VISUALIZATION_CHART_TYPE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Color hex-code to use for assets with a recovery strategy.
		 *
		 * @type {string}
		 */
		coveredAssetsColorHex: {
			default: DEFAULT_COVERED_ASSETS_COLOR_HEX,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Color hex-code to use for assets without a recovery strategy.
		 *
		 * @type {string}
		 */
		uncoveredAssetsColorHex: {
			default: DEFAULT_UNCOVERED_ASSETS_COLOR_HEX,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label to appear for covered assets (tooltip and legent-label).
		 *
		 * @type {string}
		 */
		coveredAssetsLabel: {
			default: DEFAULT_COVERED_ASSETS_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Label to appear for uncovered assets (tooltip and legent-label).
		 *
		 * @type {string}
		 */
		uncoveredAssetsLabel: {
			default: DEFAULT_UNCOVERED_ASSETS_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Boolean indicating whether tooltips display on chart hover.
		 *
		 * @type {boolean}
		 */
		isTooltipEnabled: {
			default: DEFAULT_SHOW_TOOLTIP_SETTING,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Label to show for total Plan-loss-scenario asset counts.
		 *
		 * @type {string}
		 */
		totalCountLabel: {
			default: DEFAULT_TOTAL_ASSETS_COUNT_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * SysId of the BCM Plan record this widget is displayed on.
		 *
		 * @type {string}
		 */
		businessContinuityPlanSysId: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Text to display when no PLan Loss Scenarios are found.
		 *
		 * @type {string}
		 */
		emptyStateLabel: {
			default: DEFAULT_EMPTY_STATE_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},

		/**
		 * Number of plan Loss Scenario charts to show per carousel slide/section.
		 *
		 * @type {number}
		 */
		maxElementsPerSection: {
			default: DEFAULT_SECTION_ELEMENT_COUNT,
			required: false,
			schema: {
				type: 'number',
			},
		},
		/**
		 * Hex color to indicate active carousel section (ie. #00000).
		 *
		 * @type {string}
		 */
		activeSectionIndicatorColor: {
			default: DEFAULT_ACTIVE_SECTION_INDICATOR_COLOR,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Hex color to indicate inactive carousel section (ie. #00000).
		 *
		 * @type {string}
		 */
		inactiveSectionIndicatorColor: {
			default: DEFAULT_INACTIVE_SECTION_INDICATOR_COLOR,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Size of carousel scroll buttons (ie. 'sm','md').
		 *
		 * @type {string}
		 */
		carouselScrollButtonSize: {
			default: DEFAULT_CAROUSEL_BUTTON_SIZE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * If enabled, a record watcher will keep this component updated in real-time.
		 *
		 * @type {boolean}
		 */
		isRecordWatcherEnabled: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * Related Item Tab Index number for icon button
		 *
		 * @type {number}
		 */
		relatedItemTabIndex: {
			default: DEFAULT_CARD_TAB_INDEX,
			required: false,
			schema: {
				type: 'number',
			},
		},
	},
	initialState: {
		isLoading: true,
		assetDependenciesByLossScenario: [],
		isRecordWatcherActive: false,
		lossScenarioRecordWatcherFilter: '',
		relatedAssetsRecordWatcherFilter: '',
		isAccessibilityModeActive: false,
	},
	actions: {
		...recordWatcherActions,
	},
	actionHandlers: {
		...lifecycleActionHandlers,
		...planAssetsQueryActionHandlers,
		...recordWatcherActionHandlers,
	},
	styles,
});

export default TAG_NAME;
