import { actionTypes } from '@servicenow/ui-core';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import {
	FETCH_TABLE_RECORD_METADATA_REQUESTED,
	FETCH_TABLE_RECORD_METADATA_SUCCEEDED,
	FETCH_TABLE_RECORD_METADATA_FAILED,
	FORM_UPDATE_HEADER,
} from '../constants/RecordModalActionTypes';
import { TABLE_RECORD_METADATA_FETCH_ERROR_MESSAGE } from '../constants/RecordModalConstants';
import recordMetaDataQuery from '../queries/TableRowMetaDataQuery';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const fetchTableRecordMetaData = createGraphQLEffect(recordMetaDataQuery, {
	variableList: ['sysId'],
	templateVarList: ['table'],
	successActionType: FETCH_TABLE_RECORD_METADATA_SUCCEEDED,
	errorActionType: FETCH_TABLE_RECORD_METADATA_FAILED,
});

const loadFormHeader = ({ updateState }) => {
	updateState({
		isModalLoading: false,
	});
};

const getTableRecordMetaDataQuerySuccess = ({
	action,
	updateState,
	properties,
}) => {
	const fetchResult = action.payload;
	const fetchErrors = fetchResult.errors;

	if (fetchErrors.length) {
		console.error(TABLE_RECORD_METADATA_FETCH_ERROR_MESSAGE);
		console.error(fetchErrors);

		return;
	}

	const { table } = properties;
	const recordCanWrite = get(
		action.payload,
		`data.GlideRecord_Query.${table}._results[0]._row_data.canWrite`
	);

	updateState({
		isRecordEditable: recordCanWrite,
	});
};

const getTableRecordMetaDataQueryFailure = ({ action }) => {
	const error = action.payload;

	console.error(TABLE_RECORD_METADATA_FETCH_ERROR_MESSAGE);
	console.error(error);
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ dispatch, state }) => {
		const { properties } = state;
		const { sysId, table } = properties;

		if (sysId !== '-1') {
			dispatch(FETCH_TABLE_RECORD_METADATA_REQUESTED, {
				sysId,
				table,
			});
		}
	},
	[FORM_UPDATE_HEADER]: loadFormHeader,
	[FETCH_TABLE_RECORD_METADATA_REQUESTED]: fetchTableRecordMetaData,
	[FETCH_TABLE_RECORD_METADATA_SUCCEEDED]: getTableRecordMetaDataQuerySuccess,
	[FETCH_TABLE_RECORD_METADATA_FAILED]: getTableRecordMetaDataQueryFailure,
};

export default actionHandlers;
