import { createHttpEffect } from '@servicenow/ui-effect-http';
import { get } from 'lodash';
import {
	EXCLUDED_ITEMS_QUERY_REQUESTED,
	EXCLUDED_ITEMS_QUERY_STARTED,
	EXCLUDED_ITEMS_QUERY_SUCCEDED,
	EXCLUDED_ITEMS_QUERY_FAILED,
} from '../constants/ItemPickerModalActionTypes';
import {
	excludedItemsQueryUrl,
	EXCLUDED_ITEMS_QUERY_ERROR_MESSAGE,
} from '../constants/ItemPickerModalConstants';

const actions = {
	[EXCLUDED_ITEMS_QUERY_REQUESTED]: {
		private: true,
	},
	[EXCLUDED_ITEMS_QUERY_STARTED]: {
		private: true,
	},
	[EXCLUDED_ITEMS_QUERY_SUCCEDED]: {
		private: true,
	},
	[EXCLUDED_ITEMS_QUERY_FAILED]: {
		private: true,
	},
};

const getExcludedItemsQuerySuccess = ({ action, state, updateState }) => {
	const excludedItems = get(action, 'payload.result.excludedItems');
	const excludedItemsEncodedQuery = excludedItems.length
		? `sys_idNOT IN${excludedItems.join(',')}`
		: '';
	const { fetchedTableEncodedQuery } = state;
	const itemPickerOptionsFilter = fetchedTableEncodedQuery
		? `${fetchedTableEncodedQuery}^${excludedItemsEncodedQuery}`
		: excludedItemsEncodedQuery;

	updateState({
		itemPickerOptionsFilter,
		isLoading: false,
	});
};

const getExcludedItemsQueryFailure = ({ action, updateState }) => {
	const { data } = action.payload;

	console.error(data);
	updateState({
		isLoading: false,
		errorMessage: EXCLUDED_ITEMS_QUERY_ERROR_MESSAGE,
	});
};

const actionHandlers = {
	[EXCLUDED_ITEMS_QUERY_REQUESTED]: createHttpEffect(excludedItemsQueryUrl, {
		method: 'GET',
		queryParams: ['parentRecordSysId', 'excludedItemsTableIdentifier'],
		startActionType: EXCLUDED_ITEMS_QUERY_STARTED,
		successActionType: EXCLUDED_ITEMS_QUERY_SUCCEDED,
		errorActionType: EXCLUDED_ITEMS_QUERY_FAILED,
		batch: false,
	}),
	[EXCLUDED_ITEMS_QUERY_SUCCEDED]: getExcludedItemsQuerySuccess,
	[EXCLUDED_ITEMS_QUERY_FAILED]: getExcludedItemsQueryFailure,
};

export { actions, actionHandlers };
