import { get } from 'lodash';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	PLAN_ACTIVITIES_AGGREGATE_URL,
	FETCH_ERROR_MESSAGE,
	DEFAULT_PLAN_LOSS_SCENARIO_TABLE,
	DEFAULT_PLAN_LOSS_SCENARIO_FIELD_NAME,
	DEFAULT_ASSET_DEPENDENCY_TABLE,
	DEFAULT_RECOVERY_STRATEGY_TABLE,
	DEFAULT_BUSINESS_CONTINUITY_PLAN_FIELD_NAME,
} from '../constants/PlanAssetsOverviewCardConstants';
import {
	RECORD_WATCHER_STATUS_CHANGE_REQUESTED,
	FETCH_PLAN_ASSET_DETAILS_REQUESTED_REQUESTED,
	FETCH_PLAN_ASSET_DETAILS_REQUESTED_SUCCEEDED,
	FETCH_PLAN_ASSET_DETAILS_REQUESTED_FAILED,
	REPORT_ACCESSIBILITY_PREFERENCE_REQUESTED,
	REPORT_ACCESSIBILITY_PREFERENCE_SUCCEEDED,
	REPORT_ACCESSIBILITY_PREFERENCE_FAILED,
} from '../constants/PlanAssetsOverviewCardActionTypes';
import { getRecordWatcherFilter } from '../../../util/ambSubscriptionUtil';
import userPreferenceQuery from '../queries/PieChartUserPreferenceQuery';

const requestPlanAssetsData = createHttpEffect(PLAN_ACTIVITIES_AGGREGATE_URL, {
	method: 'GET',
	queryParams: ['businessContinuityPlanSysId'],
	successActionType: FETCH_PLAN_ASSET_DETAILS_REQUESTED_SUCCEEDED,
	errorActionType: FETCH_PLAN_ASSET_DETAILS_REQUESTED_FAILED,
	batch: false,
});

const getPlanAssetsQuerySuccess = ({
	action,
	state,
	properties,
	updateState,
	dispatch,
}) => {
	const { isRecordWatcherActive } = state;
	const { isRecordWatcherEnabled, businessContinuityPlanSysId } = properties;

	const assetDependenciesByLossScenario = get(
		action,
		'payload.result.requestBody'
	);

	const lossScenarioSysIds = assetDependenciesByLossScenario.map(
		(lossScenario) => lossScenario.scenarioSysId
	);
	const lossScenarioRecordWatcherFilter = getRecordWatcherFilter(
		`${DEFAULT_BUSINESS_CONTINUITY_PLAN_FIELD_NAME}=${businessContinuityPlanSysId}`
	);
	const relatedAssetsFilterConditions = lossScenarioSysIds.map(
		(sysId) => `${DEFAULT_PLAN_LOSS_SCENARIO_FIELD_NAME}=${sysId}`
	);
	const relatedAssetsEncodedQueryString = relatedAssetsFilterConditions.join(
		'^NQ'
	);
	const relatedAssetsRecordWatcherFilter = getRecordWatcherFilter(
		relatedAssetsEncodedQueryString
	);

	if (isRecordWatcherEnabled) {
		if (!isRecordWatcherActive) {
			dispatch(RECORD_WATCHER_STATUS_CHANGE_REQUESTED, {
				table: DEFAULT_PLAN_LOSS_SCENARIO_TABLE,
				filter: lossScenarioRecordWatcherFilter,
				subscribeToRecordWatcher: true,
			});
		}

		dispatch(RECORD_WATCHER_STATUS_CHANGE_REQUESTED, {
			table: DEFAULT_ASSET_DEPENDENCY_TABLE,
			filter: relatedAssetsRecordWatcherFilter,
			subscribeToRecordWatcher: true,
		});

		dispatch(RECORD_WATCHER_STATUS_CHANGE_REQUESTED, {
			table: DEFAULT_RECOVERY_STRATEGY_TABLE,
			filter: relatedAssetsRecordWatcherFilter,
			subscribeToRecordWatcher: true,
		});
	}

	return updateState({
		isLoading: false,
		assetDependenciesByLossScenario,
		lossScenarioRecordWatcherFilter,
		relatedAssetsRecordWatcherFilter,
	});
};

const getPlanAssetsQueryFailed = ({ action, updateState }) => {
	const { data } = action.payload;
	const { error } = data;
	const { message } = error;

	console.error(message);
	console.error(FETCH_ERROR_MESSAGE);
	updateState({
		error: true,
		isLoading: false,
	});
};

const getUserPreferenceSuccess = ({ action, updateState }) => {
	const { payload } = action;

	const _userPreferences = get(
		payload,
		'data.GlideDomain_Query.user.preferences[0].value',
		{}
	);
	let accessibilityMode = false;

	if (_userPreferences === 'true') {
		accessibilityMode = true;
	}

	updateState({
		isAccessibilityModeActive: accessibilityMode,
	});
};

const getUserPreferenceFailure = ({ action }) => {
	const { data } = action.payload;

	console.error(data.error);
	console.error('Error occured fetching accessibility user preference');
};

const actionHandlers = {
	[FETCH_PLAN_ASSET_DETAILS_REQUESTED_REQUESTED]: requestPlanAssetsData,
	[FETCH_PLAN_ASSET_DETAILS_REQUESTED_SUCCEEDED]: {
		effect: getPlanAssetsQuerySuccess,
		stopPropogation: true,
	},
	[FETCH_PLAN_ASSET_DETAILS_REQUESTED_FAILED]: {
		effect: getPlanAssetsQueryFailed,
		stopPropogation: true,
	},
	[REPORT_ACCESSIBILITY_PREFERENCE_REQUESTED]: createGraphQLEffect(
		userPreferenceQuery,
		{
			successActionType: REPORT_ACCESSIBILITY_PREFERENCE_SUCCEEDED,
			errorActionType: REPORT_ACCESSIBILITY_PREFERENCE_FAILED,
		}
	),
	[REPORT_ACCESSIBILITY_PREFERENCE_SUCCEEDED]: {
		effect: getUserPreferenceSuccess,
		stopPropagation: true,
	},
	[REPORT_ACCESSIBILITY_PREFERENCE_FAILED]: {
		effect: getUserPreferenceFailure,
		stopPropagation: true,
	},
};

export default actionHandlers;
