import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './RecoveryAddPlansModalView';
import styles from './RecoveryAddPlansModalElement.scss';
import { MODAL_CLOSE_CLICKED } from './constants/RecordModalActionTypes';
import {
	OPTION_SELCTION_SCREEN_SUBTITLE,
	MODAL_HEADER_LABEL,
	NEXT_BUTTON_LABEL,
} from './constants/RecoveryAddPlansModalConstants';
import {
	actions as recoveryAddPlansActions,
	actionHandlers as recoveryAddPlansActionsHanlders,
} from './actions/RecoveryAddPlansActions';
import planFilterQueryActionHandlers from './actions/PlanFilterQueryActions';
import itemInsertionActionHandlers from './actions/ItemInsertionActions';
import eventAssetDataFetchActionHandlers from './actions/EventAssetDataQueryActions';

const tagName = 'sn-bcm-recovery-add-plan-modal';

/**
 * A modal to Add plans to recovery event asset
 * And add event asset and corresponding activated plans
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-recovery-add-plan-modal
 * 	listOfOptions=
 * </sn-bcm-recovery-add-plan-modal>
 * ```
 *
 * @seismicElement sn-bcm-recovery-add-plan-modal
 * @summary A modal to Add plans to a recovery event and add event asset and corresponding activated plans
 */

createCustomElement(tagName, {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		 * Boolean used to show/hide the modal
		 *
		 * @type {boolean}
		 */
		isModalOpen: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * shows confirmation when event in progress
		 *
		 * @type {boolean}
		 */
		isEventInProgress: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * list of options to select a plan i.e show all plans or select plan for impacted asset.
		 *
		 * @type {object}
		 */
		listOfOptions: {
			default: [],
			required: false,
			schema: {
				type: 'object',
			},
		},
		/**
		 * Event record sysId
		 *
		 * @type {string}
		 */
		eventSysId: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Columns to be displayed on Add Plans modal to show list of plans.
		 *
		 * @type {string}
		 */
		planColumns: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Columns to be displayed on Add Plans modal to show list of assets for selected plans.
		 *
		 * @type {string}
		 */
		planAssetColumns: {
			required: true,
			schema: {
				type: 'string',
			},
		},
	},
	initialState: {
		isOpenList: false,
		modalSubTitle: OPTION_SELCTION_SCREEN_SUBTITLE,
		selectionTable: '',
		selectionTableFields: '',
		showEventAssets: false,
		eventAssetsList: [],
		selectedAsset: {},
		actionButtonLabel: NEXT_BUTTON_LABEL,
		listOfSelectedItemsSysIds: [],
		disableActionButton: true,
		showAllPlans: false,
		assetBased: false,
		listQueryConditions: '',
		showConfirmation: false,
		errorMessage: '',
		modalTitle: MODAL_HEADER_LABEL,
		listPageNumber: 0,
	},
	actions: {
		...recoveryAddPlansActions,
	},
	actionHandlers: {
		...recoveryAddPlansActionsHanlders,
		...itemInsertionActionHandlers,
		...eventAssetDataFetchActionHandlers,
		...planFilterQueryActionHandlers,
	},
	styles,
});
export default tagName;
