import { createHttpEffect } from '@servicenow/ui-effect-http';
import PLAN_EXERCISE_ACTIONS from '../constants/ActionTypes';
import {
	REQUEST_RESULTS_URL,
	FETCH_ERROR_MESSAGE,
	NULL_TABLE_ERROR_MESSAGE,
	COMPLETED_EXERCISE_RESULT,
	EXERCISE_NO_RESULTS_LABEL,
} from '../constants/GeneralConstants';
import { NOW_COLOR_PALETTE_2 } from '../../../util/nowColorPalette';

const requestPlanExerciseResult = createHttpEffect(REQUEST_RESULTS_URL, {
	method: 'GET',
	queryParams: ['planSysId', 'eventType'],
	successActionType: PLAN_EXERCISE_ACTIONS.OVERALL_EXERCISE_RESULTS_SUCCEEDED,
	errorActionType: PLAN_EXERCISE_ACTIONS.OVERALL_EXERCISE_RESULTS_FAILED,
	batch: false,
});

const getPlanExerciseResultSuccess = ({ action, updateState }) => {
	const { payload } = action;

	if (payload.error) {
		console.error(FETCH_ERROR_MESSAGE);
		return updateState({
			error: true,
			isLoading: false,
		});
	}

	const { result: dataReturned } = payload;

	if (!dataReturned) {
		console.error(NULL_TABLE_ERROR_MESSAGE);
		return updateState({
			error: true,
			isLoading: false,
		});
	}

	if (!dataReturned.length) {
		return updateState({
			donutSeriesData: [],
			showDataLabels: false,
			isLoading: false,
		});
	}

	const _getDonutSectionColor = (resultValue) => {
		switch (resultValue) {
			case COMPLETED_EXERCISE_RESULT.SUCCESSFUL:
				return NOW_COLOR_PALETTE_2.POSITIVE;
			case COMPLETED_EXERCISE_RESULT.SUCCESSFUL_WITH_ISSUES:
				return NOW_COLOR_PALETTE_2.WARNING;
			case COMPLETED_EXERCISE_RESULT.NO_RESULTS:
				return NOW_COLOR_PALETTE_2.INFO;
			case COMPLETED_EXERCISE_RESULT.UNSUCCESSFUL:
				return NOW_COLOR_PALETTE_2.CRITICAL;
			default:
				return '';
		}
	};

	let totalCount = 0;
	const donutSeriesData = dataReturned.map(
		({ count, resultDisplayValue, resultValue }) => {
			totalCount += count;
			return {
				name: resultDisplayValue || EXERCISE_NO_RESULTS_LABEL,
				count: count,
				color: _getDonutSectionColor(resultValue),
			};
		}
	);

	const showDataLabels = totalCount ? true : false;

	updateState({
		donutSeriesData,
		showDataLabels,
		isLoading: false,
	});
};

const getPlanExerciseResultFailed = ({ action, updateState }) => {
	const { data } = action.payload;
	const { error } = data;
	const { message } = error;

	console.error(message);
	console.error(FETCH_ERROR_MESSAGE);
	updateState({
		error: true,
		isLoading: false,
	});
};

const actionHandlers = {
	[PLAN_EXERCISE_ACTIONS.OVERALL_EXERCISE_RESULTS_REQUESTED]: requestPlanExerciseResult,
	[PLAN_EXERCISE_ACTIONS.OVERALL_EXERCISE_RESULTS_SUCCEEDED]: {
		effect: getPlanExerciseResultSuccess,
	},
	[PLAN_EXERCISE_ACTIONS.OVERALL_EXERCISE_RESULTS_FAILED]: {
		effect: getPlanExerciseResultFailed,
	},
};

export default actionHandlers;
