import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	CMDB_CI_DEPENDENCY_QUERY_REQUESTED,
	CMDB_CI_DEPENDENCY_QUERY_STARTED,
	CMDB_CI_DEPENDENCY_QUERY_SUCCEEDED,
	CMDB_CI_DEPENDENCY_QUERY_FAILED,
} from '../constants/BiaDependencyItemPickerModalActionTypes';
import {
	DEFAULT_ITEM_PICKER_LOADING_MESSAGE,
	CMDB_CI_FETCH_ERROR_MESSAGE,
	MODAL_VIEW_TYPES,
} from '../constants/BiaDependencyItemPickerModalConstants';
import fetchCmdbCiRecordsQuery from '../queries/FetchCmdbCiRecordsQuery';

const requestCmdbCIChildRelations = createGraphQLEffect(
	fetchCmdbCiRecordsQuery,
	{
		variableList: ['queryConditions', 'maxRecordLimit'],
		startActionType: CMDB_CI_DEPENDENCY_QUERY_STARTED,
		successActionType: CMDB_CI_DEPENDENCY_QUERY_SUCCEEDED,
		errorActionType: CMDB_CI_DEPENDENCY_QUERY_FAILED,
	}
);

const getCmdbCiQueryStarted = ({ updateState }) => {
	updateState({
		loadingMessage: DEFAULT_ITEM_PICKER_LOADING_MESSAGE,
		isLoading: true,
	});
};

const getCmdbCiQuerySuccess = ({ state, action, updateState }) => {
	const { excludedItems, currentScreenView } = state;

	const dataReturned = get(
		action.payload,
		`data.GlideRecord_Query.cmdb_rel_ci._results`
	);

	if (!dataReturned || !dataReturned.length) {
		return updateState({
			cmdbCiRecordListPickerFilterConditions: 'sys_idIN',
			isLoading: false,
			currentScreenView: MODAL_VIEW_TYPES.CMDB_REL_TYPE_LIST_PICKER,
			prevScreenView: currentScreenView,
		});
	}

	const cmdbRelationshipItems = dataReturned.filter(
		({ child: { value } }) => !excludedItems.includes(value)
	);

	const cmdbRelationshipTypesFilterConditions = cmdbRelationshipItems
		.filter(
			(item, index) =>
				cmdbRelationshipItems.findIndex(
					({ type: { value } }) => value === item.type.value
				) === index
		)
		.map((item) => item.type.value)
		.join(',');

	updateState({
		isLoading: false,
		cmdbRelationshipTypesFilterConditions,
		cmdbRelationshipItems,
		currentScreenView: MODAL_VIEW_TYPES.CMDB_REL_TYPE_LIST_PICKER,
		prevScreenView: currentScreenView,
	});
};

const getCmdbCiQueryFailed = ({ action, updateState }) => {
	const error = action.payload;

	console.error(error);

	updateState({
		isLoading: false,
		errorMessage: CMDB_CI_FETCH_ERROR_MESSAGE,
	});
};

const actions = {
	[CMDB_CI_DEPENDENCY_QUERY_REQUESTED]: {
		private: true,
	},
	[CMDB_CI_DEPENDENCY_QUERY_STARTED]: {
		private: true,
	},
	[CMDB_CI_DEPENDENCY_QUERY_SUCCEEDED]: {
		private: true,
	},
	[CMDB_CI_DEPENDENCY_QUERY_FAILED]: {
		private: true,
	},
};

const actionHandlers = {
	[CMDB_CI_DEPENDENCY_QUERY_REQUESTED]: requestCmdbCIChildRelations,
	[CMDB_CI_DEPENDENCY_QUERY_STARTED]: getCmdbCiQueryStarted,
	[CMDB_CI_DEPENDENCY_QUERY_SUCCEEDED]: getCmdbCiQuerySuccess,
	[CMDB_CI_DEPENDENCY_QUERY_FAILED]: getCmdbCiQueryFailed,
};

export { actions, actionHandlers };
