import { actionTypes } from '@servicenow/ui-core';
import {
	RELATED_ASSETS_QUERY_REQUESTED,
	RECORD_WATCHER_STATUS_CHANGE_REQUESTED,
} from '../constants/AssetDependenciesScoreCardActionTypes';
import { getRecordWatcherFilter } from '../../../../util/ambSubscriptionUtil';
import { PLAN_LOSS_SCENARIO_FIELD } from '../constants/AssetDependenciesScoreCardConstants';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED } = actionTypes;

const fetchLossScenarioData = ({ properties, dispatch }) => {
	const {
		sysId,
		sysid,
		relatedAssetDependenciesTable,
		recoveryStrategiesTable,
	} = properties;

	dispatch(RELATED_ASSETS_QUERY_REQUESTED, {
		sysId: sysId || sysid,
		relatedAssetDependenciesTable,
		recoveryStrategiesTable,
	});

	const planLossScenarioFilter = getRecordWatcherFilter(
		`${PLAN_LOSS_SCENARIO_FIELD}=${sysId || sysid}`
	);

	dispatch(RECORD_WATCHER_STATUS_CHANGE_REQUESTED, {
		table: relatedAssetDependenciesTable,
		filter: planLossScenarioFilter,
		subscribeToRecordWatcher: true,
	});

	dispatch(RECORD_WATCHER_STATUS_CHANGE_REQUESTED, {
		table: recoveryStrategiesTable,
		filter: planLossScenarioFilter,
		subscribeToRecordWatcher: true,
	});
};

const disconnectRecordWatcher = ({ properties, dispatch }) => {
	const {
		sysid,
		sysId,
		relatedAssetDependenciesTable,
		recoveryStrategiesTable,
	} = properties;
	const planLossScenarioFilter = getRecordWatcherFilter(
		`${PLAN_LOSS_SCENARIO_FIELD}=${sysId || sysid}`
	);

	dispatch(RECORD_WATCHER_STATUS_CHANGE_REQUESTED, {
		table: relatedAssetDependenciesTable,
		filter: planLossScenarioFilter,
		subscribeToRecordWatcher: false,
	});

	dispatch(RECORD_WATCHER_STATUS_CHANGE_REQUESTED, {
		table: recoveryStrategiesTable,
		filter: planLossScenarioFilter,
		subscribeToRecordWatcher: false,
	});
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect: fetchLossScenarioData,
	},
	[COMPONENT_DISCONNECTED]: {
		effect: disconnectRecordWatcher,
	},
};

export default actionHandlers;
