import { getDefaultModalButton } from '../ItemPickerModal/utils/ItemPickerModalCommonUtil';
import {
	MODAL_VIEW_TYPES,
	DEFAULT_NEXT_BUTTON_LABEL,
	DEFAULT_BACK_BUTTON_LABEL,
} from './constants/RelatedAssetDependencyItemPickerModalConstants';
import {
	MODAL_NEXT_BUTTON_CLICKED,
	MODAL_BACK_BUTTON_CLICKED,
} from './constants/RelatedAssetDependencyItemPickerModalActionTypes';

const getModalFooterAction = (
	currentModalViewType,
	disableListPickerButton,
	disableNextButton
) => {
	const backButton = {
		variant: 'tertiary',
		label: DEFAULT_BACK_BUTTON_LABEL,
		clickActionType: MODAL_BACK_BUTTON_CLICKED,
	};

	const nextButton = {
		variant: 'primary',
		label: DEFAULT_NEXT_BUTTON_LABEL,
		clickActionType: MODAL_NEXT_BUTTON_CLICKED,
		disabled: disableNextButton,
	};

	switch (currentModalViewType) {
		case MODAL_VIEW_TYPES.RADIO_BUTTONS:
			return [nextButton, ...getDefaultModalButton(false)];
		case MODAL_VIEW_TYPES.RECORD_LIST_PICKER:
		default:
			return [
				...getDefaultModalButton(true, disableListPickerButton),
				backButton,
			];
	}
};

const getIfCheckedRadioList = (list, selectedValue) => {
	if (list && list.length) {
		return list.map((choice) => {
			if (choice.id === selectedValue) {
				return {
					...choice,
					checked: true,
				};
			}

			return choice;
		});
	}

	return list;
};

const getIfTypeaheadValueIsSet = (selectedPlanScopeValue) => {
	var additionalProps = {
		value: selectedPlanScopeValue.label || '',
		selectedItem: selectedPlanScopeValue.id || '',
	};

	return additionalProps;
};

export {
	getModalFooterAction,
	getIfCheckedRadioList,
	getIfTypeaheadValueIsSet,
};
