const planDocumentResetMutation = `mutation ($sysId: String!, $contentFieldValue: String!) {
	GlideRecord_Mutation {
		update_{{ tableName }} (
            sys_id: $sysId
            {{ contentField }}: $contentFieldValue
        ) {
			sys_id {
				value
			}
			{{ contentField }} {
				value
			}
		}
	}
}`;

export default planDocumentResetMutation;
