import { actionTypes } from '@servicenow/ui-core';
import { FETCH_PLAN_DOCUMENT_QUERY_REQUESTED } from '../constants/PlanDocumentOverviewScoreCardActionTypes';
import { RECORD_WATCHER_SUBSCRIPTION_REQUESTED } from '../../../behaviors/RecordWatcherBehavior';
import {
	PLAN_DOCUMENT_TABLE,
	DEFAULT_GROUP_BY_COLUMN,
} from '../constants/PlanDocumentOverviewScoreCardConstants';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const fetchDocumentOverviewData = ({ properties, dispatch }) => {
	const { sysid, isRecordWatcherEnabled } = properties;

	dispatch(FETCH_PLAN_DOCUMENT_QUERY_REQUESTED, {
		targetTableIdentifier: PLAN_DOCUMENT_TABLE,
		parentRecordSysId: sysid,
		groupByColumn: DEFAULT_GROUP_BY_COLUMN,
	});

	if (isRecordWatcherEnabled) {
		dispatch(RECORD_WATCHER_SUBSCRIPTION_REQUESTED, {
			table: PLAN_DOCUMENT_TABLE,
			filter: `plan=${sysid}`,
			onItemChangeAction: {
				actionType: FETCH_PLAN_DOCUMENT_QUERY_REQUESTED,
				actionPayload: {
					targetTableIdentifier: PLAN_DOCUMENT_TABLE,
					parentRecordSysId: sysid,
					groupByColumn: DEFAULT_GROUP_BY_COLUMN,
				},
			},
		});
	}
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect: fetchDocumentOverviewData,
	},
};

export default actionHandlers;
