import '@servicenow/now-modal';
import '@servicenow/now-record-list-connected';
import '@servicenow/now-select';
import '@servicenow/now-label-value';
import '@servicenow/now-alert';
import '@servicenow/now-radio-buttons';
import '@servicenow/now-loader';
import '@servicenow/now-heading';
import {
	CONSTANTS,
	ACTION_TYPES,
} from '../constants/ItemPickerModalCommonConstants';

const _getSectionHeading = (heading) =>
	heading ? (
		<now-label-value-stacked
			className="item-list-container-message"
			size="md"
			items={[
				{
					value: {
						type: 'string',
						value: heading,
					},
				},
			]}
		/>
	) : null;

const getRecordListPickerElement = (
	heading,
	table,
	queryConditions,
	limit,
	selectedColumns,
	optionalProperties = {}
) => {
	return (
		<div className="item-list-container">
			{_getSectionHeading(heading)}
			<now-record-list-connected-picker
				className="item-list"
				table={table}
				query={queryConditions}
				limit={limit}
				columns={selectedColumns}
				{...optionalProperties}
				hideSelectAll
			/>
		</div>
	);
};

const getErrorAlert = (errorMessage, header) => (
	<now-alert
		className={errorMessage ? 'now-alert-error-element' : 'hidden'}
		status="critical"
		icon="exclamation-triangle-fill"
		header={header}
		content={errorMessage}
	/>
);

const getNowSelectItem = (heading, items, label, required, error) => {
	let errorMsg = null;
	if (error) {
		errorMsg = [
			{
				status: 'critical',
				content: error,
				icon: 'circle-info-outline',
			},
		];
	}

	return (
		<div>
			<div>{_getSectionHeading(heading)}</div>
			<div className="select-impacted-item-container">
				<now-select
					items={items}
					label={label}
					required={required}
					search="initial"
					messages={errorMsg}
				/>
			</div>
		</div>
	);
};

const getRadioChoiceList = (
	heading,
	options,
	groupLabel = '',
	helperContent = ''
) => {
	return (
		<div className="item-picker-choice">
			{_getSectionHeading(heading)}
			<now-radio-buttons
				layout="vertical"
				className="item-picker-radio-buttons"
				name="choice"
				options={options}
				label={groupLabel}
				helperContent={helperContent}
			/>
		</div>
	);
};

const getLoaderElement = (loadingMessage) => (
	<div className="loading">
		<now-loader label={loadingMessage} />
	</div>
);

const getDefaultModalButton = (
	displaySaveButton = false,
	disableListPickerButton = true
) => {
	const cancelButton = {
		variant: 'secondary',
		label: CONSTANTS.DEFAULT_CANCEL_BUTTON_LABEL,
		clickActionType: ACTION_TYPES.ITEM_PICKER_EDIT_CANCELLED,
	};
	const saveButton = {
		variant: 'primary',
		label: CONSTANTS.DEFAULT_SAVE_BUTTON_LABEL,
		clickActionType: ACTION_TYPES.ITEM_PICKER_EDIT_SAVED,
		disabled: disableListPickerButton,
	};

	const modalFooterButtons = [cancelButton];

	if (displaySaveButton) {
		modalFooterButtons.unshift(saveButton);
	}

	return modalFooterButtons;
};

const getItemPickerModalView = (
	isModalOpen,
	modalHeaderLabel,
	modalFooterButtons,
	errorAlert,
	modalBody
) => (
	<div>
		<now-modal
			opened={isModalOpen}
			size="lg"
			header-label={modalHeaderLabel}
			footer-actions={modalFooterButtons}>
			{errorAlert}
			{modalBody}
		</now-modal>
	</div>
);

export {
	getRecordListPickerElement,
	getErrorAlert,
	getNowSelectItem,
	getRadioChoiceList,
	getLoaderElement,
	getDefaultModalButton,
	getItemPickerModalView,
};
