import '../EditableList/EditableListElement';
import { RECOVERY_TASK_LOADING } from './constants/PlanActivitiesListColumnConstants';
import { USER_PREFERENCE_PREFIX } from '../../util/grid-util/constants/GeneralConstants';

const view = (state) => {
	const { properties, config, hasWriteAccess } = state;
	const {
		tableName,
		recordLoadLimit,
		queryCondition,
		modalViewName,
	} = properties;
	const loadingViewMessage = RECOVERY_TASK_LOADING;
	const userPreferenceName = `${USER_PREFERENCE_PREFIX}.${tableName}.workspace`;

	const planActivitiesView = (
		<div className="activities-list-container">
			<sn-bcm-editable-list
				tableName={tableName}
				listConfig={config}
				filterQueryConditions={queryCondition}
				loadingViewMessage={loadingViewMessage}
				recordLoadLimit={recordLoadLimit}
				isReadOnly={!hasWriteAccess}
				modalViewName={modalViewName}
				allowColumnHidding={true}
				userPreferenceName = {userPreferenceName}
			/>
		</div>
	);

	return planActivitiesView;
};

export default view;
