import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	FORM_LAYOUT_QUERY_FAILED,
	FORM_LAYOUT_QUERY_SUCCEEDED,
	FORM_LAYOUT_QUERY_REQUESTED,
	CURRENT_USER_QUERY_REQUESTED,
} from '../constants/NewBiaDynamicModalActionTypes';
import { FETCH_ERROR_MESSAGE } from '../constants/NewBiaDynamicModalConstants';
import tableFormLayoutQuery from '../queries/TableFormLayoutQuery';

const requestFormLayout = createGraphQLEffect(tableFormLayoutQuery, {
	templateVarList: ['layoutView'],
	successActionType: FORM_LAYOUT_QUERY_SUCCEEDED,
	errorActionType: FORM_LAYOUT_QUERY_FAILED,
});

const requestFormLayoutSuccess = ({ state, action, updateState, dispatch }) => {
	const { payload } = action;

	if (payload.errors.length) {
		console.error(FETCH_ERROR_MESSAGE);
		console.error(payload.errors);
		updateState({
			error: true,
			isLoading: false,
		});
	}

	const { userAssignedColumnName } = state;

	const dataReturned = get(payload, 'data.GlideLayout_Query.formLayout');
	const formElements = dataReturned.elementsData;
	const mandatoryFields = formElements.map((element) => {
		if (element.name === userAssignedColumnName) {
			dispatch(CURRENT_USER_QUERY_REQUESTED);
		}

		const type = get(element, 'dictionary.type');

		if (type === 'choice') {
			const firstChoice = get(element, 'choices[0]');

			updateState({
				[element.name]: firstChoice.value,
				[`${element.name}DisplayValue`]: firstChoice.displayValue,
			});
		} else if (type === 'document_id') {
			const dependentField = get(element, 'dictionary.dependentField');

			updateState({
				dependentField,
				documentIDField: element.name,
			});
		} else if (type === 'reference' && element.reference === 'sys_user') {
			updateState({ userAssignedColumnName: element.name });
			dispatch(CURRENT_USER_QUERY_REQUESTED);
		}

		if (get(element, 'dictionary.isMandatory')) {
			return element.name;
		}
	});

	const { recordValues } = dataReturned;

	recordValues.map((field) => {
		if (field.value) {
			const { name, value, displayValue } = field;

			updateState({
				[name]: value || displayValue,
				[`${name}DisplayValue`]: displayValue || value,
			});
		}
	});

	updateState({
		mandatoryFields,
		formElements: dataReturned.elementsData,
		isLoading: false,
	});
};

const requestFormLayoutFail = ({ action, updateState }) => {
	const error = action.payload;

	console.error(FETCH_ERROR_MESSAGE);
	console.error(error);
	updateState({
		error: true,
		isLoading: false,
	});
};

const formLayoutActionHandlers = {
	[FORM_LAYOUT_QUERY_REQUESTED]: requestFormLayout,
	[FORM_LAYOUT_QUERY_SUCCEEDED]: {
		effect: requestFormLayoutSuccess,
		stopPropagation: true,
	},
	[FORM_LAYOUT_QUERY_FAILED]: {
		effect: requestFormLayoutFail,
		stopPropagation: true,
	},
};

export default formLayoutActionHandlers;