import '@servicenow/now-record-typeahead';
import '@servicenow/now-avatar';
import '@servicenow/now-button';
import '@servicenow/now-loader';
import '@servicenow/now-heading';
import '@servicenow/now-alert';
import '@servicenow/now-text-link';
import '../EmptyState/EmptyStateElement';
import TYPEAHEAD_ACTIONS from './constants/TypeAheadActionTypes';
import actionTypes from './constants/SelectionTableQueryActionTypes';
import { REMOVE_BUTTON_PAYLOAD_KEY } from './constants/DefaultPropertyValues';

const view = (state, { dispatch, updateState }) => {
	const {
		isLoading,
		typeaheadSearchTerm,
		typeaheadOptions,
		currentlySelected,
		usersAggregateCount,
		properties
	} = state;
	const {
		isNewRecord,
		loadingMessage,
		emptyStateLabel,
		avatarSize,
		sideActionHeaderLabel,
		typeaheadLabel,
		typeaheadPlaceholder,
		selectionTable,
		newRecordAlertMessage,
		removeButtonLabel,
		removeButtonTooltipAddendum,
		removeButtonAriaAddendum
	} = properties;

	const loadingView = (
		<div className="contributors-container loading">
			<now-loader
				label={loadingMessage}
			/>
		</div>
	);

	if (isLoading) {
		return loadingView;
	}

	const emptyContentElement = (
		<sn-bcm-empty-state emptyStateLabel={emptyStateLabel} />
	);

	const currentlySelectedListElement = (
		<div className="current-contributors-container">
			{
				currentlySelected.map(contributor => (
					<div className="current-contributor">
						<now-avatar
							className="avatar"
							userName={contributor.displayValue}
							imageSrc={contributor.avatar}
							size={avatarSize}>
						</now-avatar>
						<p className="contributor-name">
							<now-text-link
								label={contributor.displayValue}
								href="javascript:void(0)"
								on-click={() => {
									dispatch(actionTypes.SELECTED_VALUE_CLICKED, {
										table: selectionTable,
										sys_id: contributor.value
									});
								}}
							/>
						</p>
						<now-button
							className="remove-contributor-button"
							label={removeButtonLabel}
							tooltipContent={`${removeButtonTooltipAddendum} ${contributor.displayValue}`}
							size="sm"
							variant="secondary"
							configAria={{'aria-label':`${removeButtonAriaAddendum} ${contributor.displayValue}`}}
							append-to-payload={{
								buttonAction: REMOVE_BUTTON_PAYLOAD_KEY,
								valueToRemove: contributor.value
							}}>
						</now-button>
					</div>
				))
			}
		</div>
	);

	const selectedValuesContent = currentlySelected.length ? currentlySelectedListElement : emptyContentElement;

	const isNewRecordAlert = (
		<now-alert
			className={!isNewRecord ? 'hidden' : 'is-new-record-alert'}
			status="info"
			icon="info-circle-outline"
			content={newRecordAlertMessage}>
		</now-alert>
	);

	const contributorsView = (
		<div className="contributors-container">
			<now-heading
				className="contributors-main-header"
				label={sideActionHeaderLabel}
				variant="header-secondary"
			/>

			<now-record-typeahead
				className="contributors-typeahead-input"
				readonly={isNewRecord}
				value={typeaheadSearchTerm}
				label={typeaheadLabel}
				placeholder={typeaheadPlaceholder}
				options={typeaheadOptions}
				onChange={e => {
					e.persist();
					e.stopPropagation();
					updateState({
						typeaheadSearchTerm: e.target.value
					});
				}}
				onClick={() => {
					dispatch(TYPEAHEAD_ACTIONS.TYPEAHEAD_VALUE_ENTERED, {
						value: '',
					});
				}}
				totalMatchesCount={usersAggregateCount}
			/>
			{isNewRecordAlert}
			{selectedValuesContent}
		</div>
	);

	return contributorsView;
};

export default view;