const relatedItemStatusMutationQuery = `mutation ($sysid: String!, $statusValue: String!){
    GlideRecord_Mutation {
      update_{{relatedTableName}}(sys_id: $sysid {{relatedTableStatusFieldName}}: $statusValue) {
        sys_id {
          value
        }
        {{relatedTableStatusFieldName}} {
          value
        }
      }
    }
  }`;

export default relatedItemStatusMutationQuery;
