import {
	PLAN_DOCUMENTATION_RESULTS,
	DOCUMENT_OBJECTIVES_SECTION,
	DOCUMENT_OBJECTIVES_RESULTS,
	DOCUMENT_TEMPLATE_TITLE,
	DOCUMENTATION_RECORD_TITLE,
	PLAN_TEMPLATE_FIELD_NAME,
} from '../constants/GeneralConstants';

const planDocumentationListQuery = `query ($queryConditions: String!) {
    GlideRecord_Query {
        {{ tableName }} (queryConditions: $queryConditions, pagination: { limit: {{ maxDocumentationSectionCount }} })  {
            ${PLAN_DOCUMENTATION_RESULTS}: _results {
                _row_data{
                    canWrite
                }
                sys_id {
                    value
                }
                contents {
                    value
                }
                status {
                    value
                }
                ${DOCUMENTATION_RECORD_TITLE}: title {
                    value
                }
                ${DOCUMENT_OBJECTIVES_SECTION}: template {
                    ${DOCUMENT_OBJECTIVES_RESULTS} : _reference {
                        ${PLAN_TEMPLATE_FIELD_NAME}: sys_id {
                            value
                        }
                        description {
                            value
                        }
                        ${DOCUMENT_TEMPLATE_TITLE}: title {
                            value
                        }
                        default_text {
                            value
                        }
                    }
                }
            }
        }
    }
}`;

export default planDocumentationListQuery;
