import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { USER_PREFERENCE_ACTION_TYPES } from '../constants/UserPreferenceActionTypes';
import SET_USER_PREFERNCE_QUERY from '../queries/UserPreferenceQuery';
import {
	USER_PREFERENCE_SAVE_ERROR_MESSAGE,
	USER_PREFERENCE_PREFIX,
} from '../constants/GeneralConstants';

const getUserPreferenceSaveFailure = ({ action, state }) => {
    const { properties } = state;
    const { tableName, listViewName } = properties;
    const { data } = action.payload;
    const userPreferenceName = USER_PREFERENCE_PREFIX + '.'+ tableName+'.'+listViewName;

	console.log(data.error);
	console.log(
		USER_PREFERENCE_SAVE_ERROR_MESSAGE + userPreferenceName
	);
};

const actionHandlers = {
	[USER_PREFERENCE_ACTION_TYPES.SET_USER_PREFERENCE]: createGraphQLEffect(
		SET_USER_PREFERNCE_QUERY,
		{
			variableList: ['preferenceName', 'preferenceValue'],
			errorActionType:
			USER_PREFERENCE_ACTION_TYPES.SET_USER_PREFERENCE_FAILED,
		}
	),
	[USER_PREFERENCE_ACTION_TYPES.SET_USER_PREFERENCE_FAILED]: {
		effect: getUserPreferenceSaveFailure,
		stopPropagation: true,
	},
};

export default actionHandlers;
