import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './NewBiaDynamicModalView';
import lifecycleActionhandlers from './actions/DynamicModalLifeCycleActions';
import modalFieldActionHandlers from './actions/DynamicModalFieldActions';
import formLayoutActionHandlers from './actions/BiaFormLayoutActions';
import modalFooterActionHandlers from './actions/DynamicModalFooterActions';
import currentUserActionHandlers from './actions/CurrentLoggedUserActions';
import createBiaActionHandlers from './actions/CreateNewBiaActions';
import modalVisibilityActions from './actions/DynamicModalVisibilityActions';
import styles from './NewBiaDynamicModalElement.scss';
import {
	DEFAULT_MODAL_SIZE,
	DEFAULT_LAYOUT_VIEW,
} from './constants/NewBiaDynamicModalConstants';
import { PREVIEW_RECORD } from './constants/NewBiaDynamicModalActionTypes';

const TAG_NAME = 'sn-bcm-new-bia-dynamic-modal';

// TODO: Investigate the matter below.
// Whats the difference between 'showErrorMessage' and 'error' in your state variables?
// There are two variables in the state: error and showErrorMessage.
// I think this is by mistake. Please verify.
// We can merge afterwards.

// I suspect they were supposed to handle the same responsibility, but maybe we lost track of them.

// Answer for TODO: 'showErrorMessage' and 'error' are two different things, but only 'showErrorMessage' is a property not error.
// 'showErrorMessage' is the message that is being displayed on top of the modal as an alert message to the user.
// Telling them to fill out the mandatory fields.
// 'error' is for when an action throws an error, where also being used in many other components
// If we will need to make some changes, we should do it with all other 'error's.

/**
 * Modal that renders fields from platform view to create a new BIA
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-new-bia-dynamic-modal
 * 		layout-view=""
 * </sn-bcm-new-bia-modal>
 * ```
 *
 * @seismicElement sn-bcm-new-bia-dynamic-modal
 * @summary A modal that renders form along with document ID fields to create a new bia
 */
createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		 * View of form in the modal
		 * @type {string}
		 */
		layoutView: {
			default: DEFAULT_LAYOUT_VIEW,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The size of modal window. "lg", "md", or "sm". Default is large ("lg")
		 * @type {string}
		 */
		modalSize: {
			default: DEFAULT_MODAL_SIZE,
			required: false,
			schema: {
				type: 'string',
			},
		},
	},
	initialState: {
		/**
		 * Alert message shown to user
		 */
		alertMessage: '',
		/**
		 * Loading flag of the component
		 */
		isLoading: false,
		/**
		 * Form layout elements from layout view property
		 */
		formElements: [],
		/**
		 * Fields that are to be mandatory
		 */
		mandatoryFields: [],
		/**
		 * Flag to show error message
		 */
		showErrorMessage: false,
		/**
		 * Flag to know the status of modal
		 */
		isModalOpen: true,
	},
	actionHandlers: {
		...lifecycleActionhandlers,
		...modalFieldActionHandlers,
		...formLayoutActionHandlers,
		...modalFooterActionHandlers,
		...currentUserActionHandlers,
		...createBiaActionHandlers,
		...modalVisibilityActions,
	},
	dispatches: {
		/**
		 * Dispatched when a record is created
		 *
		 * @type {object}
		 */
		[PREVIEW_RECORD]: {
			type: {
				table: '',
				sys_id: '',
			},
		},
	},
	styles,
});
export default TAG_NAME;
