import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './BIAPieChartRibbonCardView';
import styles from './BIAPieChartRibbonCardElement.scss';
import {
	actions as dataFetchActions,
	actionHandlers as dataFetchActionHandlers,
} from './actions/BIAPieChartRibbonCardQueryActions';
import lifecycleActionHandlers from './actions/BIAPieChartRibbonCardLifecycleActions';
import {
	actions as recordWatcherActions,
	actionHandlers as recordWatcherActionHandlers,
} from './actions/RecordWatcherActions';
import accessibilityUserPreferenceActionHandlers from './actions/AccessibilityUserPreferenceActions';
import {
	DEFAULT_TOTAL_LABEL,
	DEFAULT_PIE_CHART_OPTIONS,
	DEFAULT_CARD_HEADER_LABEL,
	DEFAULT_GROUP_BY_COLUMN,
	DEFAULT_QUERY_FILTER_COLUMN,
} from './constants/BIAPieChartRibbonCardConstants';

const TAG_NAME = 'sn-bcm-bia-pie-chart-ribbon-card';

/**
 * Displays progress of related BIA Records such as Dependency Assessment Groups
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-bia-pie-chart-ribbon-card
 * 		query-table=""
 * 		sys-id=""
 * 		card-header-label=""
 * 		total-label=""
 * 		query-filter-column=""
 * ></sn-bcm-bia-pie-chart-ribbon-card>
 * ```
 *
 * @seismicElement sn-bcm-bia-pie-chart-ribbon-card
 * @summary Displays progress of related BIA Records such as Dependency Assessment Groups
 */

createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
	},
	view,
	properties: {
		/**
		 * The sys_id value of the record displaying this widget in the ribbon
		 * @type {String}
		 */
		sysid: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The name of the table to query data from
		 * @type {String}
		 */
		queryTable: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Boolean value to show/hide the legend (percentages) in the chart key. Default is true
		 * @type {boolean}
		 */
		legendOnly: {
			default: true,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * The column of the table which the records have to be grouped. Default is state
		 * @type {String}
		 */
		groupByColumn: {
			default: DEFAULT_GROUP_BY_COLUMN,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The column to filter record. Default is impact_analysis
		 * @type {String}
		 */
		queryFilterColumn: {
			default: DEFAULT_QUERY_FILTER_COLUMN,
			required: false,
			schema: {
				type: 'string',
			},
		},
	},
	initialState: {
		options: DEFAULT_PIE_CHART_OPTIONS,
		isLoading: true,
		isError: false,
		totalLabel: '',
		cardHeaderLabel: '',
		isAccessibilityModeActive: false,
	},
	actions: {
		...dataFetchActions,
		...recordWatcherActions,
	},
	actionHandlers: {
		...lifecycleActionHandlers,
		...dataFetchActionHandlers,
		...recordWatcherActionHandlers,
		...accessibilityUserPreferenceActionHandlers,
	},
	styles,
});

export default TAG_NAME;
