import { getCardElement } from './CombinedScoreCardUtil';

const view = (state, dispatch) => {
	const { properties } = state;
	const {
		cardHeaderLabel,
		fillHeight,
		orientation,
		primaryScore,
		primaryScoreFontSize,
		secondaryScores,
		secondaryScoreOrientation,
		lastUpdated,
		isActionButtonDisplayed,
		actionButtonIcon,
		actionButtonActionName,
		actionButtonActionPayload,
		actionButtonTooltip,
		actionButtonAriaLabel,
		cardInteraction,
		isLoading,
		loadingMessage,
	} = properties;

	const combinedScoreCardElement = getCardElement(
		cardHeaderLabel,
		fillHeight,
		orientation,
		primaryScore,
		primaryScoreFontSize,
		secondaryScores,
		secondaryScoreOrientation,
		lastUpdated,
		dispatch,
		isActionButtonDisplayed,
		actionButtonIcon,
		actionButtonActionName,
		actionButtonActionPayload,
		actionButtonTooltip,
		actionButtonAriaLabel,
		cardInteraction,
		isLoading,
		loadingMessage
	);

	return combinedScoreCardElement;
};

export default view;
