import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	RELATED_ASSETS_QUERY_REQUESTED,
	RELATED_ASSETS_QUERY_SUCCEEDED,
	RELATED_ASSETS_QUERY_FAILED,
} from '../constants/AssetDependenciesScoreCardActionTypes';
import relatedAssetsQuery from '../queries/RelatedAssetDependenciesQuery';
import {
	FETCH_ERROR_MESSAGE,
	NULL_TABLE_ERROR_MESSAGE,
} from '../constants/AssetDependenciesScoreCardConstants';

const requestRelatedAssetsData = createGraphQLEffect(relatedAssetsQuery, {
	templateVarList: [
		'sysId',
		'relatedAssetDependenciesTable',
		'recoveryStrategiesTable',
	],
	successActionType: RELATED_ASSETS_QUERY_SUCCEEDED,
	errorActionType: RELATED_ASSETS_QUERY_FAILED,
});

const getRelatedAssetsQuerySuccess = ({ action, properties, updateState }) => {
	const fetchResult = action.payload;
	const fetchErrors = fetchResult.errors;
	const { relatedAssetDependenciesTable, recoveryStrategiesTable } = properties;

	if (fetchErrors.length) {
		console.error(FETCH_ERROR_MESSAGE);
		console.error(fetchErrors);
		updateState({
			error: true,
			loading: false,
		});

		return;
	}

	const assetDependenciesDataReturned = get(
		fetchResult,
		`data.GlideRecord_Query.${relatedAssetDependenciesTable}.results`
	);
	const recoveryStrategiesDataReturned = get(
		fetchResult,
		`data.GlideRecord_Query.${recoveryStrategiesTable}.results`
	);

	if (!assetDependenciesDataReturned || !recoveryStrategiesDataReturned) {
		console.error(NULL_TABLE_ERROR_MESSAGE);
		updateState({
			error: true,
			loading: false,
		});

		return;
	}

	const assetDependencies = assetDependenciesDataReturned.map((asset) =>
		get(asset, 'sys_id.value')
	);
	const coveredDependencies = recoveryStrategiesDataReturned.reduce((allGlideListValues, dependenciesCovered) => {
		const coveredDependenciesGlideList = get(dependenciesCovered, 'dependencies_covered.value');
		const listValues = coveredDependenciesGlideList
			? coveredDependenciesGlideList.split(',')
			: [];
		return [...allGlideListValues, ...listValues];
	}, []);
	const dependenciesWithStrategy = assetDependencies.filter((asset) =>
		coveredDependencies.includes(asset)
	);
	const totalRelatedAssetsCount = assetDependencies.length;
	const assetsWithStrategyCount = dependenciesWithStrategy.length;
	const assetsWithoutStrategyCount =
		totalRelatedAssetsCount - assetsWithStrategyCount;

	updateState({
		totalRelatedAssetsCount,
		assetsWithStrategyCount,
		assetsWithoutStrategyCount,
		isLoading: false,
	});
};

const getRelatedAssetsQueryFailed = ({ action, updateState }) => {
	const error = action.payload;

	console.error(FETCH_ERROR_MESSAGE);
	console.error(error);
	updateState({
		error: true,
		loading: false,
	});
};

const actionHandlers = {
	[RELATED_ASSETS_QUERY_REQUESTED]: requestRelatedAssetsData,
	[RELATED_ASSETS_QUERY_SUCCEEDED]: {
		effect: getRelatedAssetsQuerySuccess,
		stopPropogation: true,
	},
	[RELATED_ASSETS_QUERY_FAILED]: {
		effect: getRelatedAssetsQueryFailed,
		stopPropogation: true,
	},
};

export default actionHandlers;
