import { get, lowerCase } from 'lodash';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	FETCH_PLAN_DOCUMENT_QUERY_REQUESTED,
	FETCH_PLAN_DOCUMENT_QUERY_SUCCEEDED,
	FETCH_PLAN_DOCUMENT_QUERY_FAILED,
} from '../constants/PlanDocumentOverviewScoreCardActionTypes';
import {
	PLAN_DOCUMENT_PENDING_COUNT_LABEL,
	FETCH_ERROR_MESSAGE,
	NULL_TABLE_ERROR_MESSAGE,
	PLAN_DOCUMENATION_AGGREGATE_URL,
	DEFAULT_GROUP_BY_COLUMN,
} from '../constants/PlanDocumentOverviewScoreCardConstants';

const requestPlanDocumentData = createHttpEffect(
	PLAN_DOCUMENATION_AGGREGATE_URL,
	{
		method: 'GET',
		queryParams: [
			'parentRecordSysId',
			'targetTableIdentifier',
			'groupByColumn',
		],
		successActionType: FETCH_PLAN_DOCUMENT_QUERY_SUCCEEDED,
		errorActionType: FETCH_PLAN_DOCUMENT_QUERY_FAILED,
		batch: false,
	}
);

const getPlanDocumentQuerySuccess = ({ action, updateState }) => {
	const { result } = action.payload;

	if (result.error) {
		console.error(FETCH_ERROR_MESSAGE);
		console.error(result.error);
		updateState({
			error: true,
			isLoading: false,
		});

		return;
	}

	let pendingDocumentCount = 0;
	let totalDocumentCount = 0;
	let completeDocumentCount = 0;

	const { requestBody: dataReturned } = result;

	if (!dataReturned) {
		console.error(NULL_TABLE_ERROR_MESSAGE);
		updateState({
			error: true,
			isLoading: false,
		});

		return;
	}

	dataReturned.map((element) => {
		if (
			element[DEFAULT_GROUP_BY_COLUMN] === PLAN_DOCUMENT_PENDING_COUNT_LABEL
		) {
			pendingDocumentCount = element.count;
		} else {
			completeDocumentCount = element.count;
		}
	});

	totalDocumentCount = completeDocumentCount + pendingDocumentCount;

	updateState({
		totalDocumentCount,
		pendingDocumentCount,
		completeDocumentCount,
		isLoading: false,
	});
};

const getPlanDocumentQueryFailed = ({ action, updateState }) => {
	const error = action.payload;

	console.error(FETCH_ERROR_MESSAGE);
	console.error(error);
	updateState({
		error: true,
		isLoading: false,
	});
};

const actionHandlers = {
	[FETCH_PLAN_DOCUMENT_QUERY_REQUESTED]: requestPlanDocumentData,
	[FETCH_PLAN_DOCUMENT_QUERY_SUCCEEDED]: {
		effect: getPlanDocumentQuerySuccess,
		stopPropogation: true,
	},
	[FETCH_PLAN_DOCUMENT_QUERY_FAILED]: {
		effect: getPlanDocumentQueryFailed,
		stopPropogation: true,
	},
};

export default actionHandlers;
