const EVENT_RESULT_HIERARCHY_QUERY = `query getElementTableData {
    GlideRecord_Query {
        {{tableName}}(queryConditions: "event={{eventSysId}}", pagination: {limit: {{eventAssetRecordLoadLimit}}}) {
            _table_metadata {
                canCreate
            }
            _results {
                sys_id {
                    value
                    displayValue
                }
                {{#columns}} {{name}} {
                    label
                    value
                    displayValue
                }
                {{/columns}}
                _query {
                    queryWithSysId: GlideRecord_Query {
                        sn_recovery_activated_plan(queryConditions: "event={{eventSysId}}^event_asset=$$parent.sys_id$$", pagination: {limit: {{activatedPlanRecordLoadLimit}}}) {
                            _results {
                                sys_id {
                                    value
                                    displayValue
                                }
                                {{#columns}} {{name}} {
                                    label
                                    value
                                    displayValue
                                }
                                {{/columns}}
                                _query {
                                    queryWithSysId: GlideRecord_Query {
                                        sn_recovery_event_task(queryConditions: "parent={{eventSysId}}^activated_plan=$$parent.sys_id$$", pagination: {limit: {{eventTaskRecordLoadLimit}}}) {
                                            _results {
                                                sys_id {
                                                    value
                                                    displayValue
                                                }
                                                {{#columns}} {{name}} {
                                                    label
                                                    value
                                                    displayValue
                                                }
                                                {{/columns}}
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    GlideRecord_Query{
        sn_recovery_activated_plan {
            _table_metadata {
                canCreate
            }
        }
    }
    GlideRecord_Query {
        sn_recovery_event_task {
            _table_metadata {
                canCreate
            }
        }
    }
    GlideDomain_Query {
		user {
			preferences(name: ["{{userPreferenceName}}"]) {
        		name
        		value
      		}
		}
	}  

}`;

const EVENT_STATE_QUERY = `query getEventState {
    GlideRecord_Query {
        sn_recovery_event(sys_id: "{{eventSysId}}") {
            _results {
                state{
                    value
                }
            }
        }
    }
}`;

export { EVENT_RESULT_HIERARCHY_QUERY, EVENT_STATE_QUERY };
