import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './NewRecordModalView';
import { 
	DEFAULT_FORM_VIEW,
	DEFAULT_MODAL_SIZE,
} from './constants/NewRecordModalConstants';
import lifeCycleActionHanlders from './actions/NewRecordModalLifeCycleActions';

const TAG_NAME = 'sn-bcm-new-record-modal';

/**
 * A modal to create new record 
 *
 * ```
 * import '@relative-path/app-bcm-components';
 *
 * <sn-bcm-new-record-modal
 * 		table-name='sn_bcp_plan'
 * 		form-view=""
 * 		read-only-form=""
 * 		modal-size=""
 * 		query-conditions=""
 * </sn-bcm-new-record-modal>
 * ```
 *
 * @seismicElement sn-bcm-new-record-modal
 * @summary A modal to create new record for the given tableName
 */
createCustomElement(TAG_NAME, {
	renderer: {type: snabbdom},
	view,
	properties: {
		/**
		 * Table name to create record
		 *
		 * @type {string}
		 */
		tableName: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The Form View to be loaded in modale. Default workspace
		 *
		 * @type {string}
		 */
		formView: {
			default: DEFAULT_FORM_VIEW,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The size of modal window. "lg", "md", or "sm". Default is large ("lg")
		 *
		 * @type {string}
		 */
		modalSize: {
			default: DEFAULT_MODAL_SIZE,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * query conditions to be passed to form section
		 *
		 * @type {String}
		 */
		queryConditions: {
			default: '',
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * to load the page in read-only format
		 *
		 * @type {boolean}
		 */
		isReadOnly: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
		/**
		 * to reload the page after action is done, or being redirected to the form
		 *
		 * @type {boolean}
		 */
		reloadAfterSave: {
			default: false,
			required: false,
			schema: {
				type: 'boolean',
			},
		},
	},
	initialState:{
		modalHeaderLabel:'',
	},
	actionHandlers:{
		...lifeCycleActionHanlders,
	},
});
export default TAG_NAME;