import EDITABLE_LIST_GRID_ACTION_TYPES from '../constants/EditableListGridActionTypes';
import {
	EDIT_ID_CLASS,
	NOW_POPOVER_PANEL_TAGNAME,
} from '../constants/EditableListConstants';
import { containsClassOrTagInPath } from './EditableListUtils';

const editEventHandlers = [
	{
		// Override - saves grid row on outside click event
		events: ['click'],
		capture: true,
		target: document,
		effect({ state, dispatch, action }) {
			const { rowEditing } = state;
			const { event } = action.payload;

			if (
				rowEditing &&
				!(
					containsClassOrTagInPath(event, EDIT_ID_CLASS) ||
					containsClassOrTagInPath(event, NOW_POPOVER_PANEL_TAGNAME)
				)
			) {
				dispatch(EDITABLE_LIST_GRID_ACTION_TYPES.GRID_ROW_EDITED);
			}
		},
	},
];

export default editEventHandlers;
