const query = `query getPlanAssets ($parentRecordSysId: String!) {
    GlideRecord_Query {
        sn_bcp_plan_loss_scenario (sys_id:$parentRecordSysId) {
            _results {
                plan {
                    value
                }
                _query {
                    queryForPlanAssets: GlideRecord_Query {
                        sn_bcp_plan_asset(queryConditions: "plan=$$parent.plan$$") {
                            _results {
                                sys_id {
                                    value
                                }
                                name {
                                    displayValue
                                }
                                impact_analysis {
                                    value
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}`;

export default query;
