import { createEditSlots } from '@devsnc/sn-datagrid';
import '../RecordModal/RecordModalElement';
import '@devsnc/sn-glide-form-controls';
import '../EmptyState/EmptyStateElement';
import EDITABLE_LIST_MODAL_ACTION_TYPES from './constants/EditableListModalActionTypes';
import {
	GRID_NEW_BUTTON_LABEL,
	GRID_REMOVE_BUTTON_LABEL,
	GRID_ACTION_PREFIX,
	NEW_RECORD_SYS_ID,
	SYNC_OUTLINE_ICON,
	REFRESH_BUTTON_TOOLTIP,
} from './constants/EditableListConstants';
import { EDITABLE_LIST_DELETE_ACTION_TYPES } from './constants/EditableListCRUDActionTypes';
import { GRID_COLUMN_WIDTH_ACTION_TYPES } from '../../util/grid-util/constants/GridActionTypes';
import EDITABLE_LIST_GRID_ACTION_TYPES from './constants/EditableListGridActionTypes';

const view = (state, dispatch) => {
	const {
		properties,
		listRecords,
		columns,
		config,
		isLoading,
		rowEditing,
		isModalOpen,
		recordId,
		modalLabels,
		recordSelected,
	} = state;

	const {
		tableName,
		filterQueryConditions,
		loadingViewMessage,
		listViewName,
		isReadOnly,
		modalViewName,
	} = properties;

	//TODO: readonly and table can be removed once we consume paris patch2. As they will be
	// picked from config and tableName property.
	const editSlotsRenderer = createEditSlots({
		readonly: false,
		rowEditing,
		dispatch,
		table: tableName,
		config: { ...config, readonly: false },
		actionPrefix: GRID_ACTION_PREFIX,
	});

	const baseGridConfig = {
		...config,
	};

	const editGridConfig = {
		...config,
		editSlotsRenderer,
	};

	editGridConfig.readonly = false;

	const loadingView = (
		<div className="loading-view">
			<now-loader label={loadingViewMessage} size="lg" />
		</div>
	);

	const renderModal = () => {
		if (isModalOpen) {
			const { headerLabel, actionBtnLabel, cancelBtnLabel } = modalLabels;

			return (
				<sn-bcm-record-modal
					isModalOpen={isModalOpen}
					table={tableName}
					form-view={modalViewName}
					sys-id={recordId}
					header-label={headerLabel}
					action-button-label={actionBtnLabel}
					cancel-button-label={cancelBtnLabel}
					query-conditions={filterQueryConditions}
				/>
			);
		}
	};

	const editableListView = (
		<div
			className="editable-list-container"
			hook-update={(oldVnode, vnode) => {
				dispatch(GRID_COLUMN_WIDTH_ACTION_TYPES.ON_LOAD_FETCH_WIDTH, {
					width: vnode.elm.getBoundingClientRect().width,
				});
			}}>
			<sn-datagrid
				className={isReadOnly ? 'hidden' : ''}
				config={editGridConfig}
				columns={columns}
				tasks={listRecords}>
				<div slot="toolbar-icons" className="toolbar-buttons">
					<now-button
						className="-new-button"
						label={GRID_NEW_BUTTON_LABEL}
						tooltipContent={GRID_NEW_BUTTON_LABEL}
						variant="primary"
						size="md"
						on-click={() =>
							dispatch(EDITABLE_LIST_MODAL_ACTION_TYPES.MODAL_OPEN_REQUESTED, {
								sysId: NEW_RECORD_SYS_ID,
							})
						}
					/>
					<now-button
						disabled={recordSelected && recordSelected.length ? false : true}
						className="delete-button -remove-button"
						label={GRID_REMOVE_BUTTON_LABEL}
						tooltipContent={GRID_REMOVE_BUTTON_LABEL}
						variant="secondary"
						size="md"
						on-click={() =>
							dispatch(EDITABLE_LIST_DELETE_ACTION_TYPES.REMOVE_BUTTON_CLICKED)
						}
					/>
					<now-button
						className="refresh-button"
						icon={SYNC_OUTLINE_ICON}
						size="md"
						variant="secondary"
						tooltipContent={REFRESH_BUTTON_TOOLTIP}
						bare={false}
						configAria={{ 'aria-label': REFRESH_BUTTON_TOOLTIP }}
						on-click={() => {
							dispatch(EDITABLE_LIST_GRID_ACTION_TYPES.REFRESH_GRID);
						}}
					/>
				</div>
				<div slot="empty-state" className="empty-view">
					<sn-bcm-empty-state />
				</div>
			</sn-datagrid>
			<sn-datagrid
				className={!isReadOnly ? 'hidden' : ''}
				config={baseGridConfig}
				columns={columns}
				tasks={listRecords}>
				<div slot="empty-state" className="empty-view">
					<sn-bcm-empty-state />
				</div>
			</sn-datagrid>
			{renderModal()}
		</div>
	);

	if (isLoading) {
		return loadingView;
	}

	return editableListView;
};

export default view;
