import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import tableAndFilterQuery from '../querries/ItemPickerTableAndFilterQuery';
import {
	ITEM_PICKER_TABLE_NAME_REQUESTED,
	ITEM_PICKER_TABLE_NAME_STARTED,
	ITEM_PICKER_TABLE_NAME_SUCCEEDED,
	ITEM_PICKER_TABLE_NAME_FAILED,
	EXCLUDED_ITEMS_QUERY_REQUESTED,
	BIA_EXCLUDED_ITEMS_QUERY_REQUESTED,
} from '../constants/ItemPickerModalActionTypes';

const actions = {
	[ITEM_PICKER_TABLE_NAME_REQUESTED]: {
		private: true,
	},
	[ITEM_PICKER_TABLE_NAME_STARTED]: {
		private: true,
	},
	[ITEM_PICKER_TABLE_NAME_SUCCEEDED]: {
		private: true,
	},
	[ITEM_PICKER_TABLE_NAME_FAILED]: {
		private: true,
	},
};

const getSelectionTableNameQuerySuccess = ({
	action,
	properties,
	dispatch,
	updateState,
}) => {
	const {
		parentTable,
		parentRecordSysId,
		parentTableItemPickerReferenceField,
		elementDefinitionTableReferenceField,
		table,
		isImpactAnalysis,
	} = properties;
	let response;

	if (parentTableItemPickerReferenceField) {
		response = get(
			action.payload,
			`data.GlideRecord_Query.${parentTable}._results[0].${parentTableItemPickerReferenceField}._reference.${elementDefinitionTableReferenceField}._reference`
		);
	} else {
		response = get(
			action.payload,
			`data.GlideRecord_Query.${parentTable}._results[0].${elementDefinitionTableReferenceField}._reference`
		);
	}

	const selectionTable = get(response, 'source_table.value');
	const fetchedTableEncodedQuery = get(response, 'filter.value');
	const selectionTableFields = get(response, 'source_table_fields.value');

	updateState({
		selectionTable,
		fetchedTableEncodedQuery,
		selectionTableFields,
	});
	let actionToBeDispatched;

	if (isImpactAnalysis) {
		actionToBeDispatched = BIA_EXCLUDED_ITEMS_QUERY_REQUESTED;
	} else {
		actionToBeDispatched = EXCLUDED_ITEMS_QUERY_REQUESTED;
	}

	dispatch(actionToBeDispatched, {
		parentRecordSysId,
		excludedItemsTableIdentifier: table,
	});
};

const getSelectionTableNameQueryFailure = ({ action, updateState }) => {
	const error = action.payload;

	console.error(error);
	updateState({
		errorMessage: true,
	});
};

const actionHandlers = {
	[ITEM_PICKER_TABLE_NAME_REQUESTED]: createGraphQLEffect(tableAndFilterQuery, {
		variableList: ['parentRecordSysId'],
		templateVarList: [
			'parentTable',
			'parentTableItemPickerReferenceField',
			'elementDefinitionTableReferenceField',
		],
		startActionType: ITEM_PICKER_TABLE_NAME_STARTED,
		successActionType: ITEM_PICKER_TABLE_NAME_SUCCEEDED,
		errorActionType: ITEM_PICKER_TABLE_NAME_FAILED,
	}),
	[ITEM_PICKER_TABLE_NAME_SUCCEEDED]: getSelectionTableNameQuerySuccess,
	[ITEM_PICKER_TABLE_NAME_FAILED]: getSelectionTableNameQueryFailure,
};

export { actions, actionHandlers };
