import '@servicenow/now-loader';
import '../EmptyState/EmptyStateElement';
import RelatedItemAccordionList from '../RelatedItemAccordionList/RelatedItemAccordionListElement';
import {
	IMPACT_ASSESSMENT_DESCRIPTION_FIELD,
	IMPACT_ASSESSMENT_HEADER_FIELD,
	HELPER_TEXT,
	HELPER_TEXT_FOR_RPO,
	CATEGORY_RESULT_TYPE
} from './constants/BiaImpactAssessmentConstants';

const view = (state) => {
	const { isLoading, tableRecords, properties, isParentRecordEditable } = state;
	const {
		loadingViewMessage,
		tableName,
		impactAssessmentChildTable,
		impactAssessmentReferenceColumnField,
		impactAssessmentStatusField,
		impactAssessmentChildTableField,
		queryFilterCondition,
	} = properties;

	const loadingView = (
		<div className="impact-assessment-loading-view">
			<now-loader label={loadingViewMessage} />
		</div>
	);

	const emptyState = (
		<sn-bcm-empty-state />
	);

	const _getHelperText = () => {
		if (
			queryFilterCondition &&
			queryFilterCondition.includes(CATEGORY_RESULT_TYPE.QUESTION_BASE)
		) {
			return HELPER_TEXT_FOR_RPO;
		}
		return HELPER_TEXT;
	};

	if (isLoading) {
		return loadingView;
	}

	return (
		<div className="impact-assessment-list">
			<RelatedItemAccordionList
				accordionRecords={tableRecords}
				relatedTableName={tableName}
				relatedTableColumnReferenceField={impactAssessmentReferenceColumnField}
				relatedTableStatusFieldName={impactAssessmentStatusField}
				accordionListHeaderField={IMPACT_ASSESSMENT_HEADER_FIELD}
				accordionItemCardDescriptionField={IMPACT_ASSESSMENT_DESCRIPTION_FIELD}
				relatedChildTable={impactAssessmentChildTable}
				relatedTableReferenceField={impactAssessmentChildTableField}
				isActionButtonRequired={isParentRecordEditable}
				helperText={_getHelperText()}
			/>
			{!tableRecords.length ? emptyState : null}
		</div>
	);
};

export default view;
