import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import RECORD_MUTATION_ACTIONS from '../constants/RecordMutationActionTypes';
import SELECTED_VALUES_QUERY_ACTIONS from '../constants/SelectedValuesQueryActionTypes';
import recordMutation from '../querries/SelectedValuesMutation';

const requestRecordMutation = createGraphQLEffect(recordMutation, {
	variableList: ['sysId', 'glideListValue'],
	templateVarList: ['table', 'field'],
	startActionType: RECORD_MUTATION_ACTIONS.RECORD_MUTATION_STARTED,
	successActionType: RECORD_MUTATION_ACTIONS.RECORD_MUTATION_SUCCEEDED,
	errorActionType: RECORD_MUTATION_ACTIONS.RECORD_MUTATION_FAILED
});

const getRecordMutationSuccess = ({state, action, dispatch, updateState}) => {
	const { properties } = state;
	const { table, field, selectionTable, maxGlideListItems } = properties;
	const updatedSelectionGlideList = get(action, `payload.data.GlideRecord_Mutation.update_${table}.${field}.value`);
	let queryConditions = `sys_idIN${updatedSelectionGlideList}`;

	updateState({
		currentlySelectedGlideList: updatedSelectionGlideList
	});

	dispatch(SELECTED_VALUES_QUERY_ACTIONS.SELECTED_VALUES_QUERY_REQUESTED, {
		selectionTable,
		queryConditions,
		maxTypeaheadOptions: maxGlideListItems
	});
};

const getRecordMutationFailure = ({action, updateState}) => {
	const error = action.payload;
	console.error('An error occured attenmpting to edit selected values');
	console.error(error);
	updateState({
		error: true
	});
};

const actions = {
	[RECORD_MUTATION_ACTIONS.RECORD_MUTATION_REQUESTED]: {
		schema: {
			type: 'object',
			properties: {
				sysId: {type: 'string'},
				glideListValue: {type: 'string'},
				table: {type: 'string'},
				field: {type: 'string'}
			},
			required: ['sysId', 'glideListValue', 'table', 'field']
		},
		private: true
	}
};

const actionHandlers = {
	[RECORD_MUTATION_ACTIONS.RECORD_MUTATION_REQUESTED]: requestRecordMutation,
	[RECORD_MUTATION_ACTIONS.RECORD_MUTATION_SUCCEEDED]: {
		effect: getRecordMutationSuccess,
		stopPropagation: true
	},
	[RECORD_MUTATION_ACTIONS.RECORD_MUTATION_FAILED]: {
		effect: getRecordMutationFailure,
		stopPropagation: true
	}
};

export {
	actions,
	actionHandlers
};