import '@servicenow/now-loader';
import '../EmptyState/EmptyStateElement';
import BiaDependencyItemPickerModal from '../BiaDependencyItemPickerModal/BiaDependencyItemPickerModalElement';
import RelatedItemAccordionList from '../RelatedItemAccordionList/RelatedItemAccordionListElement';
import {
	DEPENDENCY_ASSESSMENT_DESCRIPTION_FIELD,
	DEPENDENCY_ASSESSMENT_HEADER_FIELD,
	HELPER_TEXT,
} from './constants/BiaDependencyAssessmentConstants';

const view = (state) => {
	const {
		isLoading,
		tableRecords,
		properties,
		recordSysId,
		isAddItemsModalOpen,
		isParentRecordEditable,
	} = state;
	const {
		loadingViewMessage,
		tableName,
		dependencyAssessmentChildTable,
		dependencyElementReferenceColumnField,
		dependencyAssessmentStatusField,
		dependencyAssessmentChildTableField,
	} = properties;

	const loadingView = (
		<div className="dependency-assessment-loading-view">
			<now-loader label={loadingViewMessage} />
		</div>
	);

	if (isLoading) {
		return loadingView;
	}

	const addItemsModalView = (
		<BiaDependencyItemPickerModal
			table={dependencyAssessmentChildTable}
			parentTable={tableName}
			parentRecordSysId={recordSysId}
			parentTableReferenceField={dependencyAssessmentChildTableField}
			elementDefinitionTableReferenceField={
				dependencyElementReferenceColumnField
			}
		/>
	);

	const emptyState = <sn-bcm-empty-state />;

	return (
		<div className="dependency-assessment-list">
			<RelatedItemAccordionList
				accordionRecords={tableRecords}
				relatedTableName={tableName}
				relatedTableColumnReferenceField={dependencyElementReferenceColumnField}
				relatedTableStatusFieldName={dependencyAssessmentStatusField}
				accordionListHeaderField={DEPENDENCY_ASSESSMENT_HEADER_FIELD}
				accordionItemCardDescriptionField={
					DEPENDENCY_ASSESSMENT_DESCRIPTION_FIELD
				}
				relatedChildTable={dependencyAssessmentChildTable}
				relatedTableReferenceField={dependencyAssessmentChildTableField}
				isActionButtonRequired={isParentRecordEditable}
				isCardHeaderRequired={true}
				helperText={HELPER_TEXT}
			/>
			{!tableRecords.length ? emptyState : null}
			{isAddItemsModalOpen ? addItemsModalView : null}
		</div>
	);
};

export default view;
