import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import RICH_TEXT_CONTENT_CARD from '../constants/RichTextCardInteractionActionTypes';
import { CARD_EDITABLE_FIELDS } from '../../RichTextContentCard/constants/GeneralConstants';
import PLAN_DOCUMENT_CONTENT_MUTATION from '../constants/DocumentationContentMutationActionTypes';
import planContentMutation from '../queries/planContentMutation';

const requestPlanContentMutation = createGraphQLEffect(planContentMutation, {
	templateVarList: ['tableName', 'contentField', 'statusField'],
	variableList: ['sysId', 'contentFieldValue', 'statusFieldValue'],
	startActionType: PLAN_DOCUMENT_CONTENT_MUTATION.STARTED,
	successActionType: PLAN_DOCUMENT_CONTENT_MUTATION.SUCCEEDED,
	errorActionType: PLAN_DOCUMENT_CONTENT_MUTATION.FAILED,
});

const handleOnFailure = ({ action }) => {
	const error = action.payload;

	console.error(error);
};

const updatePlanContents = ({ state, action, properties, updateState }) => {
	const { tableRecords } = state;
	const { tableName } = properties;
	const path = `data.GlideRecord_Mutation.update_${tableName}`;
	const recordSysId = get(action.payload, `${path}.sys_id.value`);
	const status = get(
		action.payload,
		`${path}.${CARD_EDITABLE_FIELDS.STATUS_FIELD}.value`
	);
	const contents = get(
		action.payload,
		`${path}.${CARD_EDITABLE_FIELDS.CONTENT_FIELD}.value`
	);

	const updatedRecords = tableRecords.map((record) => {
		if (record.sys_id === recordSysId) {
			return {
				...record,
				[CARD_EDITABLE_FIELDS.CONTENT_FIELD]: contents,
				[CARD_EDITABLE_FIELDS.STATUS_FIELD]: status,
			};
		}

		return record;
	});

	updateState({
		tableRecords: updatedRecords,
	});
};

const actionHandlers = {
	[RICH_TEXT_CONTENT_CARD.SAVED]: requestPlanContentMutation,
	[PLAN_DOCUMENT_CONTENT_MUTATION.SUCCEEDED]: {
		effect: updatePlanContents,
		stopPropagation: true,
	},
	[PLAN_DOCUMENT_CONTENT_MUTATION.FAILED]: {
		effect: handleOnFailure,
		stopPropagation: true,
	},
};

export default actionHandlers;
