import '@devsnc/sn-layout';
import '@servicenow/now-uxf-page';
import PlanDocumentOverviewScoreCard from '../PlanDocumentOverviewScoreCard/PlanDocumentOverviewScoreCardElement';
import PlanRecoveryTeamsScoreCard from '../PlanRecoveryTeamsScoreCard/PlanRecoveryTeamsScoreCardElement';
import PlanActivitiesScoreCard from '../PlanActivitiesScoreCard/PlanActivitiesScoreCardElement';
import PlanAssetsOverviewCard from '../PlanAssetsOverviewCard/PlanAssetsOverviewCardElement';
import PlanAssetScoreCard from '../PLanAssetScoreCard/PlanAssetScoreCardElement';
import PlanEventsOverviewCard from '../PlanEventsOverviewCard/PlanEventsOverviewCardElement';
import PlanExercisePieChartCard from '../PlanExercisePieChartCard/PlanExercisePieChartCardElement';
import {
	PAGE_LAYOUT_MODEL,
	SCORE_CARDS_ROW_LAYOUT,
	PLAN_AUTHORING_TYPE,
	PLAN_DETAILS_CONTAINER_LABEL,
	PLAN_USAGE_CONTAINER_LABEL,
} from './constants/PlanOverviewLayoutConstants';
import {
	CARD_LABELS,
	EVENT_TYPE_VALUE,
} from '../PlanEventsOverviewCard/constants/GeneralConstants';

const view = (state) => {
	const {
		properties,
		planAuthoringType,
		isPlanExerciseRowDisplayed,
		scopeTabIndex,
		documentationTabIndex,
		recoveryTeamsTabIndex,
		lossScenariosTabIndex,
		recoveryTasksTabIndex,
	} = state;
	const { sysId } = properties;

	const planAssetScoreCard = (
		<PlanAssetScoreCard
			className="plan-overview__score-card -fill-height"
			sysid={sysId}
			isActionButtonDisplayed
			relatedItemTabIndex={scopeTabIndex}
			slot="Main"
		/>
	);

	const recoveryTeamsScoreCard = (
		<PlanRecoveryTeamsScoreCard
			className="plan-overview__score-card -fill-height"
			sysid={sysId}
			isActionButtonDisplayed
			relatedItemTabIndex={recoveryTeamsTabIndex}
			slot="Main"
		/>
	);

	const documentationScoreCard = (
		<PlanDocumentOverviewScoreCard
			className="plan-overview__score-card -fill-height"
			sysid={sysId}
			isActionButtonDisplayed
			relatedItemTabIndex={documentationTabIndex}
			slot="Main"
		/>
	);

	const recoveryTasksScoreCard = (
		<PlanActivitiesScoreCard
			className="plan-overview__score-card -fill-height"
			planSysId={sysId}
			isActionButtonDisplayed
			relatedItemTabIndex={recoveryTasksTabIndex}
			slot="Main"
		/>
	);

	const getlossScenarioAssetsCoverageCard = () => {
		if (
			planAuthoringType === PLAN_AUTHORING_TYPE.ALL ||
			planAuthoringType === PLAN_AUTHORING_TYPE.LOSS_SCENARIOS
		) {
			return (
				<PlanAssetsOverviewCard
					className="plan-overview__carousel-card -fill-height -max-height"
					businessContinuityPlanSysId={sysId}
					relatedItemTabIndex={lossScenariosTabIndex}
					slot="Main"
				/>
			);
		}
		return;
	};

	const getPlanOverviewScoreCards = () => {
		switch (planAuthoringType) {
			case PLAN_AUTHORING_TYPE.ALL:
			case PLAN_AUTHORING_TYPE.DOCUMENTATION:
				return [
					planAssetScoreCard,
					recoveryTeamsScoreCard,
					documentationScoreCard,
					recoveryTasksScoreCard,
				];
			case PLAN_AUTHORING_TYPE.LOSS_SCENARIOS:
			case PLAN_AUTHORING_TYPE.RECOVERY_TASKS:
				return [
					planAssetScoreCard,
					recoveryTeamsScoreCard,
					recoveryTasksScoreCard,
				];
			default:
				return [];
		}
	};

	const planOverviewLossScenarioContainer = (
		<now-uxf-page-container
			slot="Content"
			className="plan-overview__uxf-container">
			<sn-layout model={SCORE_CARDS_ROW_LAYOUT}>
				{getlossScenarioAssetsCoverageCard()}
			</sn-layout>
		</now-uxf-page-container>
	);

	const planOverviewScoreCardsContainer = (
		<now-uxf-page-container
			slot="Content"
			label={PLAN_DETAILS_CONTAINER_LABEL}
			className="plan-overview__uxf-container -pt-lg">
			<sn-layout model={SCORE_CARDS_ROW_LAYOUT}>
				{[...getPlanOverviewScoreCards()]}
			</sn-layout>
		</now-uxf-page-container>
	);

	const getPlanEventsOverviewContainer = () => {
		if (isPlanExerciseRowDisplayed) {
			return (
				<now-uxf-page-container
					slot="Content"
					className="plan-overview__uxf-container -pt-lg -pb-lg"
					label={PLAN_USAGE_CONTAINER_LABEL}>
					<sn-layout model={SCORE_CARDS_ROW_LAYOUT}>
						<PlanEventsOverviewCard
							cardHeaderLabel={CARD_LABELS.EVENTS.CARD_HEADER_LABEL}
							totalEventsLabel={CARD_LABELS.EVENTS.TOTAL_EVENTS_LABEL}
							ongoingEventsLabel={CARD_LABELS.EVENTS.ONGOING_EVENTS_LABEL}
							lastEventDateLabel={CARD_LABELS.EVENTS.LAST_EVENT_DATE_LABEL}
							eventType={EVENT_TYPE_VALUE.ACTUAL}
							planSysId={sysId}
							className="plan-overview__events-card -fill-height"
							slot="Main"
						/>
						<PlanEventsOverviewCard
							eventType={EVENT_TYPE_VALUE.EXERCISE}
							cardHeaderLabel={CARD_LABELS.EXERCISE.CARD_HEADER_LABEL}
							totalEventsLabel={CARD_LABELS.EXERCISE.TOTAL_EVENTS_LABEL}
							ongoingEventsLabel={CARD_LABELS.EXERCISE.ONGOING_EVENTS_LABEL}
							lastEventDateLabel={CARD_LABELS.EXERCISE.LAST_EVENT_DATE_LABEL}
							lastEventResultLabel={
								CARD_LABELS.EXERCISE.LAST_EVENT_RESULT_LABEL
							}
							planSysId={sysId}
							className="plan-overview__events-card -fill-height"
							slot="Main"
						/>
						<PlanExercisePieChartCard
							planSysId={sysId}
							className="plan-overview__events-card -fill-height"
							slot="Main"
						/>
					</sn-layout>
				</now-uxf-page-container>
			);
		}
		return;
	};

	const _getClassName = () => {
		if (
			!isPlanExerciseRowDisplayed &&
			(planAuthoringType !== PLAN_AUTHORING_TYPE.ALL ||
				planAuthoringType !== PLAN_AUTHORING_TYPE.LOSS_SCENARIOS)
		) {
			return 'plan-overview -bg-gray';
		}

		return 'plan-overview';
	};

	const planOverviewLayoutView = (
		<div className={_getClassName()}>
			<now-uxf-page bare={true}>
				<sn-layout model={PAGE_LAYOUT_MODEL}>
					{[
						getPlanEventsOverviewContainer(),
						planOverviewScoreCardsContainer,
						planOverviewLossScenarioContainer,
					]}
				</sn-layout>
			</now-uxf-page>
		</div>
	);

	return planOverviewLayoutView;
};

export default view;
