import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	IMPACT_ASSESSMENT_DATA_REQUESTED,
	IMPACT_ASSESSMENT_DATA_SUCCEEDED,
	IMPACT_ASSESSMENT_DATA_FAILED,
} from '../constants/BiaImpactAssessmentActionTypes';
import {
	FETCH_ERROR_MESSAGE,
	NULL_TABLE_ERROR_MESSAGE,
	GRID_CONFIGURATION,
	WORKSPACE_VIEW,
} from '../constants/BiaImpactAssessmentConstants';
import ImpactAssessmentDataQuery from '../queries/BiaImpactAssessmentQuery';
import { ACCORDION_STATUS } from '../../RelatedItemAccordionList/constants/RelatedItemAccordionListConstants';
import { USER_PREFERENCE_PREFIX } from '../../../util/grid-util/constants/GeneralConstants';

const requestImpactAssessmentData = createGraphQLEffect(
	ImpactAssessmentDataQuery,
	{
		variableList: ['queryConditions'],
		templateVarList: [
			'tableName',
			'impactAssessmentChildTable',
			'impactAssessmentStatusField',
			'accordionDescriptionField',
			'accordionHeaderField',
			'impactAssessmentReferenceColumnField',
		],
		successActionType: IMPACT_ASSESSMENT_DATA_SUCCEEDED,
		errorActionType: IMPACT_ASSESSMENT_DATA_FAILED,
	}
);

const getImpactAssessmentDataQuerySuccess = ({
	action,
	state,
	updateState,
}) => {
	const fetchResult = action.payload;
	const fetchErrors = fetchResult.errors;
	const {
		tableName,
		impactAssessmentChildTable,
		impactAssessmentStatusField,
		impactAssessmentReferenceColumnField,
		accordionHeaderField,
		accordionDescriptionField,
		impactAssessmentChildTableField,
	} = state.properties;

	if (fetchErrors.length) {
		console.error(FETCH_ERROR_MESSAGE);
		console.error(fetchErrors);
		updateState({
			error: true,
			isLoading: false,
		});

		return;
	}

	const dataReturned = get(
		fetchResult,
		`data.GlideRecord_Query.${tableName}._results`
	);

	const tableRecords = dataReturned
		.map((record) => {
			record[impactAssessmentStatusField] = get(
				record,
				`${impactAssessmentStatusField}.value`
			);
			record[impactAssessmentChildTable] = get(
				record,
				`_query.GlideRecord_Query.${impactAssessmentChildTable}._results`
			);

			record[
				`${impactAssessmentReferenceColumnField}_${accordionHeaderField}`
			] = get(
				record,
				`${impactAssessmentReferenceColumnField}._reference.${accordionHeaderField}.value`
			);
			record[
				`${impactAssessmentReferenceColumnField}_${accordionDescriptionField}`
			] = get(
				record,
				`${impactAssessmentReferenceColumnField}._reference.${accordionDescriptionField}.value`
			);

			record.gridConfig = { ...GRID_CONFIGURATION };
			if (record[impactAssessmentStatusField] === ACCORDION_STATUS.COMPLETE) {
				record.readOnly = true;
			} else {
				record.readOnly = false;
			}

			record.queryCondition = `${impactAssessmentChildTableField}=${record.sys_id.value}^ORDERBYimpact_rating.value`;
			record.allowColumnHidding = false;
			record.userPreferenceName = `${USER_PREFERENCE_PREFIX}.${impactAssessmentChildTable}.${WORKSPACE_VIEW}`;
			record.isBiaDependency= false;
			delete record._query;
			delete record[impactAssessmentReferenceColumnField];

			return record;
		})
		.filter((item) => (item ? item : false));

	if (!tableRecords) {
		console.error(NULL_TABLE_ERROR_MESSAGE);
		updateState({
			error: true,
			isLoading: false,
		});

		return;
	}

	updateState({
		tableRecords,
		isLoading: false,
	});
};

const getImpactAssessmentDataQueryFailed = ({ action, updateState }) => {
	const error = action.payload;

	console.error(FETCH_ERROR_MESSAGE);
	console.error(error);
	updateState({
		error: true,
		isLoading: false,
	});
};

const actionHandlers = {
	[IMPACT_ASSESSMENT_DATA_REQUESTED]: requestImpactAssessmentData,
	[IMPACT_ASSESSMENT_DATA_SUCCEEDED]: {
		effect: getImpactAssessmentDataQuerySuccess,
		stopPropogation: true,
	},
	[IMPACT_ASSESSMENT_DATA_FAILED]: {
		effect: getImpactAssessmentDataQueryFailed,
		stopPropogation: true,
	},
};

export default actionHandlers;
