import { MODAL_CLOSE_CLICKED, ITEM_SELECTED, RECORD_DATA_CHANGED } from '../constants/HomeActionLinksActionTypes';

const actionHandlers = {
	[MODAL_CLOSE_CLICKED]: {
		effect: ({ action: { payload }, updateState }) => {
			if (payload.value === false) {
				updateState({
					isRecordModalOpen: false,
				});
			}
		},
		stopPropagation: true,
	},
	[RECORD_DATA_CHANGED]: ({ action: { payload }, dispatch }) => {
		const { table, sys_id } = payload;
		dispatch(ITEM_SELECTED, {
			table,
			sys_id,
			row: {
				sys_id: {
					value: sys_id,
				},
			},

		});
	},
};

export default actionHandlers;
