import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { RELATED_ASSET_DEPENDENCY } from '../constants/RelatedAssetDependencyItemPickerModalActionTypes';
import {
	EXCLUDE_ITEMS_QUERY_URL_FOR_RELATED_ASSET_DEPENDENCY,
	FETCH_DEPENDENCIES_FROM_BIA_QUERY_URL,
	INSERT_ITEMS_QUERY_URL_FOR_RELATED_ASSET_DEPENDENCY,
	FETCH_BIA_DEPENDENCIES_QUERY_ERROR_MESSAGE,
} from '../constants/RelatedAssetDependencyItemPickerModalConstants';
import selectionTableNameRequestQuery from '../queries/SelectionTableAndFilterQuery';
import { ACTION_TYPES } from '../../ItemPickerModal/constants/ItemPickerModalCommonConstants';

const fetchBiaDependeciesQuerySuccess = ({ state, action, updateState }) => {
	const { fetchedTableEncodedQuery } = state;
	const itemsToInclude = get(action, 'payload.result.itemsToInclude');
	const itemsToIncludeEncodedQuery = itemsToInclude.length
		? `sys_idIN${itemsToInclude.join(',')}`
		: 'sys_idIN';
	const recordListPickerFilterConditions = fetchedTableEncodedQuery
		? `${fetchedTableEncodedQuery}^${itemsToIncludeEncodedQuery}`
		: itemsToIncludeEncodedQuery;

	updateState({
		recordListPickerFilterConditions,
		isLoading: false,
	});
};

const fetchBiaDependeciesQueryFailed = ({ action }) => {
	const { data } = action.payload;

	console.error(data);
	updateState({
		isLoading: false,
		errorMessage: FETCH_BIA_DEPENDENCIES_QUERY_ERROR_MESSAGE,
	});
};

const actionHandlers = {
	[RELATED_ASSET_DEPENDENCY.ITEM_PICKER_TABLE_NAME_REQUESTED]: createGraphQLEffect(
		selectionTableNameRequestQuery,
		{
			variableList: ['parentRecordSysId'],
			templateVarList: [],
			startActionType: ACTION_TYPES.ITEM_PICKER_TABLE_NAME_STARTED,
			successActionType: ACTION_TYPES.ITEM_PICKER_TABLE_NAME_SUCCEEDED,
			errorActionType: ACTION_TYPES.ITEM_PICKER_TABLE_NAME_FAILED,
		}
	),
	[RELATED_ASSET_DEPENDENCY.EXCLUDED_ITEMS_QUERY_REQUESTED]: createHttpEffect(
		EXCLUDE_ITEMS_QUERY_URL_FOR_RELATED_ASSET_DEPENDENCY,
		{
			method: 'GET',
			queryParams: ['parentRecordSysId', 'excludedItemsTableIdentifier'],
			startActionType: ACTION_TYPES.EXCLUDED_ITEMS_QUERY_STARTED,
			successActionType: ACTION_TYPES.EXCLUDED_ITEMS_QUERY_SUCCEDED,
			errorActionType: ACTION_TYPES.EXCLUDED_ITEMS_QUERY_FAILED,
			batch: false,
		}
	),
	[RELATED_ASSET_DEPENDENCY.FETCH_BIA_DEPENDENCIES_QUERY_REQUESTED]: createHttpEffect(
		FETCH_DEPENDENCIES_FROM_BIA_QUERY_URL,
		{
			method: 'GET',
			queryParams: [
				'parentRecordSysId',
				'itemsToIncludeTableIdentifier',
				'biaSysId',
				'selectionTable',
			],
			successActionType:
				RELATED_ASSET_DEPENDENCY.FETCH_BIA_DEPENDENCIES_QUERY_SUCCEEDED,
			errorActionType:
				RELATED_ASSET_DEPENDENCY.FETCH_BIA_DEPENDENCIES_QUERY_FAILED,
			batch: false,
		}
	),
	[RELATED_ASSET_DEPENDENCY.ITEM_INSERTION_REQUESTED]: createHttpEffect(
		INSERT_ITEMS_QUERY_URL_FOR_RELATED_ASSET_DEPENDENCY,
		{
			method: 'POST',
			dataParam: 'data',
			startActionType: ACTION_TYPES.ITEM_INSERTION_STARTED,
			successActionType: ACTION_TYPES.ITEM_INSERTION_SUCCEEDED,
			errorActionType: ACTION_TYPES.ITEM_INSERTION_FAILED,
			batch: false,
		}
	),
	[RELATED_ASSET_DEPENDENCY.FETCH_BIA_DEPENDENCIES_QUERY_SUCCEEDED]: fetchBiaDependeciesQuerySuccess,
	[RELATED_ASSET_DEPENDENCY.FETCH_BIA_DEPENDENCIES_QUERY_FAILED]: fetchBiaDependeciesQueryFailed,
};

export { actionHandlers };
