const getCarouselSectionIndicator = (
	totalSections,
	currentSectionIndex,
	activeSectionColor,
	inactiveSectionColor
) => {
	const sectionItems = [];

	for (let i = 0; i < totalSections; i++) {
		const isCurrentSection = i === currentSectionIndex;

		sectionItems.push(
			<div
				className="carousel-section-marker"
				style={{
					backgroundColor: isCurrentSection
						? activeSectionColor
						: inactiveSectionColor,
				}}
			/>
		);
	}

	return (
		<div
			className={totalSections > 1 ? 'carousel-section-indicator' : 'hidden'}
		>
			{sectionItems}
		</div>
	);
};

export { getCarouselSectionIndicator };
