import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	MODAL_RECORD_DELETED,
	EDIT_RECORD_DELETE_REQUESTED,
	EDIT_RECORD_DELETE_SUCCEEDED,
	EDIT_RECORD_DELETE_FAILED,
} from '../constants/PlanAssetsListActionTypes';
import deleteRecordQuery from '../queries/DeleteModalRecordQuery';
import { RELATED_LIST_REFRESH_REQUESTED } from '../../ItemPickerModal/constants/ItemPickerModalActionTypes';
import { SINGLE_ITEM_DELETION_ERROR_MESSAGE } from '../constants/PlanAssetsListConstants';

const recordDeleteEffect = createGraphQLEffect(deleteRecordQuery, {
	variableList: ['sysId'],
	templateVarList: ['table'],
	successActionType: EDIT_RECORD_DELETE_SUCCEEDED,
	errorActionType: EDIT_RECORD_DELETE_FAILED,
});

const deleteAssetListRecord = ({ action, dispatch }) => {
	const { payload } = action;
	const { sysId, table } = payload;

	dispatch(EDIT_RECORD_DELETE_REQUESTED, {
		sysId,
		table,
	});
};

const actionHandlers = {
	[MODAL_RECORD_DELETED]: {
		effect: deleteAssetListRecord,
		stopPropogation: true,
	},
	[EDIT_RECORD_DELETE_REQUESTED]: recordDeleteEffect,
	[EDIT_RECORD_DELETE_SUCCEEDED]: ({ dispatch, updateState }) => {
		updateState({
			isEditItemModalOpen: false,
		});
		dispatch(RELATED_LIST_REFRESH_REQUESTED);
	},
	[EDIT_RECORD_DELETE_FAILED]: ({ updateState, action }) => {
		const error = action.payload;

		updateState({
			isEditItemModalOpen: false,
			errorMessage: error.message,
		});
		console.error(SINGLE_ITEM_DELETION_ERROR_MESSAGE);
		console.error(error);
	},
};

export default actionHandlers;
