import { MODAL_CLOSE_CLICKED } from '../constants/PlanAssetsListActionTypes';

const actionHandlers = {
    [MODAL_CLOSE_CLICKED]: {
        effect: ({ updateState }) => {
            updateState({
                isEditItemModalOpen: false,
                isAddItemsModalOpen: false,
            });
        },
        stopPropogation: true,
    }
};

export default actionHandlers;
