import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './PlanAssetsListView';
import styles from './PlanAssetsListElement.scss';
import recordListActionHandlers from './actions/RecordListActions';
import assetListLifeCycleActionHandlers from './actions/PlanAssetListLifeCycleActions';
import itemPickerModalActionHandlers from './actions/ItemPickerModalButtonActions';
import modalVisbilityActions from './actions/ModalVisibilityActions';
import {
	actions as cardHeaderButtonActions,
	actionHandlers as cardHeaderButtonActionHandlers,
} from './actions/CardHeaderButtonActions';
import editModalActionHandlers from './actions/EditModalActions';
import {
	RECORD_LIST_TABLE_NAME,
	RECORD_LIST_COLUMNS,
	PARENT_TABLE_REFERENCE_FIELD,
	ELEMENT_DEFINITION_TABLE_REFERENCE_FIELD,
	PARENT_TABLE_ITEM_PICKER_REFERENCE_FIELD,
	CARD_HEADER_LABEL,
	RECORD_PARENT_TABLE_NAME,
	DEFAULT_RECORD_MODAL_HEADING,
	DEFAULT_EDIT_MODAL_FORM_VIEW,
} from './constants/PlanAssetsListConstants';

const TAG_NAME = 'sn-bcm-plan-assets-list';

/**
 * A card overview widget, intended for use on workpsace overview tab.
 * Displays a card with list of assets to be recovered for a plan.
 *
 * ```
 * <sn-bcm-plan-assets-list
 * 		parentTable="parentTable"
 * 		parentRecordSysId="parentTableRecordSysId"
 * 		table="sn_bcp_plan_Asset"
 *
 * ></sn-bcm-plan-assets-list>
 *
 * ```
 *
 * @seismicElement sn-bcm-plan-assets-list
 * @summary Displays a card with list of assets to be recovered for plan.
 */
createCustomElement(TAG_NAME, {
	renderer: { type: snabbdom },
	view,

	properties: {
		/**
		 * The table name for which record list component will be displayed on. Default is sn_bcp_plan_asset
		 *
		 * @type {string}
		 */
		table: {
			default: RECORD_LIST_TABLE_NAME,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The name of the parent table where this modal action is used, not the related table name.
		 *
		 * @type {string}
		 */
		parentTable: {
			default: RECORD_PARENT_TABLE_NAME,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * parent record sysid of the record the overview will be displayed on
		 *
		 * @type {string}
		 */
		parentRecordSysId: {
			required: true,
			schema: {
				type: 'string',
			},
		},
		/**
		 * List of columns to be displayed in record list
		 *
		 * @type {string}
		 */
		recordListColumns: {
			default: RECORD_LIST_COLUMNS,
			required: false,
			schema: {
				type: 'string',
			},
		},

		/**
		 * The reference field name on the parent table which refers to table that has reference to element definition.
		 * Example: template field on sn_bcp_plan Table.
		 *
		 * @type {String}
		 */
		parentTableItemPickerReferenceField: {
			default: PARENT_TABLE_ITEM_PICKER_REFERENCE_FIELD,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The reference field name of the element defintion table on parent table.
		 * Example: primary_element_recovered field on sn_bcp_template table.
		 *
		 * @type {String}
		 */
		elementDefinitionTableReferenceField: {
			default: ELEMENT_DEFINITION_TABLE_REFERENCE_FIELD,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The reference field name of the parent table on related table.
		 *
		 * @type {String}
		 */
		parentTableReferenceField: {
			default: PARENT_TABLE_REFERENCE_FIELD,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * The card header label to be displayed.
		 *
		 * @type {String}
		 */
		cardHeaderLabel: {
			default: CARD_HEADER_LABEL,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Name of the form view to display in the edit-modal.
		 *
		 * @type {String}
		 */
		recordModalFormView: {
			default: DEFAULT_EDIT_MODAL_FORM_VIEW,
			required: false,
			schema: {
				type: 'string',
			},
		},
		/**
		 * Text to display in the edit-modal heading.
		 *
		 * @type {String}
		 */
		recordModalHeading: {
			default: DEFAULT_RECORD_MODAL_HEADING,
			required: false,
			schema: {
				type: 'string',
			},
		},
	},
	initialState: {
		isAddItemsModalOpen: false,
		isEditItemModalOpen: false,
		editItemModalConfig: {},
		currentlySelectedEncodedQuery: '',
		refreshRequested: {},
		errorMessage: '',
		isParentRecordEditable: true,
	},
	actions: {
		...cardHeaderButtonActions,
	},
	actionHandlers: {
		...recordListActionHandlers,
		...itemPickerModalActionHandlers,
		...cardHeaderButtonActionHandlers,
		...modalVisbilityActions,
		...assetListLifeCycleActionHandlers,
		...editModalActionHandlers,
	},
	styles,
});

export default TAG_NAME;
