import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import {
	LEFT_CAROUSEL_SCROLL_REQUESTED,
	RIGHT_CAROUSEL_SCROLL_REQUESTED,
} from './constants/DynamicCarouselActionTypes';
import { getCarouselSectionIndicator } from './util/DynamicCarouselUtil';
import '@servicenow/now-button';

const view = (state, { dispatch }) => {
	const {
		startDisplayIndex,
		endDisplayIndex,
		carouselSectionsCount,
		currentSectionIndex,
		properties,
	} = state;
	const {
		elements,
		activeSectionIndicatorColor,
		inactiveSectionIndicatorColor,
		carouselScrollButtonSize,
	} = properties;
	const carouselSectionDots = getCarouselSectionIndicator(
		carouselSectionsCount,
		currentSectionIndex,
		activeSectionIndicatorColor,
		inactiveSectionIndicatorColor
	);
	const elementsToDisplay = elements.slice(startDisplayIndex, endDisplayIndex);
	const carouselButtons = (
		<Fragment>
			<now-button-bare
				className={
					carouselSectionsCount > 1 ? 'left-carousel-button' : 'hidden'
				}
				icon-start="arrow-left-fill"
				variant="secondary"
				size={carouselScrollButtonSize}
				on-click={() => {
					dispatch(LEFT_CAROUSEL_SCROLL_REQUESTED);
				}}
			/>
			<now-button-bare
				className={
					carouselSectionsCount > 1 ? 'right-carousel-button' : 'hidden'
				}
				icon-start="arrow-right-fill"
				variant="secondary"
				size={carouselScrollButtonSize}
				on-click={() => {
					dispatch(RIGHT_CAROUSEL_SCROLL_REQUESTED);
				}}
			/>
		</Fragment>
	);

	const carouselElement = (
		<div className="carousel-container">
			<div className="carousel-element">
				{elementsToDisplay}
				{carouselButtons}
			</div>
			{carouselSectionDots}
		</div>
	);

	return carouselElement;
};

export default view;
