import '@servicenow/now-icon';
import '@servicenow/now-label-value';

const _getNowLabelValueProps = (item) => ({
	align: 'vertical',
	size: 'md',
	itemMinWidth: '',
	delimiter: ',',
	items: [item],
});

const getIconLabelValueElement = ({ icon, nowLabelValueStackedProps }) => (
	<div className="bia-summary-card__col-item">
		{icon ? (
			<now-icon icon={icon} size="sm" className="pre-label-icon" />
		) : null}
		<now-label-value-stacked
			{..._getNowLabelValueProps(nowLabelValueStackedProps)}
		/>
	</div>
);

export default getIconLabelValueElement;
