import { getCardElement } from './PieChartCardUtil';

const view = (state) => {
	const { properties } = state;
	const {
		cardHeaderLabel,
		fillHeight,
		seriesData,
		chartColors,
		lastUpdated,
		type,
		height,
		width,
		extraMargin,
		showDataLabels,
		showTotal,
		totalLabel,
		showTooltip,
		hideRegister,
		registerPosition,
		legendOnly,
		showPercentages,
		totalLabelSize,
		tooltipFontSize,
		registerFontSize,
		orderBy,
		cardInteraction,
		isLoading,
		loadingMessage,
		isChartAccessibilityModeActive,
		showEmptyState,
		cardSize,
		innerRadius,
	} = properties;

	const donutChartProperties = {
		type,
		width,
		height,
		seriesConfig: {
			series1: {
				x: 'name',
				y: 'count',
				colorPalette: chartColors,
			},
		},
		seriesData,
		extraMargin,
		showDataLabels,
		dataLabelConfig: {
			showTotal,
			totalLabelSize,
			totalLabel,
			valueType: 'value',
		},
		orderBy,
		showTooltip,
		tooltipConfig: {
			config: {
				fontSize: tooltipFontSize,
			},
		},
		hideRegister,
		registerPosition,
		registerConfig: {
			legendOnly,
			showPercentages,
			config: {
				fontSize: registerFontSize,
			},
		},
		accessibilityMode: isChartAccessibilityModeActive,
		accessibilityConfig: {
			dashPatterns: false,
			monochrome: false,
			strokeWidth: false,
			opacity: false,
			markers: false,
		},
		innerRadius,
		yFormat: ',d', // d3 formatter http://bl.ocks.org/zanarmstrong/05c1e95bf7aa16c4768e
	};

	const pieChartCardElement = getCardElement(
		cardHeaderLabel,
		fillHeight,
		donutChartProperties,
		lastUpdated,
		cardInteraction,
		isLoading,
		loadingMessage,
		showEmptyState,
		cardSize
	);

	return pieChartCardElement;
};

export default view;
