import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import {
	FETCH_ELEMENT_DEFINITIONS,
	FETCH_ELEMENT_DEFINITIONS_SUCCEEDED,
	FETCH_ELEMENT_DEFINITIONS_FAILED,
} from '../constants/ItemPickerModalActionTypes';
import elementTableQuery from '../querries/ElementDefinitionDataQuery';

const fetchElementData = createGraphQLEffect(elementTableQuery, {
	variableList: [],
	templateVarList: [],
	successActionType: FETCH_ELEMENT_DEFINITIONS_SUCCEEDED,
	errorActionType: FETCH_ELEMENT_DEFINITIONS_FAILED,
});

const getElementDataQuerySuccess = ({ action, updateState }) => {
	const dataResults = get(
		action.payload,
		`data.GlideRecord_Query.sn_bcm_element_definition._results`
	);

	const listOfElements = dataResults.map((record) => {
		record['id'] = get(record, `source_table.value`);
		record['label'] = get(record, `name.displayValue`);
		record['fields'] = get(record, `source_table_fields.value`);
		return record;
	});

	updateState({
		listOfElements
	});
};

const getElementDataQueryFailure = ({ action }) => {
	const error = action.payload;

	console.error(error);
};

const actionHandlers = {
	[FETCH_ELEMENT_DEFINITIONS]: fetchElementData,
	[FETCH_ELEMENT_DEFINITIONS_SUCCEEDED]: getElementDataQuerySuccess,
	[FETCH_ELEMENT_DEFINITIONS_FAILED]: getElementDataQueryFailure,
};

export default actionHandlers;
